import React, {Component} from 'react';
import {CssBaseline, FormControl, FormControlLabel, FormGroup, Grid, Paper, Typography} from "@material-ui/core";
import {Field, Form} from "react-final-form";
import {Checkbox, TextField} from "final-form-material-ui";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import {DatePickerWrapper, TimePickerWrapper} from "../../../fields/DatePickers";
import {CancelButton, SaveButtonSpinner} from "../../../buttons/StandartButtons";
import GlobalContext from "../../../../GlobalContext";
import FormHelperText from "@material-ui/core/FormHelperText";
import {t, Trans} from "@lingui/macro";

class SubEventForm extends Component {

    static contextType = GlobalContext;

    state = {

    };

    render() {

        const ctx = this.context;

        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1000 }}>
                <CssBaseline />
                <Typography variant="h4" align="left" component="h1" gutterBottom>
                    {
                        this.props.title ? this.props.title : <Trans>Crear evento alterno</Trans>
                    }
                </Typography>

                <Form
                    onSubmit={this.props.onSubmit}
                    initialValues={this.props.initialValues}
                    validate={this.props.validate}
                    render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                        <form onSubmit={handleSubmit} noValidate>
                            <Paper style={{ padding: 16 }}>
                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                    Datos generales
                                </Typography>
                                <Grid container style={{marginTop: 10}} alignItems="flex-start" spacing={2}>
                                    <Grid item xs={12}>
                                        <Field
                                            fullWidth
                                            required
                                            name="name"
                                            component={TextField}
                                            type="text"
                                            label="Título"
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                        <Grid item xs={6}>
                                            <Field
                                                name="startDate"
                                                component={DatePickerWrapper}
                                                fullWidth
                                                required
                                                // margin="normal"
                                                label="Fecha inicial"
                                                inputVariant="outlined"
                                            />
                                        </Grid>
                                        <Grid item xs={6}>
                                            <Field
                                                name="endDate"
                                                component={DatePickerWrapper}
                                                fullWidth
                                                required
                                                // margin="normal"
                                                label="Fecha final"
                                                inputVariant="outlined"
                                            />
                                        </Grid>
                                        <Grid item xs={6}>
                                            <Field
                                                name="startTime"
                                                component={TimePickerWrapper}
                                                fullWidth
                                                required
                                                // margin="normal"
                                                label="Hora inicial"
                                                inputVariant="outlined"
                                            />
                                        </Grid>
                                        <Grid item xs={6}>
                                            <Field
                                                name="endTime"
                                                component={TimePickerWrapper}
                                                fullWidth
                                                required
                                                // margin="normal"
                                                label="Hora final"
                                                inputVariant="outlined"
                                            />
                                        </Grid>
                                    </MuiPickersUtilsProvider>


                                    <Grid item xs={12}>
                                        <Field
                                            fullWidth
                                            required
                                            name="place"
                                            component={TextField}
                                            type="text"
                                            label="Lugar"
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Field
                                            fullWidth
                                            required
                                            name="address"
                                            component={TextField}
                                            multiline
                                            label="Dirección"
                                            variant="outlined"
                                        />
                                    </Grid>

                                    <Grid item xs={12}>
                                        <FormControlLabel
                                            disabled={this.props.isUpd}
                                            label="¿Requiere acceso por código QR?"
                                            control={
                                                <Field
                                                    name="requireQrCodeAccess"
                                                    component={Checkbox}
                                                    type="checkbox"
                                                />
                                            }
                                        />
                                        <FormHelperText id="component-helper-text">Al crear un evento alterno, no es posible actualizar este campo.</FormHelperText>
                                    </Grid>

                                    <Grid item xs={12} style={{marginTop: 20}}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Perfil de invitados
                                        </Typography>
                                        <FormControl component="fieldset">
                                            <FormGroup row>
                                                {
                                                    this.props.roles.map(
                                                        (rol, index) => (
                                                            <FormControlLabel
                                                                disabled={this.props.isUpd}
                                                                key={index}
                                                                label={rol.name}
                                                                control={
                                                                    <Field
                                                                        name="groups"
                                                                        component={Checkbox}
                                                                        type="checkbox"
                                                                        value={rol._id}
                                                                    />
                                                                }
                                                            />
                                                        )
                                                    )
                                                }
                                            </FormGroup>
                                            <FormHelperText id="component-helper-text">Al crear un evento alterno, no es posible actualizar los perfiles de los invitados.</FormHelperText>
                                        </FormControl>
                                    </Grid>



                                    {submitError && <div className="error">{submitError}</div>}
                                    <Grid item style={{ marginTop: 16 }} xs={12}>

                                        <CancelButton
                                            to={`/${ctx.slug}/admin/sub-event/list/`}
                                        />

                                        <SaveButtonSpinner
                                            loading={this.props.loading}
                                        />
                                    </Grid>
                                </Grid>
                            </Paper>
                        </form>
                    )}
                />
            </div>
        );
    }
}

export default SubEventForm;