import React, { Component } from 'react';
import { Grid, Paper } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import { CancelButton, SaveButtonSpinner } from "../../../buttons/StandartButtons";
import GlobalContext from "../../../../GlobalContext";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import codes from 'iso-language-codes';
import Select from '@material-ui/core/Select';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import { Trans } from '@lingui/macro';
class FormLangs extends Component {

    static contextType = GlobalContext;

    render() {
        let ctx = this.context;
        return (
            <React.Fragment>
                <Paper style={{ padding: '20px' }}>
                    <Typography variant="h6" gutterBottom>
                        {this.props.title}
                    </Typography>
                    <br />

                    <form onSubmit={this.props.handleSubmit}>

                        <Grid container spacing={3}>

                            <Grid item xs={12} md={6}>
                                <InputLabel ><Trans>Etiqueta de lenguaje</Trans></InputLabel>
                                <TextField disabled={this.props.showActive} variant="outlined" required name="name" id="code" value={this.props.name} onChange={this.props.handleInputChange} fullWidth />
                            </Grid>

                            <Grid item xs={12} md={6}>
                                <InputLabel ><Trans>Código de lenguaje</Trans></InputLabel>
                                <Select disabled={this.props.showActive} variant="outlined" required label="Lenguaje" name='code' fullWidth value={this.props.code} onChange={this.props.handleInputChange} >
                                    {
                                        codes.map((code, i) => {
                                            return (<MenuItem key={i} value={code.iso639_1}>{code.iso639_1} - ({code.name})</MenuItem>)
                                        })
                                    }
                                </Select>
                            </Grid>

                            {
                                this.props.showActive ?
                                    <Grid item xs={12} md={6}>
                                        <FormControlLabel control={
                                                <Checkbox checked={this.props.isActive} name={'isActive'} onChange={this.props.handleInputChange} value="true" />
                                            } label="Activo" />
                                    </Grid> : ''
                            }

                            <Grid item xs={12}>
                                <CancelButton to={`/${ctx.slug}/admin/lang/list/`} />
                                <SaveButtonSpinner loading={this.props.loading} />
                            </Grid>
                        </Grid>

                    </form>

                </Paper>
            </React.Fragment>
        );
    }
}

export default FormLangs;