import React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import {FormControl, FormControlLabel, FormGroup, Grid, Paper, Typography} from "@material-ui/core";
import {Field, Form} from "react-final-form";
import Box from "@material-ui/core/Box";
import Image from "material-ui-image/lib/components/Image/Image";
import FileBase64 from "react-file-base64";
import {Checkbox, TextField, Radio, Select} from "final-form-material-ui";
import {CancelButton, SaveButtonSpinner} from "../../buttons/StandartButtons";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import MenuItem from "@material-ui/core/MenuItem";
import {ApiServiceEvents} from "../../../api-services/events";
import {GlobalContextBuilder} from "../../../GlobalContext";
import _ from "lodash";
import { Trans } from '@lingui/macro';

const apiEvent = new ApiServiceEvents();


class ConfigCard extends React.Component{

    static contextType = GlobalContextBuilder;

    state = {
        image: 'https://x.kinja-static.com/assets/images/logos/placeholders/default.png',
        imageName: '',
        imageType: 'imageBase64',
        viewsList: [],
        loading: true,
        pollList:[],
        initValues: {
            title: '',
            description: '',
            buttonType: '',
            urlRedirect: '',
            viewId: '',
            phoneNumber: '',
        },
        propertiesRaw: {
            image: {
                value: '',
                type: 'imageBase64',
                name: ''
            },
            title: {
                value: '',
                type: 'string'
            },
            description: {
                value: '',
                type: 'string'
            },
            buttonType: {
                value: '',
                type: 'string'
            },
            buttonName: {
                value: '',
                type: 'string'
            },
            buttonAlign: {
                value: '',
                type: 'string'
            },
            urlRedirect: {
                value: '',
                type: 'string'
            },
            viewId: {
                value: '',
                type: 'string'
            },
            phoneNumber: {
                value: '',
                type: 'string'
            },
        }
    };

    getRequest = (values) => {
        return {
            image: {
                value: this.state.image,
                type: _.has(this, 'state.propertiesRaw.image.type') ? this.state.propertiesRaw.image.type : 'imageBase64',
                name: this.state.imageName
            },
            title: {
                value: values.title ? values.title : '',
                type: 'string'
            },
            description: {
                value: values.description ? values.description : '',
                type: 'string'
            },
            buttonType: {
                value: values.buttonType ? values.buttonType : '',
                type: 'string'
            },
            buttonAlign: {
                value: values.buttonAlign ? values.buttonAlign : '',
                type: 'string'
            },
            buttonName: {
                value: values.buttonName ? values.buttonName : '',
                type: 'string'
            },
            urlRedirect: {
                value: values.urlRedirect ? values.urlRedirect : '',
                type: 'string'
            },
            pollId: {
                value: values.pollId,
                type: 'string'
            },
            viewId: {
                value: values.viewId ? values.viewId : '',
                type: 'string'
            },
            phoneNumber: {
                value: values.phoneNumber ? values.phoneNumber : '',
                type: 'string'
            },
        }
    };

    handleSubmit = (values) => {
        this.props.onSaveConfig(this.getRequest(values));
    };

    validate = (values) => {
        // console.log(values);
    };

    getFiles=(files)=>{
        // console.log('Files', files);
        if(files.base64) {
            let newState = this.state;
            newState.image = files.base64;
            newState.imageName = files.name;
            newState.propertiesRaw.image.type = 'imageBase64';
            this.setState({
                image: files.base64,
                imageName: files.name,
            })
        }
    };


    getPolls=()=>{
        let ctx = this.context;
        let slug = ctx.slug;
        apiEvent.getPollEvents(slug).then((res)=>{
            this.setState({
                pollList:res
            })
        });
    }

    getViews = () => {
        let ctx = this.context;
        let moduleId = ctx._idModule;
        // console.log('module', moduleId);
        apiEvent.getViewsList(moduleId).then(
            (res) => {
                let array = [];
                res.map((view,i)=>{
                    array.push(view.views)
                });

                if(array.length>0){
                    this.setState({
                        viewsList:array
                    })
                }
            }
        ).catch(
            (err) => {
                console.log('Error en carga de vistas', err);
            }
        );
    };

    deserializeProperties = (properties) => {
        return {
            image: this.context.getImage(properties, false),
            title: _.has(properties, 'title.value') ? properties.title.value : '',
            description: _.has(properties, 'description.value') ? properties.description.value : '',
            buttonType: _.has(properties, 'buttonType.value') ? properties.buttonType.value : '',
            buttonAlign: _.has(properties, 'buttonAlign.value') ? properties.buttonAlign.value : '',
            urlRedirect: _.has(properties, 'urlRedirect.value') ? properties.urlRedirect.value : '',
            viewId: _.has(properties, 'viewId.value') ? properties.viewId.value : '',
            phoneNumber: _.has(properties, 'phoneNumber.value') ? properties.phoneNumber.value : '',
            pollId: _.has(properties, 'pollId.value') ? properties.pollId.value : '',
            buttonName: _.has(properties, 'buttonName.value') ? properties.buttonName.value : '',
        }
    };

    setInitialValues = () => {
        const ctx = this.context;
        const component = ctx.currentComponent;


        if(!component.properties) {
            this.setState({loading:false});
            return;
        }

        let properties = this.deserializeProperties(component.properties);
        let image = properties.image;

        let newState = this.state;
        newState.image = image;
        newState.initValues = properties;
        newState.loading = false;
        newState.propertiesRaw = component.properties;
        this.setState(newState);

    };

    componentDidMount() {
        this.getViews();
        this.setInitialValues();
        this.getPolls();
    }


    render () {

        let imageToView = this.state.image;
        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                {
                    this.state.loading ? '' : (
                        <Form
                            onSubmit={this.handleSubmit}
                            initialValues={this.state.initValues}
                            validate={this.validate}
                            render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                                <form onSubmit={handleSubmit} noValidate>
                                    <Paper style={{ padding: 16 }}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Propiedades
                                        </Typography>
                                        <Grid container alignItems="flex-start" spacing={2}>
                                            <Grid item xs={12}>
                                                <Box align="center">
                                                    <Image
                                                        alt={'logo modulo'}
                                                        src={imageToView}
                                                        imageStyle={{
                                                            margin: 10,
                                                            width: 150,
                                                            height: 150,
                                                        }}
                                                        style={{
                                                            margin: 10,
                                                            width: 150,
                                                            height: 150,
                                                            paddingTop: 30
                                                        }}
                                                    /><br/>

                                                    <FileBase64
                                                        onDone={this.getFiles} />
                                                </Box>
                                            </Grid>

                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="title"
                                                    component={TextField}
                                                    multiline
                                                    label="Título"
                                                    variant="outlined"
                                                />
                                            </Grid>
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="description"
                                                    component={TextField}
                                                    multiline
                                                    label="Área de texto"
                                                    variant="outlined"
                                                    rows="5"
                                                    margin="normal"
                                                />
                                            </Grid>

                                            <Grid item xs={12} style={{marginTop: '20px'}}>
                                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                                    <Trans>Propiedades del botón</Trans>
                                                </Typography>
                                            </Grid>

                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="buttonName"
                                                    component={TextField}
                                                    label="Etiqueta del botón"
                                                    variant="outlined"
                                                    margin="normal"
                                                />
                                            </Grid>
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="buttonAlign"
                                                    component={Select}
                                                    label="Alineación"
                                                    formControlProps={{fullWidth: true}}
                                                    // variant="outlined"
                                                >
                                                        <MenuItem value={'left'}>Izquierda</MenuItem>
                                                        <MenuItem value={'center'}>Centro</MenuItem>
                                                        <MenuItem value={'right'}>Derecha</MenuItem>
                                                </Field>

                                            </Grid>

                                            <Grid item xs={12} style={{marginTop: '20px'}}>
                                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                                    Acción del botón
                                                </Typography>
                                            </Grid>

                                            <Grid item>
                                                <FormControl component="fieldset">
                                                    <FormLabel component="legend">Tipo de acción del botón</FormLabel>
                                                    <RadioGroup row>
                                                        <FormControlLabel
                                                            label="Redirección a una página externa"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="redirect"
                                                                />
                                                            }
                                                        />
                                                        <FormControlLabel
                                                            label="Redirección a una vista"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="router"
                                                                />
                                                            }
                                                        />

                                                        <FormControlLabel
                                                            label="Mostrar encuesta"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="poll"
                                                                />
                                                            }
                                                        />

                                                        <FormControlLabel
                                                            label="Hacer llamada"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="phone"
                                                                />
                                                            }
                                                        />
                                                    </RadioGroup>
                                                </FormControl>
                                            </Grid>


                                            {
                                                values.buttonType === 'redirect' ? (
                                                    <Grid item xs={12}>
                                                        <Field
                                                            fullWidth
                                                            name="urlRedirect"
                                                            component={TextField}
                                                            multiline
                                                            label="URL externa"
                                                            variant="outlined"
                                                        />
                                                    </Grid>
                                                ) : ''
                                            }

                                            {
                                                values.buttonType === 'poll' ? (
                                                    <Grid item xs={12}>
                                                        <Field
                                                            fullWidth
                                                            name="pollId"
                                                            component={Select}
                                                            label="Seleccionar un cuestionario/formulario"
                                                            formControlProps={{fullWidth: true}}
                                                            variant="outlined"
                                                        >
                                                            {
                                                                this.state.pollList.map(
                                                                    (item, index) => (
                                                                        <MenuItem key={index} value={item._id}>{item.name}</MenuItem>
                                                                    )
                                                                )
                                                            }
                                                        </Field>
                                                    </Grid>
                                                ) : ''
                                            }


                                            {
                                                values.buttonType === 'router' ? (
                                                    (
                                                        <Grid item xs={12}>
                                                            <Field
                                                                fullWidth
                                                                name="viewId"
                                                                component={Select}
                                                                label="Seleccionar una vista"
                                                                formControlProps={{fullWidth: true}}
                                                                variant="outlined"
                                                            >
                                                                {
                                                                    this.state.viewsList.map(
                                                                        (item, index) => (
                                                                            <MenuItem key={index} value={item._id}>{item.name}</MenuItem>
                                                                        )
                                                                    )
                                                                }
                                                            </Field>
                                                        </Grid>
                                                    )
                                                ) : ''
                                            }

                                            {
                                                values.buttonType === 'phone' ? (
                                                    <Grid item xs={12}>
                                                        <Field
                                                            fullWidth
                                                            name="phoneNumber"
                                                            component={TextField}
                                                            label="Teléfono a marcar"
                                                            variant="outlined"
                                                        />
                                                    </Grid>
                                                ) : ''
                                            }


                                            {submitError && <div className="error">{submitError}</div>}
                                            <Grid item style={{ marginTop: 16 }} xs={12}>

                                                <SaveButtonSpinner
                                                    loading={this.props.loading}
                                                />
                                            </Grid>
                                        </Grid>
                                    </Paper>
                                </form>
                            )}
                        />
                    )
                }

            </div>
        )
    }
}

export default ConfigCard