import React, {Component} from 'react';
import GlobalContext from "../../GlobalContext";
import {CssBaseline, FormControl, FormControlLabel, FormGroup, Grid, Paper, Typography} from "@material-ui/core";
import {Field, Form} from "react-final-form";
import {Checkbox, TextField, Input} from "final-form-material-ui";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import {DatePickerWrapper, TimePickerWrapper} from "../fields/DatePickers";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import { Trans } from '@lingui/macro';
import moment from "moment";
import {CancelButton, SaveButtonSpinner} from "../buttons/StandartButtons";
import {Link} from "react-router-dom";
import AlertText from "../alerts/AlertText";

class ImportUserForm extends Component {
    static contextType = GlobalContext;

    render() {
        let ctx = this.context;
        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 800 }}>
                <CssBaseline />
                <Typography variant="h4" align="center" component="h1" gutterBottom>
                    <Trans>Importar</Trans>
                </Typography>

                <Form
                    onSubmit={this.props.onSubmit}
                    initialValues={this.props.initialValues}
                    validate={this.props.validate}
                    render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                        <form onSubmit={handleSubmit} noValidate>
                            <Paper style={{ padding: 16 }}>
                                <Grid container style={{marginTop: 10}} alignItems="flex-start" spacing={2}>
                                    <Grid item xs={12}>
                                        <input type="file" accept=".csv" required onChange={ (e) => this.props.handleChange(e.target.files) } />
                                        <br/>
                                        <Typography variant={'p'} component={'small'} style={{color:'red'}}>
                                            * <Trans>Adjunta un archivo de formato .CSV</Trans>
                                        </Typography>

                                        {
                                            this.props.downloadCSV? <div>
                                                <Link to={this.props.linkfile} target="_blank" download><Trans>Descargar template de ejemplo</Trans></Link>
                                            </div>:''
                                        }


                                    </Grid>


                                    <Grid item xs={12} style={{marginTop: 2}}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            <Trans>Perfiles</Trans>
                                        </Typography>
                                        <FormControl component="fieldset">
                                            {/*<FormLabel component="legend">Sauces</FormLabel>*/}
                                            <FormGroup row>
                                                {
                                                    this.props.roles.map(
                                                        (rol, index) => (
                                                            <FormControlLabel
                                                                key={index}
                                                                label={rol.name}
                                                                required
                                                                control={
                                                                    <Field
                                                                        name="groups"
                                                                        component={Checkbox}
                                                                        type="checkbox"
                                                                        value={rol._id}
                                                                    />
                                                                }
                                                            />
                                                        )
                                                    )
                                                }
                                            </FormGroup>
                                        </FormControl>
                                    </Grid>


                                    {submitError && <div className="error">{submitError}</div>}
                                    <Grid item style={{ marginTop: 16 }} xs={12}>
                                        <SaveButtonSpinner
                                            loading={this.props.loading}
                                        />
                                        {
                                            this.props.buttonTemplate ? (
                                                this.props.buttonTemplate
                                            ) : ''
                                        }
                                    </Grid>
                                </Grid>
                            </Paper>
                        </form>
                    )}
                />
            </div>
        );
    }
}

export default ImportUserForm;