import React from 'react';
import MaterialTable from 'material-table';
import { Redirect } from "react-router-dom";
import CircularProgress from "@material-ui/core/CircularProgress";
import GlobalContext from "../../../../GlobalContext";
import { ApiServiceEvents } from "../../../../api-services/events";
import { t, Trans } from "@lingui/macro";
import { withI18n } from "@lingui/react";

const apiEvent = new ApiServiceEvents();
class PollListTable extends React.Component {
    static contextType = GlobalContext;

    state = {
        redirectToEdit: false,
        redirectToBuilder: false,
        redirectToAnswers: false,
        columns: [
            { title: <Trans>Encuesta</Trans>, field: 'name' }
        ]
    };


    goDownloadCSV(idPoll) {
        let urlDownloadcsv = apiEvent.downloadcsvAnswer();
        window.open(urlDownloadcsv + idPoll, '_blank')
    }

    render() {
        let props = this.props;


        let ctx = this.context;

        if (this.state.redirectToEdit) return <Redirect push to={`/${ctx.slug}/admin/poll/${this.state.selectedRow}/`} />;
        if (this.state.redirectToBuilder) return <Redirect push to={`/${ctx.slug}/admin/poll/${this.state.selectedRow}/builder-poll/`} />;
        if (this.state.redirectToAnswers) return <Redirect push to={`/${ctx.slug}/admin/poll/${this.state.selectedRow}/answers/`} />;

        // /:slug/admin/poll/:poll/answers/



        return (
            <MaterialTable
                title={<Trans>Encuestas</Trans>}
                columns={this.state.columns}
                data={props.data}
                options={{
                    actionsColumnIndex: -1,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100, 500],
                    paging: true,
                }}
                localization={ctx.tableConfig.localization}
                actions={[
                    {
                        icon: 'edit',
                        tooltip: this.props.i18n._(t`Editar`),
                        onClick: (event, rowData) => this.setState({ redirectToEdit: true, selectedRow: rowData._id })
                    },
                    {
                        icon: 'settings',
                        tooltip: this.props.i18n._(t`Configurar`),
                        onClick: (event, rowData) => this.setState({ redirectToBuilder: true, selectedRow: rowData._id })
                    },
                    // {
                    //     icon: 'list',
                    //     tooltip: 'Ver respuestas',
                    //     onClick: (event, rowData) => this.setState({redirectToAnswers: true, selectedRow: rowData._id})
                    // },
                    {
                        icon: 'cloud_download',
                        tooltip: this.props.i18n._(t`Descargar respuestas`),
                        onClick: (event, rowData) => this.goDownloadCSV(rowData._id)
                    },
                    {
                        icon: 'filterNone',
                        tooltip: this.props.i18n._(t`Duplicar`),
                        onClick: (event, rowData) => props.onCloneRow(rowData._id)
                    },
                ]}
                editable={{
                    onRowDelete: props.handlerDelete,
                }}
            />
        );
    }
}


export default withI18n()(PollListTable)