import React from "react";
import Box from "@material-ui/core/Box";
import Image from 'material-ui-image'
import Button from '@material-ui/core/Button';
import Icon from "@material-ui/core/Icon";
import {ArrowDownward, Replay} from "@material-ui/icons";


class BoxQr extends React.Component{

    /**
     * Creates an anchor element `<a></a>` with
     * the base64 pdf source and a filename with the
     * HTML5 `download` attribute then clicks on it.
     * @param  {string} pdf
     * @return {void}
     */

    render() {
        return (
                <>
                    <Box align="center">
                        <Image alt={'logo modulo'} src={this.props.imageQr} />
                    </Box>
                    <Box align="center">
                        <Button
                            size="small"
                            variant="contained"
                            color="primary"
                            startIcon={<Icon>send</Icon>}
                            onClick={this.props.openSendEmailQr}
                        >
                            Enviar al invitado
                        </Button>
                    </Box>
                    <Box align="center" style={{marginTop: 10}}>
                        <Button
                            size="small"
                            variant="contained"
                            color="default"
                            startIcon={<ArrowDownward/>}
                            onClick={this.props.downloadQr}
                        >
                            Descargar
                        </Button>
                    </Box>
                    {/*<Box align="center" style={{marginTop: 10}}>*/}
                    {/*    <Button*/}
                    {/*        variant="contained"*/}
                    {/*        size="small"*/}
                    {/*        color="default"*/}
                    {/*        spacing={1}*/}
                    {/*        startIcon={<Replay />}*/}
                    {/*    >*/}
                    {/*        Actualizar código QR*/}
                    {/*    </Button>*/}
                    {/*</Box>*/}
                </>
        )
    }
}

export default BoxQr