import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import AddIcon from '@material-ui/icons/Add';
import ViewListTable from "../../../../components/events/admin/views/ViewListTable";
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import GlobalContext from "../../../../GlobalContext";
import TableLangList from "../../../../components/events/admin/lang/TableLangList";
import {ApiServiceEvents} from "../../../../api-services/events";
import _ from "lodash";
import {Trans} from "@lingui/macro";
import ImportUserForm from "../../../../components/security/ImportUserForm";
import FullScreenDialogGeneric from "../../../../components/modals/FullScreenDialogGeneric";
import FormImportPo from "../../../../components/events/admin/lang/FormImportPO";
import {withI18n} from "@lingui/react";

const apiEvent = new ApiServiceEvents();

/**
 *
 * View that allows to display the list of languages
 * @class LangList
 * @extends {Component}
 */
class LangList extends Component {

    static contextType = GlobalContext;

    state={
        slug:null,
        data:[],
        idEvent:null,
        breadCrumbs:[
            {
                name:<Trans>Lista de lenguajes</Trans>,
                active:true,
                link:``
            },
        ],
        loadingImportPo: false,
        openImportPo: false,
        langCodeSelected: '',
        importPoFile: '',
        loading: false
    };

    componentDidMount() {
        let slug = this.props.match.params.slug;
        this.setState({
            slug,
            loading:true
        });

        let ctx = this.context;

        apiEvent.getEventInfo('',slug).then(
            (response) => {
                this.setState({
                    idEvent:response.event._id
                })

                this.getIdiomList(response.event._id);
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );

    }

    /**
     * function to get languages list 
     * @param  {number} _idEvent
     */
    getIdiomList=(_idEvent)=>{
        apiEvent.getIdiomList(_idEvent).then((res)=>{
            console.log(res)
            this.setState({
                data:res
            })
        }).catch((err)=>{
            if (_.has(err, 'response.status') && err.response.status === 403) {
                this.context.openPermissionNotification();
            }
            console.log(err)
        }).finally(()=> this.setState({loading:false}));
    };
    /**
     * function to download PO file
     * @param  {Object} content
     * @param  {string} project
     */
    downloadPo = (content, project) => {
        let data = new Blob([content], {type: 'text/plain'});
        let ctx = this.context;
        // console.log('Generando', content);
        const linkSource = content;
        const downloadLink = document.createElement("a");
        const fileName = `${project}_messages.po`;

        downloadLink.href = window.URL.createObjectURL(data);;
        downloadLink.download = fileName;
        downloadLink.click();
        ctx.openSuccessNotification(<Trans>PO descargado</Trans>);

    };
    /**
     * function to get details for PO file
     * @param  {string} project
     * @param  {Object} rowData
     */
    getPoLanguage = (project='front', rowData) => {
        let langCode = _.has(rowData, 'availableLang.code') && rowData.availableLang.code ? rowData.availableLang.code : this.context.language;
        apiEvent.getPoLanguage(langCode, project).then(
            (res) => {
                // console.log('res', res);
                this.downloadPo(res, project)
            }
        ).catch((err) => {
            console.log('err', err);
            if (_.has(err, 'response.status') && err.response.status === 403) {
                this.context.openPermissionNotification();
            }
        });
    };
    /**
     * function to validate the selection of a project when loading a PO file
     * @param  {Object} values
     * @returns {Object} errors 
     */
    validateImportPo = (values) => {
        const errors = {};
        if(!values.project){
            errors.project = <Trans>Debes seleccionar un tipo de proyecto</Trans>;
        }
        return errors;
    };

    /**
     * función para importar el archivo Po
     * @param  {Object} values
     */
    importPoLanguage = (values) => {
        const ctx = this.context;
        let poFile = this.state.importPoFile;
        if(!poFile || poFile.length < 1){
            ctx.openNotification(<Trans>Debes cargar un archivo PO</Trans>, 'error');
            return
        }
        let formData = new FormData();
        formData.append('po_file', this.state.importPoFile[0]);
        formData.append('code', this.state.langCodeSelected);
        formData.append('project', values.project);
        this.setState({loadingImportPo: true});
        apiEvent.importPoLanguage(formData).then(
            (res) => {
                console.log('res import po', res);
                this.context.openNotification(<Trans>Archivo importado.</Trans>);
                this.context.clearLanguageCatalog(this.state.langCodeSelected);
                this.setOpenDialogImportPO(false);
            }
        ).catch((err) => {
            console.log('err', err);
            if (_.has(err, 'response.status') && err.response.status === 403) {
                ctx.openPermissionNotification();
            }
        }).finally(() => {
            this.setState({loadingImportPo: false})
        });
    };

    /**
     * function to change state openImportPo
     * @param  {boolean} val
     */
    setOpenDialogImportPO = (val) => {this.setState({openImportPo: val})};

    /**
     * function to setstate in openImportPo and langCode 
     * @param  {boolean} value
     * @param  {Object} rowData
     */
    handlerDialogImportPo = (value, rowData) => {
        let langCode = _.has(rowData, 'availableLang.code') && rowData.availableLang.code ? rowData.availableLang.code : this.context.language;
        this.setState({openImportPo: value, langCodeSelected: langCode});
    };

    /**
     * Function to get the file uploaded
     * @param  {Object} selectorFiles
     */
    handleChange = (selectorFiles) => {
        console.log(selectorFiles);
        this.setState({importPoFile: selectorFiles});
    };
    
    /**
     * Function to delete selected lang
     * @param  {Object} currentLang
     */
    handlerDelete = (currentLang) =>
        new Promise(resolve => {
            let id = currentLang.availableLang._id;
            let ctx = this.context;
            let {idEvent} = this.state;
            console.log('currentLang',currentLang)


            apiEvent.deleteIdiom(id,idEvent).then((res)=>{
                console.log(res)
                this.getIdiomList(idEvent);
            }).catch((err)=>{
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
            }).finally(()=>{
                resolve()
            })

        });


    render() {
        let {slug,breadCrumbs,data} = this.state;

        return (
            <AdminEventLayout breadcrumbs={breadCrumbs} title={<Trans>Listado de vistas</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Lista de idiomas</Trans>
                            <IconButton component={Link} push='true' to={`/${slug}/admin/lang/create/`} color="primary" aria-label="">
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>
                    <Grid item xs={12}>
                            { this.state.loading ?   
                            <Paper xs={12} style={{'textAlign': "center","paddingTop":50,"paddingBottom":50}}><CircularProgress /> </Paper>
                            :
                            <TableLangList
                            handlerDelete={this.handlerDelete}
                            slug={slug}
                            data={data}
                            getPoLanguage={this.getPoLanguage}
                            handlerDialogImportPo={this.handlerDialogImportPo}
                            />
                            }
                    </Grid>
                </Grid>
                <FullScreenDialogGeneric
                    setOpen={this.setOpenDialogImportPO}
                    open={this.state.openImportPo}
                    title={<Trans>Importación de de archivo PO</Trans>}
                >
                    <FormImportPo
                        onSubmit={this.importPoLanguage}
                        handleChange={this.handleChange}
                        loading={this.state.loadingImportPo}
                        validate={this.validateImportPo}
                    />

                </FullScreenDialogGeneric>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(LangList);