import React from "react";
import moment from "moment";
import Grid from "@material-ui/core/Grid";
import CreateViewForm from "../../../../components/events/admin/views/FormCreateView";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import SingletonAvailabilityForm from "../../../../components/events/admin/availability/SingletonAvailabilityForm";
import {Typography} from "@material-ui/core";
import ModalFormGeneric from "../../../../components/modals/ModalFormGeneric";
import TextField from "@material-ui/core/TextField";
import FormQr from "../../../../components/events/admin/qr/FormQr";
import {ApiServiceEvents} from "../../../../api-services/events";
import GlobalContext from "../../../../GlobalContext";
import FacebookProgress from "../../../../components/FacebookProgress";
import { Trans } from '@lingui/macro';
import _ from "lodash";


const apiEvent = new ApiServiceEvents();

class ManageAvailability extends React.Component{
    static contextType = GlobalContext;

    state = {
        breadCrumbs: [
            {
                name:<Trans>Disponibilidad</Trans>,
                active:true,
                link:``
            },
        ],
        loading: false,
        loadingEventInfo: false,
        request: {
            validFromDate: '',
            validToDate: '',
            fromHour: '',
            toHour: '',
        }
    };
    /**
     * This function send the form data to a api save/update event availability
     * @param {Object} values data form fields
     */
    handleSubmit = (values) => {
        let ctx = this.context;
        console.log(values);
        this.setState({loading: true});
        let dateTimeFrom = '';
        let dateTimeTo = '';

        if(values.validFromDate && values.fromHour) {
            let dateFromString = moment(values.validFromDate).format();
            let fromHourString = moment(values.fromHour).format();
            let indexDateFrom = dateFromString.indexOf('T');
            let indexTimeFrom = fromHourString.indexOf('T');
            let dateFrom = dateFromString.slice(0, indexDateFrom);
            let hourFrom = fromHourString.slice(indexTimeFrom + 1, -1);
            dateTimeFrom = `${dateFrom}T${hourFrom}0`;
        }

        if(values.validToDate) {
            let dateToString = moment(values.validToDate).format();
            let toHourString = moment(values.toHour).format();
            let indexDateTo = dateToString.indexOf('T');
            let indexTimeTo = toHourString.indexOf('T');
            let dateTo = dateToString.slice(0, indexDateTo);
            let hourTo = toHourString.slice(indexTimeTo + 1, -1);
            dateTimeTo = `${dateTo}T${hourTo}0`;
        }

        let request = {
            slug: ctx.slug,
            startDate: dateTimeFrom,
            endDate: dateTimeTo,
            undefinedEnd: false,
        };
        console.log('Request', request, values);
        apiEvent.updateAvailability(request).then(
            (response) => {
                console.log('Respuesta', response);
                this.setValues(response);
                this.setState({loading: false});
                ctx.openSuccessNotification(<Trans>Temporalidad actualizado con éxito</Trans>);
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    this.context.openPermissionNotification();
                }
                console.log('Error al actualizar temporalidad del evento', err);
                this.setState({loading: false});
            }
        );
    };

    /**
     * This function retrieves event info. The data retrieved fills inputs form
     */
    getEventInfo = () => {
        let ctx = this.context;
        this.setState({
            loadingEventInfo: true,
        });
        apiEvent.getEventInfo('', ctx.slug).then(
            (info) => {
                console.log('Evento data', info);
                let availability = info.event.availability;
                this.setValues(availability);
                this.setState({
                    loadingEventInfo: false,
                });
            }
        ).catch(
            (err) => {
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                console.log('Error al consultar el detalle del evento', err);
                this.setState({
                    loadingEventInfo: false,
                });
            }
        );
    };

    
    /**
     * This function allows set data into form fields with data from api getEventInfo
     * @param  {Object} availability response from api getEventInfo 
     */
    setValues = (availability) => {
        let dateFrom = availability && availability.startDate ? moment(availability.startDate) : '';
        let dateTo = availability && availability.endDate ? moment(availability.endDate) : '';
        // console.log('Start date', dateFrom, 'To date', dateTo);
        let oldState = this.state;
        oldState.request = {
            validFromDate: dateFrom,
            validToDate: dateTo,
            fromHour: dateFrom,
            toHour: dateTo,
        };
        this.setState(oldState);
    };

    componentDidMount() {
        this.getEventInfo();
    }

    render() {
        let loadingEventInfo = this.state.loadingEventInfo;
        return (
            <AdminEventLayout title={<Trans>Administrador de eventos</Trans>} breadcrumbs={this.state.breadCrumbs}>
                <Grid container>
                    <Grid item xs={12}>
                        <div style={{ padding: 16, margin: 'auto', maxWidth: 800 }}>
                            <Typography variant="h4" align="center" component="h1" gutterBottom>
                                <Trans>Disponibilidad del evento</Trans> { loadingEventInfo ? (<FacebookProgress />) : ''}
                            </Typography>
                            <SingletonAvailabilityForm
                                onSubmit={this.handleSubmit}
                                initialValues={this.state.request}
                                loading={this.state.loading}
                            />
                        </div>
                    </Grid>
                </Grid>
            </AdminEventLayout>
        )
    }
}

export default ManageAvailability