import React, {Component} from "react";
import {Grid, makeStyles, Paper, withStyles} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import GlobalContext from "../../../../GlobalContext";
import InputBase from '@material-ui/core/InputBase';
import {CancelButton, SaveButtonSpinner} from "../../../buttons/StandartButtons";
import Checkbox from "@material-ui/core/Checkbox";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import _ from 'lodash'

const BootstrapInput = withStyles(theme => ({
    root: {
        'label + &': {
            marginTop: theme.spacing(3),
        },
    },
    input: {
        borderRadius: 4,
        position: 'relative',
        backgroundColor: theme.palette.background.paper,
        border: '1px solid #ced4da',
        fontSize: 16,
        padding: '10px 26px 10px 12px',
        transition: theme.transitions.create(['border-color', 'box-shadow']),
        // Use the system font instead of the default Roboto font.
        fontFamily: [
            '-apple-system',
            'BlinkMacSystemFont',
            '"Segoe UI"',
            'Roboto',
            '"Helvetica Neue"',
            'Arial',
            'sans-serif',
            '"Apple Color Emoji"',
            '"Segoe UI Emoji"',
            '"Segoe UI Symbol"',
        ].join(','),
        '&:focus': {
            borderRadius: 4,
            borderColor: '#80bdff',
            boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
        },
    },
}))(InputBase);

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    margin: {
        margin: theme.spacing(1),
    },
}));

class PollsForm extends React.Component{
    static contextType = GlobalContext;

    componentDidMount() {

    }

    handleCheckChange = id => event =>{
        let isChecked = event.target.checked;
        let {groupsSelected} = this.props;
        let existInArray = groupsSelected.includes(id); // validamos que exista en el arreglo de grupos

        if(!existInArray && isChecked){
            groupsSelected.push(id)
        }else if(existInArray && !isChecked){
            groupsSelected = _.remove(groupsSelected, function (o) {
                return o !== id
            });
        }
        this.props.handleCheckGroups(groupsSelected);
    }



    render() {
        let ctx = this.context;
        let slug = ctx.slug;
        let {groups,groupsId,groupsSelected,urlCurrentFile} = this.props;
        return (
            <React.Fragment>
                <Paper style={{padding:'20px'}}>
                    <Typography variant="h6" gutterBottom>
                        {this.props.titleForm}
                    </Typography>
                    <br/>

                    <form onSubmit={this.props.handleSubmit}>

                        <Grid container spacing={3}>

                            <Grid item xs={12} md={12}>
                                <TextField variant="outlined" required name="name" id="name" label="Nombre de la encuesta" value={this.props.name} onChange={this.props.handleInputChange} fullWidth />
                            </Grid>




                        </Grid>

                        <br/>
                        <Typography variant="h6" gutterBottom>
                            Roles de seguridad
                        </Typography>
                        <br/>

                        <Grid container spacing={3}>

                            <Grid item xs={12} md={12}>
                                {
                                    groups.map((group, i)=>{
                                        return (
                                            <FormControlLabel
                                                key={group._id}
                                                control={<Checkbox checked={groupsSelected.includes(group._id)}
                                                                   onChange={this.handleCheckChange(group._id)}
                                                                   value={group._id} />}
                                                label={group.name}
                                            />
                                        )
                                    })
                                }
                            </Grid>



                            <Grid item xs={12}>


                                <CancelButton
                                    to={`/${slug}/admin/poll/list/`}
                                />

                                <SaveButtonSpinner
                                    loading={this.props.loading}
                                />
                            </Grid>
                        </Grid>
                    </form>

                </Paper>
            </React.Fragment>
        )
    }
}

export default PollsForm