import React from "react";
import Grid from "@material-ui/core/Grid";
import CreateModuleForm from "../../../../components/events/admin/modules/CreateModuleForm";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import GlobalContext from "../../../../GlobalContext";
import {ApiServiceEvents} from "../../../../api-services/events";
import {Redirect, withRouter} from "react-router-dom";
import FacebookProgress from "../../../../components/FacebookProgress";
import {t, Trans} from "@lingui/macro";
import {withI18n} from "@lingui/react";


const apiEvent = new ApiServiceEvents();


function intersection(a, b) {
    return a.filter(value => b.indexOf(value) !== -1);
}

function union(a, b) {
    return [...a, ...not(b, a)];
}

function not(a, b) {
    return a.filter(value => b.indexOf(value) === -1);
}

/**
 *
 * This view allows you to update a module
 * @class UpdateModule
 * @extends {React.Component}
 */
class UpdateModule extends React.Component{

    static contextType = GlobalContext;

    // leftChecked = () => {
    //     intersection(checked, left);
    // };
    // rightChecked = () => {
    //     intersection(checked, right);
    // };

    state = {
        logo: {
            value: 'data:image/png;base64,',
            name: 'example.png',
            type: 'imageBase64'
        },
        eventInfo: '',
        availableGroups: [],
        availableGroupsIds: [],
        selectedGroupsIds: [],
        checked: [],
        name: '',
        errorMessage: '',
        showNotiError: false,
        redirect: false,
        eventId: '',
        loadingDetail: true,
        status: 0,
        loading: false,
        breadCrumbs:[],
        changeLogo: false,
        isPublic: false,
    };

    /**
     * Function to set state on isPublic when a checkbox change 
     */
    handleChangeCheck = () => {
        this.setState({
            isPublic: !(this.state.isPublic)
        })
    };

    /**
     * Function to get image load
     * @param  {Object} files
     */
    getFiles=(files)=>{
        if(files.base64) {
            let image = {
                value: files.base64,
                type: 'imageBase64',
                name: files.name
            };
            this.setState({
                logo: image,
                changeLogo: true
            })
        }
    };

    
    /**
     * Function to set state when an input changes
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const ctx = this.context
        let value = null
        const name = event.target.name;
        const patternAlphanum = /^[,.;a-zA-ZÀ-ÿ\d\s-_]+$/ig

        if(event.target.name === 'name'){
            if(event.target.value.length > 50){
                event.target.value = event.target.value.slice(0,50);
                ctx.openNotification('Límite de caracteres excedido', 'error');
            } else {
                if(!patternAlphanum.test(event.target.value)){
                    event.target.value = event.target.value.slice(0,-1);
                    ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
                }
            }
        }
        value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;
        this.setState({
            [name]: value
        });
    };

    
    /**
     * Function to setstate on availableGroupsIds from item selected
     * @param  {Object} items
     */
    handleSetGroup = (items) => {
        let oldState = this.state;
        oldState.availableGroupsIds = items;
        this.setState(oldState);
    };

    
    /**
     * Function to set state on selectedGroupsIds from items selected
     * @param  {Object} items
     */
    handleSetGroupSelected = (items) => {
        let oldState = this.state;
        oldState.selectedGroupsIds = items;
        this.setState(oldState);
    };

    
    /**
     * Function to set state on checkedItems from items selected
     * @param  {Object} checkedItems
     */
    handleSetChecked = (checkedItems) => {
        let oldState = this.state;
        oldState.checked = checkedItems;
        this.setState(oldState);
    };

    /**
     * Function to get event id 
     * @returns {string} event id or empty
     */
    getEventId = () => {
        let eventInfo = this.state.eventInfo;
        return eventInfo ? eventInfo.event._id : '';
    };

    /**
     * Function to clear Request and reset 
     * @returns {Object} json with values empty
     */
    getCleanedRequest = () => {
        const {logo, name, selectedGroupsIds, status, isPublic} = this.state;
        let request = {
            name,
            event: this.getEventId(),
            groups: selectedGroupsIds,
            status,
            isPublic
        };
        if(this.state.changeLogo)
            request.icon = logo;

        return request;
    };

    
    /**
     * function to send module creation form
     * @param  {Object} e
     */
    handleSubmit = (e)  => {
        e.preventDefault();
        let ctx = this.context;
        this.setState({errorMessage: '', showNotiError: false, loading: true});
        let request = this.getCleanedRequest();
        request._id = this.props.match.params.id;
        // console.log('Request', request);
        apiEvent.updateModule(request).then(
            (response) => {
                this.setState({redirect:true, loading:false});
            }
        ).catch(
            err => {
                console.log(err);
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                this.setState({loading:false})
            }
        );
    };

    /**
     * Function to get event details and froups availables 
     */
    getInfoEventAndGroups = () => {
        let ctx = this.context;
        let slug = ctx.slug;
        const getInfo = this.getDetailInfo;
        apiEvent.getEventInfo('', slug).then(
            (response) => {
                // console.log('Response', response);
                this.setState({
                    eventInfo: response,
                    availableGroups: response.event.groups,
                    availableGroupsIds: response.event.groups.map(o => o._id),
                });
                // console.log('Antes del state', this.state);
                getInfo();
            }
        ).catch(
            (err) => {
                console.log('Error al obtener info del evento para los grupos', err);
                this.setState({loadingDetail: false})
            }
        );
    };

    // handleCheckedRight = () => {
    //     setRight(right.concat(leftChecked));
    //     setLeft(not(left, leftChecked));
    //     setChecked(not(checked, leftChecked));
    // };

    /**
     * Function to get module details
     */
    getDetailInfo = () => {
        let moduleId = this.props.match.params.id;
        let ctx = this.context;


        apiEvent.getModuleDetail(moduleId).then(
            (res) => {
                // console.log('Grupos del modulo', res.groups);
                // this.handleSetGroupSelected(res.groups);

                let breadCrumbs = [
                    {
                        name:<Trans>Lista de módulos</Trans>,
                        active:false,
                        link:`/${ctx.slug}/admin/module/list/`
                    },
                    {
                        name:this.props.i18n._(t`Editar módulo `) +' '+ res.name,
                        active:true,
                        link:'/'
                    }
                ];

                console.log('Module detail', res);
                let data = {
                    logo: res.icon,
                    name: res.name,
                    eventId: res.event,
                    selectedGroupsIds: res.groups,
                    availableGroupsIds: this.getIntersectionGroups(res.groups),
                    loadingDetail: false,
                    status: res.status,
                    isPublic: res.isPublic,
                    breadCrumbs
                };
                this.setState(data);
                // console.log('despues', this.state)
            }
        ).catch(
            err => {
                console.log('Error al obtener detail', err);
                this.setState({loadingDetail: false})
            }
        )
    };

    
    /**
     * Function to exclude match of groups availables and group of module
     * @param  {} groupB
     */
    getIntersectionGroups = (groupB) => {
        let groupA = this.state.availableGroupsIds;
        // let groupB = this.state.selectedGroupsIds;
        let newGroupA = not(groupA, groupB);
        // console.log(groupA, groupB);
        // console.log('newGroupA', newGroupA);
        return newGroupA;
    };

    componentDidMount() {
        this.getInfoEventAndGroups();
        let ctx = this.context;
        let breadCrumbs = [
            {
                name:<Trans>Lista de módulos</Trans>,
                active:false,
                link:`/${ctx.slug}/admin/module/list/`
            },
            {
                name:<Trans>Editar módulo</Trans>,
                active:true,
                link:'/'
            }
        ];

        this.setState({breadCrumbs})

        // this.setState({availableGroupsIds:this.getIntersectionGroups(this.state.selectedGroupsIds)});

    }

    render() {
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/module/list/`} />;
        return (
            <AdminEventLayout breadcrumbs={this.state.breadCrumbs} title={<Trans>Administrador de eventos</Trans>}>
                <Grid item xs={12}>
                    { this.state.loadingDetail ? (<FacebookProgress />) : ''}
                    <CreateModuleForm
                        handleLogo={this.getFiles}
                        title={<Trans>Editar módulo</Trans>}
                        handleInputChange={this.handleInputChange}
                        availableGroupsIds={this.state.availableGroupsIds}
                        availableGroups={this.state.availableGroups}
                        selectedGroupsIds={this.state.selectedGroupsIds}
                        checked={this.state.checked}
                        handleSetGroup={this.handleSetGroup}
                        handleSetGroupSelected={this.handleSetGroupSelected}
                        handleSetChecked={this.handleSetChecked}
                        logo={this.state.logo}
                        name={this.state.name}
                        handleSubmit={this.handleSubmit}
                        status={this.state.status}
                        loading={this.state.loading}
                        isPublic={this.state.isPublic}
                        handleChangeCheck={this.handleChangeCheck}
                    />
                </Grid>
            </AdminEventLayout>
        )
    }
}

export default withI18n() (withRouter(UpdateModule));