import React from "react";
import {Grid, Paper} from "@material-ui/core";
import AdminLayout from "../../layouts/AdminLayout";
import {ApiServiceSecurity} from "../../api-services/security";
import UserListBox from "../../components/security/UserListBox";
import Title from "../../components/security/Title";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import AddIcon from '@material-ui/icons/Add';
import {Redirect} from "react-router-dom";
import _ from 'lodash';

const apiSecurity = new ApiServiceSecurity();

/**
 *
 * This view allow display user list
 * @class AdminUserList
 * @extends {React.Component}
 */
class AdminUserList extends React.Component{

    state = {
        userList: [],
        redirectToCreate: false
    };

    /**
     * Function to set state for redirect to create a new user
     */
    handleCreateLink = () => {
        this.setState({
            redirectToCreate: true,
        })
    };

    /**
     * function to delet user selected
     * @param  {Object} oldData
     */
    handlerDelete = (oldData) =>
        new Promise(resolve => {
            // setTimeout(() => {
            //     resolve();
            //     alert('Se resolvió');
            //     // const data = [...state.  data];
            //     // data.splice(data.indexOf(oldData), 1);
            //     // setState({...state, data});
            // }, 600);
            let isActive = !oldData.isActive;
            apiSecurity.updActiveSuperUser(oldData._id, isActive).then(
                (response) => {
                    resolve();
                    // console.log('Response', response);
                    this.getUserList();
                    // let newUserList = this.state.userList;
                    // let index = _.findIndex(this.state.userList, {_id: oldData._id});
                    // newUserList.splice(index, 1, {id: 100, name: 'New object.'})
                }
            ).catch(
                (err) => {
                    resolve();
                    console.log('Error', err.response);
                }
            );

        });

        
    /**
     * Function to get user list
     */
    getUserList = () => {
        let promise = apiSecurity.getSuperUserList();

        promise.then(
            (response) => {
                // console.log('Lista de usuarios', response);
                let userList = _.map(response, function (o) {
                    o.isActiveDisplay = o.isActive ? 'Activo': 'Desactivo';
                    return o;
                });
                if(response.length>0)
                    this.setState({userList:userList})
            }
        ).catch(
            error => {
                console.log('Error', error)
            }
        )
    };

    componentDidMount() {
        this.getUserList();
    }

    render() {
        if(this.state.redirectToCreate) return <Redirect push to='/user/new/' />;
        return (
            <AdminLayout>
                <Grid container title=''>

                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            Lista de usuarios
                            <IconButton onClick={this.handleCreateLink} color="primary" aria-label="add to shopping cart">
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>


                    <Grid item xs={12}>
                        {/*<Paper style={{padding: '10px'}}>*/}
                            {/*<Title>Usuarios</Title>*/}
                            <UserListBox rows={this.state.userList} handlerDelete={this.handlerDelete}/>
                        {/*</Paper>*/}
                    </Grid>
                </Grid>
            </AdminLayout>
        )
    }
}

export default AdminUserList