import React from "react";
import _ from "lodash";
import {Grid, Typography} from "@material-ui/core";
import {Link as LinkRouter, withRouter} from "react-router-dom";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import FormQr from "../../../../components/events/admin/qr/FormQr";
import {ApiServiceEvents} from "../../../../api-services/events";
import GlobalContext from "../../../../GlobalContext";
import ModalFormGeneric from "../../../../components/modals/ModalFormGeneric";
import TextField from '@material-ui/core/TextField';
import Image from 'material-ui-image';
import {t,Trans} from "@lingui/macro";
import {withI18n} from "@lingui/react";

const apiEvent = new ApiServiceEvents();

/**
 *
 * This view is for QR config
 * @class QrConfig
 * @extends {React.Component}
 */
class QrConfig extends React.Component{
    static contextType = GlobalContext;

    state = {
        _id: '',
        imageQr: '',
        validFromDate: '',
        validToDate: '',
        qtyToUse: '',
        unlimited: false,
        loading: false,
        active: false,
        redirectSuccess: false,
        openSendEmailQr: false,
        emailUser: '',
        loadingSendEmailQr: false,
        breadcrumbs: [],
        alternativeEvents: [],
        selectedSubEventId: '',
        selectedSubEvent: {},
        isSubEventSelected: false,
        openModalPreview: false,
        openModalEditSubEvent: false,
        subEventInitialData: {},
        noQR: 'data:image/jpeg;base64,',

};
    /**
     * Function for submit QR form and update settings
     * @param  {Object} valuesComing
     */
    handleSubmit = async (valuesComing) => {
        let values = _.clone(valuesComing);
        // console.log('Form values', valuesComing);
        let ctx = this.context;
        let slug = ctx.slug;
        let userId = this.props.match.params.id;
        let request = {
            "_idUser": userId,
            "slug": slug,
            "validStart": values.validFromDate,
            "validEnd": values.validToDate,
            "unlimited": values.unlimited,
            "useTimes": values.qtyToUse,
            "isActive": values.active,
        };
        console.log('Request', request);
        let oldState = this.state;
        oldState.loading = true;
        this.setState(oldState);
        apiEvent.updateQrSettings(request).then(
            (res) => {
                // console.log('respuesta', res);
                let config = res.temporalityConfig;
                this.setState({
                    loading:false,
                    // imageQr: res.base64,
                    // validFromDate: config.validStart,
                    // validToDate: config.validEnd,
                    // qtyToUse: config.useTimes,
                    // unlimited: config.unlimited,
                    active: res.isActive,
                });
                ctx.openSuccessNotification(<Trans>Configuración guardada</Trans>);
            }
        ).catch(
            (err) => {
                console.log('Error en la actualización de qrs', err.response);
                this.setState({loading:false});
            }
        );
    };

    /**
     * function to submit sub-event update form
     * @param  {Object} valuesComing
     */
    handleSubmitSubEvent = async (valuesComing) => {
        let values = _.clone(valuesComing);
        // console.log('Form values', valuesComing);
        let ctx = this.context;
        let slug = ctx.slug;
        let request = {
            _idAlternativeEvent: this.state.selectedSubEvent._id,
            startTime: values.validFromDate,
            endTime: values.validToDate,
            unlimited: values.unlimited,
            useTimes: values.qtyToUse,
            isActive: values.active,
            slug
        };
        console.log('Request', request);
        let oldState = this.state;
        oldState.loading = true;
        this.setState(oldState);
        apiEvent.updateQrSettingsSubEvent(request).then(
            (res) => {
                console.log(res);
                // let config = res.temporalityConfig;
                // this.setState({
                //     loading:false,
                //     imageQr: res.base64,
                //     validFromDate: config.validStart,
                //     validToDate: config.validEnd,
                //     qtyToUse: config.useTimes,
                //     unlimited: config.unlimited,
                //     active: res.isActive,
                // });
                ctx.openSuccessNotification(<Trans>Configuración guardada</Trans>);
            }
        ).catch(
            (err) => {
                console.log('Error en la actualización de QR', err.response);
                this.setState({loading:false});
            }
        );
    };

    
    /**
     * Function to get user QR
     */
    getUserQr = () => {
        let ctx = this.context;
        let slug = ctx.slug;
        let userId = this.props.match.params.id;
        // apiEvent.getQrUser(userId, slug).then(
        //     (res) => {
                apiEvent.getEventUserDetail(userId, slug).then(
                    (userData) => {
                        console.log('UserDetail', userData);
                        let eventUser = userData.eventUser;
                        let qrCode = eventUser.qrCode;
                        // let config = qrCode.temporalityConfig;
                        let eventInfo = _.has(eventUser, '_idEvent') && eventUser._idEvent ? eventUser._idEvent : '';
                        let availability = _.has(eventInfo, 'availability') && eventInfo.availability ? eventInfo.availability : '';
                        // console.log('config', config);
                        // let validStart = config.validStart ? moment(config.validStart).format() : '';
                        // let validEnd = config.validEnd ? moment(config.validEnd).format() : '';
                        // console.log('Valid start', validStart);
                        let newState = {
                            imageQr: _.has(qrCode, 'base64') && qrCode.base64 ? qrCode.base64 : this.state.noQR,
                            validFromDate: _.has(availability, 'startDate') && availability.startDate ? availability.startDate : '',
                            validToDate: _.has(availability, 'endDate') && availability.endDate ? availability.endDate : '',
                            qtyToUse: 0,
                            unlimited: _.has(availability, 'undefinedEnd') && availability.undefinedEnd ? availability.undefinedEnd : false,
                            active: _.has(qrCode, 'isActive') && qrCode.isActive ? qrCode.isActive : false,
                            emailUser: userData.email,
                            alternativeEvents: [],
                            breadcrumbs: [
                                {
                                    name: <Trans>Lista de usuarios</Trans>,
                                    active: false,
                                    link: `/${ctx.slug}/admin/user/list/`
                                },
                                {
                                    name:this.props.i18n._(t`Configuración QR de `) +' '+ eventUser.firstName +' ' +eventUser.lastName,
                                    active: true,
                                    link: '',
                                }
                            ],
                        };
                        this.setState(newState);
                        // console.log('New state', newState);
                    }
                ).catch(
                    (err) => {
                        console.log(err);
                    }
                );
            // }
        // ).catch(
        //     (err) => {
        //         console.log(err);
        //     }
        // );
    };
    
    /**
     * function to download QR of sub event
     * @param  {Object} e
     * @param  {Object} subEvent=''
     */
    downloadQr = (e, subEvent='') => {
        let ctx = this.context;
        // console.log('Generando');
        const linkSource = subEvent && _.has(subEvent, 'base64') ? subEvent.base64 : this.state.imageQr;
        const downloadLink = document.createElement("a");
        const fileName = "qr.png";

        downloadLink.href = linkSource;
        downloadLink.download = fileName;
        downloadLink.click();
        ctx.openSuccessNotification(<Trans>Qr descargado</Trans>);

    };

    /**
     * function to send QR by email
     */
    sendEmailQr = () => {
        let ctx = this.context;
        let slug = ctx.slug;
        let userId = this.props.match.params.id;
        let request = {
            "_idUser": userId,
            "slug": slug,
            "alternativeEmail": this.state.emailUser
        };
        if(this.state.isSubEventSelected){
            request._idAlternativeEvent = this.state.selectedSubEventId;
        }
        console.log('Request', request);
        this.setState({loadingSendEmailQr: true});
        apiEvent.sendEmailQr(request).then(
            (res) => {
                // console.log('Qr enviado', res);
                ctx.openSuccessNotification(<Trans>El email ha sido enviado</Trans>);
                this.setState({loadingSendEmailQr: false});
                this.handleCloseSendEmailDialog();
            }
        ).catch(
            (err) => {
                console.log('Error al enviar el email', err);
                ctx.openNotification(<Trans>El email no se pudo enviar. Intente más tarde</Trans>, 'error');
                this.setState({loadingSendEmailQr: false});
            }
        );
    };

    /**
     * function to close dialog for send mail
     */
    handleCloseSendEmailDialog = () => {
        this.setState({openSendEmailQr: false});
    };

    /**
     * function to open dialog for send mail
     */
    openSendEmailQr = () => {
        this.setState({
            openSendEmailQr:true,
            selectedSubEventId: '',
            isSubEventSelected: false
        });
    };

    /**
     * function to open dialog for send subevent mail
     * @param  {Object} event
     * @param  {Object} subEvent
     */
    openSendEmailQrSubEvent = (event, subEvent) => {
        // console.log(subEvent);
        this.setState({
            openSendEmailQr: true,
            selectedSubEventId: subEvent._id,
            isSubEventSelected: true
        })
    };

    
    /**
     * function to open dialog preview QR
     * @param  {Object} event
     * @param  {Object} data
     */
    openModalPreviewQR = (event, data) => {
        console.log('selectedSubEvent', data);
        this.setState({
            selectedSubEvent: data,
            openModalPreview: true,
        })
    };

    /**
     * function to open dialog of subevent
     * @param  {Object} event
     * @param  {Object} data
     */
    openModalSubEvent = (event, data) => {
        const tempo = data.temporalityConfig;
        let initialData = {
            validFromDate: tempo.validStart,
            validToDate: tempo.validEnd,
            qtyToUse: tempo.useTimes,
            unlimited: tempo.unlimited,
            active: data.isActive,
        };
        this.setState({
            selectedSubEvent: data,
            openModalEditSubEvent: true,
            subEventInitialData: initialData
        })
    };

    /**
     * Function to close  preview QR
     */
    closeModalPreviewQr = () => {
        this.setState({
            openModalPreview: false,
        })
    };

    /**
     * Function to close modal dialog of sub-event
     */
    closeModalSubEvent = () => {
        this.setState({
            openModalEditSubEvent: false,
        })
    };

    /**
     * function to set state when an input changes
     * @param  {Object} event
     */
    changeInputEmail = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({
            [name]: value
        });
    };

    componentDidMount() {
        this.getUserQr()
    }

    render() {

        const imgPreview = this.state.selectedSubEvent && _.has(this.state, 'selectedSubEvent.base64') ? this.state.selectedSubEvent.base64 : this.props.imageQr;

        return (
            <AdminEventLayout title={<Trans>Panel de configuración del evento</Trans>} breadcrumbs={this.state.breadcrumbs}>
                <Grid container title=''>
                    <Grid item xs={12}>
                        <div style={{ padding: 16, margin: 'auto', maxWidth: 1000 }}>
                            <Typography variant="h4" align="center" component="h5" gutterBottom>
                                <Trans>Configuración de código QR del evento principal</Trans>
                            </Typography>
                            <ModalFormGeneric
                                open={this.state.openSendEmailQr}
                                handleClose={this.handleCloseSendEmailDialog}
                                text={<Trans>¿Deseas enviar un email al invitado con el código QR?</Trans>}
                                title={<Trans>Confirmar email</Trans>}
                                confirm={this.sendEmailQr}
                                loading={this.state.loadingSendEmailQr}
                            >
                                <TextField
                                    autoFocus
                                    margin="dense"
                                    id="name"
                                    name="emailUser"
                                    label={<Trans>Email alternativo</Trans>}
                                    type="email"
                                    value={this.state.emailUser}
                                    fullWidth
                                    onChange={this.changeInputEmail}
                                />
                            </ModalFormGeneric>

                            {/*Modal preview qr sub event*/}
                            <ModalFormGeneric
                                open={this.state.openModalPreview}
                                handleClose={this.closeModalPreviewQr}
                                text={<Trans>Visualización QR del evento alternativo</Trans>}
                                title=""
                                confirm={this.closeModalPreviewQr}
                                loading={this.state.loadingSendEmailQr}
                                maxWidth={'lg'}
                            >
                                {
                                    imgPreview ? (
                                        <Image
                                            alt={<Trans>logo modulo</Trans>}
                                            src={imgPreview}
                                        />
                                    ) : ''
                                }
                            </ModalFormGeneric>

                            {/*Modal edit sub event temporality */}
                            <ModalFormGeneric
                                open={this.state.openModalEditSubEvent}
                                handleClose={this.closeModalSubEvent}
                                text={<Trans>Configuración de temporarlidad de evento alternativo</Trans>}
                                title=""
                                confirm={()=>{}}
                                loading={false}
                                fullScreen={false}
                                onlyCancel={true}
                            >
                                <FormQr
                                    onSubmit={this.handleSubmitSubEvent}
                                    imageQr={this.state.selectedSubEvent.base64}
                                    initialValues={this.state.subEventInitialData}
                                    loading={this.state.loading}
                                    downloadQr={() => this.downloadQr(null, this.state.selectedSubEvent)}
                                    openSendEmailQr={() => this.openSendEmailQrSubEvent(null, this.state.selectedSubEvent)}
                                />
                            </ModalFormGeneric>

                            <FormQr
                                onSubmit={this.handleSubmit}
                                imageQr={this.state.imageQr}
                                initialValues={this.state}
                                loading={this.state.loading}
                                downloadQr={this.downloadQr}
                                openSendEmailQr={this.openSendEmailQr}
                            />
                        </div>
                    </Grid>
                    {/*<Grid container item>*/}
                    {/*    <Grid item xs={12}>*/}
                    {/*        <div style={{ padding: 16, margin: 'auto', maxWidth: 1000 }}>*/}
                    {/*            <SubEventQRList*/}
                    {/*                rows={this.state.alternativeEvents}*/}
                    {/*                openSendEmailQr={this.openSendEmailQrSubEvent}*/}
                    {/*                downloadQr={this.downloadQr}*/}
                    {/*                openModalPreview={this.openModalPreviewQR}*/}
                    {/*                openModalSubEvent={this.openModalSubEvent}*/}
                    {/*            />*/}
                    {/*        </div>*/}
                    {/*    </Grid>*/}
                    {/*</Grid>*/}
                </Grid>
            </AdminEventLayout>
        )
    }
}

export  default withI18n() (withRouter(QrConfig))
