import React, {Component} from 'react';
import {CssBaseline, FormControl, FormControlLabel, FormGroup, Grid, Paper, Typography} from "@material-ui/core";
import {Trans} from "@lingui/macro";
import {Field, Form} from "react-final-form";
import {Link} from "react-router-dom";
import {Checkbox, Select, TextField} from "final-form-material-ui";
import {SaveButtonSpinner} from "../../../buttons/StandartButtons";
import GlobalContext from "../../../../GlobalContext";
import MenuItem from "@material-ui/core/MenuItem";

class FormImportPo extends Component {

    static contextType = GlobalContext;
    
    render() {
        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 800 }}>
                <CssBaseline />
                <Typography variant="h4" align="center" component="h1" gutterBottom>
                    <Trans>Importar archivo PO traducido</Trans>
                </Typography>

                <Form
                    onSubmit={this.props.onSubmit}
                    initialValues={{project: 'api'}}
                    validate={this.props.validate}
                    render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                        <form onSubmit={handleSubmit} noValidate>
                            <Paper style={{ padding: 16 }}>
                                <Grid container style={{marginTop: 10}} alignItems="flex-start" spacing={2}>
                                    <Grid item xs={12} style={{marginTop: 2}}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            <Trans>Selecciona un archivo PO</Trans>
                                        </Typography>
                                    </Grid>
                                    <Grid item xs={12}>
                                        <input type="file" required onChange={ (e) => this.props.handleChange(e.target.files) } />

                                    </Grid>


                                    <Grid item xs={12} style={{marginTop: 2}}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            <Trans>Selecciona en dónde se instalará la traducción:</Trans>
                                        </Typography>
                                    </Grid>

                                    <Grid item xs={12}>
                                        <Field
                                            fullWidth
                                            name="project"
                                            component={Select}
                                            label={<Trans>Seleccionar un tipo de proyecto</Trans>}
                                            formControlProps={{fullWidth: true}}
                                            variant="outlined"
                                        >
                                            <MenuItem value={'api'}><Trans>API</Trans></MenuItem>
                                            <MenuItem value={'front'}><Trans>Interfaz gráfica</Trans></MenuItem>
                                            <MenuItem value={'app'}><Trans>Aplicación móvil</Trans></MenuItem>
                                        </Field>
                                    </Grid>


                                    {submitError && <div className="error">{submitError}</div>}
                                    <Grid item style={{ marginTop: 16 }} xs={12}>
                                        <SaveButtonSpinner
                                            loading={this.props.loading}
                                        />
                                        {
                                            this.props.buttonTemplate ? (
                                                this.props.buttonTemplate
                                            ) : ''
                                        }
                                    </Grid>
                                </Grid>
                            </Paper>
                        </form>
                    )}
                />
            </div>
        );
    }
}

export default FormImportPo;