import React,{useContext} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import FileCopyIcon from '@material-ui/icons/FileCopy';
import AppBar from "@material-ui/core/AppBar";
import SettingsIcon from '@material-ui/icons/Settings';
import AddIcon from '@material-ui/icons/Add';
import Toolbar from "@material-ui/core/Toolbar";
import IconButton from "@material-ui/core/IconButton";
import Typography from "@material-ui/core/Typography";
import ImageIcon from '@material-ui/icons/Image';
import SubjectIcon from '@material-ui/icons/Subject';
import CodeIcon from '@material-ui/icons/Code';
import YouTubeIcon from '@material-ui/icons/YouTube';
import FacebookIcon from '@material-ui/icons/Facebook';
import RemoveIcon from '@material-ui/icons/Remove';
import KeyboardReturnIcon from '@material-ui/icons/KeyboardReturn';
import BurstModeIcon from '@material-ui/icons/BurstMode';
import CropDinIcon from '@material-ui/icons/CropDin';
import Crop75Icon from '@material-ui/icons/Crop75';
import StarsIcon from '@material-ui/icons/Stars';
import LockIcon from '@material-ui/icons/Lock';
import DeleteIcon from '@material-ui/icons/Delete';
import PaletteIcon from '@material-ui/icons/Palette';
import {CtxProviderBuilder,GlobalContextBuilder} from "../../GlobalContext";
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import FileCopyOutlinedIcon from '@material-ui/icons/FileCopyOutlined';
import SettingsOutlinedIcon from '@material-ui/icons/SettingsOutlined';
import ArrowDropUpOutlinedIcon from '@material-ui/icons/ArrowDropUpOutlined';
import ArrowDropDownOutlinedIcon from '@material-ui/icons/ArrowDropDownOutlined';
import MapIcon from '@material-ui/icons/Map';
import Tooltip from '@material-ui/core/Tooltip';

import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import {Trans, t} from "@lingui/macro";
import MoreVertIcon from '@material-ui/icons/MoreVert';
import BorderColorIcon from '@material-ui/icons/BorderColor';
import MenuIcon from '@material-ui/icons/Menu';

const useStyles = makeStyles(theme => ({
    root: {
        width: '100%',
        maxWidth: 360,
        backgroundColor: theme.palette.background.paper,
    }
}));



const styleOptions = {
    marginRight:'10px'
};

const optionsMore = [
    {type:'button',item:(<> <Crop75Icon style={styleOptions}/> <Trans>Botón</Trans> </>)},
    {type:'image',item:(<> <ImageIcon style={styleOptions}/> <Trans>Imagen</Trans> </>)},
    {type:'text',item:(<> <SubjectIcon style={styleOptions}/> <Trans>Texto </Trans></>)},
    // {type:'code',item:(<> <CodeIcon style={styleOptions}/> Código </>)},
    {type:'video',item:(<> <YouTubeIcon style={styleOptions}/> <Trans>Video Youtube</Trans> </>)},
    {type:'divisor',item:(<> <RemoveIcon style={styleOptions}/> <Trans>Divisor</Trans> </>)},
    {type:'space',item:(<> <KeyboardReturnIcon style={styleOptions}/> <Trans>Espacio</Trans> </>)},
    {type:'card',item:(<> <CropDinIcon style={styleOptions}/> <Trans>Card </Trans></>)},
    {type:'social',item:(<> <FacebookIcon style={styleOptions}/> <Trans>Redes sociales </Trans></>)},
    {type:'gallery',item:(<> <BurstModeIcon style={styleOptions}/> <Trans>Galería </Trans></>)},
    {type:'rating',item:(<> <StarsIcon style={styleOptions}/> <Trans>Calificación </Trans></>)},
    {type:'map',item:(<> <MapIcon style={styleOptions}/><Trans> Mapa </Trans></>)},
];


const optionSettings = [
    // {type:'changefondo',item:(<> <PaletteIcon style={styleOptions}/> Cambiar fondo </>)},
    {type:'changeName',item:(<> <BorderColorIcon style={styleOptions}/> <Trans>Cambiar Nombre</Trans> </>)},
    {type:'delete',item:(<> <DeleteIcon style={styleOptions}/> <Trans>Eliminar </Trans></>)},
    {type:'clone',item:(<> <FileCopyOutlinedIcon style={styleOptions}/><Trans> Clonar</Trans> </>)},
    {type:'perms',item:(<> <LockIcon style={styleOptions}/> <Trans>Permisos </Trans></>)}
];


export default function SimpleListMenu(props) {
    const classes = useStyles();
    const [anchorEl, setAnchorEl] = React.useState(null);
    const [anchorElSettings, setAnchorElSettings] = React.useState(null);
    const globalContext = useContext(GlobalContextBuilder);

    function handleClickListItem(type,event) {
        if(type==='add'){
            setAnchorEl(event.currentTarget);
        }else if(type==='settings'){
            setAnchorElSettings(event.currentTarget);
        }else if(type==='sort-up'){
            props.onSortUp(props.section);
        }else if (type=='sort-down'){
            props.onSortDown(props.section);
        }
    }

    /**
     * Para las opciones fuera del menu settings
     * @param e
     * @param option
     */
    function handleMenuItemClick(e,option) {
        setAnchorElSettings(null);
        setAnchorEl(null);
        props.onSelectedItem(option)
    }


    /**
     * Para el menu de settings (los puntitos)
     * @param e
     * @param option
     */
    function handleMenuSettingsItemClick(e,option) {
        setAnchorElSettings(null);
        setAnchorEl(null);
        switch (option) {
            case 'delete':
                props.onDelete(props.section);
                break;
            case 'clone':
                props.onCloneSection(props.section);
                break;
            case 'changeName':
                props.onChangeName(props.section)
                break;
            case 'perms':
                props.onChangePerms(props.section)
                break;
            default:
                break;
        }
    }

    function handleClose() {
        setAnchorEl(null);
        setAnchorElSettings(null);
    }

    return (

        <AppBar color={'default'} position="static">
            <Toolbar variant="dense">


                <Typography variant="h6" edge="start" color="inherit">
                    {props.section.name}
                </Typography>
                <Tooltip title={<Trans>Acciones</Trans>}>
                <IconButton
                    align={'right'}
                    aria-label="show more"
                    aria-haspopup="true"
                    color="inherit"
                    onClick={handleClickListItem.bind(this,'settings')}
                >
                    <MenuIcon />
                </IconButton>
                </Tooltip>





                <Tooltip title={<Trans>Agregar componente</Trans>}>
                <IconButton
                    align={'right'}
                    aria-label="show more"
                    aria-haspopup="true"
                    onClick={handleClickListItem.bind(this,'add')}
                    color="inherit"
                >
                    <AddIcon/>
                </IconButton>
                </Tooltip>




                <Menu
                    id="menu-components"
                    anchorEl={anchorEl}
                    keepMounted
                    open={Boolean(anchorEl)}
                    onClose={handleClose}
                >
                    {optionsMore.map((option, index) => (
                        <MenuItem
                            key={index}
                            onClick={event => handleMenuItemClick(this,option.type)}
                        >
                            {option.item}
                        </MenuItem>
                    ))}
                </Menu>




                <Menu
                    id="menu-settings"
                    anchorEl={anchorElSettings}
                    keepMounted
                    open={Boolean(anchorElSettings)}
                    onClose={handleClose}
                >
                    {optionSettings.map((option, index) => (
                        <MenuItem
                            key={index}
                            onClick={event => handleMenuSettingsItemClick(this,option.type)}
                        >
                            {option.item}
                        </MenuItem>
                    ))}
                </Menu>


                {
                   props.position!==0?
                       <Tooltip title={<Trans>Subir sección</Trans>}>
                       <IconButton
                           align={'right'}
                           aria-label="up"
                           aria-haspopup="true"
                           color="inherit"
                           onClick={handleClickListItem.bind(this,'sort-up')}
                       >
                           <ArrowUpwardIcon />
                       </IconButton>
                       </Tooltip>
                           :''
                }

                {
                    props.sizeSections!==props.position+1?<Tooltip title={<Trans>Bajar sección</Trans>}>
                            <IconButton
                        align={'right'}
                        aria-label="up"
                        aria-haspopup="true"
                        color="inherit"
                        onClick={handleClickListItem.bind(this,'sort-down')}
                    >
                        <ArrowDownwardIcon />
                        </IconButton></Tooltip>:''
                }



            </Toolbar>
        </AppBar>


    );
}