import React, {Component} from 'react';
import {t, Trans} from "@lingui/macro";
import Grid from "@material-ui/core/Grid";
import StepForm from "../../../../components/events/admin/chatbot/StepForm";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import LoadingOverlay from "react-loading-overlay";
    import Chart from "react-google-charts";
import Helmet from 'react-helmet'
import {ApiServiceEvents} from "../../../../api-services/events";
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import RefreshIcon from '@material-ui/icons/Refresh';
import {
    LineChart, Line, XAxis, YAxis, CartesianGrid, Tooltip, Legend,Brush
} from 'recharts';
import PrintIcon from '@material-ui/icons/Print';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import {withI18n} from "@lingui/react";
const apiEvent = new ApiServiceEvents();



/**
 *
 * This view allows to visualize the statistics of the event
 * @class ChartDashboard
 * @extends {Component}
 */
class ChartDashboard extends Component {
    // this.props.i18n._(t`Las contraseñas no coinciden`)

    options = {
        title: this.props.i18n._(t`Asistencia general del evento`),
        pieHole: 0.4,
        is3D: false
    };

    state = {
        loading:false,
        loadingcsv:false,
        background:'white',
        dataConfirmPie:[
            [this.props.i18n._(t`Tipo`), "Confirmados"],
            [this.props.i18n._(t`Pendiente por confirmar`), 0],
            [this.props.i18n._(t`Confirmado`), 0],
            [this.props.i18n._(t`Declinado`), 0]
        ],
        dataAccessByGroups:[
            [this.props.i18n._(t`Perfil`), this.props.i18n._(t`Accesos`)],
            ['--', 0],
        ],
        dataTableAccessByDate:[

        ],
        dataTableGroupsCount:[
            [
                { type: 'string', label: this.props.i18n._(t`Perfil`) },
                { type: 'number', label: this.props.i18n._(t`Número de usuarios`)}
            ]
        ],
        dataTableAssistanceVsRegisters:[
            [
                { type: 'string', label: this.props.i18n._(t`Acción`)},
                { type: 'number', label: this.props.i18n._(t`Contador`) }
            ]
        ],
        dataTableAssistanceIndustry:[
            [
                { type: 'string', label:this.props.i18n._(t`Eventos privados`)},
                { type: 'number', label: this.props.i18n._(t`Invitados`)},
                { type: 'number', label: this.props.i18n._(t`Confirmados`)},
                { type: 'number', label: this.props.i18n._(t`Asistieron`)},
                //{ type: 'number', label: this.props.i18n._(t`Asistentes`)}
            ]
        ],
        breadCrumbs: [
            {
                name:<Trans>Estadísticas</Trans>,
                active:true,
                link:'',
            }
        ],
    }
    /**
     * This function obtains the list of people attending the event
     */
    downLoadAssistant=()=>{
        let slug = this.props.match.params.slug;
        this.setState({
            loadingcsv:true
        })

        apiEvent.getAssistantFile(slug).then((res)=>{
                this.exportCSVFile(res,`lista_asistentes_${slug}.csv`);
        }).catch((err)=>{

        }).finally(()=>{
            this.setState({
                loadingcsv:false
            })
        })
    }

    /**
     * this function downloads the csv file of attendees
     * @param  {Object} csv
     * @param  {string} name="lista_asisten.csv"
     */
    exportCSVFile(csv,name="lista_asisten.csv") {
        const exportName = name;

        const blob = new Blob([csv], { type: "text/csv;charset=utf-8;" });

        if (navigator.msSaveBlob) {
            navigator.msSaveBlob(blob, exportName);
        } else {
            const link = document.createElement("a");
            if (link.download !== undefined) {
                const url = URL.createObjectURL(blob);
                link.setAttribute("href", url);
                link.setAttribute("download", exportName);
                link.style.visibility = "hidden";
                document.body.appendChild(link);
                link.click();
                document.body.removeChild(link);
            }
        }
    }

    
    componentDidMount() {
        this.getData();
    }

    getData=()=>{
        let slug = this.props.match.params.slug;
        this.setState({
            loading:true
        })
        apiEvent.getDashboardStats(slug).then((res)=>{
            console.log('respuesta--',res);
            let {eventConfirmAssistance,eventUserAccessByDate,eventUserWithAccessbyGroups,totalUsersByGroup,eventConfirmAssistanceVsRegisters,totalConfirmByEventIndustry} = res;
            let dataAccessByGroups=[
                ['Perfil', 'Accesos']
            ];

            let dataTableGroupsCount = [
                [
                    { type: 'string', label: this.props.i18n._(t`Perfil`) },
                    { type: 'number', label: this.props.i18n._(t`Número de usuarios`) }
                ]
            ]

            let  dataTableAccessByDate=[
                [
                    { type: 'date', label: this.props.i18n._(t`Fecha`) },
                    this.props.i18n._(t`Número de entradas por día`)
                ],
                [new Date(2014, 0), -0.5]
            ]


            let dataTableAssistanceVsRegisters=[
                [
                    { type: 'string', label: this.props.i18n._(t`Acción`) },
                    { type: 'number', label: this.props.i18n._(t`Contador`) }
                ]
            ];


            let dataTableAssistanceIndustry=[
                [
                    { type: 'string', label:this.props.i18n._(t`Eventos privados`)},
                    { type: 'number', label: this.props.i18n._(t`Invitados`)},
                    { type: 'number', label: this.props.i18n._(t`Confirmados`)},
                    { type: 'number', label: this.props.i18n._(t`Asistieron`)},
                   // { type: 'number', label: this.props.i18n._(t`Asistentes`)}
                ]
            ];

            console.log(eventConfirmAssistance,eventUserAccessByDate,eventUserWithAccessbyGroups,totalUsersByGroup);

            let dataConfirmPie = [
                [this.props.i18n._(t`Tipo`),this.props.i18n._(t`Confirmados`)],
                [this.props.i18n._(t`Pendiente por confirmar`), eventConfirmAssistance.totalPendingConfirm.value],
                [this.props.i18n._(t`Confirmado`), eventConfirmAssistance.totalconfirmAssistance.value],
                [this.props.i18n._(t`Declinado`), eventConfirmAssistance.totalDeclinedAssistance.value]
            ]




            if(eventUserWithAccessbyGroups){
                eventUserWithAccessbyGroups.map((item,i)=>{
                    dataAccessByGroups.push(
                        [`${item.name} (${item.count})`,item.count]
                    )
                })
            }


            if(totalUsersByGroup){
                console.log('totalUsersByGroup',totalUsersByGroup);
                totalUsersByGroup.map((item,i)=>{
                    dataTableGroupsCount.push(
                        [item.name,{v:item.count,f:item.count}]
                    )
                })
            }


            if(eventUserAccessByDate){
                eventUserAccessByDate.map((item,i)=>{
                    dataTableAccessByDate.push(
                        {
                            name:`${item.day}/${item.month}/${item.year}`, asistencias:item.count
                        }
                    )
                })
            }


            if(eventConfirmAssistanceVsRegisters){
                dataTableAssistanceVsRegisters.push(

                    [this.props.i18n._(t`Total de confirmados`),{v:eventConfirmAssistanceVsRegisters.totalconfirmAssistance, f:eventConfirmAssistanceVsRegisters.totalconfirmAssistance}]
                )
                dataTableAssistanceVsRegisters.push(
                    [this.props.i18n._(t`Confirmaron y no han ingresado`),{v:eventConfirmAssistanceVsRegisters.pendingRegisters, f:eventConfirmAssistanceVsRegisters.pendingRegisters}]
                );

                dataTableAssistanceVsRegisters.push(
                    [this.props.i18n._(t`Confirmaron y han ingresado`),{v:eventConfirmAssistanceVsRegisters.totalRegisterAccess, f:eventConfirmAssistanceVsRegisters.totalRegisterAccess}]
                )
            }

            if(totalConfirmByEventIndustry){
                totalConfirmByEventIndustry.map((item,i)=>{
                    dataTableAssistanceIndustry.push(
                        [item.label,{v:item.guestsUsers,f:item.guestsUsers},item.count,item.accessCount]
                    )
                })
            }


            this.setState({
                dataConfirmPie,
                dataAccessByGroups,
                dataTableGroupsCount,
                dataTableAccessByDate,
                dataTableAssistanceVsRegisters,
                dataTableAssistanceIndustry
            })

        }).catch((err)=>{
            console.log(err)
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })
    }

    render() {


        return (
            <LoadingOverlay
                active={this.state.loading||this.state.loadingcsv}
                spinner
                text={<Trans>Cargando...</Trans>}
            >
                <Helmet bodyAttributes={{style: `background-color : ${this.state.background}`}}/>


                <AdminEventLayout
                    title={<Trans>Administrador de eventos</Trans>}
                    breadcrumbs={this.state.breadCrumbs}
                >
                    {
                        !this.state.loading?<Grid container>
                            <Grid item xs={12}>
                                <Typography align={'center'} style={{marginBottom:20}} variant="h5" component="h5">
                                    <Trans>Estadísticas del evento</Trans>
                                </Typography>
                                <Button
                                    size="small"
                                    startIcon={<PrintIcon />}
                                    style={{marginRight:10}}
                                    onClick={() => window.print()}>
                                    <Trans>Imprimir estadísticas</Trans>
                                </Button>

                                <Button
                                    size="small"
                                    startIcon={<CloudDownloadIcon />}
                                    onClick={() => this.downLoadAssistant() }   color="primary">
                                    <Trans>Personas que asistieron al evento</Trans>
                                </Button>


                                <Button
                                    size="small"
                                    style={{marginLeft:10}}
                                    startIcon={<RefreshIcon />}
                                    onClick={() => this.getData() }   color="secondary">
                                    <Trans>Refrescar datos</Trans>
                                </Button>
                                <hr
                                    style={{
                                        color: '#cfcfcf',
                                        backgroundColor: '#cfcfcf',
                                        height: 5
                                    }}
                                />
                            </Grid>


                            <Grid item xs={12} md={12} >
                                <Chart
                                    chartType="PieChart"
                                    width="100%"
                                    height="400px"
                                    data={this.state.dataConfirmPie}
                                    options={this.options}
                                />
                            </Grid>



                            <Grid item xs={12}>
                                <hr
                                    style={{
                                        color: '#cfcfcf',
                                        backgroundColor: '#cfcfcf',
                                        height: 5
                                    }}
                                />
                            </Grid>



                            <Grid item xs={12} md={12}>
                                <LineChart
                                    width={900}
                                    height={300}
                                    data={this.state.dataTableAccessByDate}
                                    margin={{
                                        top: 5, right: 30, left: 20, bottom: 5,
                                    }}
                                >
                                    <CartesianGrid strokeDasharray="3 3" />
                                    <XAxis dataKey="name" />
                                    <YAxis />
                                    <Tooltip />
                                    <Legend verticalAlign="top" height={36}/>
                                    <Line type="monotone" dataKey="asistencias" stroke="#8884d8" activeDot={{ r: 8 }} />
                                    <Brush />
                                </LineChart>
                            </Grid>


                            <Grid item xs={12}>
                                <hr
                                    style={{
                                        color: '#cfcfcf',
                                        backgroundColor: '#cfcfcf',
                                        height: 5
                                    }}
                                />
                            </Grid>


                            <Grid item xs={12} md={12}>



                                <Chart
                                    width={'100%'}
                                    height={'300px'}
                                    chartType="BarChart"
                                    loader={<div>Loading Chart</div>}
                                    data={this.state.dataAccessByGroups}
                                    options={{
                                        title: this.props.i18n._(t`Entradas al evento por perfil`),
                                        chartArea: { width: '50%' },
                                        hAxis: {
                                            title: this.props.i18n._(t`Número de accesos`),
                                            minValue: 0,
                                        },
                                        vAxis: {
                                            title: 'Perfil',
                                        },
                                    }}
                                    // For tests
                                    rootProps={{ 'data-testid': '1' }}
                                />
                            </Grid>



                            <Grid item xs={12}>
                                <hr
                                    style={{
                                        color: '#cfcfcf',
                                        backgroundColor: '#cfcfcf',
                                        height: 5
                                    }}
                                />

                            </Grid>

                            <Grid item xs={12} md={6}>
                                <Typography variant="subtitle2"><Trans>Perfiles en el sistema</Trans></Typography>
                                <Chart
                                    width={'100%'}
                                    chartType="Table"
                                    data={this.state.dataTableGroupsCount}
                                    options={{
                                        showRowNumber: true,
                                    }}
                                    rootProps={{ 'data-testid': '1' }}
                                />


                            </Grid>


                            <Grid item xs={12} md={6}>
                                <Typography variant="subtitle2"><Trans>Asistencia del evento</Trans></Typography>
                                <Chart
                                    width={'100%'}
                                    chartType="Table"
                                    data={this.state.dataTableAssistanceVsRegisters}
                                    options={{
                                        showRowNumber: true,
                                    }}
                                    rootProps={{ 'data-testid': '1' }}
                                />
                            </Grid>


                            <Grid item xs={12} md={12}>
                                <Typography style={{marginTop:20}} variant="subtitle2"><Trans>Asistencia a eventos privados</Trans></Typography>
                                <Chart
                                    width={'100%'}
                                    chartType="Table"
                                    data={this.state.dataTableAssistanceIndustry}
                                    options={{
                                        showRowNumber: true,
                                    }}
                                    rootProps={{ 'data-testid': '1' }}
                                />
                            </Grid>

                        </Grid>:''
                    }

                </AdminEventLayout>
            </LoadingOverlay>
        );
    }
}

export default withI18n()(ChartDashboard);