import React, {Component} from 'react';
import {Grid, Paper} from "@material-ui/core";
import FileBase64 from "react-file-base64";
import TextField from "@material-ui/core/TextField";
import CreateUserForm from "../security/CreateUserForm";
import Typography from "@material-ui/core/Typography";
import InputAdornment from "@material-ui/core/InputAdornment";
import Button from "@material-ui/core/Button";
import {Link} from "react-router-dom";
import CircularProgress from "@material-ui/core/CircularProgress";
import {Trans} from "@lingui/macro";
import Image from "material-ui-image/lib/components/Image/Image";
import {
    KeyboardTimePicker,
    KeyboardDatePicker,
    KeyboardDateTimePicker
} from '@material-ui/pickers';
import {DatePickerWrapper, TimePickerWrapper} from "../fields/DatePickers";

let CreateEventForm = (props) => {
    let today = new Date();
    let actualYear = today.getFullYear();
    return (
        <React.Fragment>
            <Paper style={{padding:'20px'}}>
                <Typography variant="h6" gutterBottom>

                </Typography>
                <br/>
            <form  onSubmit={props.onHandleSubmit}>
                <Grid container spacing={3}>

                    <Grid item xs={12} md={6} >

                        {/*<img alt={'logo evento'} src={props.logo} style={{*/}
                        {/*    margin: 10,*/}
                        {/*    width: '100%'*/}
                        {/*}}/><br/>*/}

                        <Image
                            alt={'logo modulo'}
                            src={props.logo}
                            imageStyle={{
                                margin: 10,
                                width: 200
                            }}
                            style={{
                                margin: 10,
                                width: 150,
                                height: 150,
                                paddingTop: 30
                            }}
                        />
                        <br/>



                        <FileBase64
                            accept="image/*"
                            onDone={props.handleChandeImage.bind(this)}/>
                            <br/>
                        <Typography variant="caption" color={'primary'}  display="block" gutterBottom>
                            * <Trans>Logo representativo del evento</Trans>
                        </Typography>
                            <span style={{color:'red'}}>{props.messagefile}</span>

                    </Grid>



                    <Grid item xs={12} md={6} >

                        <Image
                            alt={'logo header'}
                            src={props.header}
                            imageStyle={{
                                margin: 10,
                                width: 200
                            }}
                            style={{
                                margin: 10,
                                width: 150,
                                height: 150,
                                paddingTop: 30
                            }}
                        /><br/>


                        <FileBase64
                            accept="image/*"
                            onDone={props.handleChandeIconImage.bind(this)}/>
                        <br/>
                        <Typography variant="caption" color={'primary'}  display="block" gutterBottom>
                            * <Trans>Imagen para el menú lateral de la aplicación, así como del login del usuario</Trans>
                        </Typography>

                        <span style={{color:'red'}}>{props.messagefileIcon}</span>

                    </Grid>

                    <Grid item xs={12} md={12}>
                        <TextField variant="outlined" value={props.name} required name="name" id="name" label="Nombre"
                                   onChange={props.handleEventInput} fullWidth/>
                    </Grid>


                    <Grid item xs={12} md={6}>
                        <TextField multiline
                                   rows="4" variant="outlined" id="description" value={props.description} name="description"
                                   onChange={props.handleEventInput} label="Descripción" fullWidth/>
                    </Grid>


                    <Grid item xs={12} md={6}>
                        <label style={{marginRight:'10px'}}>Color del evento</label>
                        <input variant="outlined" type='color' required value={props.color} placeholder={'#FFFF00'} name="color"
                                   id="color" onChange={props.handleEventInput}/>
                        <br/>

                        <p style={{color:'gray', fontSize:'12px'}}>* <Trans>Este color será utilizado como principal en la aplicación móvil</Trans></p>
                    </Grid>


                    <Grid item xs={12} md={6}>
                        <TextField variant="outlined" value={props.capacity} type="number" name="capacity" min="0" pattern="^[0-9]+" required id="numAssistans" onChange={props.handleEventInput} label="Num. Asistentes" fullWidth />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <TextField variant="outlined" value={props.location} required name="location" id="ubication" onChange={props.handleEventInput} label="Ubicación" fullWidth />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <TextField
                            variant="outlined"
                            label="Url personalizada del evento"
                            required
                            fullWidth
                            value={props.slug}
                            name="slug"
                            onChange={props.handleEventInput}
                            InputProps={{
                                startAdornment: <InputAdornment position="start">app.tianguisturistico.com/</InputAdornment>,
                            }}
                        />
                    </Grid>





                </Grid>

                <br/>


                <Grid container spacing={3}>
                    <Grid item xs={12} style={{marginTop: 10}}>
                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                            <Trans> Disponibilidad del evento </Trans>
                        </Typography>
                        {/*<p>{props.startDate}</p>*/}
                        {/*<p>{props.endDate}</p>*/}
                    </Grid>


                    <Grid item xs={12}>


                        <KeyboardDateTimePicker
                            value={props.startDate}
                            style={{marginRight:10}}
                            onChange={props.handleStartDateChange.bind(this,false)}
                            label={<Trans>Fecha y hora de inicio del evento</Trans>}
                            minDate={new Date(`${actualYear-3}-01-01`)}
                            maxDate={new Date(`${actualYear+5}-01-01`)}
                            onError={console.log}
                            format="dd/MM/yyyy hh:mm a"
                        />


                        <KeyboardDateTimePicker
                            value={props.endDate}
                            onChange={props.handleEndDateChange.bind(this,false)}
                            label={<Trans>Fecha y hora de fin del evento</Trans>}
                            minDate={new Date(`${actualYear-3}-01-01`)}
                            maxDate={new Date(`${actualYear+5}-01-01`)}
                            onError={console.log}
                            format="dd/MM/yyyy hh:mm a"
                        />


                    </Grid>

                    <Grid item xs={12}>
                        <Typography variant="p" gutterBottom>
                            <Trans>*Esta configuración de fechas será la que determine la temporalidad de los códigos QR del evento</Trans>
                        </Typography>
                    </Grid>


                    {/*<Grid item xs={12} style={{marginTop: 10}}>*/}
                    {/*    <KeyboardDatePicker*/}
                    {/*        disableToolbar*/}
                    {/*        variant="inline"*/}
                    {/*        format="dd/MM/yyyy"*/}
                    {/*        margin="normal"*/}
                    {/*        style={{marginRight:10}}*/}
                    {/*        id="date-picker-inline-start"*/}
                    {/*        label={<Trans>Fecha de fin del evento</Trans>}*/}
                    {/*        value={props.endDate}*/}
                    {/*        onChange={props.handleEndDateChange.bind(this,false)}*/}
                    {/*        KeyboardButtonProps={{*/}
                    {/*            'aria-label': 'change date',*/}
                    {/*        }}*/}
                    {/*    />*/}

                    {/*    <KeyboardTimePicker*/}
                    {/*        margin="normal"*/}
                    {/*        id="time-picker-start"*/}
                    {/*        label={<Trans>Hora de finalización del evento</Trans>}*/}
                    {/*        value={props.endDate}*/}
                    {/*        onChange={props.handleEndDateChange.bind(this,true)}*/}
                    {/*        KeyboardButtonProps={{*/}
                    {/*            'aria-label': 'change time',*/}
                    {/*        }}*/}
                    {/*    />*/}
                    {/*</Grid>*/}



                </Grid>
                <br/>


                <Typography variant="h6" gutterBottom>
                    <Trans>Administrador</Trans>
                </Typography>
                <br/>

                <Grid container spacing={3}>
                    <Grid item xs={12} md={6}>
                        <TextField variant="outlined" value={props.nameAdmin ? props.nameAdmin : ''} disabled required id="nameAdmin" label="Nombre" fullWidth />
                    </Grid>
                    <Grid item xs={12} md={6}>
                        <TextField variant="outlined" value={props.emailAdmin ? props.emailAdmin : ''}  disabled required name="emailAdmin" id="emailAdmin" label="Correo" fullWidth />
                    </Grid>
                </Grid>



                <Grid container spacing={3}>



                    <Grid item xs={12}>
                        <Button component={Link} to='/event/list' style={{marginRight:'10px'}}   variant="contained">
                            <Trans>Cancelar</Trans>
                        </Button>

                        {
                            props.loading ? <CircularProgress size={20}/> :
                                <Button type="submit" variant="contained" color="primary">
                                    <Trans>Guardar</Trans>
                                </Button>
                        }



                    </Grid>
                </Grid>


            </form>







            </Paper>
        </React.Fragment>


    )
};

export default CreateEventForm