import React, {Component} from 'react';
import { Button, Grid,Modal, Form,Image,Container } from 'semantic-ui-react'
import './movil.css'
import MainLayout from "./MainLayout";
import footerimg from './footer.png'; // with import
import headerimg from './header.png'; // with import

class LoginMovil extends Component {

    state = {
        slug : '',
        open:false
    };

    show = () => () => this.setState({ open: true })
    close = () => this.setState({ open: false })

    handleLogin = ()=>{
        // console.log('Login');
        this.props.history.push("/tianguis/view/123/");
        this.setState({
            open:false
        })
    }

    componentDidMount () {
        const { slug } = this.props.match.params
        this.setState({slug})
        console.log({slug})
    }

    handleSubmit(e){
        e.preventDefault();
    }

    render() {

        const { open } = this.state

        return (
            <MainLayout content={
                <Grid>
                    <Grid.Row columns={2}>
                        <span style={{paddingLeft:'10px',color:'white'}}>Tianguis turístico</span>
                        <Grid.Column width={10} textAlign='right'>
                          <p onClick={this.show()} style={{color:'white'}}>Acceder</p>
                        </Grid.Column>
                    </Grid.Row>
                </Grid>
            }>
                <Grid centered>
                    <Grid.Row id='img-login' style={{paddingTop:'0px'}}>
                        <Image src={footerimg} fluid />

                        <Container>
                            <br/>
                           <small>
                               El Tianguis Turístico es uno de los eventos más importantes del sector turismo en México, ya que reúne a empresarios, hoteleros, agentes de viajes, operadores, meeting planners y medios de la industria turística de más de 80 países del mundo, mostando la riqueza de México representada a través de sus playas, cultura, gastronomia y herencia histórica.
                           </small>

                            <Image src={headerimg} fluid/>
                        </Container>
                    </Grid.Row>
                </Grid>

                <Modal open={open} dimmer="inverted" onClose={this.close}>
                    <Modal.Content image>
                        <Form>
                            <h2 align="center">Acceso</h2>
                            <Form.Field>
                                <label>Correo</label>
                                <input type='email'/>
                            </Form.Field>
                            <Form.Field>
                                <label>Contraseña</label>
                                <input type='password' />
                            </Form.Field>
                            <Button type='button' onClick={this.close}>Cancelar</Button>
                            <Button type='button' color="blue" onClick={this.handleLogin}>Aceptar</Button>
                        </Form>
                    </Modal.Content>
                </Modal>
            </MainLayout>

        );
    }
}


export default LoginMovil;



