import React from 'react';
import { Trans } from '@lingui/macro';
import ReactDOM from 'react-dom';
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import LoadingOverlay from "react-loading-overlay";
import { Grid, Paper } from "@material-ui/core";
import MaterialTable from 'material-table';
import { ApiServiceEvents } from "../../../api-services/events";
import Button from '@material-ui/core/Button';
import _ from 'lodash';
import moment from 'moment';
import GlobalContext from "../../../GlobalContext";

const apiEvent = new ApiServiceEvents();

/**
 *
 * This view allow view push notifications history
 * @export
 * @class EventUserList
 * @extends {React.Component}
 */
export default class EventUserList extends React.Component {
    constructor(props) {
        super(props)

        this.tableRef = React.createRef();
    }

    static contextType = GlobalContext;

    state = {
        loading: true,
        msgLoading: <Trans>cargando...</Trans>,
        columns: [
            { title: <Trans>Titulo</Trans>, field: 'title' },
            { title: <Trans>Mensaje</Trans>, field: 'message' },
            { title: <Trans>Grupos</Trans>, field: 'groups' },
            { title: <Trans>Creado Por</Trans>, field: 'createdBy' },
            { title: <Trans>Fecha</Trans>, field: 'createdAt' },
        ],
        dataTable: [],
        breadcrumbs: [
            {
                name: <Trans>Historial de notificaciones push</Trans>,
                active: true,
                link: ''
            }]
    }

    /**
     * function to get notifications list
     */
    getNotificationsList() {
        let ctx = this.context;
        let slug = ctx.slug;
        //let {isLoading} = this.state
        //this.setState({slug})
        apiEvent.getPushNotifications(slug).then(
            (res) => {
                console.log(res)
                let data = [];
                let row = {};
                res.map((item) => {
                    row = {
                        title: item.title,
                        message: item.message,
                        groups: item.groups.name,
                        method: item.method,
                        createdBy: item.createdBy,
                        createdAt: moment(item.createdAt).format('MM/DD/YYYY, h:mm:ss a')
                    }
                    data.push(row);
                })

                this.setState({ dataTable: data })
            }
        ).catch((err) => {
            if (_.has(err, 'response.status') && err.response.status === 403) {
                ctx.openPermissionNotification();
            }
            console.log('Error', err.response);
        }).finally(() => {
            console.log(this.state)
            this.setState({ loading: false })
        }
        );
    }

    componentDidMount() {
        this.getNotificationsList()

        let slug = this.props.match.params.slug;

        let breadcrumbs = [
            {
                name: <Trans>Lista de usuarios</Trans>,
                active: false,
                link: `/${slug}/admin/user/list/`
            },
            {
                name: <Trans>Historial de notificaciones push</Trans>,
                active: true,
                link: ''
            }];


        this.setState({
            breadcrumbs
        })

    }

    render() {
        let ctx = this.context;
        let state = this.state;
        return (
            <LoadingOverlay
                active={this.state.loading}
                spinner
                text={this.state.msgLoading}
            >
                <AdminEventLayout title={<Trans>Historial de notificaciones push</Trans>} breadcrumbs={this.state.breadcrumbs}>
                    <Grid item xs={12}>
                        <Paper elevation={0} >
                            {state.loading ? '' : <MaterialTable width="100%"
                                title={<Trans>Historial de Notificaciones push</Trans>}
                                columns={state.columns}
                                data={state.dataTable}
                                options={{
                                    actionsColumnIndex: -1,
                                    pageSize: 10,
                                    pageSizeOptions: [10, 20, 50, 100, 500],
                                    paging: true,
                                }}
                                localization={ctx.tableConfig.localization}
                            />}
                        </Paper>
                    </Grid>
                </AdminEventLayout>
            </LoadingOverlay>
        )
    }

}
