import React, { Component } from 'react';
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import TableUserList from "../../../components/events/security/TableUserList";
import { Grid } from "@material-ui/core";
import { ApiServiceEvents } from "../../../api-services/events";
import CircularProgress from "@material-ui/core/CircularProgress";
import MaterialTable from "material-table";
import GlobalContext from "../../../GlobalContext";
import Link from '@material-ui/core/Link';
import parse from 'html-react-parser';
import moment from 'moment'
import { Trans, t } from '@lingui/macro';
import { withI18n } from "@lingui/react";

const apiEvent = new ApiServiceEvents();

/**
 *
 * This view allows you to see the list of emails sent by user
 * @class EmailSentList
 * @extends {Component}
 */
class EmailSentList extends Component {

    static contextType = GlobalContext;


    state = {
        idUser: '',
        isLoading: true,
        data: [],
        breadcrumbs: [],
        columns: [
            { title: 'Fecha', field: 'createdAt', render: rowData => <Grid> {moment(rowData.createdAt).format('L')} {moment(rowData.createdAt).format('LT')}  </Grid> },
            { title: 'Asunto', field: 'subject' },
            { title: 'Contenido', field: 'message', render: rowData => parse(rowData.message) },
            { title: 'Adjuntos', field: 'attachments', render: rowData => <Grid>{this.getComponentAttachments(rowData.attachments)}</Grid> }
        ]
    }

    /**
     * Function to get components related to the email sent
     * @param  {Object} adjuntos
     * @retuns {string} html response
     */
    getComponentAttachments = (adjuntos) => {
        let listitems = '';

        adjuntos.map((adjunto, i) => {
            listitems += `<li><a href="${adjunto}" target="_blank">${adjunto.split('/')[adjunto.split('/').length - 1]}</a></li>`;
        });

        return parse(`<ul style="list-style-type: none;">
            ${listitems}
        </ul>`);
    }


    /**
     * I'll get the data user
     * @param {userid} user identifier
     * @param {slug} slug event
     */
    getDataUser = (userid, slug) => {
        apiEvent.getEventUserDetail(userid, slug).then((res) => {
            // let eventUser
            let { eventUser } = res;
            this.setState({
                breadcrumbs: [
                    {
                        name: <Trans>Lista de usuarios</Trans>,
                        active: false,
                        link: `/${slug}/admin/user/list/`
                    },
                    {
                        name: this.props.i18n._(t`Lista de email enviados al usuario `) + ' ' + `${eventUser.firstName} ${eventUser.lastName}`,
                        active: true,
                        link: ''
                    }

                ]
            });
        }).catch((err) => {
            console.log(err)
        })
    }

    componentDidMount() {
        let userid = this.props.match.params.user;
        let slug = this.props.match.params.slug;

        let breadCrumbs = [
            {
                name: <Trans>Lista de usuarios</Trans>,
                active: false,
                link: `/${slug}/admin/user/list/`
            },
            {
                name: this.props.i18n._(t`Lista de email enviados al usuario`),
                active: true,
                link: ''
            }
        ];



        this.setState({
            idUser: userid,
            breadcrumbs: breadCrumbs
        })
        this.getEmailSent(userid);
        this.getDataUser(userid, slug)

    }

    /**
     * function for get emails send per user
     * @param  {number} userid
     */
    getEmailSent = (userid) => {
        apiEvent.getEmailSent(userid).then((res) => {
            console.log(res)
            this.setState({
                data: res
            })
        }).catch((err) => {
            console.log(err)
        }).finally(() => {
            this.setState({ isLoading: false })
        })
    }


    render() {
        let { isLoading, columns, data, breadcrumbs } = this.state;
        let ctx = this.context;
        return (
            <AdminEventLayout breadcrumbs={breadcrumbs} title={<Trans>Listado de emails enviados</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        {
                            isLoading ? <CircularProgress /> : <MaterialTable
                                title={<Trans>Mensajes enviados</Trans>}
                                columns={columns}
                                data={data}
                                options={{
                                    actionsColumnIndex: -1,
                                    pageSize: 10,
                                    pageSizeOptions: [10, 20, 50, 100, 500],
                                    paging: true,
                                }}
                                localization={ctx.tableConfig.localization}
                            />
                        }
                    </Grid>
                </Grid>

            </AdminEventLayout>
        );
    }
}

export default withI18n()(EmailSentList);