import React from 'react';
import MaterialTable from 'material-table';
import { Redirect } from "react-router-dom";
import CircularProgress from "@material-ui/core/CircularProgress";
import GlobalContext from "../../../../GlobalContext";
import { t, Trans } from '@lingui/macro';
import { withI18n } from "@lingui/react";

class ResourceListTable extends React.Component {
    static contextType = GlobalContext;


    state = {
        redirectToEdit: false,
        redirectToEditMyResources: false,
        columns: [
            { title: <Trans>Archivo</Trans>, render: rowData => <a href={rowData.file}>{rowData.file.split('/').reverse()[0].substring(0, 20) + '...'}</a> },
            { title: <Trans>Descripción</Trans>, field: 'name' }
        ]
    };


    componentDidMount() {
    }


    render() {
        let props = this.props;
        let { isLoading, fromMyResources, userId } = this.props;

        let ctx = this.context;



        if (this.state.redirectToEdit) return <Redirect push to={`/${ctx.slug}/admin/resources/${this.state.selectedRow}/`} />;
        if (this.state.redirectToEditMyResources) return <Redirect push to={`/${ctx.slug}/admin/user-resources/edit/${userId}/${this.state.selectedRow}/`} />;

        return (
            <MaterialTable
                title={<Trans>Recursos</Trans>}
                columns={this.state.columns}
                data={props.data}
                options={{
                    actionsColumnIndex: -1,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100, 500],
                    paging: true,
                }}
                localization={ctx.tableConfig.localization}
                actions={[
                    {
                        icon: 'edit',
                        tooltip: this.props.i18n._(t`Editar`),
                        onClick: (event, rowData) => {
                            if (fromMyResources) {
                                this.setState({ redirectToEditMyResources: true, selectedRow: rowData._id })
                            } else {
                                this.setState({ redirectToEdit: true, selectedRow: rowData._id })
                            }

                        }
                    },
                    // rowData => ({
                    //     icon: 'delete',
                    //     tooltip: 'Delete User',
                    //     onClick: (event, rowData) => alert("You want to delete " + rowData.name),
                    //     disabled: rowData.birthYear < 2000
                    // })
                ]}
                editable={{
                    onRowDelete: props.handlerDelete,
                }}
            />

        );
    }
}


export default withI18n()(ResourceListTable)