import React from "react";

import Avatar from '@material-ui/core/Avatar';
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import PeopleOutline from '@material-ui/icons/PeopleOutline';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import { Trans, t } from '@lingui/macro';

import {Link} from "react-router-dom";
import {CancelButton, SaveButtonSpinner} from "../buttons/StandartButtons";


const useStyles = makeStyles(theme => ({
    '@global': {
        body: {
            backgroundColor: theme.palette.common.white,
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(3),
        backgroundColor: theme.palette.primary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));

let CreateUserForm = (props) => {
    const classes = useStyles();

    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <div className={classes.paper}>
                <Avatar className={classes.avatar}>
                    {/*<LockOutlinedIcon />*/}
                    <PeopleOutline />
                </Avatar>
                <Typography component="h1" variant="h5">
                    {
                        props.title ? (
                            props.title
                        ) : 'Registro de super usuario'
                    }

                </Typography>
                <form className={classes.form} onSubmit={props.handleSubmit}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                autoComplete="fname"
                                name="firstName"
                                variant="outlined"
                                required
                                fullWidth
                                id="firstName"
                                label="Nombre(s)"
                                autoFocus
                                value={props.firstName}
                                onChange={props.handleInputChange}
                            />
                        </Grid>
                        <Grid item xs={12} sm={6}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="lastName"
                                label="Apellidos"
                                name="lastName"
                                autoComplete="lname"
                                value={props.lastName}
                                onChange={props.handleInputChange}
                            />
                        </Grid>
                        <Grid item xs={12}>
                            <TextField
                                variant="outlined"
                                required
                                fullWidth
                                id="email"
                                label="Email"
                                name="email"
                                autoComplete="email"
                                value={props.email}
                                onChange={props.handleInputChange}
                            />
                        </Grid>
                        { props.showPassword ? (
                                <Grid item xs={12}>
                                    <TextField
                                        variant="outlined"
                                        required
                                        fullWidth
                                        name="password"
                                        label="Contraseña"
                                        type="password"
                                        id="password"
                                        autoComplete="current-password"
                                        value={props.password}
                                        onChange={props.handleInputChange}
                                    />
                                </Grid>
                            ): ''
                        }
                    </Grid>

                    <CancelButton
                        name='Volver'
                        to={`/user/list/`}
                    />

                    <SaveButtonSpinner
                        name='Actualizar'
                        disabled={props.disabledButton}
                    />

                </form>

                    {
                        props.isProfile?<form className={classes.form} onSubmit={props.handleSubmitPassword}>
                            <Grid container spacing={2}>
                                <Typography component="h1" style={{marginTop:20, marginBottom:10}} variant="h5">
                                    <Trans>Cambiar contraseña</Trans>
                                </Typography>
                            <Grid item xs={12}>
                                <TextField
                                    variant="outlined"
                                    required
                                    fullWidth
                                    name="passwordChange"
                                    label="Contraseña"
                                    type="password"
                                    id="passwordchange"
                                    autoComplete="current-password"
                                    value={props.passwordChange}
                                    onChange={props.handleInputChange}
                                />
                            </Grid>


                            <Grid item xs={12}>
                                <TextField
                                    variant="outlined"
                                    required
                                    fullWidth
                                    name="passwordConfirm"
                                    label="Confirmar contraseña"
                                    type="password"
                                    id="passwordConfirm"
                                    autoComplete="current-password"
                                    value={props.passwordConfirm}
                                    onChange={props.handleInputChange}
                                />
                            </Grid>

                                <Grid item style={{ marginTop: 16 }} xs={12}>

                                    <CancelButton
                                        to={`/`}
                                    />

                                    <SaveButtonSpinner
                                        loading={props.isLoadingPassword}
                                    />
                                </Grid>

                        </Grid></form>:''
                    }


            </div>
        </Container>
    );
};


export default CreateUserForm