import React from 'react';
import MaterialTable from 'material-table';
import { Redirect } from "react-router-dom";
import Avatar from '@material-ui/core/Avatar';
import GlobalContext from "../../../GlobalContext";
import Icon from '@material-ui/core/Icon';
import { loadCSS } from 'fg-loadcss';
import _ from 'lodash';
import { Trans, t } from '@lingui/macro';
import { withI18n } from "@lingui/react";
import imgDefault from '../../../assets/userprofile.png';


class TableUserList extends React.Component {
    static contextType = GlobalContext;

    state = {
        redirectToEdit: '',
        selectedRow: '',
        redirectToQr: false,
        redirectToResources: false,
        columns: [
            { title: <Trans>Imagen</Trans>, field: 'image', render: rowData => <Avatar alt={rowData.firstName} src={_.has(rowData, 'image.value') ? rowData.image.value : imgDefault} /> },
            { title: <Trans>Nombre</Trans>, field: 'firstName' },
            { title: <Trans>Apellido(s)</Trans>, field: 'lastName' },
            { title: <Trans>Email</Trans>, field: 'email' },
            { title: <Trans>Admin</Trans>, field: 'isAdmin', type: 'boolean' },
            { title: <Trans>Staff</Trans>, field: 'isStaff', type: 'boolean' },
            { title: <Trans>Empresa</Trans>, field: 'company' },
            // { title: '¿Activo?', field: 'isActiveDisplay' },
            // { title: 'ID', field: '_id' },
            // { title: 'Birth Year', field: 'birthYear', type: 'numeric' },
            // {
            //     title: 'Birth Place',
            //     field: 'birthCity',
            //     lookup: { 34: 'İstanbul', 63: 'Şanlıurfa' },
            // },
        ],
        // data: [
        //     { name: 'Mehmet', surname: 'Baran', birthYear: 1987, birthCity: 63 },
        //     {
        //         name: 'Zerya Betül',
        //         surname: 'Baran',
        //         birthYear: 2017,
        //         birthCity: 34,
        //     },
        // ],
    };

    componentDidMount() {
        loadCSS(
            'https://use.fontawesome.com/releases/v5.1.0/css/all.css',
            document.querySelector('#font-awesome-css'),
        );
    }

    render() {

        let ctx = this.context;

        if (this.state.redirectToEdit) return <Redirect push to={`/${ctx.slug}/admin/user/${this.state.selectedRow}/`} />;
        if (this.state.redirectToQr) return <Redirect push to={`/${ctx.slug}/admin/user/${this.state.selectedRow}/qr/`} />;
        if (this.state.redirectToAgenda) return <Redirect push to={`/${ctx.slug}/admin/user/${this.state.selectedRow}/agenda/`} />;
        if (this.state.redirectToResources) return <Redirect push to={`/${ctx.slug}/admin/user/user-resources/${this.state.selectedRow}/`} />;
        if (this.state.redirectToEmailList) return <Redirect push to={`/${ctx.slug}/admin/${this.state.selectedRow}/email-list/`} />;

        let state = this.state;
        let setState = this.setState;
        let data = this.props.rows;

        return (
            <MaterialTable
                title={this.props.i18n._(t`Usuarios`)}
                tableRef={this.props.tableRef}
                columns={state.columns}
                data={data}
                editable={{
                    isEditable: true,
                    onRowDelete: this.props.handlerDelete,
                }}
                actions={[
                    {
                        icon: 'edits',
                        tooltip: this.props.i18n._(t`Editar`),
                        onClick: (event, rowData) => this.setState({ redirectToEdit: true, selectedRow: rowData._id })
                    },
                    {
                        icon: () => (<Icon className="fa fa-qrcode" />),
                        tooltip: this.props.i18n._(t`Código QR`),
                        onClick: (event, rowData) => this.setState({ redirectToQr: true, selectedRow: rowData._id })
                    },
                    {
                        icon: () => (<Icon className="fa fa-calendar-check" />),
                        tooltip: this.props.i18n._(t`Agenda`),
                        onClick: (event, rowData) => this.setState({ redirectToAgenda: true, selectedRow: rowData._id })
                    },
                    {
                        icon: () => (<Icon className="fa fa-paperclip" />),
                        tooltip: this.props.i18n._(t`Archivos del usuario`),
                        onClick: (event, rowData) => this.setState({ redirectToResources: true, selectedRow: rowData._id })
                    },
                    {
                        icon: () => (<Icon className="fa fa-paper-plane" />),
                        tooltip: this.props.i18n._(t`Enviar email a usuario`),
                        onClick: (event, rowData) => this.props.handleClickOpenMail(rowData)
                    },
                    {
                        icon: () => (<Icon className="fa fa-list" />),
                        tooltip: this.props.i18n._(t`Emails enviados`),
                        onClick: (event, rowData) => this.setState({ redirectToEmailList: true, selectedRow: rowData._id })
                    }
                ]}
                options={{
                    actionsColumnIndex: -1,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100, 500],
                    paging: true,
                    sorting: false,
                    paginationType: 'normal',
                }}
                localization={ctx.tableConfig.localization}
            />
        );
    }
}

export default withI18n()(TableUserList)