import React, {Component} from 'react';
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import PersonIcon from '@material-ui/icons/Person';
import {Link} from "react-router-dom";
import GroupIcon from '@material-ui/icons/Group';
import WidgetsIcon from '@material-ui/icons/Widgets';
import EventAvailableIcon from '@material-ui/icons/EventAvailable';
import GlobalContext from "../../../GlobalContext";
import PermMediaIcon from '@material-ui/icons/PermMedia';
import LibraryBooksIcon from '@material-ui/icons/LibraryBooks';
import SupervisedUserCircleIcon from '@material-ui/icons/SupervisedUserCircle';
import PolicyIcon from '@material-ui/icons/Policy';
import BarChartIcon from '@material-ui/icons/BarChart';
import LanguageIcon from '@material-ui/icons/Language';
import ForumIcon from '@material-ui/icons/Forum';
import { Trans } from '@lingui/macro';
import Typography from '@material-ui/core/Typography';
import Tooltip from '@material-ui/core/Tooltip';
import InfoIcon from '@material-ui/icons/Info';

const styleLink = {
    textDecoration: 'none',
    color:'gray'
};

class MainAdminEvent extends Component {
    static contextType = GlobalContext;



    state = {
        breadcrumbs: [
            {
                name: <Trans>Panel de configuración del evento</Trans>,
                active: true,
                link: '',
            }
        ],
        tooltipText:{
            usuarios:<Trans>Información relacionada a todos los usuarios que participarán en el evento. Envíales notificaciones, agrega documentos a cada usuario, da de alta, da de baja, asigna perfiles de seguridad, entre otros.</Trans>,
            roles:<Trans>Administra permisos en cada catálogo para un grupo de usuarios, podrás también seleccionar que otros perfiles se pueden administrar.</Trans>,
            modulos:<Trans>Crea grupos de vistas (páginas que se mostrarán en la aplicación móvil) a través del “builder” o “diseñador” y agrúpalos en diferentes módulos según lo requieras.</Trans>,
            agenda:<Trans> Podrás dar de alta las actividades que conforman el programa del evento.</Trans>,
            recursos:<Trans>Agrega documentos relacionados al evento, podrás configurarlos para que sean visibles en la aplicación.</Trans>,
            encuestas:<Trans>Crea formularios para los usuarios, una vez contestados, descarga las respuestas.</Trans>,
            lenguajes:<Trans>Crea nuevos idiomas, podrás activarlos y desactivarlos.</Trans>,
            subeventos:<Trans> Eventos alternos al programa del evento.</Trans>,
            info:<Trans>Información general del evento.</Trans>,
            log:<Trans>Historial de cambios que otros usuarios han realizado en el sistema.</Trans>,
            chatbot:<Trans>Configura los mensajes que se mostrarán en el chatbot de la aplicación móvil.</Trans>,
            configuracion:<Trans></Trans>,
            estadisticas:<Trans>Analiza los datos recopilados del evento.</Trans>,
        }
    };

    render() {

        const ctx = this.context;
        let {tooltipText} = this.state;

        const AdminTitle = <Trans>Administrador de eventos</Trans>;

        return (
            <AdminEventLayout title={AdminTitle} breadcrumbs={this.state.breadcrumbs}>
                <Grid container>
                        <Grid align='center' item xs={12} md={3} lg={2}>

                            <Paper   style={{padding:'30px',margin:'20px',height:'80%'}}>
                                <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/user/list/`}>
                                    <PersonIcon style={{ fontSize: 100, color:'#01579B' }}/>
                                    <br/>
                                    <Tooltip title={tooltipText.usuarios}>
                                        <Typography variant="caption" display="block" gutterBottom>
                                            <Trans>Usuarios</Trans>
                                        </Typography>
                                    </Tooltip>
                                </Link>
                            </Paper>

                        </Grid>


                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}}>

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/role/list/`}>
                                <GroupIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.roles}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Perfiles</Trans>
                                    </Typography>
                                </Tooltip>
                            </Link>
                        </Paper>
                    </Grid>


                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >
                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/module/list/`}>
                                <WidgetsIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.modulos}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Módulos</Trans>
                                    </Typography>
                                </Tooltip>
                            </Link>
                        </Paper>
                    </Grid>
                    {/*<Grid align='center' item xs={12} md={3} lg={2}>*/}
                    {/*    <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >*/}

                    {/*        <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/availability/`}>*/}
                    {/*            <AccessTimeIcon style={{ fontSize: 100,color:'#01579B' }}/>*/}
                    {/*            <br/>*/}
                    {/*            <Tooltip title={tooltipText.disponibilidad}>*/}
                    {/*                <Typography variant="caption" display="block" gutterBottom>*/}
                    {/*                    <Trans>Disponibilidad</Trans>*/}
                    {/*                </Typography>*/}
                    {/*            </Tooltip>*/}

                    {/*        </Link>*/}
                    {/*    </Paper>*/}
                    {/*</Grid>*/}
                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/agenda/list/`}>
                                <EventAvailableIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.agenda}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Programa del evento</Trans>
                                    </Typography>
                                </Tooltip>
                            </Link>
                        </Paper>
                    </Grid>



                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/resources/list/`}>
                                <PermMediaIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.recursos}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Archivos</Trans>
                                    </Typography>
                                </Tooltip>
                            </Link>
                        </Paper>
                    </Grid>


                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/poll/list/`}>
                                <LibraryBooksIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.encuestas}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Encuestas</Trans>
                                    </Typography>
                                </Tooltip>

                            </Link>
                        </Paper>
                    </Grid>


                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/lang/list/`}>
                                <LanguageIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.lenguajes}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Lenguajes</Trans>
                                    </Typography>
                                </Tooltip>

                            </Link>
                        </Paper>
                    </Grid>
{/*
                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/sub-event/list/`}>
                                <SupervisedUserCircleIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.subeventos}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Eventos alternos</Trans>
                                    </Typography>
                                </Tooltip>

                            </Link>
                        </Paper>
                    </Grid>
*/}


                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >
                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/changelog/`}>
                                <PolicyIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.log}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Registro de cambios</Trans>
                                    </Typography>
                                </Tooltip>

                            </Link>
                        </Paper>
                    </Grid>

                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/chatbot/`}>
                                <ForumIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.chatbot}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Chatbot</Trans>
                                    </Typography>
                                </Tooltip>
                            </Link>
                        </Paper>
                    </Grid>


                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/stats`}>
                                <BarChartIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.estadisticas}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Estadísticas</Trans>
                                    </Typography>
                                </Tooltip>
                            </Link>
                        </Paper>
                    </Grid>


                    <Grid align='center' item xs={12} md={3} lg={2}>
                        <Paper  style={{padding:'30px',margin:'20px',height:'80%'}} >

                            <Link style={styleLink} push="true" to={`/${ctx.slug}/admin/info-event/`}>
                                <InfoIcon style={{ fontSize: 100,color:'#01579B' }}/>
                                <br/>
                                <Tooltip title={tooltipText.info}>
                                    <Typography variant="caption" display="block" gutterBottom>
                                        <Trans>Información del evento</Trans>
                                    </Typography>
                                </Tooltip>

                            </Link>
                        </Paper>
                    </Grid>



                </Grid>
            </AdminEventLayout>
        );
    }
}

export default MainAdminEvent;