import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import CreateRoleForm from "../../../components/events/security/CreateRoleForm";
import {ApiServiceEvents} from "../../../api-services/events";
import {Redirect} from "react-router-dom";
import CircularProgress from "@material-ui/core/CircularProgress";
import GlobalContext from "../../../GlobalContext";
import _ from "lodash";
import {Trans, t} from '@lingui/macro';
import {withI18n} from "@lingui/react";

const apiEvents = new ApiServiceEvents();

/**
 *
 * This view is for resource update per user
 * @class EditRoleEvent
 * @extends {Component}
 */
class EditRoleEvent extends Component {

    static contextType = GlobalContext;

    state = {
        perms: [],
        groups: [],
        slug: '',
        _idGroup: '',
        name: '',
        color: '',
        description: '',
        loading: false,
        loadingDetail: true,
        openSnackbar: false,
        redirect: false,
        msg: this.props.i18n._(t`Editado correctamente`),
        breadCrumbs: [],
        roles: [],
        profileType: "0",
        isUserForeignCREA: false,
        externalCode: ""
    };

    /**
     * Function to manage permissions and roles
     * @param  {number} action
     * @param  {Object} e
     */
    handleManageProfiles = (action, e) => {
        let checked = e.target.checked;
        let {groups} = this.state;

        if (checked) {
            groups.indexOf(action) !== -1 || groups.push(action);
        } else {
            let index = groups.indexOf(action);
            if (index > -1) {
                groups.splice(index, 1);
            }
        }

        this.setState({
            groups
        })

    };

    /**
     * Function to get all roles
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvents.getRoles(ctx.slug).then(
            (res) => {
                console.log('Perfiles', res);
                this.setState({roles: res});
            }
        ).catch(
            (err) => {
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                console.log('Error en la carga de perfiles', err)
            }
        );
    };

    componentDidMount() {
        let slug = this.props.match.params.slug;
        this.loadRoles();

        let breadCrumbs = [
            {
                name: this.props.i18n._(t`Lista de perfiles`),
                active: false,
                link: `/${slug}/admin/role/list/`
            },
            {
                name: this.props.i18n._(t`Editar perfil`),
                active: true,
                link: '/'
            }
        ];


        let _idGroup = this.props.match.params.role;
        this.setState({slug, _idGroup, breadCrumbs})
        this.getRoleDetail(slug, _idGroup);
    }

    msgDefault = this.props.i18n._(t`Guardado correctamente.`)

    /**
     * Generate a Toast type message, receiving a text as a message
     * @param  {string} msg
     */
    showToast = (msg = this.msgDefault) => {
        this.setState({openSnackbar: true, msg})
    };


    /**
     * We get the profile detail to set in the corresponding fields
     * @param  {string} slug
     * @param  {number} _idGroup
     */
    getRoleDetail(slug, _idGroup) {
        apiEvents.getRoleDetail(_idGroup, slug).then((res) => {
            console.log("externalCode",res)
            this.setDataRole(res)


            let breadCrumbs = [
                {
                    name: this.props.i18n._(t`Lista de perfiles`),
                    active: false,
                    link: `/${slug}/admin/role/list/`
                },
                {
                    name: this.props.i18n._(t`Editar perfil `) + ' ' + res.name,
                    active: true,
                    link: '/'
                }
            ];
            this.setState({breadCrumbs})
            console.log('res', res)
            if (_.has(res, 'groupsCanAdmin') && res.groupsCanAdmin) {
                this.setState({groups: res.groupsCanAdmin});
            }
        }).catch((err) => console.log(err)).finally(() => this.setState({loadingDetail: false}))
    }


    /**
     * Function to set state on name,color, perms, description from getRoleDetail
     * @param  {Object} data
     */
    setDataRole(data) {
        let {name, color, externalCode, perms, description, type, isCREA} = data;

        let profileType = type ? type.toString() : '0';

        this.setState({
            name, color, externalCode, perms, description, profileType, isUserForeignCREA: isCREA
        })

    }


    /**
     * It is executed when the role permission controls are checked
     * @param  {string} action
     * @param  {Object} e
     */
    handleChange = (action, e) => {
        let checked = e.target.checked
        let {perms} = this.state;

        if (checked) {
            perms.indexOf(action) !== -1 || perms.push(action);
        } else {
            let index = perms.indexOf(action);
            if (index > -1) {
                perms.splice(index, 1);
            }
        }        

        this.setState({
            perms
        })
    }


    /**
     * This function is executed when the user writes or fits the information in the input of the forms
     * @param  {Object} event
     */
     handleEventInput = (event) => {
        let value = null
        const name = event.target.name;
        let ctx = this.context;
        const patternChars = /[^A-Za-z0-9 ñÑáéíóúÁÉÍÓÚüÜ\,.:!#?]/;
        // const patternCharsName = /[^A-Za-z ñÑáéíóúÁÉÍÓÚüÜ]/;

        
        if(event.target.name == 'name'){
            if(event.target.value.length>50){
                event.target.value = event.target.value.slice(0,50);
            }
            if(patternChars.test(event.target.value)){
                event.target.value = event.target.value.slice(0,-1);
                ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
            }
        }
        if(event.target.name == 'description'){
            if(event.target.value.length>250){
                event.target.value = event.target.value.slice(0,250);
            }
            if(patternChars.test(event.target.value)){
                event.target.value = event.target.value.slice(0,-1);
                ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
            }
        }
        
        value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;
        
        this.setState({
            [name]: value
        });
    };

    handleChangeProfileType = event => {
        // console.log('Event', event);
        let value = event.target.value;
        this.setState({profileType: value});
    };

    handleManageIsUserCREA = (e) => {
        // Manejo de usuarioos crea
        let checked = e.target.checked;
        let {externalCode}  = this.state;
        if (!checked){
            externalCode =""
        }
        this.setState({isUserForeignCREA: checked,externalCode: externalCode});
    }

    /**
     * We generate the submit form to save Profile data
     * @param  {Object} e
     */
    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({loading: true})
        let {perms, slug, name, color, description, externalCode, _idGroup, groups, profileType, isUserForeignCREA} = this.state;
        let request = {
            perms,
            slug,
            name,
            color,
            description,
            externalCode,
            _idGroup,
            groupsCanAdmin: groups,
            type: profileType,
            isCREA: isUserForeignCREA
        };
        let ctx = this.context;
        apiEvents.updateRole(request).then((res) => {
            // this.showToast();
            this.context.openSuccessNotification(this.props.i18n._(t`Perfil guardado.`));
            this.setState({redirect: true})
        })
            .catch((err) => {
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                } else {
                    if (_.has(err, 'response.data.message') && err.response.data.message) {
                        let errorMsg = err.response.data.message;
                        console.log('errorMsg', errorMsg);
                        ctx.openNotification(this.props.i18n._(t`Se ha presentado un problema al guardar la información, vuelve a intentar.`), 'error');
                    }

                }
            })
            .finally(() => {
                this.setState({loading: false})
            })

    }


    render() {

        if (this.state.redirect) return <Redirect push to={`/${this.state.slug}/admin/role/list`}/>;
        return (
            <AdminEventLayout breadcrumbs={this.state.breadCrumbs} title={<trans>Administrador de eventos</trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        {
                            this.state.loadingDetail ? <CircularProgress color="primary"/> :
                                <CreateRoleForm handleEventInput={this.handleEventInput}
                                                handleSubmit={this.handleSubmit}
                                                titleform={<Trans>Editar perfil de usuario</Trans>}
                                                slug={this.state.slug}
                                                name={this.state.name}
                                                color={this.state.color}
                                                loading={this.state.loading}
                                                description={this.state.description}
                                                perms={this.state.perms}
                                                buttonTitle={<Trans>Guardar</Trans>}
                                                roles={this.state.roles}
                                                profileType={this.state.profileType}
                                                handleChangeProfileType={this.handleChangeProfileType}
                                                isEdit={true}
                                                externalCode={this.state.externalCode}
                                                isUserForeignCREA={this.state.isUserForeignCREA}
                                                handleManageIsUserCREA={this.handleManageIsUserCREA}
                                                selectedGroups={this.state.groups}
                                                handleManageProfiles={this.handleManageProfiles}
                                                handleChange={this.handleChange}/>
                        }
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(EditRoleEvent);