import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
// import FormControlLabel from '@material-ui/core/FormControlLabel';
// import FormLabel from '@material-ui/core/FormLabel';
// import Radio from '@material-ui/core/Radio';
// import RadioGroup from '@material-ui/core/RadioGroup';
// import Switch from '@material-ui/core/Switch';
import SpeedDial from '@material-ui/lab/SpeedDial';
// import SpeedDialIcon from '@material-ui/lab/SpeedDialIcon';
import MenuIcon from '@material-ui/icons/Menu';
import SpeedDialAction from '@material-ui/lab/SpeedDialAction';
// import FileCopyIcon from '@material-ui/icons/FileCopyOutlined';
// import SaveIcon from '@material-ui/icons/Save';
// import PrintIcon from '@material-ui/icons/Print';
// import ShareIcon from '@material-ui/icons/Share';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowDownwardIcon from '@material-ui/icons/ArrowDownward';
import ArrowUpwardIcon from '@material-ui/icons/ArrowUpward';
import SettingsIcon from '@material-ui/icons/Settings';

const useStyles = makeStyles(theme => ({
    root: {
        transform: 'translateZ(0px)',
        flexGrow: 1,
    },
    exampleWrapper: {
        position: 'relative',
        marginTop: theme.spacing(0),
        // height: 100,
    },
    radioGroup: {
        margin: theme.spacing(0, 0),
    },
    speedDial: {
        position: 'absolute',
        '&.MuiSpeedDial-directionUp, &.MuiSpeedDial-directionLeft': {
            // bottom: theme.spacing(1),
            // right: theme.spacing(1),
            zIndex: 1200
        },
        '&.MuiSpeedDial-directionDown, &.MuiSpeedDial-directionRight': {
            top: theme.spacing(-1),
            left: theme.spacing(-1),
            zIndex: 1200
        },
    },
}));

export default function MenuComponent(props) {
    const classes = useStyles();
    const [direction, setDirection] = React.useState('up');
    // const [open, setOpen] = React.useState(false);
    // const [hidden, setHidden] = React.useState(false);

    const handleDirectionChange = event => {
        setDirection(event.target.value);
    };

    // const handleHiddenChange = event => {
    //     setHidden(event.target.checked);
    // };

    // const handleClose = () => {
    //     setOpen(false);
    // };
    //
    // const handleOpen = () => {
    //     setOpen(true);
    // };

    const handleClose = props.handleCloseMenuComponent;
    const handleOpen = props.handleOpenMenuComponent;

    const componentType = props.component.type;

    const componentWithoutMenu = ['space', 'divisor'];

    let actions = [
        { icon: <ArrowUpwardIcon />, name: 'Mover arriba', action: props.moveUp },
        { icon: <ArrowDownwardIcon />, name: 'Mover abajo', action: props.moveDown},

    ];

    if(!componentWithoutMenu.includes(componentType)){
        actions.push(
            { icon: <SettingsIcon />, name: 'Configuración', action: props.openSettings},
        );
    }

    actions.push(
        { icon: <DeleteIcon />, name: 'Borrar' , action: props.deleteComponent},
    );



    const {hidden, open} = props;

    return (
        <div>
            <div className={classes.exampleWrapper}>
                <SpeedDial
                    ariaLabel="Menu componente"
                    className={classes.speedDial}
                    hidden={hidden}
                    // icon={<SpeedDialIcon />}
                    icon={<MenuIcon />}
                    onClose={handleClose}
                    onOpen={handleOpen}
                    open={open}
                    direction={'right'}
                    // transitionDuration={1}
                    FabProps={{
                        color: 'primary',
                        size: 'small',
                    }}
                >
                    {actions.map(action => (
                        <SpeedDialAction
                            title={action.name}
                            key={action.name}
                            icon={action.icon}
                            tooltipTitle={action.name}
                            onClick={action.action}
                            FabProps={{
                                // color: 'secondary',
                                size: 'small'
                            }}
                        />
                    ))}
                </SpeedDial>
            </div>
        </div>
    );
}
