import React, {Component} from 'react';
import {Grid,Typography} from "@material-ui/core";
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import _ from 'lodash'
import Divider from '@material-ui/core/Divider';
import GlobalContext from "../../../GlobalContext";
import Chip from '@material-ui/core/Chip';
import {ApiServiceEvents} from "../../../api-services/events";
import { Trans } from '@lingui/macro';
import LoadingOverlay from "react-loading-overlay";
import {Field, Form} from "react-final-form";
import {TextField} from "final-form-material-ui";
import Avatar from '@material-ui/core/Avatar';
import FormHelperText from '@material-ui/core/FormHelperText';
import Input from '@material-ui/core/Input';
import InputLabel from '@material-ui/core/InputLabel';
import InputAdornment from '@material-ui/core/InputAdornment';
import FormControl from '@material-ui/core/FormControl';
import FileBase64 from "react-file-base64";
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import IconButton from '@material-ui/core/IconButton';
import CheckIcon from '@material-ui/icons/Check';
import {Redirect} from "react-router-dom";
import {CancelButton, SaveButtonSpinner} from "../../../components/buttons/StandartButtons";

const apiEvent = new ApiServiceEvents();


/**
 *
 * This view is for view my profile details
 * @class MyProfile
 * @extends {Component}
 */
class MyProfile extends Component {
    static contextType = GlobalContext;

    state = {
        isLoading: false,
        isLoadingPassword:false,
        password:'',
        passwordConfirm:'',
        showErrorPassword:false,
        redirectToMyProfileAdmin: false,
        firstName: '',
        lastName: '',
        email: '',
        groups: [],
        arrayGroups: [],
        image: '',
        _id:'',
        isAdmin: '',
        isStaff: '',
        phone: '',
        slug: '',
        nameGroup: '',
        eventUser:{},
        breadcrumbs:[
            {
                name: <Trans>Mi perfil</Trans>,
                active: true,
                link: '',
            }

        ]
    }

    componentWillMount(){
        const userStr = localStorage.getItem('user');
        const user = JSON.parse(userStr);
        console.log('jsonuser',user);
        let eventUser = {
            firstName: user.firstName,
            lastName: user.lastName,
            email: user.email,
            groups: user.groups,
            image: user.image,
            isAdmin: user.isAdmin,
            isStaff: user.isStaff,
            phone: user.phone
        };

        this.setState({
            _id:user._id,
            eventUser
        });

        if(user){
            let slug = this.props.match.params.slug;
            this.getUserDetail(user._id,slug);
        }
    }

    componentDidMount(){
        console.log(this.state.groups)
    }


    getUserDetail=(userId,slug)=>{
        this.setState({
            isLoading:true
        })
        apiEvent.getEventUserDetail(userId, slug).then((res)=>{
            console.log('user', res);
            this.setState({
                eventUser:res.eventUser
            })


            if(res.eventUser.groups)
            res.eventUser.groups.map(group => {
                this.getnameRol(group._idGroup )
            })

        }).catch((err)=>{
            if(err.response){
                if(err.response.status===404){
                    /// no hay un usuario tipo eventUser, por lo que debe ser un usuario admin
                    this.setState({
                        redirectToMyProfileAdmin:true
                    })
                }
            }
            console.log(err.response.status)
        }).finally(()=>{
            this.setState({
                isLoading:false
            })
        })
    }

    
    /**
     * Function to get group name
     * @param  {number} idGroup
     * @returns {string} user group name
     */
    getnameRol = (idGroup) => {
        let slug = this.props.match.params.slug;
        let nameGroup = '' ;
        apiEvent.getRoleDetail(idGroup ,slug).then(
            (res) => {
                nameGroup = res.name;
                let arrayGroups = this.state.arrayGroups;
                arrayGroups.push(nameGroup)
                this.setState({arrayGroups:arrayGroups})
            }
        ).catch(
            (err) => {
                console.log('Error en la carga de perfiles', err)
                nameGroup = '';
                this.setState({nameGroup})
            }
        )
        return nameGroup;
    }


    handleSubmitPassword=(values)=>{
        let request = _.clone(values);
        let ctx = this.context;
        if(request.password!==request.passwordConfirm){
            this.setState({
                showErrorPassword:true,
                isLoadingPassword:false
            })
        }else{
            this.setState({
                isLoadingPassword:true,
            })

            apiEvent.changePasswordUser(request.password).then((res)=>{
                if(res.message)
                ctx.openNotification(res.message,'success')
            }).catch((err)=>{
                ctx.openNotification(<Trans>Ha ocurrido un error por favor intenta mas tarde.</Trans>,'success')
            }).finally(()=>{
                this.setState({
                    isLoadingPassword:false
                })
            })


        }



    };


    handleSubmit=(values)=>{
        let slug = this.props.match.params.slug;
        let request = _.clone(values);
        let {eventUser, _id} = this.state;
        let ctx = this.context;
        this.setState({
            isLoading:true
        });
        request.slug = slug;
        request._idUser = _id;


        if(eventUser.image){
            request.image = eventUser.image
        }else{
            delete request.image;
        }

        apiEvent.updEventUser(request).then((res)=>{
            console.log('success',res);
            localStorage.setItem('user', JSON.stringify(res));
            ctx.openNotification(<Trans>Actualizado correctamente</Trans>, 'success');

        }).catch((err)=>{
            console.log(err)
        }).finally(()=>{
            this.setState({
                isLoading:false
            })
        })

    }


    handleChange = prop => event => {
        this.setState({ [prop]: event.target.value });
    };


    /**
     * Function for get file image and set to state in base64
     * @param  {Object} files
     */
    getFiles=(files)=>{
        if(files.base64) {
            let oldState = this.state;
            oldState.eventUser.image = {
                type:'imageBase64',
                value:files.base64,
                name:files.name
            };
            this.setState(oldState);
        }
    };


    validate = values => {
        const errors = {};
        if(!values.password)
            errors.password = 'Required';

        if(values.password && values.password !== values.passwordConfirm){
            errors.password = <Trans>Las contraseñas no coinciden</Trans>
        }

        return errors;
    };

    render () {
        const ctx = this.context;
        let {eventUser} = this.state;
        const userStr = localStorage.getItem('user');
        const user = JSON.parse(userStr);
        if(this.state.redirectToMyProfileAdmin) return <Redirect push to={`/user/${user._id}/`} />;

        return (
            <LoadingOverlay
                active={this.state.isLoading || this.state.isLoadingPassword}
                spinner
                text={<Trans>Cargando...</Trans>}
            >
            <AdminEventLayout breadcrumbs={this.state.breadcrumbs}>
                <Grid container direction="row" title=''>
                    <Grid item xs={12}>
                        <Card>
                            <CardContent>
                                <Grid container direction="row" justify="center" alignItems="center">
                                    <Grid item md={6} xs={6}>
                                        {
                                            _.has(eventUser,'image.value')?
                                                <Avatar alt={eventUser.firstName} src={eventUser.image.value} style={{width:170, height:170, padding:10}} />:''
                                        }

                                        <FileBase64
                                            onDone={this.getFiles} />

                                        <Typography variant="p"  color="textSecondary" style={{marginTop:20, display:'block'}}>
                                            <b><Trans>Email: </Trans></b> {eventUser.email}
                                        </Typography>

                                    </Grid>

                                    <Grid item md={6} xs={6}>
                                        { eventUser.isAdmin ? <Typography  color="textSecondary" style={{marginTop:20}}><CheckIcon style={{ color: 'green' }}/> <b><Trans>Administrador</Trans></b></Typography> : '' }
                                        { eventUser.isStaff ? <Typography  color="textSecondary" style={{marginTop:20}}><CheckIcon style={{ color: 'green' }}/><b><Trans>Staff</Trans></b></Typography> : ''}
                                    </Grid>

                                    <Grid item  xs={12} style={{marginTop:30}}>
                                        <Form
                                            onSubmit={this.handleSubmit}
                                            initialValues={this.state.eventUser}
                                            render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                                                <form onSubmit={handleSubmit}>
                                                    <Grid container alignItems="flex-start" spacing={2}>

                                                        <Grid item xs={12} md={6}>
                                                            <Field
                                                                fullWidth
                                                                name="firstName"
                                                                component={TextField}
                                                                label="Nombre"
                                                                required
                                                                variant="outlined"
                                                            />
                                                        </Grid>
                                                        <Grid item xs={12} md={6}>
                                                            <Field
                                                                fullWidth
                                                                name="lastName"
                                                                required
                                                                component={TextField}
                                                                label="Apellidos"
                                                                variant="outlined"
                                                            />

                                                        </Grid>


                                                        <Grid item xs={12} md={12}>
                                                            <Field
                                                                fullWidth
                                                                name="company"
                                                                component={TextField}
                                                                label="Empresa"
                                                                variant="outlined"
                                                            />

                                                        </Grid>


                                                    </Grid>



                                                    <Grid item xs={12}>

                                                        <Typography  color="textSecondary" style={{marginTop:20}}>
                                                            <Trans>Rol</Trans>
                                                        </Typography>
                                                        <Typography variant="h5" component="h5">
                                                            {
                                                                this.state.arrayGroups.map((item,i) =>
                                                                    {
                                                                        return (<Chip style={{marginRight:10}} key={i} label={item}/>)
                                                                    }

                                                                )
                                                            }
                                                        </Typography>
                                                    </Grid>

                                                    <Grid item style={{ marginTop: 16 }} xs={12}>

                                                        <CancelButton
                                                            to={`/${ctx.slug}/admin/`}
                                                        />

                                                        <SaveButtonSpinner
                                                            loading={this.state.isLoading}
                                                        />
                                                    </Grid>

                                                </form>
                                            )}
                                        />
                                    </Grid>

                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                    <Grid item xs={12} style={{marginTop:20}}>
                        <Card>
                            <CardContent>
                                <Grid container direction="row" justify="center" alignItems="center">
                                    <Grid item xs={12}>
                                        <Form
                                            onSubmit={this.handleSubmitPassword}
                                            initialValues={this.state}
                                            validate={this.validate}
                                            render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                                                <form onSubmit={handleSubmit}>
                                                    <Typography  color="textSecondary" variant={'h5'} style={{marginTop:20,marginBottom:20}}>
                                                        <Trans>Cambiar contraseñas</Trans>
                                                    </Typography>
                                                    <Grid container container alignItems="flex-start" spacing={2}>
                                                    <Grid item md={6} xs={12}>
                                                        <Field
                                                            fullWidth
                                                            name="password"
                                                            required
                                                            type={'password'}
                                                            component={TextField}
                                                            label={<Trans>Contraseña</Trans>}
                                                            variant="outlined"
                                                        />
                                                    </Grid>

                                                    <Grid item md={6} xs={12}>
                                                        <Field
                                                            fullWidth
                                                            name="passwordConfirm"
                                                            required
                                                            type={'password'}
                                                            component={TextField}
                                                            label={<Trans>Confirmar contraseña</Trans>}
                                                            variant="outlined"
                                                        />
                                                        {/*{*/}
                                                        {/*    this.state.showErrorPassword?<FormHelperText id="component-error-text"  error><Trans>Las contraseñas no coinciden</Trans></FormHelperText>:''*/}
                                                        {/*}*/}

                                                        {submitError && <div className="error">{submitError}</div>}


                                                    </Grid>

                                                        <Grid item style={{ marginTop: 16 }} xs={12}>

                                                            <CancelButton
                                                                to={`/${ctx.slug}/admin/`}
                                                            />

                                                            <SaveButtonSpinner
                                                                loading={this.state.isLoadingPassword}
                                                            />
                                                        </Grid>

                                                    </Grid>
                                                </form>
                                            )}
                                        />
                                    </Grid>


                                </Grid>
                            </CardContent>
                        </Card>
                    </Grid>
                </Grid>
            </AdminEventLayout>
            </LoadingOverlay>
        )
    }

}

export default MyProfile;

