import React from 'react';
import { Form, Field } from 'react-final-form';
import { TextField, Checkbox, Radio, Select } from 'final-form-material-ui';
import {Link as LinkRouter} from "react-router-dom";
import {
    Typography,
    Paper,
    Link,
    Grid,
    Button,
    CssBaseline,
    RadioGroup,
    FormLabel,
    MenuItem,
    FormGroup,
    FormControl,
    FormControlLabel,
} from '@material-ui/core';
import {
    MuiPickersUtilsProvider,
    TimePicker,
    DatePicker,
} from '@material-ui/pickers';
import FileBase64 from "react-file-base64";
import Box from "@material-ui/core/Box";
import Image from 'material-ui-image';
import DateFnsUtils from '@date-io/date-fns';
import GlobalContext from "../../../GlobalContext";
import {CancelButton, SaveButtonSpinner} from "../../buttons/StandartButtons";
import {DatePickerWrapper,EndDatePickerWrapper,TimePickerWrapper} from "../../fields/DatePickers";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import moment from 'moment';
import PropTypes from 'prop-types';

class UserAgendaForm extends React.Component{
    static contextType = GlobalContext;

    render() {
        let ctx = this.context;
        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 800 }}>
                <CssBaseline />
                <Typography variant="h4" align="center" component="h1" gutterBottom>
                    {
                        this.props.title ? this.props.title : 'Crear usuario'
                    }
                </Typography>

                <Form
                    onSubmit={this.props.onSubmit}
                    initialValues={this.props.initialValues}
                    validate={this.props.validate}
                    render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                        <form onSubmit={handleSubmit} noValidate>
                            <Paper style={{ padding: 16 }}>
                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                    Datos generales
                                </Typography>
                                <Grid container style={{marginTop: 10}} alignItems="flex-start" spacing={2}>
                                    <Grid item xs={6}>
                                        <Field
                                            fullWidth
                                            required
                                            name="name"
                                            component={TextField}
                                            type="text"
                                            label="Nombre"
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <Grid item xs={6}>
                                        <Field
                                            fullWidth
                                            required
                                            name="place"
                                            component={TextField}
                                            type="text"
                                            label="Lugar"
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Field
                                            fullWidth
                                            required
                                            name="address"
                                            component={TextField}
                                            multiline
                                            label="Dirección"
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <Grid item xs={12}>

                                        <Field
                                            fullWidth
                                            required
                                            name="description"
                                            rows="4"
                                            component={TextField}
                                            multiline
                                            label="Descripción"
                                            variant="outlined"
                                        />
                                    </Grid>

                                    <Grid item xs={12} style={{marginTop: 10}}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Periodo de tiempo
                                        </Typography>
                                    </Grid>

                                    <MuiPickersUtilsProvider utils={DateFnsUtils}>

                                        <Grid item xs={6}>
                                            <Field
                                                name="startTimeDate"
                                                component={DatePickerWrapper}
                                                fullWidth
                                                required
                                                // margin="normal"
                                                label="Fecha y hora de inicio"
                                                inputVariant="outlined"
                                            />
                                        </Grid>
                                    </MuiPickersUtilsProvider>
                                    <MuiPickersUtilsProvider utils={DateFnsUtils}>

                                        <Grid item xs={6}>
                                            <Field
                                                name="endTimeDate"
                                                component={EndDatePickerWrapper}
                                                fullWidth
                                                required
                                                //margin="normal"
                                                label="Fecha y hora de término"
                                                inputVariant="outlined"
                                            />
                                        </Grid>
                                    </MuiPickersUtilsProvider>
                                    
                                    {submitError && <div className="error">{submitError}</div>}
                                    <Grid item style={{ marginTop: 16 }} xs={12}>

                                        <CancelButton
                                            to={`/${ctx.slug}/admin/user/${this.props.userId}/agenda/`}
                                        />

                                        <SaveButtonSpinner
                                            loading={this.props.loading}
                                        />
                                    </Grid>
                                </Grid>
                            </Paper>
                        </form>
                    )}
                />
            </div>
        );
    }
}


UserAgendaForm.propTypes = {
    industryConfirmList:PropTypes.array,
}


UserAgendaForm.defaultProps = {
    industryConfirmList: []
};


export default UserAgendaForm
