import React from "react";
import _ from "lodash";
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import AddIcon from '@material-ui/icons/Add';
import Paper from '@material-ui/core/Paper'
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import {ApiServiceEvents} from "../../../api-services/events";
import GlobalContext from "../../../GlobalContext";
import {Link, Redirect} from "react-router-dom";
import AgendaUserTable from "../../../components/events/security/UserAgendaTable";
import Button from "@material-ui/core/Button";
import CircularProgress from '@material-ui/core/CircularProgress';
import moment from "moment";
import {t,Trans} from "@lingui/macro";
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import FullScreenDialogGeneric from "../../../components/modals/FullScreenDialogGeneric";
import {withI18n} from "@lingui/react";

const apiEvent = new ApiServiceEvents();


class EventUserAgendaList extends React.Component{

    static contextType = GlobalContext;
    
    state = {
        idUser: '',
        agendaList: [],
        openModalImportCsv:false,
        redirectToCreate: false,
        loadingImport:false,
        groups:[],
        loading:true,
        msgLoading:'cargando...',
        fileCsv: '',
        breadcrumbs: []
    };



    /**
     * Function to delete user schedule selected
     * @param  {Object} oldData
     */
    handlerDelete = (oldData) =>
        new Promise(resolve => {
            let ctx = this.context;
            let userid = this.props.match.params.id;
            apiEvent.deleteAgendaUser(ctx.slug,userid,oldData._id).then(
                (response) => {
                    resolve();
                    console.log('se borro agenda usuario', response);
                    this.getData(userid,ctx.slug);
                    // let newUserList = this.state.userList;
                    // let index = _.findIndex(this.state.userList, {_id: oldData._id});
                    // newUserList.splice(index, 1, {id: 100, name: 'New object.'})
                }
            ).catch(
                (err) => {
                    resolve();
                    console.log(err, err);
                    try{
                        if(err.response.status === 403){
                            ctx.openPermissionNotification();
                        }
                        console.log('Error', err.response);
                    }catch (e) {

                    }

                }
            );

        });

    componentDidMount() {
        let userid = this.props.match.params.id;
        this.setState({'idUser':userid})
        let ctx = this.context;
        console.log(ctx.slug)
        this.setState({
            breadcrumbs: [
                {
                    name: this.props.i18n._(t`Lista de usuarios`),
                    active: false,
                    link: `/${ctx.slug}/admin/user/list/`
                },
                {
                    name: this.props.i18n._(t`Agenda del usuario`),
                    active: true,
                    link: '',
                }

            ]
        });
        this.getData(userid,ctx.slug);
        this.getDataUser(userid,ctx.slug)

    }


    getDataUser = (userid,slug)=>{
        apiEvent.getEventUserDetail(userid, slug).then((res)=>{
            // let eventUser
            let {eventUser} = res;
            let name = eventUser.firstName  + ' ' +eventUser.lastName;
            this.setState({
                breadcrumbs: [
                    {
                        name: this.props.i18n._(t`Lista de usuarios`),
                        active: false,
                        link: `/${slug}/admin/user/list/`
                    },
                    {
                        name: this.props.i18n._(t`Agenda del usuario `)+' ' + name,
                        active: true,
                        link: '',
                    }

                ]
            });
        }).catch((err)=>{
            console.log(err)
        })
    }

    getData = (userid,slug) => {
        apiEvent.getAgendaUserList(userid,slug).then(
            (res) => {
                /* this.setState({agendaList: res}); */
                let row = {name:'', place:'', address:'', startTime: '', endTime:''}
                let data = []
                res.map((item,index) => {
                    row = {
                        _id:item._id,
                        name:item.name,
                        place:item.place,
                        address:item.address,
                        startTime: moment(item.startTime).format('MM/DD/YYYY, h:mm:ss a'),
                        endTime: moment(item.endTime).format('MM/DD/YYYY, h:mm:ss a')
                    }
                    //datacsv.push([row.date,row.module, row.action, row.user,row.bodyRequest]);
                    data.push(row);
        
                })
                
                this.setState({agendaList:data})
                console.log(this.state.agendaList);
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    this.context.openPermissionNotification();
                }
            }
        ).finally(()=> this.setState({loading:false}));
    };


    redirectCreate = () => this.setState({redirectToCreate: true});

    render() {
        let ctx = this.context;
        if(this.state.redirectToCreate) return <Redirect push to={`/${ctx.slug}/admin/agenda/user/${this.state.idUser}/create/`}/>;

        return (
                <AdminEventLayout title={<Trans>Administrador de eventos</Trans>} breadcrumbs={this.state.breadcrumbs}>
                <Grid container>

                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            Agenda
                            <IconButton onClick={this.redirectCreate} color="primary" aria-label={<Trans>add activity</Trans>}>
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>


                    <Grid item xs={12}>
                        { this.state.loading ? 
                            <Paper xs={12} style={{'textAlign': "center","paddingTop":50,"paddingBottom":50}}><CircularProgress /> </Paper>
                            :
                        <AgendaUserTable rows={this.state.agendaList} UserId={this.state.idUser} handlerDelete={this.handlerDelete}/> }
                    </Grid>
                </Grid>



            </AdminEventLayout>
        )
    }
}

export default withI18n() (EventUserAgendaList)