import React, { Component } from 'react';
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import { Grid } from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import { Link } from "react-router-dom";
import AddIcon from "@material-ui/core/SvgIcon/SvgIcon";
import { Trans } from '@lingui/macro';
import { ApiServiceEvents } from "../../../../api-services/events";
import MaterialTable from "material-table";
import GlobalContext from "../../../../GlobalContext";
import _ from 'lodash'

const apiEvent = new ApiServiceEvents();

/**
 *
 * This view allows you to view poll list
 * @class AnswerListPoll
 * @extends {Component}
 */
class AnswerListPoll extends Component {

    static contextType = GlobalContext;

    state = {
        answers: [],
        questions: [],
        idPoll: null,
        columns: []
    }

    componentDidMount() {
        let poll = this.props.match.params.poll;
        this.setState({
            idPoll: poll
        })

        this.getAnswer(poll);
    }

    /**
     * funtion to get poll answer 
     * @param  {number} idPoll
     */
    getAnswer = (idPoll) => {
        apiEvent.getPollAnswer(idPoll).then((response) => {
            console.log(response);

            this.setState({
                answer: response.answers,
                questions: response.questions
            })

            this.makeColumns(response.questions.questions, response.answers);
        }).catch((err) => {
            console.log(err);
        })
    }

    /**
     * This function create columns for question
     * @param  {} questions
     * @param  {} answers
     */
    makeColumns = (questions, answers) => {
        console.log('make', questions, answers)

        answers.map((res, i) => {
            console.log('ress', res)
        });

        this.setState({
            headers: _.map(questions, 'title')
        })

    }

    render() {

        let ctx = this.context;


        return (
            <AdminEventLayout title={<Trans>Encuestas del evento</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <Typography variant="h5" style={{ padding: '10px' }} component="h3">
                            <Trans>Listado de respuestas</Trans>
                        </Typography>
                    </Grid>
                </Grid>
                <Grid item xs={12}>
                    <MaterialTable
                        title={<Trans>Respuestas de la encuesta</Trans>}
                        columns={this.state.columns}
                        data={this.state.data}
                        options={{
                            actionsColumnIndex: -1,
                            pageSize: 10,
                            pageSizeOptions: [10, 20, 50, 100, 500],
                            paging: true,
                        }}
                        localization={ctx.tableConfig.localization}
                        actions={[
                            // {
                            //     icon: 'edit',
                            //     tooltip: 'Editar',
                            //     onClick: (event, rowData) => this.setState({redirectToEdit: true, selectedRow: rowData._id})
                            // }
                        ]}
                    // editable={{
                    //     onRowDelete: props.handlerDelete,
                    // }}
                    />
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default AnswerListPoll;