import React from "react";
import { Form, Field } from 'react-final-form';
import { TextField, Checkbox, Radio, Select } from 'final-form-material-ui';
import { FormControlLabel, Grid, Paper, Typography} from "@material-ui/core";
import GlobalContext from "../../../../GlobalContext";
import BoxQr from "./BoxQr";
import DateFnsUtils from '@date-io/date-fns';
import {
    MuiPickersUtilsProvider,
} from '@material-ui/pickers';
import {CancelButton, SaveButtonSpinner} from "../../../buttons/StandartButtons";
import {DatePickerWrapper} from "../../../fields/DatePickers";
import { Trans } from '@lingui/macro';
import moment from "moment"
import _ from "lodash"


class FormQr extends React.Component{

    static contextType = GlobalContext;

    componentDidMount() {
        moment.locale('es');
    }

    render() {

        let ctx = this.context;
        let props = this.props;

        let validFrom = _.has(props, 'initialValues.validFromDate') && props.initialValues.validFromDate ? props.initialValues.validFromDate : '' ;
        let validTo = _.has(props, 'initialValues.validToDate') && props.initialValues.validToDate ? props.initialValues.validToDate : '';

        if(validFrom)
            validFrom = moment(validFrom).format('L');
        if(validTo)
            validTo = moment(validTo).format('L');

        // console.log('validFrom', validFrom);
        // console.log('validTo', validTo);

        return (
            <Form
                onSubmit={this.props.onSubmit}
                initialValues={this.props.initialValues}
                validate={this.props.validate}
                render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                    <form onSubmit={handleSubmit} noValidate>
                        <Paper style={{ padding: 16 }}>




                            <Grid container alignItems="flex-start" spacing={2}>

                                <Grid item xs={12} lg={6}>
                                    <BoxQr
                                        imageQr={this.props.imageQr}
                                        downloadQr={this.props.downloadQr}
                                        openSendEmailQr={this.props.openSendEmailQr}
                                    />
                                </Grid>

                                <Grid item xs={12} lg={6}>
                                    <Typography variant="h6" align="left" component="h6" gutterBottom>
                                        <Trans>Temporalidad</Trans>
                                    </Typography>
                                    <Grid container>
                                        <Grid item xs={12} style={{marginTop: 5, marginBottom: 0}}>
                                            <Typography>
                                                Valido del {validFrom} al {validTo}
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={12} style={{marginTop: 35}}>
                                            <FormControlLabel
                                                label="Activo"
                                                control={
                                                    <Field
                                                        name="active"
                                                        component={Checkbox}
                                                        type="checkbox"
                                                    />
                                                }
                                            />
                                        </Grid>

                                        {submitError && <div className="error">{submitError}</div>}
                                        <Grid item style={{ marginTop: 16 }} xs={12}>
                                            <CancelButton
                                                to={`/${ctx.slug}/admin/user/list/`}
                                            />

                                            <SaveButtonSpinner
                                                loading={this.props.loading}
                                            />
                                        </Grid>
                                    </Grid>
                                </Grid>



                            </Grid>
                        </Paper>
                    </form>
                )}
            />
        )
    }
}

export default FormQr