import React, {Component} from 'react';
import CssBaseline from "@material-ui/core/CssBaseline";
import {Field, Form} from "react-final-form";
import arrayMutators from 'final-form-arrays'
import { FieldArray } from 'react-final-form-arrays'
import {FormControl, FormControlLabel, Grid, Paper, Typography} from "@material-ui/core";
import {Select, Radio, TextField, Checkbox} from "final-form-material-ui";
import {SaveButtonSpinner} from "../../buttons/StandartButtons";
import Button from '@material-ui/core/Button';
import Box from "@material-ui/core/Box";
import Image from "material-ui-image/lib/components/Image/Image";
import FileBase64 from "react-file-base64";
import _ from "lodash";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import MenuItem from "@material-ui/core/MenuItem";
import {ApiServiceEvents} from "../../../api-services/events";
import GlobalContext from "../../../GlobalContext";
import AddIcon from '@material-ui/icons/Add';


const apiEvent = new ApiServiceEvents();

class EventUserMetaDataForm extends Component {

    static contextType = GlobalContext;

    state = {
        loading: true,
        initialData: {
            metadata: []
        }
    };

    handleSubmit = (values) => {
        let ctx = this.context;
        let slug = ctx.slug;
        const request = {
            slug: slug,
            _idUser: this.props.userData._id,
            metadata: values.metadata,
        };
        console.log('Request', request);
        apiEvent.addMetadata(request).then(
            (res) => {
                console.log('Response', res);
                this.props.setMetadata(values.metadata);
                ctx.openSuccessNotification('Datos guardados');
            }
        ).catch(
            (err) => {
                console.log('err', err);
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
            }
        );
    };

    validate = (values) => {
        // console.log(values)
    };

    componentDidMount() {
        // this.setState({loading: false});
        // this.getViews();
        // this.setInitialValues();
        // console.log('Metadatos', this.props.userData);
        const metadata = _.has(this.props, 'userData.metadata') ? this.props.userData.metadata : [];
        this.setState({
            initialData: {
                metadata: metadata
            },
            loading: false
        });
    }

    setInitialValues = () => {

    };

    getFiles=(index, files)=>{

    };

    getViews = () => {

    };

    render() {
        let templateItem = {
            key: '',
            value: '',
            isVisible: false,
        };

        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                {
                    this.state.loading ? '' : (
                        <Form
                            onSubmit={this.handleSubmit}
                            mutators={{
                                ...arrayMutators
                            }}
                            validate={this.validate}
                            initialValues={this.state.initialData}
                            render={({
                                         handleSubmit,
                                         form: {
                                             mutators: { push, pop }
                                         }, // injected from final-form-arrays above
                                         pristine,
                                         form,
                                         submitting,
                                         values
                                     }) => {
                                return (
                                    <form onSubmit={handleSubmit} noValidate>
                                        <Paper style={{ padding: 16 }}>

                                            <Typography variant="h6" align="left" component="h6" gutterBottom>
                                                Agregar información adicional
                                            </Typography>

                                            <Grid
                                                container
                                                // alignItems="flex-start"
                                                spacing={2}
                                                style={{marginTop: 20}}
                                            >
                                                {/*Row Buttons*/}
                                                <Grid container item xs={12} spacing={2}>
                                                    <div className="buttons">
                                                        {/*<Button*/}
                                                        {/*    variant="contained"*/}
                                                        {/*    color="primary"*/}
                                                        {/*    onClick={() => push('metadata', templateItem)}*/}
                                                        {/*    style={{marginRight: 20}}*/}
                                                        {/*>*/}
                                                        {/*    Agregar línea de información*/}
                                                        {/*</Button>*/}
                                                        {/*<Button*/}
                                                        {/*    variant="contained"*/}
                                                        {/*    color="secondary"*/}
                                                        {/*    onClick={() => pop('metadata')}*/}
                                                        {/*>*/}
                                                        {/*    Eliminar línea de información*/}
                                                        {/*</Button>*/}
                                                    </div>
                                                </Grid>

                                                <FieldArray name="metadata">
                                                    {({fields}) =>
                                                        fields.map((name, index) => (
                                                            // Rows image item
                                                                <Grid container item xs={12} spacing={1} key={index}>
                                                                    {/*Col Image input*/}
                                                                    <Grid item xs={3}>
                                                                            <Field
                                                                                fullWidth
                                                                                name={`${name}.key`}
                                                                                component={TextField}
                                                                                label="Título"
                                                                                variant="outlined"
                                                                                margin="normal"
                                                                            />
                                                                    </Grid>

                                                                    {/*Col properties image*/}
                                                                    <Grid item xs={6}>
                                                                            <Field
                                                                                fullWidth
                                                                                name={`${name}.value`}
                                                                                component={TextField}
                                                                                label="Valor"
                                                                                variant="outlined"
                                                                                margin="normal"
                                                                            />

                                                                    </Grid>
                                                                    <Grid item xs={2}>
                                                                        <FormControlLabel
                                                                            label="¿Visible en app?"
                                                                            labelPlacement="bottom"
                                                                            control={
                                                                                <Field
                                                                                    name={`${name}.isVisible`}
                                                                                    component={Checkbox}
                                                                                    type="checkbox"
                                                                                />
                                                                            }
                                                                        />

                                                                    </Grid>
                                                                    <Grid item xs={1}>
                                                                        <button title = "Remover elemento" onClick={() => fields.remove(index)}
                                                                            style={{cursor: 'pointer', alignSelf: 'center', marginTop: 10, border: 'none', background : 'white'}}>
                                                                               ❌     
                                                                        </button>
                                                                        
                                                                    </Grid>

                                                                </Grid>

                                                        ))
                                                    }
                                                </FieldArray>

                                                {/*Row buttons submit*/}
                                                <Grid container item xs={12} spacing={3}>
                                                    <div className="buttons">
                                                        <Button
                                                            variant="contained"
                                                            color="default"
                                                            onClick={() => push('metadata', templateItem)}
                                                            style={{marginRight: 20}}
                                                        >
                                                            <AddIcon/>
                                                            Agregar datos
                                                        </Button>
                                                        <SaveButtonSpinner/>
                                                    </div>
                                                </Grid>

                                                {/*<pre>{JSON.stringify(values, 0, 2)}</pre>*/}
                                            </Grid>
                                        </Paper>
                                    </form>
                                )
                            }}
                        />
                    )
                }
            </div>
        );
    }
}

export default EventUserMetaDataForm;