import React, {Component} from 'react';
import MainLayout from "./MainLayout";
import {Grid} from "semantic-ui-react";
import view from './dataView.json'
import view1 from './dataView1.json'
import view2 from './dataView2.json'
import view3 from './dataView3.json'
import view4 from './dataView4.json'
import view5 from './dataView5.json'
import FactorySection from "./FactorySection";

class ViewMovil extends Component {

    state = {
        sections:[],
        title:'',
        backButton:false
    };

    arrayView = [
        view1,view2,view3,view4,view5
    ];

    goView1 = ()=>{
        this.setState({
            sections:view.sections,
            title:view.title,
            backButton:view.backbutton
        })
    };

    goView2 =(num)=>{
        const view = this.arrayView[num];
        this.setState({
            sections:view.sections,
            title:view.title,
            backButton:view.backbutton
        })
    };



    backButton = (
        <Grid.Column width={2}><i aria-hidden="true"  onClick={this.goView1} className="chevron left  icon"/></Grid.Column>
    );

    componentDidMount() {
        this.setState({
            sections: view.sections,
            title:view.title
        });
    }


    render() {
        return (
            <MainLayout content={
                <Grid>
                    <Grid.Row columns={2}>
                            {this.state.backButton ? this.backButton : ''}
                        <Grid.Column>
                           <span style={{color:'white',fontSize:'1.5em',textShadow:'2px 2px 4px #000000'}}>{this.state.title}</span>
                            <small style={{display:'block', color:'white'}}>Sol y playa - Negocios - Lujo</small>
                        </Grid.Column>
                        <Grid.Column>
                            {/*<img style={{position:'absolute',top:'-5px',right:'5px'}} src={'https://is3-ssl.mzstatic.com/image/thumb/Purple113/v4/8b/84/48/8b844810-8d69-6a41-ac8a-fe7b29617a5c/source/60x60bb.jpg'}/>*/}
                        </Grid.Column>
                    </Grid.Row>
            </Grid>
            }>
                <Grid columns='equal'>
                    <Grid.Row>
                        <Grid.Column>
                           {
                               this.state.sections.map(section=>
                                   <FactorySection order={section.id} goView={this.goView2} key={section.id} section={section}/>
                               )
                           }
                        </Grid.Column>
                    </Grid.Row>
                </Grid>

            </MainLayout>
        )




    }

}

export default ViewMovil;