import React, {useContext} from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Link from '@material-ui/core/Link';
import MenuIcon from '@material-ui/icons/Menu';
import { Trans } from '@lingui/macro';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import NotificationsIcon from '@material-ui/icons/Notifications';
import {mainListItems, secondaryListItems, MainListItems} from "../components/base/ListItems";
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import KeyboardTabIcon from '@material-ui/icons/KeyboardTab';
import MeetingRoomIcon from '@material-ui/icons/MeetingRoom';
import {Redirect} from "react-router-dom";
import GlobalContext from "../GlobalContext";
import {ApiServiceSecurity} from "../api-services/security";
import logoHeader from '../assets/TURISMO_horizontal.png';
import {Helmet} from "react-helmet";
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import Button from "@material-ui/core/Button";
import Tooltip from '@material-ui/core/Tooltip';
// import Chart from './Chart';
// import Deposits from './Deposits';
// import Orders from './Orders';

function Copyright(props) {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <Link color="inherit" href="https://www.gob.mx/sectur/">
                SECTUR
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'} v {props.version}
        </Typography>
    );
}

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        minHeight: '100vh',
    },
    toolbar: {
        paddingRight: 24, // keep right padding when drawer closed
    },
    toolbarIcon: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: '0 8px',
        ...theme.mixins.toolbar,
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    menuButtonHidden: {
        display: 'none',
    },
    title: {
        flexGrow: 1,
    },
    drawerPaper: {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerPaperClose: {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing(7),
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9),
        },
    },
    appBarSpacer: theme.mixins.toolbar,
    footer: {
        padding: theme.spacing(2),
        marginTop: 'auto'
    },
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
    },
    container: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(4),
    },
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 240,
    },
}));


const AdminLayoutContent = (props) => {

    const classes = useStyles();
    const [open, setOpen] = React.useState(true);
    const handleDrawerOpen = () => {
        setOpen(true);
    };
    const handleDrawerClose = () => {
        setOpen(false);
    };
    // const fixedHeightPaper = clsx(classes.paper, classes.fixedHeight);
    const ctx = useContext(GlobalContext);

    const menuId = 'primary-search-account-menu';
    const userStr = localStorage.getItem('user');
    const user = JSON.parse(userStr);
    // const userName = `${user.firstName} ${user.lastName}`;
    const userName = `${user.firstName}`;

    return (
        <div className={classes.root}>

            <Helmet>
                <title>Administrador de eventos</title>
            </Helmet>

            <CssBaseline />
            <AppBar position="absolute" className={clsx(classes.appBar, open && classes.appBarShift)}>
                <Toolbar className={classes.toolbar}>

                    <IconButton
                        edge="start"
                        color="inherit"
                        aria-label="open drawer"
                        onClick={handleDrawerOpen}
                        className={clsx(classes.menuButton, open && classes.menuButtonHidden)}
                    >
                        <MenuIcon />
                    </IconButton>
                    <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
                        {props.title}
                    </Typography>
                    <Button
                        edge="end"
                        aria-label="username"
                        aria-controls={menuId}
                        aria-haspopup="true"
                        onClick={props.handleProfile.bind(this, `/user/${user._id}/`)}
                        color="inherit"
                    >
                        <AccountCircleIcon />  {userName}
                    </Button>
                    <Tooltip title={<Trans>Cerrar Sesión</Trans>}>
                        <IconButton color="inherit" onClick={props.handleLogOut}>
                                <MeetingRoomIcon />
                        </IconButton>
                    </Tooltip>
                </Toolbar>
            </AppBar>
            <Drawer
                variant="permanent"
                classes={{
                    paper: clsx(classes.drawerPaper, !open && classes.drawerPaperClose),
                }}
                open={open}
            >
                <div className={classes.toolbarIcon}>
                    <IconButton onClick={handleDrawerClose}>
                        <ChevronLeftIcon />
                    </IconButton>
                </div>
                <Divider />
                {/*<List><MainListItems/></List>*/}
                <List>{mainListItems}</List>
                {/*<Divider />*/}
                {/*<List>{secondaryListItems}</List>*/}
            </Drawer>
            <Container component="main"  className={classes.main}>
                <main className={classes.content}>
                    <div className={classes.appBarSpacer} />
                    <Container maxWidth="lg" className={classes.container}>
                        <Grid container spacing={3}>
                            {props.children}
                        </Grid>
                    </Container>
                </main>

                <footer className={classes.footer}>
                    <Container maxWidth="sm">
                        <Copyright version={ctx.version}/>
                    </Container>
                </footer>

            </Container>



        </div>
    );
};

const apiEvents = new ApiServiceSecurity();

class AdminLayout extends React.Component{
    static contextType = GlobalContext;

    state = {
        redirectToLogOut: false,
        urlLogIn: '/security/signin/',
        urlProfile:'',
        redirectToProfile:false
    };

    handleLogOut = () => {
        localStorage.removeItem('jwt');
        this.setState({redirectToLogOut: true})
    };

    testTokenValidOrExpired = () => {
        let ctx = this.context;        
        // if (!ctx.tokenInvalidOrExpired) {
            // apiEvents.getSuperUserDetail(this.props.match ? this.props.match.params.id : '').then(
            //     (res) => {
            //         // console.log('si se puedo');
            //     }
            // ).catch(
            //     (err) => {
            //         // console.log('Respuesta de test token si', err, typeof err);

            //         if(err.response) {
            //             ctx.tokenInvalidOrExpired = !!(err.response.status && err.response.status === 498);
            //             if (ctx.tokenInvalidOrExpired)
            //                 ctx.openNotification('Sesión expirada');
            //         }else{
            //             if(err.message === 'Network Error'){
            //                 ctx.openNotification('Error de red');
            //             }
            //         }
            //     }
            // )
        // }
    };

    componentDidMount() {
        this.testTokenValidOrExpired();
    }

    handleProfile=(url)=>{
        console.log(url)
        this.setState({
            urlProfile:url,
            redirectToProfile:true
        })
    }

    render() {
        let ctx = this.context;

        if (this.state.redirectToLogOut || ctx.tokenInvalidOrExpired) return <Redirect push to={this.state.urlLogIn} />;
        if (this.state.redirectToProfile ) return <Redirect push to={this.state.urlProfile} />;

        return (
            <AdminLayoutContent handleLogOut={this.handleLogOut} handleProfile={this.handleProfile}>
                {this.props.children}
            </AdminLayoutContent>
        )
    }
}

export default AdminLayout