import React from "react";

import {Grid, Paper} from "@material-ui/core";
// import _ from 'lodash';

import AdminLayout from "../../layouts/AdminLayout";
import CreateUserForm from "../../components/security/CreateUserForm";
import {ApiServiceSecurity} from "../../api-services/security";
import Noti from "../../components/notifications/Noti";
import {Redirect} from "react-router-dom";
import {withI18n} from "@lingui/react";
import {t} from "@lingui/macro";
const apiSecurity = new ApiServiceSecurity();

/**
 *
 * View for user registration
 * @class SignUp
 * @extends {React.Component}
 */
class SignUp extends React.Component{

    state = {
        firstName: '',
        lastName: '',
        email: '',
        password: '',
        errorMessage: '',
        redirectLogin: false,
        urlRedirectLogin: '/user/list/',
        showNotiError: false,
        titleCreateForm: this.props.i18n._(t`Crear super usuario`),
        errorFirstName: '',
        errorLastName: '',
        errorEmail: '',
        errorPassword: '',
        disabledButton: true,

    };
    
    /** Function validate email format
     * @param  {string} email
     * @returns {string} lowercase email
     */
    validateEmail = (email) => {
        var re = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        return re.test(String(email).toLowerCase());
    };

    
    /**
     * function to set state when a input element change
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });

        let state = this.state;
        state[name] = value;

        state.disabledButton = !(state.firstName && state.lastName && state.email && state.password);
        this.setState(state);

    };

    /**
     * Function to submit signup form
     * @param  {Object} e
     */
    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({errorMessage: '', showNotiError: false});
        let request = this.state;
        let isValid = true;

        if(!this.validateEmail(this.state.email)){
            this.setState({
                errorMessage: this.props.i18n._(t`El email es inválido`),
                showNotiError: true,
            });
            isValid = false;
        }
        if(isValid) {

            let promise = apiSecurity.addSuperUser(request);

            promise.then(
                (response) => {
                    // console.log('Todo ok', response);
                    this.setState({redirectLogin: true})
                }
            ).catch(error => {
                 console.log('Error', error.response);
                this.setState({
                    errorMessage: error.response.data.message ? error.response.data.message : error.response.data.message,
                    showNotiError: true,
                });
            })
        }
    };
    
    /**
     * Function to close notification
     */
    handleCloseNoti = () => {
        this.setState({
            errorMessage: '',
            showNotiError: false
        })
    };

    render() {

        if (this.state.redirectLogin) {
            return <Redirect push to={this.state.urlRedirectLogin} />;
        }

        let {firstName} = this.state;
        let {lastName} = this.state;
        let {email} = this.state;
        let {password} = this.state;
        let {titleCreateForm} = this.state;
        let {errorFirstName} = this.state;
        let {errorLastName} = this.state;
        let {errorEmail} = this.state;
        let {errorPassword} = this.state;
        let {disabledButton} = this.state;

        return (
            <AdminLayout>
                <Grid container>
                    <Grid item xs={12} md={12} lg={12} >
                        {/*<Paper style={{padding: '10px'}}>*/}
                        {/*{this.state.errorMessage}*/}
                        <Noti msg={this.state.errorMessage} open={this.state.showNotiError} handleClose={this.handleCloseNoti}/>
                            <CreateUserForm
                                firstName={firstName}
                                lastName={lastName}
                                email={email}
                                password={password}
                                handleInputChange={this.handleInputChange}
                                handleSubmit={this.handleSubmit}
                                showPassword={true}
                                title={titleCreateForm}
                                buttonTitle={this.props.i18n._(t`Crear usuario`)}
                                errorFirstName={errorFirstName}
                                errorLastName={errorLastName}
                                errorEmail={errorEmail}
                                errorPassword={errorPassword}
                                disabledButton={disabledButton}
                            />
                        {/*</Paper>*/}
                    </Grid>
                </Grid>
            </AdminLayout>
        )
    }
}

export default withI18n()(SignUp);