import React from "react";
import {DatePicker, TimePicker, DateTimePicker, KeyboardDateTimePicker} from "@material-ui/pickers";
import {Trans} from "@lingui/macro";


export function DatePickerWrapper(props) {
    const {
        input: { name, onChange, value, ...restInput },
        meta,
        ...rest
    } = props;
    const showError =
        ((meta.submitError && !meta.dirtySinceLastSubmit) || meta.error) &&
        meta.touched;
    let today = new Date();
    let actualYear = today.getFullYear();

    return (
        <KeyboardDateTimePicker
            {...rest}
            name={name}
            helperText={showError ? meta.error || meta.submitError : undefined}
            error={showError}
            inputProps={restInput}
            onChange={onChange}
            minDate={new Date(`${actualYear-3}-01-01`)}
            maxDate={new Date(`${actualYear+5}-01-01`)}
            format="dd/MM/yyyy hh:mm a"            
            value={value === '' ? null : value}
        />
    );
}

