import React, {Component} from 'react';
import Grid from "@material-ui/core/Grid";
import MenuSection from "./MenuSection";
import ComponentBuilder from "./ComponentBuilder";
import {GlobalContextBuilder} from "../../GlobalContext";
import Slide from '@material-ui/core/Slide';
import Button from "@material-ui/core/Button";
import FullScreenDialog from "../modals/FullScreenDialog";
import ConfigBuilderComponent from "./ConfigBuilderComponent";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from "@material-ui/core/TextField";
import _ from "lodash";
import {Trans, t} from "@lingui/macro";
import {Text} from "grommet";
import {Field} from "react-final-form";
import {Checkbox} from "final-form-material-ui";
import {FormControlLabel} from "@material-ui/core";
import SectionPermsDialog from "./Dialog";
import {withI18n} from "@lingui/react";


class SectionBuilder extends Component {

    static contextType = GlobalContextBuilder;



    handleClose=()=> {
        this.setState({
            anchorEl:null,
            openDialog:false
        });
    }


    handleDelete=()=>{
        let ctx = this.context;
        let canDelete = window.confirm(this.props.i18n._(t`¿Deseas eliminar la sección?`));
        if(canDelete){
            ctx.deleteSection(this.props.section)
        }
    }


    handleClone=()=>{
        let ctx = this.context;
        let canClone = window.confirm(this.props.i18n._(t`¿Deseas duplicar la sección?`));
        if(canClone){
            ctx.cloneSection(this.props.section)
        }
    }


    handleItemSelected=(type)=>{
        let component = {};
        const {section} = this.props;
        component.type = type;
        component.order = section.components?section.length:1;
        component.properties={};
        let ctx = this.context;
        ctx.setComponent(component, this.props.section)
    }

    handleDown=()=>{
        let ctx = this.context;
        ctx.sortSection('down', this.props.section)
    }


    handleUp=()=>{
        let ctx = this.context;
        ctx.sortSection('up', this.props.section)
    }

    /***
     * Sirve para setear los current component y section
     * @param component
     * @param evt
     */
    handleClickOpen = (component,evt) => {
        let ctx = this.context;
        const {section} = this.props;
        // console.log('Selected Component',component, 'Selected section', section);
        this.setState({openDialog:false, currentComponent: component});
        ctx.setCurrentSectionAndComponent(section, component);
    };

    openComponentSettings = () => {
        // console.log('Componente open setings', this.context.currentComponent);
        this.setState({
            openDialog:true
        })
    };

    closeComponentSettings = () => {
        this.setState({
            openDialog:false
        })
    };

    // handleClose = () => {
    //     this.setState({openDialog:false});
    // };


    openDialogChangeName=()=>{
        this.setState({openChangeNameDialog:true, nameSection:this.props.section.name});
    }

    openDialogChangePerms=()=>{
        this.setState({openChangePerms:true, nameSection:this.props.section.name});
    }


    handleCloseDialogName=()=>{
        this.setState({openChangeNameDialog:false});
    }

    handleCloseDialogPerms=()=>{
        this.setState({openChangePerms:false});
    }


    changeName=()=>{
        let {section} = this.props;
        section.name = this.state.nameSection;
        let ctx = this.context;
        ctx.changeNameSection(section);
        this.handleCloseDialogName();

    };

    changeSectionPerms = () => {
        let {section} = this.props;
        section.name = this.state.nameSection;
        let ctx = this.context;
        ctx.changeSectionPerms(section, this.state.permsValues);
        this.handleCloseDialogPerms();
    };


    handleInputChane=(e)=>{
        let text = e.target.value;
        this.setState({
            nameSection:text
        })
    }

    valuesPerms = values => {
        this.setState({permsValues: values});
    }


    state = {
        anchorEl:null,
        setAnchorEl:null,
        openDialog:false,
        currentComponent:{},
        openChangeNameDialog:false,
        openChangePerms:false,
        nameSection:'',
        permsValues: '',
    };


    render() {
        let  {section} = this.props;
        let {currentComponent} = this.state;

        let componentsOrdered = section.components;
        console.log(section, 'sections')

        if(section.needOrder){
            componentsOrdered = _.orderBy(componentsOrdered, ['order'], ['asc']);
        }

        return (
            <Grid container style={{minHeight:'80px', background:'white'}}>
                <Grid item xs={12}>
                    <MenuSection
                        sizeSections={this.props.sizeSections}
                        position={this.props.position}
                        onCloneSection={this.handleClone}
                        onSortDown={this.handleDown} onSortUp={this.handleUp}
                        onDelete={this.handleDelete}
                        onChangeName={this.openDialogChangeName}
                        onChangePerms={this.openDialogChangePerms}
                        onSelectedItem={this.handleItemSelected} section={section} />
                </Grid>

                <Grid container
                      direction="row"
                      alignItems="center"
                      justify="center">
                    {
                        componentsOrdered.map((component,index)=>{
                            return(
                            <Grid
                                item xs={12}
                                key={index}
                            >
                                <ComponentBuilder
                                    handleCLick={this.handleClickOpen.bind(this,component)}
                                    component={component}
                                    section={section}
                                    openSettings={this.openComponentSettings}
                                    closeSettings={this.closeComponentSettings}
                                />
                            </Grid>)
                        })
                    }
                </Grid>




                <FullScreenDialog
                    open={this.state.openDialog}
                    handleClose={this.handleClose}>
                    <ConfigBuilderComponent onCloseConfig={this.handleClose} section={section} component={currentComponent}/>
                </FullScreenDialog>


                <Dialog
                    open={this.state.openChangeNameDialog}
                    onClose={this.handleCloseDialogName}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title"><Trans>Cambiar nombre</Trans></DialogTitle>
                    <DialogContent>
                        <TextField
                            id="standard-name"
                            label={<Trans>Nombre sección</Trans>}
                            value={this.state.nameSection}
                            onChange={this.handleInputChane}
                            margin="normal"
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleCloseDialogName} color="primary">
                            <Trans>Cancelar</Trans>
                        </Button>
                        <Button onClick={this.changeName} color="primary" autoFocus>
                            <Trans>Aceptar</Trans>
                        </Button>
                    </DialogActions>
                </Dialog>

                {/*Dialog for perms*/}
                <Dialog
                    open={this.state.openChangePerms}
                    onClose={this.handleCloseDialogPerms}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">{`Permisos de la sección ${this.state.nameSection}`}</DialogTitle>
                    <DialogContent>
                        <SectionPermsDialog
                            viewPermissions={this.props.viewPermissions}
                            values={this.valuesPerms}
                            allPerms={this.props.allPerms}
                            initialValues={{
                                viewPerms: section.groups
                            }}
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleCloseDialogPerms} color="primary">
                            <Trans>Cancelar</Trans>
                        </Button>
                        <Button onClick={this.changeSectionPerms} color="primary" autoFocus>
                            <Trans>Aceptar</Trans>
                        </Button>
                    </DialogActions>
                </Dialog>


            </Grid>
        );
    }
}

export default withI18n() (SectionBuilder);