import React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import {Field, Form} from "react-final-form";
import {FormControl, FormControlLabel, Grid, Paper, Typography} from "@material-ui/core";
import Box from "@material-ui/core/Box";
import Image from "material-ui-image/lib/components/Image/Image";
import FileBase64 from "react-file-base64";
import {Radio, Select, TextField} from "final-form-material-ui";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import MenuItem from "@material-ui/core/MenuItem";
import {SaveButtonSpinner} from "../../buttons/StandartButtons";
import {ApiServiceEvents} from "../../../api-services/events";
import _ from "lodash";
import {GlobalContextBuilder} from "../../../GlobalContext";


const apiEvent = new ApiServiceEvents();

class ConfigImage extends React.Component{

    static contextType = GlobalContextBuilder;

    state = {
        imageChanged: false,
        image: 'https://x.kinja-static.com/assets/images/logos/placeholders/default.png',
        imageName: '',
        viewsList: [],
        pollList:[],
        buttonType: 'none',
        urlRedirect: '',
        viewId: '',
        title: '',
        loading: true,
        phoneNumber: '',
        propertiesRaw: {
            image: {
                value: '',
                type: 'url',
                name: ''
            },
            title: {
                value: '',
                type: 'string',
            },
            buttonType: {
                value: '',
                type: 'string'
            },
            urlRedirect: {
                value: '',
                type: 'string'
            },
            viewId: {
                value: '',
                type: 'string'
            },
            phoneNumber: {
                value: '',
                type: 'string'
            }
        }
    };

    componentDidMount() {
        // console.log('Se inicia el componente imagen')
        this.getViews();
        this.setInitialValues();
        this.getPolls();
    }

    getRequest = (values) => {
        let request = {
            title: {
                value: values.title,
                type: 'string',
            },
            buttonType: {
                value: values.buttonType,
                type: 'string'
            },
            urlRedirect: {
                value: values.urlRedirect,
                type: 'string'
            },
            pollId: {
                value: values.pollId,
                type: 'string'
            },
            viewId: {
                value: values.viewId,
                type: 'string'
            },
            phoneNumber: {
                value: values.phoneNumber,
                type: 'string'
            },

        };
        let image = {
            value: this.state.image,
            type: _.has(this, 'state.propertiesRaw.image.type') ? this.state.propertiesRaw.image.type : 'url',
            name: this.state.imageName
        };
        // if(image.type === 'imageBase64'){
            request.image = image;
        // }
        return request;
    };

    handleSubmit = (values) => {
        let request = this.getRequest(values);
        this.props.onSaveConfig(request);
    };

    validate = (values) => {
        // console.log(values);
    };

    getFiles=(files)=>{
        // console.log('Files', files);
        if(files.base64) {
            let newState = this.state;
            newState.image = files.base64;
            newState.imageName = files.name;
            newState.propertiesRaw.image.type = 'imageBase64';
            this.setState({
                image: files.base64,
                imageName: files.name,
                imageChanged: true,
            })
        }
    };

    getPolls=()=>{
        let ctx = this.context;
        let slug = ctx.slug;
        apiEvent.getPollEvents(slug).then((res)=>{
            this.setState({
                pollList:res
            })
        });
    }

    getViews = () => {
        let ctx = this.context;
        let moduleId = ctx._idModule;
        apiEvent.getViewsList(moduleId).then(
            (res) => {
                let array = [];
                res.map((view,i)=>{
                    array.push(view.views)
                });

                if(array.length>0){
                    this.setState({
                        viewsList:array
                    })
                }
            }
        ).catch(
            (err) => {
                console.log('Error en carga de vistas', err);
            }
        );
    };

    setInitialValues = () => {
        const ctx = this.context;
        const component = ctx.currentComponent;

        let properties = component.properties;

        if(!properties) {
            this.setState({loading:false});
            return;
        }

        // console.log('propertttt', properties);

        let customProperties = {
            buttonType: _.has(properties, 'buttonType.value') ? properties.buttonType.value : 'none',
            urlRedirect: _.has(properties, 'urlRedirect.value') ? properties.urlRedirect.value : '',
            viewId: _.has(properties, 'viewId.value') ? properties.viewId.value : '',
            phoneNumber: _.has(properties, 'phoneNumber.value') ? properties.phoneNumber.value : '',
            title: _.has(properties, 'title.value') ? properties.title.value : '',
            pollId: _.has(properties, 'pollId.value') ? properties.pollId.value : '',
            image: this.context.getImage(properties, false),
            loading: false,
            propertiesRaw: properties
        };

        // let defaultValues = {
        //     buttonType: 'none',
        //     urlRedirect: '',
        //     viewId: '',
        //     title: '',
        // };

        let newState = _.merge(this.state, customProperties);
        // console.log('newstte', newState);

        this.setState(newState);
    };

    render() {

        let imageToView = this.state.image;
        // if(_.has(this.state, 'propertiesRaw.image.type')){
        //     if(this.state.propertiesRaw.image.type === 'imageBase64'){
        //         imageToView = this.state.image
        //     }
        // }

        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                {
                    this.state.loading ? '' : (
                        <Form
                            onSubmit={this.handleSubmit}
                            initialValues={this.state}
                            validate={this.validate}
                            render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                                <form onSubmit={handleSubmit} noValidate>
                                    <Paper style={{ padding: 16 }}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Propiedades
                                        </Typography>
                                        <Grid container alignItems="flex-start" spacing={2}>
                                            <Grid item xs={12} alig={'center'}>
                                                <Box align="center">
                                                    <Image
                                                        alt={'image'}
                                                        src={imageToView}
                                                        imageStyle={{
                                                            // margin: 10,
                                                            width: 'auto',
                                                            position: 'auto'
                                                            // height: 150,
                                                        }}
                                                        style={{
                                                            // margin: 10,
                                                            width: 'auto',
                                                            position: 'auto',
                                                            // height: 150,
                                                            paddingTop: 30
                                                        }}
                                                    /><br/>

                                                    <FileBase64
                                                        onDone={this.getFiles} />
                                                </Box>
                                            </Grid>

                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="title"
                                                    component={TextField}
                                                    multiline
                                                    label="Título"
                                                    variant="outlined"
                                                />
                                            </Grid>

                                            <Grid item xs={12} style={{marginTop: '20px'}}>
                                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                                    Acciones
                                                </Typography>
                                            </Grid>

                                            <Grid item>
                                                <FormControl component="fieldset">
                                                    <FormLabel component="legend">Tipo de acción</FormLabel>
                                                    <RadioGroup row>
                                                        <FormControlLabel
                                                            label="Ninguno"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="none"
                                                                />
                                                            }
                                                        />
                                                        <FormControlLabel
                                                            label="Redirección a una página externa"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="redirect"
                                                                />
                                                            }
                                                        />
                                                        <FormControlLabel
                                                            label="Redirección a una vista"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="router"
                                                                />
                                                            }
                                                        />


                                                        <FormControlLabel
                                                            label="Mostrar encuesta"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="poll"
                                                                />
                                                            }
                                                        />


                                                        <FormControlLabel
                                                            label="Hacer llamada"
                                                            control={
                                                                <Field
                                                                    name="buttonType"
                                                                    component={Radio}
                                                                    type="radio"
                                                                    value="phone"
                                                                />
                                                            }
                                                        />
                                                    </RadioGroup>
                                                </FormControl>
                                            </Grid>


                                            {
                                                values.buttonType === 'redirect' ? (
                                                    <Grid item xs={12}>
                                                        <Field
                                                            fullWidth
                                                            name="urlRedirect"
                                                            component={TextField}
                                                            multiline
                                                            label="URL externa"
                                                            variant="outlined"
                                                        />
                                                    </Grid>
                                                ) : ''
                                            }


                                            {
                                                values.buttonType === 'poll' ? (
                                                    <Grid item xs={12}>
                                                        <Field
                                                            fullWidth
                                                            name="pollId"
                                                            component={Select}
                                                            label="Seleccionar un cuestionario/formulario"
                                                            formControlProps={{fullWidth: true}}
                                                            variant="outlined"
                                                        >
                                                            {
                                                                this.state.pollList.map(
                                                                    (item, index) => (
                                                                        <MenuItem key={index} value={item._id}>{item.name}</MenuItem>
                                                                    )
                                                                )
                                                            }
                                                        </Field>
                                                    </Grid>
                                                ) : ''
                                            }

                                            {
                                                values.buttonType === 'router' ? (
                                                    (
                                                        <Grid item xs={12}>
                                                            <Field
                                                                fullWidth
                                                                name="viewId"
                                                                component={Select}
                                                                label="Seleccionar una vista"
                                                                formControlProps={{fullWidth: true}}
                                                                variant="outlined"
                                                            >
                                                                {
                                                                    this.state.viewsList.map(
                                                                        (item, index) => (
                                                                            <MenuItem key={index} value={item._id}>{item.name}</MenuItem>
                                                                        )
                                                                    )
                                                                }
                                                            </Field>
                                                        </Grid>
                                                    )
                                                ) : ''
                                            }

                                            {
                                                values.buttonType === 'phone' ? (
                                                    <Grid item xs={12}>
                                                        <Field
                                                            fullWidth
                                                            name="phoneNumber"
                                                            component={TextField}
                                                            label="Teléfono a marcar"
                                                            variant="outlined"
                                                        />
                                                    </Grid>
                                                ) : ''
                                            }


                                            {submitError && <div className="error">{submitError}</div>}
                                            <Grid item style={{ marginTop: 16 }} xs={12}>

                                                <SaveButtonSpinner
                                                    loading={this.props.loading}
                                                />
                                            </Grid>
                                        </Grid>
                                    </Paper>
                                </form>
                            )}
                        />
                    )
                }

            </div>
        )
    }
}

export default ConfigImage