import React from 'react'
import Button from '@material-ui/core/Button';
import CssBaseline from '@material-ui/core/CssBaseline';
import TextField from '@material-ui/core/TextField';
import Link from '@material-ui/core/Link';
import Grid from '@material-ui/core/Grid';
import Box from '@material-ui/core/Box';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Paper from "@material-ui/core/Paper";
import PropTypes from 'prop-types'
import { Trans } from '@lingui/macro';
import logoHeader from '../../assets/TURISMO_horizontal.png';
import AlertText from "../alerts/AlertText";
import CircularProgress from '@material-ui/core/CircularProgress';


function Copyright(props) {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <Link color="inherit" href="https://www.gob.mx/sectur/">
                SECTUR
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'} v {props.version}
        </Typography>
    );
}

const useStyles = makeStyles(theme => ({
    '@global': {
        body: {
            backgroundColor: theme.palette.common.gray,
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        padding:'20px',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(1),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));



export default function SignIn(props) {

    const classes = useStyles();


    return (
        <Container component="main" maxWidth="xs">
            <CssBaseline />
            <Paper className={classes.paper}>
                {
                    !props.fromMainLogin?<img width='200px' alt='logo' src={props.img?props.img.value:logoHeader}/>:
                        <Typography variant="h6" component="h6">
                            <Trans>Panel de control de eventos</Trans>
                        </Typography>
                }

                <hr/>
                <Typography component="h4">
                    {props.title}
                </Typography>

                <form className={classes.form} onSubmit={props.handleSubmit}>
                    {
                        props.showError ? <AlertText showAlert={props.showError} onCloseAlert={()=> props.closeAlert()} closable={true} type={'error'} id="component-helper-text">
                            <Trans> Las credenciales no son correctas.</Trans>
                        </AlertText>: ''
                    }
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        id="email"
                        label={<Trans>Correo electrónico</Trans>}
                        name="email"
                        autoComplete="email"
                        autoFocus
                        onChange={props.handleUser}
                    />
                    <TextField
                        variant="outlined"
                        margin="normal"
                        required
                        fullWidth
                        name="password"
                        label={<Trans>Contraseña</Trans>}
                        type="password"
                        id="password"
                        autoComplete="current-password"
                        onChange={props.handlePassword}
                    />
                    {
                        props.loading?<CircularProgress style={{marginLeft: '45%'}} />:<Button
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={classes.submit}
                        >
                            <Trans>Iniciar</Trans>
                        </Button>
                    }

                    <Grid container>


                        <Grid item xs>
                            {
                                props.recoverFromEvent?<Link href={"/security/password/reset/?slug="+props.slug} variant="body2">
                                    <Trans>Recuperar contraseña</Trans>
                                </Link>:<Link href="/security/password/reset/" variant="body2">
                                    <Trans>Recuperar contraseña</Trans>
                                </Link>
                            }
                        </Grid>
                        <Grid item>
                            {/*<Link href="#" variant="body2">*/}
                            {/*</Link>*/}
                        </Grid>
                    </Grid>
                </form>
            </Paper>
            <Box mt={8}>
                <Copyright version={props.version} />
            </Box>
        </Container>

    );

}
