import React, {Component} from 'react';
import {FormControlLabel, Grid, Paper, Typography} from "@material-ui/core";
import {Field, Form} from "react-final-form";
import {Checkbox, Select, TextField} from "final-form-material-ui";
import _ from "lodash";
import { Trans } from '@lingui/macro';
// import {SaveButtonSpinner} from "../buttons/StandartButtons";


export class SectionPermsDialog extends Component {

    state = {
    };

    handleSubmit = (values) => {
        // console.log(values)
    };

    validate = (values) => {
        this.props.values(values);
    };

    render() {
        return (
            <Form
                onSubmit={this.handleSubmit}
                initialValues={this.props.initialValues}
                validate={this.validate}
                render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                    <form onSubmit={handleSubmit} noValidate>
                            {/*<Typography variant="h6" align="left" component="h6" gutterBottom>*/}
                            {/*    Propiedades*/}
                            {/*</Typography>*/}
                            <Grid container alignItems="flex-start" spacing={2}>

                                <Grid container item xs={12} spacing={1}>
                                    <Grid item xs={12} /* lg={6} */>
                                        {console.log((this.props.viewPermissions).length)}
                                        {
                                            (this.props.viewPermissions).length > 0 ? this.props.viewPermissions.map(
                                                (el, i) => {
                                                    let found = _.find(this.props.allPerms, {_id: el});
                                                    let label = found ? found.name : `Permiso ${i}`;
                                                    return (
                                                        <FormControlLabel
                                                            key={i}
                                                            label={label}
                                                            control={
                                                                <Field
                                                                    name="viewPerms"
                                                                    component={Checkbox}
                                                                    type="checkbox"
                                                                    value={el}
                                                                />
                                                            }
                                                        />
                                                    )
                                                }
                                            )
                                            :
                                            <Trans>No hay Roles de Seguridad seleccionados en la configuración de ésta vista...</Trans>
                                        }
                                    </Grid>
                                </Grid>


                                {/*{submitError && <div className="error">{submitError}</div>}*/}
                                {/*<Grid item style={{ marginTop: 16 }} xs={12}>*/}

                                {/*    <SaveButtonSpinner*/}
                                {/*        loading={this.props.loading}*/}
                                {/*    />*/}
                                {/*</Grid>*/}
                            </Grid>
                    </form>
                )}
            />
        );
    }
}

export default SectionPermsDialog;