import React, {Component} from 'react';
import {Grid,Typography} from "@material-ui/core";
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import GlobalContext from "../../../GlobalContext";
import {ApiServiceEvents} from "../../../api-services/events";
import ResourceListTable from "../../../components/events/admin/resources/ResourceListTable";
import {Link} from "react-router-dom";
import AddIcon from '@material-ui/icons/Add';
import IconButton from "@material-ui/core/IconButton";
import {Trans, t} from "@lingui/macro";
import {withI18n} from "@lingui/react";


const apiEvent = new ApiServiceEvents();

/**
 *
 * this view allow view user resource list
 * @class ResourcesUser
 * @extends {Component}
 */
class ResourcesUser extends Component {

    static contextType = GlobalContext;

    state = {
        isLoading:false,
        open: false,
        files: [],
        breadcrumbs:[],
        userId:''
    };


    componentDidMount() {

        let ctx = this.context;
        let user = this.props.match.params.user;

        this.setState({
            userId:user,
            breadcrumbs: [
                {
                    name: <Trans>Lista de usuarios</Trans>,
                    active: false,
                    link: `/${ctx.slug}/admin/user/list/`
                },
                {
                    name: 'Archivos del usuario',
                    active: true,
                    link: '',
                }

            ]
        });


        this.getResourcesUser(user);
        this.getDataUser(user,ctx.slug);
    }


    /**
     * I'll get the data user
     * @param {userid} user identifier
     * @param {slug} slug event
     */
    getDataUser = (userid,slug)=>{
        apiEvent.getEventUserDetail(userid, slug).then((res)=>{
            // let eventUser
            let {eventUser} = res;
            this.setState({
                breadcrumbs: [
                    {
                        name: <Trans>Lista de usuarios</Trans>,
                        active: false,
                        link: `/${slug}/admin/user/list/`
                    },
                    {
                        name: this.props.i18n._(t`Archivos del usuario `) +' '+ `${eventUser.firstName} ${eventUser.lastName}`,
                        active: true,
                        link: '',
                    }

                ]
            });
        }).catch((err)=>{
            console.log(err)
        })
    }

    /**
     * Function for get user resources 
     * @param  {number} user
     */
    getResourcesUser=(user)=>{
        apiEvent.getResourcesUser(user).then((res)=>{
            this.setState({
                files:res
            })
        }).catch((err)=>{
            console.log(err)
        }).finally(()=>{})
    }

    /**
     * function to set state for close dialog
     */
    handleClose=()=> {
        this.setState({
            open: false
        });
    }

    /**
     * Function for delete resource selected
     * @param  {Object} resourceData
     */
    handlerDelete = (resourceData) =>
        new Promise(resolve => {
            let ctx = this.context;
            let {userId} = this.state;
            let _idResource = resourceData._id;

            apiEvent.removeResourceUser(userId, _idResource).then(
                (response) => {
                    resolve();
                    this.getResourcesUser(userId);
                }
            ).catch(
                (err) => {
                    resolve();
                    if(err.response.status === 403){
                        ctx.openPermissionNotification();
                    }
                }
            );

        });


    render() {


        let {isLoading,files,userId} = this.state;
        let ctx = this.context;


        return (
            <AdminEventLayout breadcrumbs={this.state.breadcrumbs} title={<Trans>Archivos del usuario</Trans>}>
                <Grid container title=''>

                    <Grid item xs={12}>

                            <Typography variant="h6" component="h6">
                                <Trans>Archivos del usuario</Trans>
                                <IconButton component={Link} push='true' to={`/${ctx.slug}/admin/user-resources/create/${userId}`} color="primary" aria-label="new resourve">
                                    <AddIcon/>
                                </IconButton>
                            </Typography>
                            <ResourceListTable
                                handlerDelete={this.handlerDelete}
                                isLoading={isLoading}
                                userId={userId}
                                fromMyResources={true}
                                data={files}/>

                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n() (ResourcesUser);