import React, {Component} from 'react';
import LoginForm from "../../../components/security/LoginForm";
import {ApiServiceEvents} from "../../../api-services/events";
import {ApiServiceSecurity} from "../../../api-services/security";
import {Redirect} from "react-router-dom"
import GlobalContext from "../../../GlobalContext";
import {withI18n} from "@lingui/react";
import {t, Trans} from "@lingui/macro";

const apiEvents = new ApiServiceEvents();
const apiSecurity = new ApiServiceSecurity();


/**
 *
 * View to login to the event
 * @class SignInEventAdmin
 * @extends {Component}
 */
class SignInEventAdmin extends Component {
    static contextType = GlobalContext;

    state = {
        showMessageError: '',
        user:'',
        password:'',
        logo:'',
        color:'',
        name:'Acceso ',
        slug:'',
        url: '/event/list/',
        loading:false
    };


    componentDidMount() {

        let slug = this.props.match.params.slug;
        this.setState({
            slug
        })
        this.getEventInfo(slug);
        this.validateJWT(slug);

    }

    /**
     * Function to validate if exis user for event
     * @param  {string} slug
     */
    getEventInfo=(slug)=>{
        apiEvents.getEventInfoBySlug(slug).then(res=>{
            let {color,logo, name} = res
            this.setState({
                color,
                logo,
                name:this.props.i18n._(t`Acceso a `)+' '+name
            })
        }).catch(err=>{
            /// cuando no exista
        }).finally(()=>{
            this.setState({
                loading_data:false
            })
        })
    };

    /**
     * Function to submit login form
     * @param  {} e
     */
    handleSubmit = (e) => {
        e.preventDefault();
        let {user,password} = this.state;
        let slug = this.props.match.params.slug;
        this.signInEvent(user, password, slug)
    };


    
    /**
     * we validate that there is a token and if it exists then we will send it to the event admin
     * @param  {string} slug
     */
    validateJWT(slug) {
        let ctx = this.context;
        if(ctx.tokenInvalidOrExpired){
            localStorage.removeItem('jwt');
            ctx.resetTokenInvalid();
        }else {
            const jwt = localStorage.getItem('jwt');
            let url = `/${slug}/admin`;
            if (jwt) {
                let new_state = {redirect: true, url};
                this.setState(new_state);
            }
        }

    };

    /**
     * Function to set state on user for login
     * @param  {Object} event
     */
    handleChangeUser = (event) => {
        this.setState({user: event.target.value});
    };

    
    /**
     * Function to set state on password for login
     * @param  {} event
     */
    handleChangePassword = (event) => {
        this.setState({password: event.target.value});
    };

    /**
     * Function to login
     * @param  {string} email
     * @param  {string} password
     * @param  {string} slug
     */
    signInEvent=(email,password,slug)=>{
        let ctx = this.context;
        this.setState({
            loading:true
        })
        apiSecurity.loginEvent(email,password,slug).then((res)=>{
            res.eventUser.eventSlug = slug;
            let userData = res.eventUser;
            userData.isSuperAdmin = res.isSuperAdmin;
            let groupsPerms = res.groupsPerms;
            delete userData.password;
            ctx.setLanguage(userData.lang);
            localStorage.setItem('jwt', res.eventUserToken);
            localStorage.setItem('user', JSON.stringify(userData));
            localStorage.setItem('groupsPerms', JSON.stringify(groupsPerms));
            localStorage.setItem('slug', ctx.slug);
            // console.log('nuevo tokesn', res.eventUserToken);
            let new_state = {redirect: true, url:`/${this.state.slug}/admin`};
            this.setState(new_state);
        }).catch((err)=>{
            if(err.response)
            this.setState({
                showMessageError:err.response.data.message,
                showError:true
            })
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })
    }


    /**
     * This function allows close the alert message in the form login
     */
    onCloseError =(e)=>{
        this.setState({
            showError:false
        })
    };

    render() {
        let state = this.state;
        if (this.state.redirect) {
            return <Redirect push to={this.state.url} />;
        }
        let ctx = this.context;
        return(
            <>
                <br/>
                <LoginForm
                    handleSubmit={this.handleSubmit}
                    handleUser={this.handleChangeUser}
                    handlePassword={this.handleChangePassword}
                    showMessageError={state.showMessageError}
                    closeAlert={this.onCloseError}
                    showError={state.showError}
                    img={state.logo}
                    fromMainLogin={false}
                    loading={state.loading}
                    version={ctx.version}
                    title={state.name}
                    recoverFromEvent={true}
                    slug={state.slug}
                />
            </>
        )
    }
}

export default withI18n()(SignInEventAdmin);