import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import FormLangs from "../../../../components/events/admin/lang/FormLangs";
import {Redirect} from "react-router-dom";
import {ApiServiceEvents} from "../../../../api-services/events";
import GlobalContext from "../../../../GlobalContext";
import {Trans} from "@lingui/macro";
const apiEvent = new ApiServiceEvents();

/**
 *
 *This view allows you to add a new language
 * @class CreateLang
 * @extends {Component}
 */
class CreateLang extends Component {

    static contextType = GlobalContext;

    state= {
        code:'',
        name:'',
        slug:null,
        loading:false,
        redirect:false,
        idEvent: null,
        breadCrumbs:[]
    };


    componentDidMount() {

        let slug = this.props.match.params.slug;



        let breadCrumbs = [
            {
                name:<Trans>Lista de lenguajes</Trans>,
                active:false,
                link:`/${slug}/admin/lang/list`
            },
            {
                name:<Trans>Crear lenguaje</Trans>,
                active:true,
                link:``
            }
        ]

        this.setState({slug,breadCrumbs})

        let ctx = this.context;
        apiEvent.getEventInfo('',slug).then(
            (response) => {
                console.log(response.event._id)

                this.setState({
                    idEvent:response.event._id
                })
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );
    }

    /**
     * 
     * function to set values ​​to state when an input changes value
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const target = event.target;
        let value = target.type === 'checkbox' ? target.checked : target.value;
        
        if(target.type !== 'checkbox'){
            if(value.length >45)
            {
                value = value.slice(0,45);
            }
        }

        const name = target.name;

        this.setState({
            [name]: value
        });
    };

    /**
     * Function that sends the language form to save
     * @param  {Object} e
     */
    handleSubmit = (e)  => {
        e.preventDefault();
        let ctx = this.context;

        let {idEvent,code,name} = this.state;

        if(!code){
            ctx.openNotification(<Trans>Es requerido seleccionar el código de lenguaje</Trans>,'warning')
        }else{
            apiEvent.addIdiom(idEvent,code,name).then((res)=>{
                this.setState({
                    redirect:true
                })
            }).catch((err)=>{
                console.log(err)
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                if(err.response.status === 404){
                    ctx.openNotification(err.response.data.status, 'error');
                }
            }).finally(()=>{
                this.setState({
                    loading:false
                })
            })
        }
    };


    render() {
        let {code,name,loading,slug,breadCrumbs} = this.state;

        if(this.state.redirect) return <Redirect push to={`/${slug}/admin/lang/list`} />
        return (
            <AdminEventLayout breadcrumbs={breadCrumbs} title={<Trans>Crear nuevo lenguaje</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <FormLangs
                            handleInputChange={this.handleInputChange}
                            code={code}
                            name={name}
                            showActive={false}
                            handleSubmit={this.handleSubmit}
                            loading={loading}
                            title={<Trans>Crear lenguaje</Trans>}
                        />
                    </Grid>

                </Grid>
            </AdminEventLayout>
        );
    }
}

export default CreateLang;