import React, {Component} from 'react';
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import {Grid} from "@material-ui/core";
import PollsForm from "../../../../components/events/admin/polls/PollsForm";
import _ from "lodash";
import ResourcesForm from "../../../../components/events/admin/resources/ResourcesForm";
import GlobalContext from "../../../../GlobalContext";
import {ApiServiceEvents} from "../../../../api-services/events";
import {Redirect} from "react-router-dom";
import { Trans, t } from '@lingui/macro';
import { withI18n } from "@lingui/react";

const apiEvents = new ApiServiceEvents();

/**
 *
 * this view is for create new poll
 * @class CreatePoll
 * @extends {Component}
 */
class CreatePoll extends Component {

    static contextType = GlobalContext;

    state = {
        name:'',
        _idEvent:null,
        groups:[],
        groupsSelected:[], // aqui se setean los grupos seleccionados
        loading:false,
        redirect: false,
        breadCrumbs:[]
    };


    componentDidMount(){
        let slug = this.context.slug;

        let breadCrumbs = [
            {
                name: <Trans>Encuestas del evento</Trans>,
                active: false,
                link: `/${slug}/admin/poll/list/`
            },
            {
                name: <Trans>Crear encuesta</Trans>,
                active: true,
                link: ''
            }
        ];

        this.setState({
            breadCrumbs
        })


        apiEvents.getEventInfo('', slug).then(
            (response) => {

                this.setState({
                    // eventInfo: response.event,
                    // availableGroups: response.event.groups,
                    // availableGroupsIds: response.eventInfo.event.groups.map(o => o._id),
                    groups:response.event.groups,
                    groupsSelected: _.map(response.event.groups, '_id'),
                    _idEvent: response.event._id
                });


            }
        ).catch(
            (err) => {
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );
    }
    /**
     * function to set status when an input changes value
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const ctx = this.context
        let value = null
        const name = event.target.name;
        const patternAlphanum = /^[a-zA-ZÀ-ÿ\d\s-_]+$/ig

        if(event.target.name === 'name'){
            if(event.target.value.length > 50){
                event.target.value = event.target.value.slice(0,50);
                ctx.openNotification('Límite de caracteres excedido', 'error');
            } else {
                if(!patternAlphanum.test(event.target.value)){
                    event.target.value = event.target.value.slice(0,-1);
                    ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
                }
            }
        }
        value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;

        this.setState({
            [name]: value
        });
    };

    /**
     * Function for set state on groupsSelected 
     * @param  {Object} arrayGroups
     */
    handleCheckGroups=(arrayGroups)=>{
        this.setState({
            groupsSelected:arrayGroups
        })
    }

    /**
     * This function is for submit poll form
     * @param  {Object} e
     */
    handleSubmit=async (e)=>{
        e.preventDefault();
        let {name, _idEvent, groupsSelected } = this.state;

        if(groupsSelected.length<=0){
            alert(this.props.i18n._(t`Al menos un rol de seguridad es requerido`));
            return;
        }

        this.setState({
            loading:true
        });

        let request = {
            name,
            _idEvent,
            groups:groupsSelected
        };

        try{
            let response = await apiEvents.createPoll(request);
            this.setState({
                redirect:true
            })
        }catch (e) {
            if(e.response.status === 403){
                this.context.openPermissionNotification();
            }else {
                alert(e.response.data.message);
            }

        }finally {
            this.setState({
                loading:false
            })
        }

    };




    render() {
        let {breadCrumbs,groups, groupsSelected, loading} = this.state;
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/poll/list/`} />;


        return (
            <AdminEventLayout breadcrumbs={breadCrumbs} title={<Trans>Nueva encuesta</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <PollsForm
                            titleForm={<Trans>Nueva encuesta</Trans>}
                            groups={groups}
                            handleSubmit={this.handleSubmit}
                            handleInputChange={this.handleInputChange}
                            groupsId={_.map(groups, '_id') }
                            groupsSelected={groupsSelected}
                            handleCheckGroups={this.handleCheckGroups}
                            loading={loading}
                        />
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(CreatePoll);