import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import ResourcesForm from "../../../components/events/admin/resources/ResourcesForm"
import GlobalContext from "../../../GlobalContext";
import {ApiServiceEvents} from "../../../api-services/events";
import _ from 'lodash'
import {Redirect} from "react-router-dom";

const apiEvents = new ApiServiceEvents();

/**
 *
 * this view allow create a new user resource
 * @class CreateMyResources
 * @extends {Component}
 */
class CreateMyResources extends Component {
    static contextType = GlobalContext;

    state = {
        resourceFile:null,
        name:'',
        _idEvent:null,
        groups:[],
        groupsSelected:[], // aqui se setean los grupos seleccionados
        eventInfo:null,
        availableGroups:[],
        availableGroupsIds:[],
        loading:false,
        redirect: false,
        breadCrumbs:[],
        userId:''
    };



    componentDidMount() {
        let slug = this.context.slug;
        let user = this.props.match.params.user;

        let breadCrumbs = [
            {
                name:'Listado de usuarios',
                active:false,
                link:`/${slug}/admin/user/list/`
            },
            {
                name:'Recursos del usuario',
                active:false,
                link:`/${slug}/admin/user/user-resources/${user}`
            },
            {
                name:'Crear recurso del usuario',
                active:true,
                link:''
            }
        ];

        this.setState({
            userId:user,
            breadCrumbs
        })


        apiEvents.getEventInfo('', slug).then(
            (response) => {
                this.setState({
                    // eventInfo: response.event,
                    // availableGroups: response.event.groups,
                    // availableGroupsIds: response.eventInfo.event.groups.map(o => o._id),
                    groups:response.event.groups,
                    _idEvent: response.event._id
                });
            }
        ).catch(
            (err) => {
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );
    }

    /**
     * function  for set state when a input element change
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const ctx = this.context
        let value = null
        const name = event.target.name;
        const patternAlphanum = /^[,.;a-zA-ZÀ-ÿ\d\s-_]+$/ig

        if(event.target.name === 'name'){
            if(event.target.value.length > 50){
                event.target.value = event.target.value.slice(0,50);
                ctx.openNotification('Límite de caracteres excedido', 'error');
            } else {
                if(!patternAlphanum.test(event.target.value)){
                    event.target.value = event.target.value.slice(0,-1);
                    ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
                }
            }
        }
        value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;

        this.setState({
            [name]: value
        });
    };

    /**
     * function to get file when a input element change
     * @param  {Object} file
     */
    handleInputChangeFile=(file)=>{
        this.setState({
            resourceFile:file
        })
    };

    /**
     * this function to submit form for create a new resource per user
     * @param  {Object} e
     */
    handleSubmit=async (e)=>{
        e.preventDefault();
        let {resourceFile,name, _idEvent, userId } = this.state;
        let ctx = this.context;


        if(!resourceFile){
            ctx.openNotification('Es requerido un archivo');
            return;
        }

        this.setState({
            loading:true
        });

        let formData = new FormData();
        formData.append("resourceFile", resourceFile);
        formData.append("name", name);
        formData.append("_idEvent", _idEvent);
        formData.append("eventUser", userId);

        try{
            let response = await apiEvents.createUserResource(formData);
            ctx.openSuccessNotification('Guardado correctamente');
            this.setState({
                loading:false,
                redirect:true
            })

        }catch (e) {
            alert(e.response.data.message)
            this.setState({
                loading:false
            })
            ctx.openNotification('Hubo un error al tratar de agregar el recurso, porfavor intente mas tarde','error');
        }finally {

        }

    };


    
    /**
     * Function to set state on groupsSelected when selected grouos
     * @param  {Object} arrayGroups
     */
    handleCheckGroups=(arrayGroups)=>{
        this.setState({
            groupsSelected:arrayGroups
        })
    }


    render() {
        let {groups, loading,groupsSelected,breadCrumbs,userId } = this.state;
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/user/user-resources/${userId}/`} />;
        // user/user-resources/5da8e674c8e5534d2cc71b34/
        return (
            <AdminEventLayout breadcrumbs={breadCrumbs} title="Nuevo recurso">
                <Grid container>
                    <Grid item xs={12}>
                        <ResourcesForm
                            titleForm={'Crear recurso'}
                            handleSubmit={this.handleSubmit}
                            groups={groups}
                            fromUserResources={true}
                            redirectBack={`/${ctx.slug}/admin/user/user-resources/${userId}/`}
                            groupsId={_.map(groups, '_id') }
                            groupsSelected={groupsSelected}
                            loading={loading}
                            handleInputChange={this.handleInputChange}
                            handleCheckGroups={this.handleCheckGroups}
                            handleInputChangeFile={this.handleInputChangeFile}
                        />
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default CreateMyResources;