import React, {Component} from 'react';
import {Button, Card, Image,Divider} from "semantic-ui-react";

class FactorySection extends Component {



    handleButton =() =>{
        this.props.goView(parseInt(this.props.order)-1)
    };

    extra = (
        <Button color='orange' onClick={
            this.handleButton.bind(this)
        } >Detalle</Button>
    );


    goToUrl=(link)=>{
        let url = link;
        window.open(url);
    }


    renderView = () => {

        const {section} = this.props;

        if(section.type==='card'){
           return (<div>
               <Card
                    key={section.id+1}
                     centered
                     image={section.portada}
                     header={section.title}
                     meta={section.subtitle}
                     description={section.description}
                     extra={this.extra}
               />
               <p></p>
           </div> )
        }else if(section.type==='image'){
            return (<Image key={section.id+section.type} src={section.url} fluid />)
        }else if(section.type==='h4'){
            return <h4 key={section.id+section.type}>{section.description}</h4>
        }
        else if(section.type==='h2'){
            return <h2 key={section.id+section.type}>{section.description}</h2>
        } else if(section.type==='p'){
            return <p key={section.id+section.type}>{section.description}</p>
        }else if(section.type==='link'){
            return <a key={section.id+section.type} rel="noopener noreferrer" target="_blank" href={section.linkTo}>{section.description}</a>
        }else  if(section.type==='divider'){
            return <Divider key={section.id+section.type} />
        }else if(section.type==="space"){
            return <br key={section.id+section.type}/>
        }else if(section.type==="button"){
            return <Button key={section.id+section.type} color='orange' fluid onClick={this.goToUrl.bind(this,section.linkTo)}>{section.description}</Button>
        }
    };


    render() {
        return (
              <div>
                  {
                      this.renderView()
                  }
              </div>

        );
    }
}

export default FactorySection;