import React from "react";
import _ from "lodash";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import Grid from "@material-ui/core/Grid";
import { FormHelperText, Typography } from "@material-ui/core";
import AgendaForm from "../../../../components/events/admin/agenda/AgendaForm";
import { ApiServiceEvents } from "../../../../api-services/events";
import GlobalContext from "../../../../GlobalContext";
import { FORM_ERROR } from "final-form";
import { Redirect, withRouter } from "react-router-dom";
import FacebookProgress from "../../../../components/FacebookProgress";
import { Trans, t } from "@lingui/macro";
import { withI18n } from "@lingui/react";
import AgendaUpdNavs from "../../../../components/events/admin/agenda/AgendaUpdNavs";
import ListInvites from "../../../../components/events/admin/agenda/ListInvites";
import EventUserMetaDataForm from "../../../../components/events/security/EventUserMetaDataForm";
// import FacebookProgress from "../../../../components/FacebookProgress";
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import { Label } from "recharts";

const apiEvent = new ApiServiceEvents();


/**
 * This view show the update form
 *
 * @class AgendaUpd
 * @extends {React.Component}
 */
class AgendaUpd extends React.Component {
    static contextType = GlobalContext;

    state = {
        breadCrumbs: [
            {
                name: <Trans>Agenda</Trans>,
                active: false,
                link: '',
            },
            {
                name: <Trans>Editar actividad de agenda</Trans>,
                active: true,
                link: ''
            },
        ],
        request: {
            slug: '',
            name: '',
            place: '',
            address: '',
            speaker: '',
            startTime: '',
            endTime: '',
            groups: [],
            name_array: []

        },
        groups: [],
        loading: false,
        redirect: false,
        loadingDetail: false,
        industryConfirmList: [],
        currentTab: 0,
        isPrivate: false,
        idEventAgenda: 0,
        nameEvent: "",
        sendNotifications: false,
        openDialog: false,
        valuesData: {}
    };

    handleOpenDialog = async (values) => {
        this.setState({
            openDialog: true,
            valuesData: values
        });
    }

    handleCloseDialog = () => {
        this.setState({
            openDialog: false,
        });
        this.handleSubmit(this.state.valuesData, false);
    }

    handleOkDialog = () => {
        this.setState({
            openDialog: false,
            sendNotifications: true
        });
        this.handleSubmit(this.state.valuesData, true);
    }


    /**
     * function to switch between navigation tabs
     * @param  {Object} event
     * @param  {Object} newValue
     */
    handleChangeNav = (event, newValue) => {
        this.setState({ currentTab: newValue });
    };
    /**
     * this function mixes date and time to form the necessary parameter to send to the api
     * @param  {string} date
     * @param  {string} time
     * @returns {string} errors text
     */
    mergeDateAndTime = (date, time) => {
        let response = '';
        if (date && time) {
            let startDate = date.toISOString().substring(0, 10);
            let startTime = time.toISOString().substring(10);
            response = startDate + startTime;
        }
        return response
    };

    /**
     * This function call api to update an agenda item
     * @param  {Object} values contains values from form components
     */
    handleSubmit = async (values, status) => {
        let ctx = this.context;
        let request = _.clone(values);
        request.slug = ctx.slug;
        request.groups = _.map(request.groups, function (x) {
            return {
                _idGroup: x
            }
        });

        if (request.userConfirm.length > 0)
            if (parseInt(request.capacity) < request.userConfirm.length) {
                // alert('El número de personas no puede ser menor al numero de confirmados');
                alert(this.props.i18n._(t`El número de personas no puede ser menor al numero de confirmados`));
                // console.log(request);
                return;
            }

        let activityId = this.props.match.params.id;
        request._idSchedule = activityId;
        request.startTime = request.startTimeDate;
        request.endTime = request.endTimeDate;
        request.sendNotification = status;

        let value_name = request.name;
        let current_lang = ctx.language;
        let obj_name = _.find(request.arrayName, { 'lang': current_lang });
        let index_name = _.findIndex(request.arrayName, { 'lang': current_lang });
        obj_name.value = value_name;

        request.arrayName[index_name] = obj_name;
        request.name = request.arrayName;




        // if(window.confirm('¿Deseas notificar a los usuarios que ha sido editada esta actividad de la agenda?')){
        /* if(window.confirm(this.props.i18n._(t`¿Deseas notificar a los usuarios que ha sido editada esta actividad de la agenda?`))){
             request.sendNotification=true;
         }*/

        let oldState = this.state;
        console.log('state', this.state)
        oldState.loading = true;
        this.setState(oldState);

        let errors = {};
        try {
            let res = await apiEvent.updateAgendaActivity(request);
            // console.log('Se creo la actividad', res);
            ctx.openSuccessNotification(<Trans>Actividad actualizada</Trans>);
            this.setState({ redirect: true, loading: false });
        } catch (e) {
            if (e.response.status === 403) {
                this.context.openPermissionNotification();
            }
            console.log('Error al crear usuario', e.response);
            if (e.response.message && typeof e.response.message === 'string') {
                errors = { [FORM_ERROR]: e.response.message };
            }

            if (e.response.data && e.response.data.message.message && typeof e.response.data.message.message === 'string') {
                errors = { [FORM_ERROR]: e.response.data.message.message };
            }
        }
        let oldState2 = this.state;
        oldState2.loading = false;
        this.setState(oldState2);
    };

    /**
     * This function validates required values from form
     * @param  {Object} values
     * @returns {Object} errors text
     */
    validate = (values) => {
        const errors = {};
        const requiredField = <Trans>Requerido</Trans>;
        const onlyAlphanum = <Trans>Sólo se permiten caracterres alfanuméricos</Trans>;
        const numCharNames = 50;
        const numCharsComments = 100;

        if (!values.name) {
            errors.name = requiredField;
        } else {
            if((values.name).length > numCharNames){
                errors.name = <Trans>Sólo se permiten 50 caracteres</Trans>;
            } else {
                if (!(/^[a-zA-ZÀ-ÿ\d\u00f1\u00d1]+(\s*[a-zA-ZÀ-ÿ\d\u00f1\u00d1]*)*[a-zA-ZÀ-ÿ\d\u00f1\u00d1]+$/g.test((values.name).trim()))) {
                    errors.name = onlyAlphanum;
                } 
            }
        }
        
        if (!values.place) {
            errors.place = requiredField;
        } else {
            if(values.place?.length > numCharsComments){
                errors.place = <Trans>Sólo se permiten 100 caracteres</Trans>;;
            } else {
                if (!(/^[,a-zA-ZÀ-ÿ\d\u00f1\u00d1]+(\s*[,a-zA-ZÀ-ÿ\d\u00f1\u00d1]*)*[,a-zA-ZÀ-ÿ\d\u00f1\u00d1]+$/g.test((values.place).trim()))) {
                    errors.place = onlyAlphanum;
                } 
            }
        }

        if (!values.address) {
            errors.address = requiredField;
        } else {
            if(values.address?.length > numCharsComments){
                errors.address = <Trans>Sólo se permiten 100 caracteres</Trans>;;
            } else {
                if (!(/^[,.;a-zA-ZÀ-ÿ\d\s-_#*]+$/ig.test((values.address).trim()))) {
                    errors.address = onlyAlphanum;
                }
            }
        }

        if(values.speaker){
            if(values.speaker?.length > numCharsComments){
                errors.speaker = <Trans>Sólo se permiten 100 caracteres</Trans>;;
            } else {
                if (!(/^[a-zA-ZÀ-ÿ\d\u00f1\u00d1]+(\s*[a-zA-ZÀ-ÿ\d\u00f1\u00d1]*)*[a-zA-ZÀ-ÿ\d\u00f1\u00d1]+$/g.test((values.speaker).trim()))) {
                    errors.speaker = onlyAlphanum;
                }
            }
        }

        if (values.isIndustryEvent) {
            if (!values.capacity) {
                errors.capacity = requiredField;
            } else {
                if (values.capacity < 1) {
                    errors.capacity = <Trans>El campo debe ser mayor a 0</Trans>
                }
            }

            if (!values.responsible) {
                errors.responsible = requiredField;
            } else {
                if (!(/^[a-zA-ZÀ-ÿ\d\u00f1\u00d1]+(\s*[a-zA-ZÀ-ÿ\d\u00f1\u00d1]*)*[a-zA-ZÀ-ÿ\d\u00f1\u00d1]+$/g.test((values.responsible).trim()))) {
                    errors.responsible = onlyAlphanum;
                } 
            }

            if (!values.contact) {
                errors.contact = requiredField;
            } else {
                if (!(/^[a-zA-ZÀ-ÿ\d\u00f1\u00d1]+(\s*[a-zA-ZÀ-ÿ\d\u00f1\u00d1]*)*[a-zA-ZÀ-ÿ\d\u00f1\u00d1]+$/g.test((values.contact).trim()))) {
                    errors.contact = onlyAlphanum;
                } 
            }
        }

        if (!values.startTimeDate) {
            errors.startTimeDate = requiredField;
        }

        if (!values.endTimeDate) {
            errors.endTimeDate = requiredField;
        }

        if (values.endTimeDate <= values.startTimeDate) {
            errors.endTimeDate = <Trans>La fecha final debe ser superior a la de inicio</Trans>
        }

        return errors;
    };

    /**
     * This function set the required roles for display in form
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvent.getRoles(ctx.slug).then(
            (res) => {
                console.log('Perfiles', res);
                this.setState({ groups: res });
            }
        ).catch(
            (err) => {
                console.log('Error en la carga de perfiles', err)
            }
        );
    };


    /**
     * When a event is for industry, we can get the assistanslist
     */
    getConfirmListAssistans = () => {
        let activityId = this.props.match.params.id;
        apiEvent.getConfirmAssistantListIndustry(activityId).then((res) => {
            console.log('res', res)
            this.setState({
                industryConfirmList: res
            })
        }).catch((err) => {
            console.log(err)
        });

    }

    /**
     * This function retrieve the data for specific agenda activity and then set the inputs field form
     */
    getDetail = () => {
        let oldState = this.state;
        oldState.loadingDetail = true;
        this.setState(oldState);
        let ctx = this.context;
        let activityId = this.props.match.params.id;
        let slug = ctx.slug;
        apiEvent.getDetailAgendaActivity(slug, activityId).then(
            (res) => {
                console.log('Detalle obtenido', res);
                this.setState({ isPrivate: res.isIndustryEvent, nameEvent: res.name });
                let oldState = this.state;
                oldState.breadCrumbs = [
                    {
                        name: <Trans>Agenda</Trans>,
                        active: false,
                        link: `/${ctx.slug}/admin/agenda/list/`,
                    },
                    {
                        name: res.name,
                        active: true,
                        link: ''
                    },
                ];
                oldState.request = res;
                oldState.request.startTimeDate = new Date(res.startTime);
                oldState.request.startTimeTime = new Date(res.startTime);
                oldState.request.endTimeDate = new Date(res.endTime);
                oldState.request.endTimeTime = new Date(res.endTime);
                oldState.request.groups = _.map(res.groups, '_idGroup');
                // oldState.request.name_array = res.name;
                // oldState.request.name = nameLang.value;
                oldState.loadingDetail = false;


                if (res.isIndustryEvent) {
                    this.getConfirmListAssistans();
                }

                this.setState(oldState)
            }
        ).catch(
            (err) => {
                if (err.response)
                    if (err.response.status === 403) {
                        this.context.openPermissionNotification();
                    }
                let oldState = this.state;
                oldState.loadingDetail = false;
                this.setState(oldState);
                console.log('Error al obtener detalle', err);
            }
        );
    };

    componentDidMount() {
        if (_.has(this.props, 'match.params.isMeta')) {
            this.setState({ currentTab: 1 })
        }
        let ctx = this.context;
        this.loadRoles();
        this.getDetail();
        this.setState({ idEventAgenda: this.props.match.params.id });
    }

    render() {

        let ctx = this.context;
        if (this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/agenda/list/`} />;

        return (
            <AdminEventLayout
                title={<Trans>Administrador de eventos</Trans>}
                breadcrumbs={this.state.breadCrumbs}>
                <Grid container>
                    <Grid
                        container
                        item xs={12}
                        direction="row"
                        justify="center"
                        alignItems="center">
                        {
                            this.state.isPrivate ?
                                <AgendaUpdNavs currentTab={this.state.currentTab}
                                    handleChangeNav={this.handleChangeNav} />
                                :
                                <div />
                        }
                    </Grid>
                    <Grid item xs={12}>
                        {this.state.loadingDetail ? (<FacebookProgress />) : ''}
                        <>
                            {
                                this.state.currentTab === 0 ? (
                                    <div style={{ padding: 16, margin: 'auto', maxWidth: 800 }}>
                                        <AgendaForm
                                            onSubmit={this.handleOpenDialog}
                                            initialValues={this.state.request}
                                            validate={this.validate}
                                            industryConfirmList={this.state.industryConfirmList}
                                            title={<Trans>Editar actividad de agenda</Trans>}
                                            loading={this.state.loading}
                                            isUpdate={true}
                                            roles={this.state.groups}
                                        />
                                    </div>
                                ) :
                                    <ListInvites slug={this.context.slug} idEventAgenda={this.state.idEventAgenda} nameEvent={this.state.nameEvent} />
                            }
                        </>
                    </Grid>
                </Grid>
                <div>
                    <Dialog open={this.state.openDialog} onCancel={this.handleCloseDialog}>
                        <DialogTitle></DialogTitle>
                        <DialogContent>
                            <p>{this.props.i18n._(t`¿Deseas notificar a los usuarios que ha sido editada esta actividad de la agenda?`)}</p>
                        </DialogContent>
                        <DialogActions>
                            <Button type='button' onClick={this.handleCloseDialog}>No</Button>
                            <Button type='button' onClick={this.handleOkDialog}>Si</Button>
                        </DialogActions>
                    </Dialog>
                </div>
            </AdminEventLayout>
        )
    }
}

export default withI18n()(withRouter(AgendaUpd));