import React, {Component} from 'react';
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import ViewListTable from "../../../../components/events/admin/views/ViewListTable";
import {ApiServiceEvents} from "../../../../api-services/events";
import AddIcon from '@material-ui/icons/Add';
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import GlobalContext from "../../../../GlobalContext";
import _ from "lodash";
import move from "lodash-move";
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogTitle from '@material-ui/core/DialogTitle';
import Button from '@material-ui/core/Button';
import InputLabel from '@material-ui/core/InputLabel';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import { Trans, t } from '@lingui/macro';
import Box from '@material-ui/core/Box';
import {withI18n} from "@lingui/react";
import LoadingOverlay from "react-loading-overlay";


const apiEvent = new ApiServiceEvents();

/**
 *
 * this view allows to see the views list
 * @class ListViews
 * @extends {Component}
 */
class ListViews extends Component {
    tableRef = React.createRef();

    static contextType = GlobalContext;

    state = {
        viewList:[],
        moduleList:[],
        moduleListSelected:null,
        moduleId:null,
        slug:null,
        redirectToEdit: '',
        redirectToBuilder:'',
        selectedRow: '',
        openDialog:false,
        langs:[],
        isLoading:false,
        redirectToCreate:false,
        selectedView:'',
        breadcrumbs:[],
        lang:'es',
        langModule:'',
        firstQueryModule:true,
        idSelectedView:null,
        nombreModulo:''
    };


    componentDidMount() {
        let moduleId = this.props.match.params.module;
        let slug = this.props.match.params.slug;
        let ctx = this.context;


        let breadcrumbs = [
            {
                name:<Trans>Lista de módulos</Trans>,
                active:false,
                link:`/${slug}/admin/module/list`
            },
            {
                name:<Trans>Lista de vistas</Trans>,
                active:true,
                link:''
            }
        ];


        this.setState({
            moduleId,
            slug,
            breadcrumbs,
            langModule:ctx.language,
            lang:ctx.language
        });
        this.getViews(moduleId);
        this.getModuleList(slug);
        this.getLangs(slug);
    }



    /**
     * Function to get language
     * @param  {string} slug
     */
    getLangs = (slug)=>{
        apiEvent.getIdiomList('',slug).then((res)=>{
            console.log('langs',res);
            this.setState({
                langs:res
            })
        }).catch((err)=>{
        });
    }

    /**
     * Function to order views
     * @param  {string} type
     * @param  {Object} view
     */
    sortViews=(type,view)=>{


        console.log(type,view);
        let {viewList} = this.state;

        // obtenemos la posicion de la vista en el arreglo (listo)
        let currentPostion = _.findIndex(viewList, function(o) { return view._id === o._id; });

        console.log(viewList.length);

        if(type==='up' && currentPostion===0){
            return;
        }


        if(type==='down' && currentPostion===viewList.length-1){
            return;
        }

        if(type==='up'){
            //move change the position of section in the current array
            viewList = move(viewList,currentPostion, currentPostion-1);
        }else{
            viewList =  move(viewList,currentPostion, currentPostion+1);
        }


        let arrayPositionsIds = _.map(viewList,'_id'); // los ids con las nuevas positions


        this.setState({isLoading:true})


        apiEvent.orderView(arrayPositionsIds).then((res)=>{
            console.log(res)
            let moduleId = this.props.match.params.module;
            this.getViews(moduleId);
        }).catch((err)=>{
            console.log(err)
        }).finally(()=>{
            this.setState({isLoading:false})
        })


    };


    /**
     * This function allows to move an element view in a especifict index/position
     * @param {int} currentIndex
     * @param {int} indexToMove Index or position that we need to move a view
     */
    moveToPositionView=(currentIndex, indexToMove)=>{
        let {viewList} = this.state;
        viewList =  move(viewList,currentIndex, indexToMove);
        let arrayPositionsIds = _.map(viewList,'_id'); // los ids con las nuevas positions
        this.setState({isLoading:true})
        apiEvent.orderView(arrayPositionsIds).then((res)=>{
            console.log(res)
            let moduleId = this.props.match.params.module;
            this.getViews(moduleId);
        }).catch((err)=>{
            console.log(err)
        }).finally(()=>{
            this.setState({isLoading:false})
        })

    }

    /**
     * function to get views from module
     * @param  {number} moduleId
     */
    getViews=(moduleId)=>{
        this.setState({isLoading:true});
        let ctx = this.context;
        apiEvent.getViewsList(moduleId).then((res)=>{
            console.log('vistas',res)
            if(res){



                let array = [];
                res.map((view,i)=>{
                    array.push(view.views)
                });

                this.setState({viewList:array});

                // if(array.length>0){
                //     this.setState({
                //         viewList:array
                //     })
                // }

            }
        }).catch(
            (e) => {
                console.log('Hubo en error', e);
                if(e.response.status === 403){
                    ctx.openPermissionNotification();
                }
            }
        ).finally(()=>this.setState({isLoading:false}))
    };

    /**
     * Function to clone view selected
     * @param  {number} idView
     */
    onCloneRow=(idView)=>{
        let ctx = this.context;
        let msg = '¿Deseas clonar esta vista?';
        let q = window.confirm(msg)
        let {moduleId} = this.state;
        if(q){
            apiEvent.duplicateView(idView, moduleId).then((res)=>{
                this.getViews(moduleId)
            }).catch(
                (err)=> {
                    console.log(err);

                    if(err.response.status === 404){
                        // alert('hubo un error interno, porfavor intente mas tarde.')
                        alert(this.props.i18n._(t`hubo un error interno, porfavor intente mas tarde.`))
                    }

                    if(err.response.status === 403){
                        ctx.openPermissionNotification();
                    }
                }
            )
        }
    }



    /**
     * Function to get module list 
     * @param  {string} slug
     * @param  {string} lang
     */
    getModuleList(slug,lang=null){
        let ctx = this.context;
        let {langModule} = this.state;
        let breadCrumbs = [];

        apiEvent.getModuleList(slug,true,lang!=null?lang:langModule).then((res)=>{


            // console.log('Module', res, this.state);


            let moduloActual = _.find(res, { '_id': this.state.moduleId});

            const moduleLang = _.has(moduloActual, 'lang') ? moduloActual.lang : 'N/A';


            if(moduleLang!=="N/A"){

                breadCrumbs = [
                    {
                        name:<Trans>Lista de módulos</Trans>,
                        active:false,
                        link:`/${this.state.slug}/admin/module/list`
                    },
                    {
                        name:this.props.i18n._(t`Lista de vistas`)+ ` - ${moduloActual.name}`,
                        active:true,
                        link:''
                    }
                ];

                this.setState({
                    breadcrumbs:breadCrumbs
                })
            }


            if(this.state.firstQueryModule){
                this.setState({
                    firstQueryModule:false,
                    nombreModulo:moduloActual.name
                })
            }

            this.setState({
                moduleList:res,
                moduleListSelected:res[0]._id
            })


        }).catch((error)=>{
            console.log(error)
        })
    }

    /**
     * Function to duplicate the module view
     */
    handleCloneRowToModule = () => {
        let {idSelectedView, moduleListSelected, moduleId} = this.state;

        let ctx = this.context;
        apiEvent.duplicateView(idSelectedView, moduleId, moduleListSelected).then((res) => {
            this.getViews(moduleId)
            ctx.openNotification(<Trans>Vista creada con éxito</Trans>);
            this.handleClose();
        }).catch(
            (err) => {
                if (err.response.status === 403) {
                    ctx.openPermissionNotification();
                }else{
                    ctx.openNotification(<Trans>hubo un error interno, porfavor intente mas tarde.</Trans>,'error')
                }

            }
        )
    }


    /**
     *
     * @param {Object} rowData
     */
    handleMovePositionData=(rowData)=>{
        let currentIndex = rowData.order;
        let moveToIndex = prompt("Mover a la posición:", currentIndex+1);
        let {viewList} = this.state;
        if(moveToIndex){
            moveToIndex = parseInt(moveToIndex);
            if(moveToIndex<=0 || moveToIndex>viewList.length){
                alert(this.props.i18n._(t`El valor ingresado no es válido`));
            }else{
                this.moveToPositionView(currentIndex,moveToIndex-1);
            }

        }

    };

    /**
     * Function to set state on idSelectedView for clone view
     * @param  {} idView
     */
    handleShowCloneViewToModule=(idView)=>{
        console.log('mover view',idView)
        this.setState({
            idSelectedView:idView
        });

        this.handleClickOpen();
    };

    /**
     * Function to open modal
     */
    handleClickOpen = () => {
        this.setState({
            openDialog:true
        })
    };

    /**
     * function to close modal
     */
    handleClose = () => {

        this.setState({
            idSelectedView:null,
            openDialog:false
        })
    };

    /**
     * Function to change state on langModule
     * @param  {Object} event
     */
    handleChange =(event)=>{
        let slug = this.props.match.params.slug;
        this.setState({
            langModule:event.target.value
        });

        this.getModuleList(slug,event.target.value);

    }

    /**
     * Function to delete view
     * @param  {Object} currentView
     */
    handlerDelete = (currentView) =>
        new Promise(resolve => {
            let id = currentView._id;
            console.log(currentView);
            let ctx = this.context;
            apiEvent.deleteView(id).then((res)=>{
                this.getViews(this.state.moduleId)
            }).catch((err)=>{
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
            }).finally(()=>resolve())
        });

    /**
     * Function to set state on moduleListSelected
     * @param  {Object} event
     */
    handleChangeModuleId=(event)=>{
        this.setState({
            moduleListSelected:event.target.value
        })
    }


    render() {

        let {viewList,moduleId,slug,isLoading,breadcrumbs,nombreModulo,langModule,langs} = this.state;

        return (

            <LoadingOverlay
                active={this.state.isLoading}
                spinner
                text={<Trans>Cargando...</Trans>}
            >
            <AdminEventLayout breadcrumbs={breadcrumbs} title={<Trans>Lista de vistas</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Lista de vistas</Trans>
                            <IconButton component={Link} push='true' to={`/${slug}/admin/${moduleId}/view/create/`} color="primary" aria-label="add to shopping cart">
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>
                    <Grid item xs={12}>
                        <ViewListTable
                            sortViews={this.sortViews}
                            slug={slug} module={moduleId}
                            handleShowCloneViewToModule={this.handleShowCloneViewToModule}
                            handleMovePositionData={this.handleMovePositionData}
                            isLoading={isLoading} onCloneRow={this.onCloneRow}
                            handlerDelete={this.handlerDelete}
                            tableRef={this.tableRef}
                            data={viewList}/>
                    </Grid>
                </Grid>
            </AdminEventLayout>



            <Dialog
                    open={this.state.openDialog}
                    onClose={this.handleClose}
                    aria-labelledby="alert-dialog-title"
                    aria-describedby="alert-dialog-description"
                >
                    <DialogTitle id="alert-dialog-title">
                        <Box fontWeight="fontWeightMedium">
                            <Trans>Copiar vista al módulo</Trans>
                        </Box>
                    </DialogTitle>
                    <DialogContent>

                        <FormControl>
                            <InputLabel id="demo-simple-select-helper-label"><Trans>Seleccione el Lenguaje</Trans></InputLabel>
                            <Select
                                labelId="demo-simple-select-helper-label"
                                id="demo-simple-select-helper"
                                value={langModule}
                                onChange={this.handleChange}
                            >
                                {
                                    langs.map((item,i)=>{
                                        return (item.availableLang?<MenuItem key={i} value={item.availableLang.code}>{item.availableLang.name}</MenuItem>:'')
                                    })
                                }
                            </Select>
                            <FormHelperText><Trans>La vista se copiará en el lenguaje y módulo seleccionado</Trans></FormHelperText>
                        </FormControl>
                        <br/><br/>


                        <FormControl>
                            <InputLabel id="module"><Trans>Modulo</Trans></InputLabel>
                            <Select
                                id="module"
                                value={this.state.moduleListSelected}
                                onChange={this.handleChangeModuleId}
                            >
                                {
                                    this.state.moduleList.map((item,i)=>{
                                        return (<MenuItem key={i} value={item._id}>{item.name}</MenuItem>)
                                    })
                                }
                            </Select>
                        </FormControl>

                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleClose} color="primary">
                            Cancelar
                        </Button>
                        <Button onClick={this.handleCloneRowToModule} color="primary" autoFocus>
                            Aceptar
                        </Button>
                    </DialogActions>
                </Dialog>

            </LoadingOverlay>
        );
    }
}

export default withI18n()(ListViews);