import React from "react";
import CssBaseline from "@material-ui/core/CssBaseline";
import {Field, Form} from "react-final-form";
import {FormControl, FormControlLabel, Grid, Paper, Typography} from "@material-ui/core";
import Box from "@material-ui/core/Box";
import Image from "material-ui-image/lib/components/Image/Image";
import FileBase64 from "react-file-base64";
import {Radio, Select, TextField} from "final-form-material-ui";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import MenuItem from "@material-ui/core/MenuItem";
import {SaveButtonSpinner} from "../../buttons/StandartButtons";
import _ from "lodash";
import Wysiwyg from "../../fields/Wysiwyg";
import {convertToRaw, EditorState, ContentState as ContentSt} from "draft-js";
import draftToHtml from "draftjs-to-html";
import htmlToDraft from 'html-to-draftjs';
import {GlobalContextBuilder} from "../../../GlobalContext";


class ConfigText extends React.Component {

    static contextType = GlobalContextBuilder;

    state = {
        textHtml: '',
        editorState: EditorState.createEmpty(),
    };

    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
            textHtml: draftToHtml(convertToRaw(editorState.getCurrentContent()))
            // text: editorState.getCurrentContent()
        });
    };

    handleSubmit = (values) => {
        let request = {
            textHtml: {
                value: values.textHtml,
                type: 'string'
            },
        };
        this.props.onSaveConfig(request);
    };

    setInitialValues = () => {
        const ctx = this.context;
        const component = ctx.currentComponent;

        let properties = component.properties;

        let newState = _.merge(this.state,{textHtml: _.has(properties, 'textHtml.value') ? properties.textHtml.value : ''});

        const contentBlock = _.has(properties, 'textHtml.value') ? htmlToDraft(properties.textHtml.value) : '';
        if (contentBlock) {
            const contentState = ContentSt.createFromBlockArray(contentBlock.contentBlocks);
            const editorState = EditorState.createWithContent(contentState);
            newState.editorState = editorState;
        }
        // console.log('newState', newState);
        this.setState(newState);
    };

    componentDidMount() {
        this.setInitialValues();
    }

    render() {
        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                {/*<Typography variant="h4" align="center" component="h1" gutterBottom>*/}
                {/*    Configuración de componente tipo tarjeta*/}
                {/*</Typography>*/}

                <Form
                    // onSubmit={this.props.onSaveConfig}
                    onSubmit={this.handleSubmit}
                    initialValues={{textHtml: this.state.textHtml}}
                    validate={this.validate}
                    render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                        <form onSubmit={handleSubmit} noValidate>
                            <Paper style={{ padding: 16 }}>
                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                    Propiedades
                                </Typography>
                                <Grid container alignItems="flex-start" spacing={2}>


                                    <Grid item xs={12}>
                                        <Wysiwyg
                                            editorState={this.state.editorState}
                                            onEditorStateChange={this.onEditorStateChange}
                                        />
                                        <Field
                                            fullWidth
                                            name="textHtml"
                                            component={TextField}
                                            multiline
                                            disabled
                                            label="HTML de salida"
                                            variant="outlined"
                                            rows="5"
                                            margin="normal"
                                        />

                                    </Grid>





                                    {submitError && <div className="error">{submitError}</div>}
                                    <Grid item style={{ marginTop: 16 }} xs={12}>

                                        <SaveButtonSpinner
                                            loading={this.props.loading}
                                        />
                                    </Grid>
                                </Grid>
                            </Paper>
                        </form>
                    )}
                />
            </div>
        )

    }

}

export default ConfigText