import React, {useContext, useState} from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import BlockIcon from '@material-ui/icons/Block';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import SentimentDissatisfiedIcon from '@material-ui/icons/SentimentDissatisfied';
import GlobalContext from "../GlobalContext";
import Grid from "@material-ui/core/Grid";
import {Link, useHistory} from "react-router-dom";


const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        minHeight: '100vh',
    },
    main: {
        marginTop: theme.spacing(8),
        marginBottom: theme.spacing(2),
    },
    footer: {
        padding: theme.spacing(2),
        marginTop: 'auto',
        backgroundColor: 'white',
    },
}));

/**
 *
 * this view is for show 403 error
 * @export
 * @returns
 */
export default function Code403() {
    const classes = useStyles();
    const ctx = useContext(GlobalContext);

    let history = useHistory();

    console.log(history)

    const [redirect, setRedirect] = useState(false);

    let slug = ctx.slug;

    if(!slug){
        slug = localStorage.getItem('slug');
    }



    return (
        <div className={classes.root}>
            <CssBaseline />
            <Container component="main" className={classes.main} maxWidth="sm">
                <Grid
                    container
                    direction="column"
                    justify="center"
                    alignItems="center"
                >
                    <SentimentDissatisfiedIcon fontSize={"large"}/><br/>
                    <Typography variant="h2" component="h1" gutterBottom>
                        403
                    </Typography>
                    <Typography variant="body1">Prohibido.</Typography>
                    <Typography variant="h5" component="h2" gutterBottom align={'center'}>

                        {'No tienes suficientes permisos para ver esta página'}<br/>

                    </Typography>
                    {/*<Typography variant="h5" component="h2" gutterBottom align={'center'}>*/}

                    {/*    <Button*/}
                    {/*        variant="outlined"*/}
                    {/*        color="primary"*/}
                    {/*        onClick={() => history.goBack()}*/}
                    {/*        startIcon={<KeyboardBackspaceIcon />}*/}
                    {/*    >*/}
                    {/*        Regresar a la pantalla anterior*/}
                    {/*    </Button>*/}

                    {/*</Typography>*/}
                    <Typography>

                        <Button
                            component={Link}
                            push="true"
                            to={`/${slug}/admin/`}
                            variant="outlined"
                            color="default"
                        >
                            Ir a home
                        </Button>
                    </Typography>
                </Grid>


            </Container>
        </div>
    );
}