import React, {Component} from 'react';
import CssBaseline from "@material-ui/core/CssBaseline";
import {Field, Form} from "react-final-form";
import {FormControlLabel, Grid, Paper, Typography} from "@material-ui/core";
import {Checkbox, Select, TextField} from "final-form-material-ui";
import {SaveButtonSpinner} from "../../buttons/StandartButtons";
import {GlobalContextBuilder} from "../../../GlobalContext";
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import YouTubeIcon from '@material-ui/icons/YouTube';
import PinterestIcon from '@material-ui/icons/Pinterest';
import InputAdornment from "@material-ui/core/InputAdornment";
import MenuItem from "@material-ui/core/MenuItem";

class ConfigSocial extends Component {

    static contextType = GlobalContextBuilder;

    state = {
        loading: true,
        request: {
            facebookUrl: {
                value: '',
                type: 'string'
            },
            facebookLabel: {
                value: '',
                type: 'string'
            },
            facebookActive: {
                value: false,
                type: 'bool'
            },

            instagramUrl: {
                value: '',
                type: 'string'
            },
            instagramLabel: {
                value: '',
                type: 'string'
            },
            instagramActive: {
                value: false,
                type: 'bool'
            },

            twitterUrl: {
                value: '',
                type: 'string'
            },
            twitterLabel: {
                value: '',
                type: 'string'
            },
            twitterActive: {
                value: false,
                type: 'bool'
            },

            pinterestUrl: {
                value: '',
                type: 'string'
            },
            pinterestLabel: {
                value: '',
                type: 'string'
            },
            pinterestActive: {
                value: false,
                type: 'bool'
            },

            youtubeUrl: {
                value: '',
                type: 'string'
            },
            youtubeLabel: {
                value: '',
                type: 'string'
            },
            youtubeActive: {
                value: false,
                type: 'bool'
            },

            position: {
                value: 'horizontal',
                type: 'string',
            }
        }
    };

    

    handleSubmit = async (values) => {
        console.log('VALUES ', values);
        this.props.onSaveConfig(values);
    };

    validateUrl =async (url)=> {
        const result = await fetch(url, { method: 'HEAD' });
        console.log('RESULT', result);
        return result.ok;
    }

    validate = (values) => {

    };

    setInitialValues = () => {
        const ctx = this.context;
        const component = ctx.currentComponent;


        if(!component.properties) {
            this.setState({loading:false});
            return;
        }

        let newState = this.state;

        if(component.properties.facebookUrl)
        {
            component.properties.facebookUrl.value = component.properties.facebookUrl.value.replace('https://www.facebook.com/','');
        }

        if(component.properties.instagramUrl)
        {
            component.properties.instagramUrl.value = component.properties.instagramUrl.value.replace('https://www.instagram.com/','');
        }
        
        if(component.properties.youtubeUrl)
        {
            component.properties.youtubeUrl.value = component.properties.youtubeUrl.value.replace('https://www.youtube.com/','');
        }

        if(component.properties.pinterestUrl)
        {
            component.properties.pinterestUrl.value = component.properties.pinterestUrl.value.replace('https://www.pinterest.com/','');
        }

        if(component.properties.twitterUrl)
        {
            component.properties.twitterUrl.value = component.properties.twitterUrl.value.replace('https://twitter.com/','');
        }

        newState.request = component.properties;
        newState.loading = false;
        console.log('STATEEEE', component);
        this.setState(newState);
    };

    componentDidMount() {
        this.setInitialValues();
    }

    render() {
        const facebookStyle = {
            marginRight:'10px',
            color: '#3B5998'
        };
        const instagramStyle = {
            marginRight:'10px',
            color: '#3f729b'
        };
        const twitterStyle = {
            color: '#00acee'
        };
        const youtubeStyle = {
            color: '#c4302b'
        };
        const pinterestStyle = {
            color: '#c8232c'
        };
        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                {
                    this.state.loading ? '' : (
                        <Form
                            onSubmit={this.handleSubmit}
                            initialValues={this.state.request}
                            validate={this.validate}
                            render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                                <form onSubmit={handleSubmit} noValidate>
                                    <Paper style={{ padding: 16 }}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Propiedades
                                        </Typography>
                                        <Typography variant="p" align="left" component="p" gutterBottom>
                                            Ingresa la extensión del perfil en los siguientes campos.
                                        </Typography>
                                        <Grid container alignItems="flex-start" spacing={2}>

                                            <Grid container item xs={12} spacing={1}>
                                                <Grid item xs={12} lg={6}>
                                                    <Field
                                                        fullWidth
                                                        name="facebookUrl.value"
                                                        component={TextField}
                                                        label="URL del facebook"
                                                        variant="outlined"
                                                        margin="normal"
                                                        InputProps={{
                                                            startAdornment: <InputAdornment position="start" style={facebookStyle}>
                                                                <FacebookIcon/> facebook.com/
                                                            </InputAdornment>,
                                                        }}
                                                    />
                                                </Grid>
                                                <Grid item xs={11} lg={5}>
                                                    <Field
                                                        fullWidth
                                                        name="facebookLabel.value"
                                                        component={TextField}
                                                        label="Leyenda facebook"
                                                        variant="outlined"
                                                        margin="normal"
                                                    />
                                                </Grid>
                                                <Grid item xs={1} lg={1}>
                                                    <br/>
                                                    <FormControlLabel
                                                        label="Activo"
                                                        control={
                                                            <Field
                                                                name="facebookActive.value"
                                                                component={Checkbox}
                                                                type="checkbox"
                                                            />
                                                        }
                                                    />
                                                </Grid>

                                            </Grid>

                                            <Grid container item xs={12} spacing={1}>
                                                <Grid item xs={12} lg={6}>
                                                    <Field
                                                        fullWidth
                                                        name="instagramUrl.value"
                                                        component={TextField}
                                                        label="URL de instagram"
                                                        variant="outlined"
                                                        margin="normal"
                                                        InputProps={{
                                                            startAdornment: <InputAdornment position="start" style={instagramStyle}>
                                                                <InstagramIcon/> instagram.com/
                                                            </InputAdornment>,
                                                        }}
                                                    />
                                                </Grid>
                                                <Grid item xs={11} lg={5}>
                                                    <Field
                                                        fullWidth
                                                        name="instagramLabel.value"
                                                        component={TextField}
                                                        label="Leyenda instagram"
                                                        variant="outlined"
                                                        margin="normal"
                                                    />
                                                </Grid>
                                                <Grid item xs={1} lg={1}>
                                                    <br/>
                                                    <FormControlLabel
                                                        label="Activo"
                                                        control={
                                                            <Field
                                                                name="instagramActive.value"
                                                                component={Checkbox}
                                                                type="checkbox"
                                                            />
                                                        }
                                                    />
                                                </Grid>
                                            </Grid>

                                            <Grid container item xs={12} spacing={1}>
                                                <Grid item xs={12} lg={6}>
                                                    <Field
                                                        fullWidth
                                                        name="twitterUrl.value"
                                                        component={TextField}
                                                        label="URL de twitter"
                                                        variant="outlined"
                                                        margin="normal"
                                                        InputProps={{
                                                            startAdornment: <InputAdornment position="start" style={twitterStyle}>
                                                                <TwitterIcon/> twitter.com/
                                                            </InputAdornment>,
                                                        }}
                                                    />
                                                </Grid>
                                                <Grid item xs={10} lg={5}>
                                                    <Field
                                                        fullWidth
                                                        name="twitterLabel.value"
                                                        component={TextField}
                                                        label="Leyenda twitter"
                                                        variant="outlined"
                                                        margin="normal"
                                                    />
                                                </Grid>
                                                <Grid item xs={1} lg={1}>
                                                    <br/>
                                                    <FormControlLabel
                                                        label="Activo"
                                                        control={
                                                            <Field
                                                                name="twitterActive.value"
                                                                component={Checkbox}
                                                                type="checkbox"
                                                            />
                                                        }
                                                    />
                                                </Grid>
                                            </Grid>

                                            <Grid container item xs={12} spacing={1}>
                                                <Grid item xs={12} lg={6}>
                                                    <Field
                                                        fullWidth
                                                        name="youtubeUrl.value"
                                                        component={TextField}
                                                        label="URL de youtube"
                                                        variant="outlined"
                                                        margin="normal"
                                                        InputProps={{
                                                            startAdornment: <InputAdornment position="start" style={youtubeStyle}>
                                                                <YouTubeIcon/> youtube.com/
                                                            </InputAdornment>,
                                                        }}
                                                    />

                                                </Grid>
                                                <Grid item xs={10} lg={5}>
                                                    <Field
                                                        fullWidth
                                                        name="youtubeLabel.value"
                                                        component={TextField}
                                                        label="Leyenda youtube"
                                                        variant="outlined"
                                                        margin="normal"
                                                    />
                                                </Grid>
                                                <Grid item xs={1} lg={1}>
                                                    <br/>
                                                    <FormControlLabel
                                                        label="Activo"
                                                        control={
                                                            <Field
                                                                name="youtubeActive.value"
                                                                component={Checkbox}
                                                                type="checkbox"
                                                            />
                                                        }
                                                    />
                                                </Grid>
                                            </Grid>

                                            <Grid container item xs={12} spacing={1}>
                                                <Grid item xs={12} lg={6}>
                                                    <Field
                                                        fullWidth
                                                        name="pinterestUrl.value"
                                                        component={TextField}
                                                        label="URL de pinterest"
                                                        variant="outlined"
                                                        margin="normal"
                                                        InputProps={{
                                                            startAdornment: <InputAdornment position="start" style={pinterestStyle}>
                                                                <PinterestIcon/> pinterest.com/
                                                            </InputAdornment>,
                                                        }}
                                                    />

                                                </Grid>
                                                <Grid item xs={10} lg={5}>
                                                    <Field
                                                        fullWidth
                                                        name="pinterestLabel.value"
                                                        component={TextField}
                                                        label="Leyenda pinterest"
                                                        variant="outlined"
                                                        margin="normal"
                                                    />
                                                </Grid>
                                                <Grid item xs={1} lg={1}>
                                                    <br/>
                                                    <FormControlLabel
                                                        label="Activo"
                                                        control={
                                                            <Field
                                                                name="pinterestActive.value"
                                                                component={Checkbox}
                                                                type="checkbox"
                                                            />
                                                        }
                                                    />
                                                </Grid>
                                            </Grid>


                                            <Grid container item xs={12} spacing={1}>
                                                <Grid item xs={12}>
                                                    <Field
                                                        fullWidth
                                                        name="position.value"
                                                        component={Select}
                                                        label="Alineación"
                                                        formControlProps={{fullWidth: true}}
                                                        // variant="outlined"
                                                    >
                                                        <MenuItem value={'horizontal'}>Horizontal</MenuItem>
                                                        <MenuItem value={'vertical'}>Vertical</MenuItem>
                                                    </Field>
                                                </Grid>
                                            </Grid>


                                            {submitError && <div className="error">{submitError}</div>}
                                            <Grid item style={{ marginTop: 16 }} xs={12}>

                                                <SaveButtonSpinner
                                                    loading={this.props.loading}
                                                />
                                            </Grid>
                                        </Grid>
                                    </Paper>
                                </form>
                            )}
                        />
                    )
                }
            </div>
        );
    }
}

export default ConfigSocial;