import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import ResourcesForm from "../../../../components/events/admin/resources/ResourcesForm";
import GlobalContext from "../../../../GlobalContext";
import _ from "lodash";
import {Redirect} from "react-router-dom";
import {ApiServiceEvents} from "../../../../api-services/events";
import {Trans, t} from "@lingui/macro";
import {withI18n} from "@lingui/react";

const apiEvents = new ApiServiceEvents();

/**
 *
 * this view allows you to edit the information of the memory
 * @class EditResource
 * @extends {Component}
 */
class EditResource extends Component {

    static contextType = GlobalContext;

    state = {
        resourceFile:null,
        name:'',
        _idEvent:null,
        _idResource:null,
        groups:[],
        groupsSelected:[], // aqui se setean los grupos seleccionados
        eventInfo:null,
        availableGroups:[],
        availableGroupsIds:[],
        loading:false,
        redirect: false,
        breadCrumbs:[],
        urlCurrentFile:''
    };


    componentDidMount() {

        let slug = this.context.slug;
        let _idResource = this.props.match.params.resource;

        let breadCrumbs = [
            {
                name:<Trans>Archivos del evento</Trans>,
                active:false,
                link:`/${slug}/admin/resources/list/`
            },
            {
                name:<Trans>Editar Archivo</Trans>,
                active:true,
                link:''
            }
        ];

        this.setState({
            breadCrumbs,
            _idResource
        })

        apiEvents.getEventInfo('', slug).then(
            (response) => {
                this.setState({
                    groups:response.event.groups,
                    _idEvent: response.event._id
                });

                this.getResourceDetail(response.event._id, _idResource)
            }
        ).catch(
            (err) => {
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );
    }

    /**
     * This function is for get resource details
     * @param  {number} _idEvent
     * @param  {number} _id
     */
    getResourceDetail(_idEvent, _id){
        apiEvents.getResourceById(_idEvent,_id).then((res)=>{
                let groupsids = res.groups
                this.setState({
                    name:res.name,
                    groupsSelected:groupsids,
                    urlCurrentFile:`${res.file}`
                })
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
            }
        }).finally(()=>{
                this.setState({
                    loading:false
                })
        })
    }

    /**
     * Function to set the status when an input element change
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const ctx = this.context
        let value = null
        const name = event.target.name;
        const patternAlphanum = /^[,.;a-zA-ZÀ-ÿ\d\s-_]+$/ig

        if(event.target.name === 'name'){
            if(event.target.value.length > 50){
                event.target.value = event.target.value.slice(0,50);
                ctx.openNotification('Límite de caracteres excedido', 'error');
            } else {
                if(!patternAlphanum.test(event.target.value)){
                    event.target.value = event.target.value.slice(0,-1);
                    ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
                }
            }
        }
        value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;

        this.setState({
            [name]: value
        });
    };

    /**
     * Function to set state to file when an input file element  change
     * @param  {Object} file
     */
    handleInputChangeFile=(file)=>{
        console.log(file)
        this.setState({
            resourceFile:file
        })
    };

    
    /**
     * Function to set state when groups selected
     * @param  {Object} arrayGroups
     */
    handleCheckGroups=(arrayGroups)=>{
        this.setState({
            groupsSelected:arrayGroups
        })
    };

    /**
     * This function is for submit resouse form
     * @param  {Object} e
     */
    handleSubmit=async (e)=>{
        e.preventDefault();
        let {resourceFile,name, _idEvent, groupsSelected, _idResource } = this.state;
        if(groupsSelected.length<=0){
            // alert('Se requiere que seleccione al menos un rol de seguridad')
            alert(this.props.i18n._(t`Al menos un rol de seguridad es requerido`))
            return;
        }

        this.setState({
            loading:true
        });

        let formData = new FormData();
        if(resourceFile){
            console.log('entra file', resourceFile);
            formData.append("resourceFile", resourceFile);
        }

        formData.append("name", name);
        formData.append("_idEvent", _idEvent);
        formData.append("_id", _idResource);
        formData.append("groups", groupsSelected);

        try{
            let response = await apiEvents.updateResource(formData);
            this.setState({
                redirect:true
            })
        }catch (e) {
            if(e.response.status === 403){
                this.context.openPermissionNotification();
            }else {
                if(_.has(e, 'response.data.message') && e.response.data.message){
                    this.context.openNotification(e.response.data.message, 'error');
                }
            }
        }finally {
            this.setState({
                loading:false
            })
        }

    };



    render() {

        let {groups, loading,groupsSelected,breadCrumbs,name,urlCurrentFile } = this.state;
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/resources/list/`} />;

        return (
            <AdminEventLayout breadcrumbs={breadCrumbs} title={<Trans>Recursos</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <ResourcesForm
                            titleForm={<Trans>Editar recurso</Trans>}
                            handleSubmit={this.handleSubmit}
                            groups={groups}
                            groupsId={_.map(groups, '_id') }
                            groupsSelected={groupsSelected}
                            loading={loading}
                            name={name}
                            urlCurrentFile={urlCurrentFile}
                            handleInputChange={this.handleInputChange}
                            handleCheckGroups={this.handleCheckGroups}
                            handleInputChangeFile={this.handleInputChangeFile}
                        />
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(EditResource);