import React, {Component} from 'react';
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import AddIcon from '@material-ui/icons/Add';
import LoadingOverlay from "react-loading-overlay";
import {ApiServiceEvents} from "../../../api-services/events";
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';
import GlobalContext from "../../../GlobalContext";
import TableRoleList from "../../../components/events/security/TableRoleList";

import {Link} from "react-router-dom";
import { Trans } from '@lingui/macro';
import _ from "lodash";

const apiEvent = new ApiServiceEvents();

/**
 *
 * This view allow view role list
 * @class EventRoleList
 * @extends {Component}
 */
class EventRoleList extends Component {


    static contextType = GlobalContext;

    state = {
        roleList: [],
        redirectToCreate: false,
        loading:true,
        msgLoading:'cargando...',
        breadCrumbs:[
            {
                name:<Trans>Lista de perfiles</Trans>,
                active:true,
                link:''
            }
        ]
    };

    /**
     * Function to delete rol selected
     * @param  {Object} currentRow
     */
    handlerDelete = (currentRow) =>
        new Promise(resolve => {
            let id = currentRow._id;
            // console.log(currentRow._id)
            apiEvent.deleteRole(id,this.state.slug).then((res)=>{
                this.getRoleList()
            }).catch((err)=>{
                let ctx = this.context;
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                // console.log(err.response)
            }).finally(()=>resolve())
        });

    /**
     * Function to get all rols list
     */
    getRoleList = () => {
        let ctx = this.context;
        let slug = ctx.slug;
        let {isLoading} = this.state
        this.setState({slug})
        apiEvent.getRoles(slug).then(
            (res) => {
                this.setState({roleList: res, isLoading});
            }
        ).catch(err => {
            if (_.has(err, 'response.status') && err.response.status === 403) {
                ctx.openPermissionNotification();
            }
            console.log('Error, no se pudo cargar los usuarios', err.response);
        }).finally(()=> this.setState({loading:false}));
        // console.log(this.state.isLoading);
    };

    componentDidMount() {
        this.getRoleList();
    }




    render() {
        let ctx = this.context;
        return (
                <AdminEventLayout breadcrumbs={this.state.breadCrumbs} title={<Trans>Administrador de eventos</Trans>}>
                        <Grid container>
                            <Grid item xs={12}>
                                <Typography variant="h5" style={{padding: '10px'}} component="h3">
                                    <Trans>Lista de perfiles</Trans>
                                    <IconButton component={Link} push='true' to={`/${ctx.slug}/admin/role/create/`}
                                                color="primary" aria-label="add to shopping cart">
                                        <AddIcon/>
                                    </IconButton>
                                </Typography>
                            </Grid>
                            <Grid item xs={12}>
                                { this.state.loading ?   
                                <Paper xs={12} style={{'textAlign': "center","paddingTop":50,"paddingBottom":50}}><CircularProgress /> </Paper>
                                :
                                <TableRoleList slug={ctx.slug} handlerDelete={this.handlerDelete} rows={this.state.roleList}/>
                                }
                            </Grid>
                        </Grid>
                </AdminEventLayout>
        );
    }
}

export default EventRoleList;