import React from "react";
import {Grid, Paper} from "@material-ui/core";
import _ from "lodash";
import { Trans } from '@lingui/macro';

import AdminEventLayout from "../../../layouts/AdminEventLayout";
import EventCreateUserForm from "../../../components/events/security/EventCreateUserForm";
import EventCreateUserFormFinalForm from "../../../components/events/security/EventCreateUserFormFinalForm";
import {ApiServiceEvents} from "../../../api-services/events";
import GlobalContext from "../../../GlobalContext";
import { FORM_ERROR } from 'final-form'
import {t} from "@lingui/macro";
import {Redirect} from "react-router-dom";
import HorizontalLinearStepper from "../../../components/Steppers";
import imgDefault from '../../../assets/userprofile.png';

const apiEvent = new ApiServiceEvents();

/**
 *
 * This view allow create a new user
 * @class EventCreateUser
 * @extends {React.Component}
 */
class EventCreateUser extends React.Component{
    static contextType = GlobalContext;

    state = {
        image: null,
        firstName: '',
        lastName: '',
        email: '',
        phone: '',
        company:'',
        isAdmin: false,
        groups: [],
        redirect: false,
        loading: false,
        roles: [],
        breadcrumbs: [],
        userId: '',
        confirmAssistance: 1
    };

    /**
     * Function for get file image and set to state in base64
     * @param  {Object} files
     */
    getFiles=(files)=>{
        console.log(files);
        if(files.base64)
            this.setState({
                image:{
                    type:'imageBase64',
                    value:files.base64,
                    name:files.name
                }
            })
    };

    /**
     * Function to load roles
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvent.getRoles(ctx.slug).then(
            (res) => {
                console.log('Perfiles', res);
                this.setState({roles: res});
            }
        ).catch(
            (err) => {
                console.log('Error en la carga de perfiles', err)
            }
        );
    };

    componentDidMount(){
        let ctx = this.context;
        this.loadRoles();

        this.setState({
            breadcrumbs: [
                {
                    name: <Trans>Lista de usuarios</Trans>,
                    active: false,
                    link: `/${ctx.slug}/admin/user/list/`
                },
                {
                    name: <Trans>Crear usuario</Trans>,
                    active: true,
                    link: '',
                }

            ]
        });
    }
    /**
     * Function to set state when a input element change
     * @param  {object} event
     */
    handleInputChange = (event) => {        
        const target = event.target;
        let value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        const patternChars = /[^A-Za-z0-9 ñÑáéíóúÁÉÍÓÚüÜ\,.:_!#?]/;
        const patternCharsName = /[^A-Za-z ñÑáéíóúÁÉÍÓÚüÜ]/;
        let ctx = this.context;

        

        if(event.target.name == 'firstName'){            
            if(event.target.value?.length>20){                
                event.target.value = event.target.value.slice(0,20);                
            }
            if(patternCharsName.test(event.target.value)){
                event.target.value = event.target.value.slice(0,-1);
                ctx.openNotification('No se permiten caracteres especiales ni números', 'error');  
            }
        }

        if(event.target.name == 'company'){            
            if(event.target.value?.length>50){                
                event.target.value = event.target.value.slice(0,50);
            }
            if(patternChars.test(event.target.value)){
                event.target.value = event.target.value.slice(0,-1);
                ctx.openNotification('No se permiten algunos caracteres especiales', 'error');  
            }
        }
        

        this.setState({
            [name]: event.target.value
        });  
        console.log(this.state.firstName);
        
    };

    /**
     * function to validate data empry
     * @param  {Object} values
     * @returns {Object} errors only if some value not match the validation
     */
    validate = (values) => {
        const errors = {};       
        const numChar = 50;       
        const numCharEmail = 100;
        
        
        const patternChars = /[^A-Za-z0-9 ñÑáéíóúÁÉÍÓÚüÜ\,.:_!#?]/;
        const patternCharsName = /[^A-Za-z ñÑáéíóúÁÉÍÓÚüÜ]/;
        const patternEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        
        if (!values.firstName) {
            errors.firstName = 'Required';
        }

        if(values.firstName || values.lastName){            
            if(patternCharsName.test(values.firstName)){
                errors.firstName = 'No se permiten caracteres especiales ni números';                
            }
            if(values.firstName?.length>numChar){
                errors.firstName = `Solo se permiten ${numChar} caracteres`;
            }
        }        

        if (!values.lastName) {
            errors.lastName = 'Required';
        }

        if(values.lastName){            
            if(patternCharsName.test(values.lastName)){
                errors.lastName = 'No se permiten caracteres especiales ni números';                
            }
            if(values.lastName?.length>numChar){
                errors.lastName = `Solo se permiten ${numChar} caracteres`;
            }
        }

        if(values.company){            
            if(patternChars.test(values.company)){
                errors.company = 'No se permiten caracteres especiales ni números';
            }
            if(values.company?.length>numChar){
                errors.company = `Solo se permiten ${numChar} caracteres`;
            }
        }

        if (!values.email) {
            errors.email = 'Required';
        }

        if(values.email){           
            if(values.email?.length>numCharEmail) {
                errors.email = `El correo solo permite ${numCharEmail} caracteres`;
            }
            if(!patternEmail.test(values.email)){
                errors.email = 'Este no es un formato de email válido';
            }
        }
        if(!values.password)
            errors.password = 'Required';

        if(values.password && values.password !== values.passwordConfirm){
            errors.password = <Trans>Las contraseñas no coinciden</Trans>
        }

        return errors;
    };

    /**
     * function to send user form
     * @param  {Object} values this are the values form
     */
    handleSubmit = async (values)  => {        
        let ctx = this.context;
        let slug = ctx.slug;        
        console.log(values, 'VALUEEES');
        this.setState({loading: true});
        let request = _.clone(values);
        
        if(this.state.image){
            request.image = this.state.image
        }else{
            delete request.image;
        }

        request.slug = slug;
        request.isDeleted = false;
        request.groups = _.map(values.groups, function (x) {
            //console.log(x);
             return {
                 _idGroup: x
             }
        });

        let errors = {};
        try {
            let res = await apiEvent.createEventUser(request, '', slug);
            this.setState({redirect: true, loading:false, userId: res._id});
        }
        catch (e) {
            //console.log('Error al crear usuario', e.response);
            if(e.response){
                if(e.response.status === 403){
                    ctx.openPermissionNotification();
                }
                if(e.response.data && e.response.data.message && typeof e.response.data.message === 'string') {
                    errors = {[FORM_ERROR]: e.response.data.message};
                }

                if(e.response.data && e.response.data.message.message && typeof e.response.data.message.message === 'string') {
                    errors = {[FORM_ERROR]: e.response.data.message.message};
                }
            }else{
                console.log(e)
            }

        }
        this.setState({loading: false});
        return errors;
    };

    render() {
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/user/${this.state.userId}/1/`} />;

        let initialValues = {
            confirmAssistance: this.state.confirmAssistance
        };
        return (
            <AdminEventLayout title={<Trans>Administrador de eventos</Trans>} breadcrumbs={this.state.breadcrumbs}>
                <Grid container title={<Trans>Crear usuario</Trans>}>
                    {/*<Grid*/}
                    {/*    container*/}
                    {/*    item*/}
                    {/*    direction="row"*/}
                    {/*    justify="center"*/}
                    {/*    alignItems="center"*/}
                    {/*>*/}
                            {/*<HorizontalLinearStepper/>*/}
                    {/*</Grid>*/}
                    <Grid item xs={12}>
                        {/*<EventCreateUserForm*/}
                        {/*    title="Crear usuario"*/}
                        {/*    logo={this.state.image}*/}
                        {/*    handleLogo={this.getFiles}*/}
                        {/*    firstName={this.state.firstName}*/}
                        {/*    lastName={this.state.lastName}*/}
                        {/*    handleInputChange={this.handleInputChange}*/}
                        {/*/>*/}
                        <EventCreateUserFormFinalForm
                            logo={this.state.image?this.state.image.value:imgDefault}
                            firstName = {this.state.firstName}
                            lastName = {this.state.lastName}
                            selectedGroups ={this.state.groups}
                            handleLogo={this.getFiles}
                            onSubmit={this.handleSubmit}
                            handleInputChange = {this.handleInputChange}
                            validate={this.validate}
                            loading={this.state.loading}
                            roles={this.state.roles}
                            initialValues={initialValues}
                        />
                    </Grid>
                </Grid>
            </AdminEventLayout>
        )
    }
}

export default EventCreateUser