import React from "react";
import _ from "lodash";
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import AddIcon from '@material-ui/icons/Add';
import Paper from '@material-ui/core/Paper'
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import AgendaTable from "../../../../components/events/admin/agenda/AgendaTable";
import {ApiServiceEvents} from "../../../../api-services/events";
import GlobalContext from "../../../../GlobalContext";
import {Link, Redirect} from "react-router-dom";
import LoadingOverlay from "react-loading-overlay";
import Button from "@material-ui/core/Button";
import CircularProgress from '@material-ui/core/CircularProgress';
import MailIcon from "@material-ui/core/SvgIcon/SvgIcon";
import {Trans} from "@lingui/macro";
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import ImportUserForm from "../../../../components/security/ImportUserForm";
import FullScreenDialogGeneric from "../../../../components/modals/FullScreenDialogGeneric";

const apiEvent = new ApiServiceEvents();
/**
 *
 * this view shows the list of activities of agenda
 * @class AgendaList
 * @extends {React.Component}
 */
class AgendaList extends React.Component {
    static contextType = GlobalContext;
    state = {
        agendaList: [],
        query: '',
        openModalImportCsv: false,
        redirectToCreate: false,
        loadingImport: false,
        groups: [],
        loading: true,
        language: 'es',
        msgLoading: 'cargando...',
        fileCsv: '',
        breadcrumbs: [
            {
                name: <Trans>Programa del evento</Trans>,
                active: true,
                link: ``
            },
        ]
    };

    /**
     * This function gets data of agenda
     */
    getData = () => {            
        apiEvent.getAgendaList(this.context.slug).then(
            (res) => {
                console.log('eventos', res)               
                this.setState({agendaList: res});
            }
        ).catch(
            (err) => {
                if (err.response.status === 403) {
                    this.context.openPermissionNotification();
                }
                
            }
        ).finally(() => this.setState({loading: false}));
    };


    /**
     * This function allows retrieve the roles to list in the form
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvent.getRoles(ctx.slug).then(
            (res) => {
                this.setState({groups: res});
            }
        ).catch(
            (err) => {
                console.log('Error en la carga de perfiles', err)
            }
        );
    };


    handleChange = (selectorFiles) => {
        console.log(selectorFiles);
        this.setState({fileCsv: selectorFiles});
    };


    /**
     * This function allows to import data to BD from a csv file
     * @param  {Object} values  from form component
     */
    handleSubmitImport = (values) => {
        console.log('values', values);
        const ctx = this.context;
        let formData = new FormData();
        let fileCsv = this.state.fileCsv;
        formData.append('slug', this.context.slug);

        /// validamos que tenga seleccionado algun rol
        if (_.has(values, 'groups') && values.groups.length > 0) {
            //let arrayGroup = values.groups.split(",");
            formData.append('groups', JSON.stringify(values.groups));
        } else {
            ctx.openNotification(<Trans>Debes seleccionar al menos un perfil</Trans>, 'error');
            return;
        }

        /// validamos que tenga seleccionado algun archivo csv
        if (fileCsv && fileCsv.length > 0 ) {
            // console.log('si entro al csv', fileCsv[0]);
            formData.append("csv_file", fileCsv[0]);
        } else {
            ctx.openNotification(<Trans>Debes cargar un archivo csv</Trans>, 'error');
            return;
        }


        this.setState({loadingImport: true});

        apiEvent.loadAgendaCsv(formData).then(
            (res) => {
                console.log('res', res);
                let msg = `Importado con éxito`;
                ctx.openSuccessNotification(msg);
                this.setOpenImportCsv(false);
                this.getData();
                this.setState({
                    fileCsv: 'null'
                })
            }
        ).catch(
            (err) => console.log('error', err)
        ).finally(() => this.setState({loadingImport: false}));
    }


    /**
     * This function allows delete an item selected from the list
     * @param  {Object} oldData
     */
    deleteItem = (oldData) => new Promise(resolve => {
        let ctx = this.context;
        apiEvent.deleteAgendaActivity(ctx.slug, oldData._id).then(
            (response) => {
                let oldState = this.state;
                oldState.agendaList = _.remove(this.state.agendaList, function (x) {
                    // {_id: oldData._id}
                    return x._id !== oldData._id;
                });
                this.setState(oldState);
                ctx.openSuccessNotification(<Trans>Actividad eliminada</Trans>);
                resolve();
                console.log('se borro usuario', response._id, oldData._id);
            }
        ).catch(
            (err) => {
                if (err.response.status === 403) {
                    this.context.openPermissionNotification();
                }
                resolve();
                console.log('Error', err.response);
            }
        );

    });

    componentDidMount() {
        let ctx = this.context;
        this.setState({
            language: ctx.language
        })
        this.getData();
        this.loadRoles();
    }


    /**
     * This function allows to download a csv with the agenda's information
     */
    importAgenda = () => {
        let ctx = this.context;
        let url = apiEvent.downloadcsvAgenda(ctx.language, ctx.slug, this.state.query);
        window.open(url, '_blank');
        console.log('import agenda', url)
    }


    /**
     * This function show or hide the import modal
     * @param  {boolean} val this param is the value to show or not the import modal
     */
    setOpenImportCsv = (val) => {       
        this.setState({
            openModalImportCsv: val
        });
    };

    childStateCallback = (childState) => {
        this.setState({
           query: childState.query
        });        
     }


    redirectCreate = () => this.setState({redirectToCreate: true});

    render() {
        let ctx = this.context;
        if (this.state.redirectToCreate) return <Redirect push to={`/${ctx.slug}/admin/agenda/create/`}/>;

        return (
            <AdminEventLayout title={<Trans>Administrador de eventos</Trans>} breadcrumbs={this.state.breadcrumbs}>
                <Grid container>

                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Programa del evento</Trans>
                            <IconButton onClick={this.redirectCreate} color="primary"
                                        aria-label={<Trans>add activity</Trans>}>
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>


                    <Grid
                        container
                        item
                        lg={12}
                        xs={12}
                        style={{marginBottom: 10}}
                        direction="row"
                        justify="flex-end"
                    >

                        {this.state.agendaList.length > 0 ?
                            <Button
                                variant="contained"
                                color="primary"
                                style={{marginRight: '20px'}}
                                startIcon={<CloudDownloadIcon/>}
                                onClick={this.importAgenda}
                            >
                                <Trans>Exportar</Trans>
                            </Button>
                            :
                            <div/>
                        }
                        <Button
                            variant="contained"
                            color="default"
                            onClick={() => {
                                this.setOpenImportCsv(true)
                            }}
                            startIcon={<CloudUploadIcon/>}
                        >
                            <Trans>Importar</Trans>
                        </Button>
                    </Grid>

                    <Grid item xs={12}>

                        {this.state.loading ?
                            <Paper xs={12} style={{
                                'textAlign': "center",
                                "paddingTop": 50,
                                "paddingBottom": 50
                            }}><CircularProgress/> </Paper>
                            :
                            <AgendaTable                                                                   
                                toCallBack={(childState) => this.childStateCallback(childState)}      
                                language={this.state.language}
                                rows={this.state.agendaList}
                                handlerDelete={this.deleteItem}
                            />
                        }
                    </Grid>
                </Grid>


                <FullScreenDialogGeneric
                    setOpen={this.setOpenImportCsv}
                    open={this.state.openModalImportCsv}
                    title={<Trans>Importación de programa </Trans>}
                >

                    <ImportUserForm
                        onSubmit={this.handleSubmitImport}
                        roles={this.state.groups}
                        downloadCSV={true}
                        linkfile={'/files/template_agenda.csv'}
                        loading={this.state.loadingImport}
                        handleChange={this.handleChange}
                        buttonConfirmBar={false}
                    />

                </FullScreenDialogGeneric>


            </AdminEventLayout>
        )
    }
}

export default AgendaList