import React, {Component} from 'react';
import AdminEventLayout from "../../layouts/AdminEventLayout";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import {Paper} from "@material-ui/core";
import CircularProgress from '@material-ui/core/CircularProgress';
import FileBase64 from "react-file-base64";
import Box from "@material-ui/core/Box";
import Button from '@material-ui/core/Button';
import {ApiServiceEvents} from "../../api-services/events";
import GlobalContext, {CtxProviderBuilder} from "../../../src/GlobalContext";
import { Trans } from '@lingui/macro';
import _ from "lodash";


const apiEvent = new ApiServiceEvents();


class SettingsEvent extends Component {

    static contextType = GlobalContext;


    state = {
        currentIcon: '',
        eventInfo: '',
        loading: false,
        msgLoading: <Trans>cargando...</Trans>,
        file: {
            base64: ''
        },
        breadCrumbs:[
            {
                name: <Trans>Configuración</Trans>,
                active: true,
                link: ``
            }
        ]


    };


    componentDidMount() {
        let ctx = this.context;
        let slug = ctx.slug;
        this.setState({loading:true})
        apiEvent.getEventInfo('',slug).then(
            (response) => {
                this.setState({
                    eventInfo: response,
                    currentIcon:response.event.icon?response.event.icon.value:''
                });
            }
        ).catch(
            (err) => {
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                console.log('Error al obtener info del evento para los grupos', err);
            }
        ).finally(()=>{this.setState({loading:false})});

    }

    setLogo = ()=>{

        let {file,eventInfo} = this.state;
        let ctx = this.context;

        this.setState({
            loading:true
        })


        let _idEvent = eventInfo.event._id;

        let icon = {
            value: file.base64,
            type: "imageBase64",
            name: file.name

        }

        console.log(icon, _idEvent)

        apiEvent.changeIconEvent(icon,_idEvent).then((res)=>{
            console.log('update',res)
            ctx.openNotification(<Trans>Configuración actualizada correctamente</Trans>);
        }).catch((err)=>{
            if (_.has(err, 'response.status') && err.response.status === 403) {
                ctx.openPermissionNotification();
            }else{
                ctx.openNotification(<Trans>Hubo un error al actualizar la configuración del evento, por favor intente mas tarde</Trans>);
            }
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })
    }

    handleLogo=(file)=>{
        console.log(file)
        this.setState({
            file,
            currentIcon:file.base64
        })
    }

    render() {
        let {currentIcon,breadCrumbs} = this.state;
        return (
            <AdminEventLayout breadcrumbs={breadCrumbs}>
                { this.state.loading ?   
                    <Grid xs={12} md={6}>
                    <Paper xs={12} style={{'textAlign': "center","paddingTop":50,"paddingBottom":50}}><CircularProgress /> </Paper>
                    </Grid>
                :
                <Paper style={{padding: '20px'}}>
                        <Grid container spacing={3}>
                            <Typography variant="h6" gutterBottom>
                                <Trans>Configuración</Trans>
                            </Typography>
                            <Grid item xs={12} md={12}>
                                <p><Trans>Imagen para el menú lateral de la aplicación, así como del login del usuario</Trans></p>
                                <Box alignItems="center">
                                    <img
                                        alt={<Trans>Header de evento</Trans>}
                                        src={currentIcon} style={{
                                        margin: 10,
                                        width: 300,
                                        border: '2px solid gray',
                                        height: 150,
                                    }}/><br/>

                                    <FileBase64
                                        onDone={this.handleLogo}/>

                                    <br/> <br/>
                                    <Button type={'button'} onClick={this.setLogo} variant="contained" color="primary">
                                        {<Trans>Guardar</Trans>}
                                    </Button>

                                </Box>
                            </Grid>
                        </Grid>
                </Paper>
                }
            </AdminEventLayout>
        );
    }
}

export default SettingsEvent;