import React, {Component} from 'react';
import GlobalContext, {CtxProviderBuilder} from "../../../../GlobalContext";
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';
import { Trans } from '@lingui/macro';
import AddIcon from '@material-ui/icons/Add';
import AgendaTable from "../../../../components/events/admin/agenda/AgendaTable";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import ChatbotConfigTable from "../../../../components/events/admin/chatbot/ChatbotConfigTable";
import {ApiServiceEvents} from "../../../../api-services/events";
import {Redirect} from "react-router-dom";
import _ from "lodash";
import LoadingOverlay from "react-loading-overlay";

const apiEvent = new ApiServiceEvents();

/**
 *
 *This view allows you to view the chatbots list
 * @class ListChatbotConfig
 * @extends {Component}
 */
class ListChatbotConfig extends Component {
    static contextType = GlobalContext;

    state = {
        redirectToCreate: false,
        data: [],
        loading:false,
        breadcrumbs: [
            {
                name:'Chatbot',
                active:true,
                link:``
            },
        ]
    };
    /**
     * This function changes the state of the variable to redirect to the form
     */
    redirectCreate = () => this.setState({redirectToCreate: true});
    
    /**
     * @param  {Object} oldData
     */
    deleteItem = (oldData) => new Promise(resolve => {
        let ctx = this.context;
        apiEvent.deleteWorkFlowChatBot(oldData._id).then(
            (response) => {
                this.getData();
                ctx.openSuccessNotification(<Trans>Flujo eliminado</Trans>);
                resolve();
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                resolve();
                console.log('Error', err.response);
            }
        );

    });
    /**
     * this function gets the chatbot configuration
     */
    getData = () => {
        let ctx = this.context;
        this.setState({loading:true})
        apiEvent.getConfigChatbot(this.context.slug).then(
            (res) => {
                this.setState({data: res});
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                console.log(<Trans>Error en obtener datos del chatbot</Trans>, err);
            }
        ).finally(()=>{this.setState({loading:false})});
    };

    componentDidMount() {
        this.getData();
    }

    /**
     * This function doubles the chatbot flow
     * @param  {number} idChatBot
     */
    onCloneRow=(idChatBot)=>{
        let ctx = this.context;
        if(window.confirm('¿Desea duplicar este flujo de chatbot?')){
            this.setState({
                loading:true
            })
            apiEvent.cloneWorkFlowChatbot(idChatBot).then((res)=>{
                ctx.openNotification(<Trans>Chatbot duplicado</Trans>,'success');
                this.getData();
            }).catch((err)=>{
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }else {
                    ctx.openNotification(<Trans>Hubo un error al clonar, por favor intente nuevamente</Trans>, 'success');
                }
                console.log(err.response)
            }).finally(()=>{
                this.setState({
                    loading:false
                })
            })
        }
    }

    render() {
        let ctx = this.context;
        if(this.state.redirectToCreate) return <Redirect push to={`/${ctx.slug}/admin/chatbot/step/new/`}/>;
        return (
            <AdminEventLayout title={<Trans>Administrador de eventos</Trans>} breadcrumbs={this.state.breadcrumbs}>
                <Grid container>

                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Agregar paso</Trans>
                            <IconButton onClick={this.redirectCreate} color="primary" aria-label="add activity">
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>


                    <Grid item xs={12}>
                        { this.state.loading ?   
                            <Paper xs={12} style={{'textAlign': "center","paddingTop":50,"paddingBottom":50}}><CircularProgress /> </Paper>
                        :
                            <ChatbotConfigTable
                                onCloneRow={this.onCloneRow}
                                rows={this.state.data}
                                handlerDelete={this.deleteItem}
                            />
                        }
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default ListChatbotConfig;