import React, {Component, Fragment} from 'react';
import AdminLayout from "../../layouts/AdminLayout";
import {ApiServiceEvents} from "../../api-services/events";
import CreateEventForm from "../../components/events/CreateEventForm";
import Typography from "@material-ui/core/Typography";
import {Grid} from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import {Redirect} from 'react-router-dom'
import _ from "lodash";
import moment from "moment";
import { Trans } from '@lingui/macro';
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import GlobalContext from "../../GlobalContext";

const apiEvents = new ApiServiceEvents();

class EditEvent extends Component {

    static contextType = GlobalContext;

    state = {
        logo: {
            value: 'data:image/png;base64,',
            name: '',
            type: ''
        },
        icon: {
            value: 'data:image/png;base64,',
            name: '',
            type: ''
        },
        color:'',
        name:'',
        description:'',
        capacity:null,
        location:'',
        slug:'',
        redirectToList:false,
        loading:false,
        file:null,
        users:[],
        endDate:new Date(),
        startDate:new Date(),
        loading_data:true,
        isActive:true,
        _id:'',
        messagefile:'',
        messagefileIcon:'',
        email:'',
        firstName:'',
        lastName:'',
        changeLogo: false,
        changeIcon: false
    };


    /**
     * it allows get the logo event and his base64
     * @param img
     */
    handleChandeImage=(img)=>{
       let size = img.file.size/1024;
        let messagefile = ''
       if(size<=4000){
           if(img.file.type.includes('image')){
               let image = {
                   value: img.base64,
                   type: 'imageBase64',
                   name: img.name
               };
               this.setState({
                   logo:image,
                   changeLogo: true
               })
           }else{
               messagefile=<Trans>Archivo no aceptado</Trans>
           }

       }else{
           messagefile=<Trans>Esta imagen es demasiado pesada, porfavor seleccione máximo 4mb</Trans>
       }


        this.setState({
            messagefile
        })
    }

    /**
     * it allows get the icon header  event and his base64
     * @param img
     */
    handleChandeIconImage=(img)=>{
       let size = img.file.size/1024;
        let messagefileIcon = '';
       if(size<=4000){
           if(img.file.type.includes('image')){
               let image = {
                   value: img.base64,
                   type: 'imageBase64',
                   name: img.name
               };

               this.setState({
                   icon:image,
                   changeIcon: true
               })
           }else{
               messagefileIcon=<Trans>Archivo no aceptado</Trans>
           }

       }else{
           messagefileIcon=<Trans>Esta imagen es demasiado pesada, porfavor seleccione máximo 4mb</Trans>
       }


        this.setState({
            messagefileIcon
        })
    }


    componentDidMount() {

        this.setState({
            _id:this.props.match.params.id
        })

        this.getEventInfo(this.props.match.params.id)
        // obtenemos la info el evento
    }

    /**
     * this function gets the event data to fill the form
     * @param {string} id of event
     */
    getEventInfo=(id)=>{
        apiEvents.getEventInfo(id).then(res=>{
            console.log('Event info', res);

            let {
                logo,
                icon,
                color,
                description,
                capacity,
                name,
                location,
                availability,
                slug,
                users,
                isActive
            } = res.event;
            
            let email = '', firstName = '', lastName = '';

            if(res.user){
                email = res.user.email;
                firstName = res.user.firstName;
                lastName = res.user.lastName;
            }

            // console.log('Data user', email, firstName, lastName);

            if(res.event.icon){
                
                this.setState({icon:res.event.icon})
            }

            this.setState({
                logo,
                color,
                description,
                capacity,
                location,
                name,
                slug,
                users,
                isActive,
                email,
                startDate: availability.startDate,
                endDate: availability.endDate,
                firstName,
                lastName
            });



        }).catch(err=>{
        }).finally(()=>{
            this.setState({
                loading_data:false
            })
        })
    }

    handleEventInput=(e)=> {
        const name = e.target.name;
        const namesFieldsInteger = ['capacity']
        const patternChars = /[^A-Za-z0-9 ñÑáéíóúÁÉÍÓÚüÜ\,.:#_!?]/;
        const patternCharsName = /[^A-Za-z ñÑáéíóúÁÉÍÓÚüÜ]/;
        let ctx = this.context;
        if(namesFieldsInteger.includes(e.target.name))
        {
            if(e.target.value<0){
                e.target.value = 0;
            }           
            e.target.value = parseInt(e.target.value);
        }

        if(e.target.name == 'name' || e.target.name == 'location' || e.target.name == 'user_firstName' 
            || e.target.name == 'user_lastName' || e.target.name == 'user_company'){
            if(e.target.value.length > 50){
                e.target.value = e.target.value.slice(0,50);
            }            
        }

        if(e.target.name == 'description'){
            if(e.target.value.length > 250){
                e.target.value = e.target.value.slice(0,250);
            }
        }

        if(e.target.name == 'name'){
            if(patternChars.test(e.target.value)){
                e.target.value = e.target.value.slice(0,-1);
                ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
            }
        }

        const value = e.target.value;
        this.setState({[name]: value});

    }



    handleSubmit=(e)=>{
        e.preventDefault()
        let ctx = this.context;


        let request = Object.assign({}, this.state);




        request.startDate = moment(request.startDate).format();
        request.endDate = moment(request.endDate).format();


        if(Date.parse(request.startDate) > Date.parse(request.endDate )){
            ctx.openNotification(<Trans>La fecha de inicio no puede ser mayor a la fecha de fin del evento</Trans>,"error")
            return;
        }


        // 2019-09-19T15:34:35.347Z
        let req = {
            logo: request.logo,
            icon: request.icon,
            _id:request._id,
            name:request.name,
            description:request.description,
            capacity:request.capacity,
            location:request.location,
            slug:request.slug,
            isActive:request.isActive,
            color:request.color,
            startDate:request.startDate,
            endDate:request.endDate,
            undefinedEnd: true
        }

        if(!request.changeLogo)
            delete req.logo;


        if(!request.changeIcon)
            delete req.icon;


        this.setState({
            loading:true
        });

        apiEvents.editEvent(req).then(res=>{
            this.setState({redirectToList:true})
        }).catch(err=>{
            if(err.response.data.status == 'Slug')
            {
                alert('El slug ya existe en otro evento');
            }
            
            console.log(err.response)
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })

    }


    handleStartDateChange = (isTime, date) =>{
        // console.log(date,isTime)
        this.setState({
            startDate:date
        })
    }


    handleEndDateChange = (isTime, date) =>{
        // console.log(date,isTime)
        this.setState({
            endDate:date
        })
    }

    render() {

        let {
            color,
            description,
            capacity,
            location,
            slug,
            name,
            loading,
            logo,
            loading_data,
            messagefile,
            messagefileIcon,
            email,
            firstName,
            lastName,
            startDate,
            endDate
        } = this.state;

        // console.log('name', name)


        if(this.state.redirectToList) return <Redirect push to={'/event/list'}/>

        const props = this.props;

        const imageDefault = 'https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png';
        let image = _.has(this.state, 'logo.value') ? this.state.logo.value : this.state.logo;
        let imageHeader = _.has(this.state, 'icon.value') ? this.state.icon.value : this.state.icon;


        return (
            <AdminLayout title="Nuevo evento">
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <Grid container>
                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Editar Evento</Trans>
                        </Typography>
                    </Grid>




                    <Grid item xs={12}>
                        {
                            loading_data?<CircularProgress style={{margin:'auto', display:'block'}} />:<CreateEventForm
                                logo={image}
                                header={imageHeader}
                                color={color}
                                name={name}
                                description={description}
                                capacity={capacity}
                                location={location}
                                slug={slug}
                                endDate={endDate}
                                startDate={startDate}
                                loading={loading}
                                messagefile={messagefile}
                                messagefileIcon={messagefileIcon}
                                nameAdmin={firstName + ' '+ lastName}
                                emailAdmin={email}
                                handleStartDateChange={this.handleStartDateChange}
                                handleEndDateChange={this.handleEndDateChange}
                                handleChandeImage={this.handleChandeImage}
                                handleChandeIconImage={this.handleChandeIconImage}
                                handleEventInput={this.handleEventInput}
                                onHandleSubmit={this.handleSubmit}
                            />
                        }

                    </Grid>
                </Grid>
                </MuiPickersUtilsProvider>

            </AdminLayout>
        );
    }
}

export default EditEvent;