import React from 'react';
import MaterialTable from 'material-table';
import { Redirect } from "react-router-dom";
import Avatar from '@material-ui/core/Avatar';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import GlobalContext from "../../../GlobalContext";
import { t, Trans } from '@lingui/macro';
import { withI18n } from "@lingui/react";

class TableRoleList extends React.Component {
    static contextType = GlobalContext;
    state = {
        redirectToEdit: '',
        selectedRow: '',
        columns: [
            { title: <Trans>Color</Trans>, field: 'color', render: rowData => <FiberManualRecordIcon style={{ color: rowData.color }} /> },
            { title: <Trans>Nombre</Trans>, field: 'name' },
            { title: <Trans>Descripción</Trans>, field: 'description' },
        ]
    };

    render() {
        let ctx = this.context;

        if (this.state.redirectToEdit) return <Redirect push to={`/${this.props.slug}/admin/role/${this.state.selectedRow}/`} />;

        let state = this.state;
        let setState = this.setState;
        let data = this.props.rows;
        let props = this.props;

        return (
            <MaterialTable
                title={<Trans>Perfiles</Trans>}
                columns={state.columns}
                data={data}
                editable={{
                    onRowDelete: props.handlerDelete,
                }}
                actions={[
                    {
                        icon: 'edit',
                        tooltip: this.props.i18n._(t`Editar`),
                        onClick: (event, rowData) => this.setState({ redirectToEdit: true, selectedRow: rowData._id })
                    }
                ]}
                options={{
                    actionsColumnIndex: -1,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100, 500],
                    paging: true,
                }}
                localization={

                    ctx.tableConfig.localization

                }
            />
        );
    }
}

export default withI18n()(TableRoleList)