import React, {Component} from 'react';
import CssBaseline from "@material-ui/core/CssBaseline";
import Container from "@material-ui/core/Container";
import {FormControl, FormControlLabel, makeStyles, Paper, Typography} from "@material-ui/core";
// import TextField from "@material-ui/core/TextField";
import Grid from "@material-ui/core/Grid";
import {CancelButton, SaveButtonSpinner} from "../../buttons/StandartButtons";
import Button from "@material-ui/core/Button";
import {GlobalContextBuilder} from "../../../GlobalContext";
import {Field, Form} from "react-final-form";
import Box from "@material-ui/core/Box";
import Image from "material-ui-image/lib/components/Image/Image";
import FileBase64 from "react-file-base64";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import {Radio, Select, TextField} from "final-form-material-ui";
import MenuItem from "@material-ui/core/MenuItem";
import {ApiServiceEvents} from "../../../api-services/events";
import _ from "lodash";




const useStyles = makeStyles(theme => ({
    '@global': {
        body: {
            backgroundColor: theme.palette.common.white,
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    form: {
        width: '100%', // Fix IE 11 issue.
        marginTop: theme.spacing(3),
    },
    submit: {
        margin: theme.spacing(3, 0, 2),
    },
}));


const apiEvent = new ApiServiceEvents();

class ConfigurationFormButton extends Component {
    static contextType = GlobalContextBuilder;

    state = {
        text:'',
        buttonType: '',
        urlRedirect: '',
        urlResource: '',
        viewId: '',
        phoneNumber: '',
        style: {
            color:'',
            background:''
        },
        pollList:[],
        viewsList:[],
    };


    style = {
        color:'',
        background:''
    };


   componentDidMount() {
       // const ctx = this.context;
       // let component = ctx.currentComponent;
       //  let newState = {
       //      text:  component.properties?component.properties.text:'',
       //      style: component.properties && component.properties.style ?component.properties.style:{color:'', background: ''}
       //  };
       //
       //  this.setState(newState);
       //  this.style.color = this.state.style.color;
       //  this.style.background = this.state.style.background;
       this.setInitialValues();
       this.getViews();
       this.getPolls();
    }



    onSave=(e)=>{
        e.preventDefault();
        let config = {
            text: this.state.text,
            style: this.state.style
        };
        
        this.props.onSaveConfig(config);
    }

    handleEventInput=(e)=>{        
        const name = e.target.name;
        const value = e.target.value;
        this.setState({[name]: value});
    }

    handleEventStyle=(e)=> {
        const name = e.target.name;
        const value = e.target.value;
        // console.log(name,value)
        // this.style[name]=value
        let styleButton = _.merge(this.state.style, {[name]: value});
        // styleButton[name] = value;
        // console.log(styleButton, name, value);
        let oldState = this.state;
        oldState.style = styleButton;
        this.setState(oldState);
    }

    getRequest = (values) => {
       let buttonStyle = this.state.style;
       if(!buttonStyle.color){
            buttonStyle.color = '#000000';
       }           
        if(!buttonStyle.background || buttonStyle.background == ''){
            buttonStyle.background = '#E0E0E0';
        }
       return {
           text: {
               value: values.text,
               type: 'string'
           },
           style: {
               value: buttonStyle,
               type: 'style'
           },
           buttonType: {
               value: values.buttonType,
               type: 'string'
           },
           urlRedirect: {
               value: values.urlRedirect,
               type: 'string'
           },
           urlResource: {
               value: values.urlResource,
               type: 'string'
           },
           viewId: {
               value: values.viewId,
               type: 'string'
           },
           pollId: {
               value: values.pollId,
               type: 'string'
           },
           phoneNumber: {
               value: values.phoneNumber,
               type: 'string'
           },
           buttonAlign: {
               value: values.buttonAlign,
               type: 'string'
           },
           buttonSize: {
               value: values.buttonSize,
               type: 'string'
           },
       };
    };

    handleSubmit = (values) => {
        // values.style = this.state.style;
        this.props.onSaveConfig(this.getRequest(values));
    };
    
    
    validate = (values) => {   
        if(values)    
        {
            if(values.length>40)
            {
                values = values.slice(0,40);
            }           
        }
        
    };
    

    getPolls=()=>{
        let ctx = this.context;
        let slug = ctx.slug;
        apiEvent.getPollEvents(slug).then((res)=>{
            this.setState({
                pollList:res
            })
        });
    }


    getViews = () => {
        let ctx = this.context;                
        let moduleId = ctx._idModule;
        // console.log('module', moduleId);
        apiEvent.getViewsList(moduleId).then(
            (res) => {
                let array = [];
                res.map((view,i)=>{
                    array.push(view.views)                    
                });

                if(array.length>0){
                    this.setState({
                        viewsList:array
                    })
                }
            }
        ).catch(
            (err) => {
                console.log('Error en carga de vistas', err);
            }
        );
    };

    deserializeProperties = (properties) => {
        return {
            text: _.has(properties, 'text.value') ? properties.text.value : '',
            buttonType: _.has(properties, 'buttonType.value') ? properties.buttonType.value : '',
            urlRedirect: _.has(properties, 'urlRedirect.value') ? properties.urlRedirect.value : '',
            urlResource: _.has(properties, 'urlResource.value') ? properties.urlResource.value : '',
            viewId: _.has(properties, 'viewId.value') ? properties.viewId.value : '',
            phoneNumber: _.has(properties, 'phoneNumber.value') ? properties.phoneNumber.value : '',
            style: _.has(properties, 'style.value') ? properties.style.value : {color:'black', background:'#E0E0E0'},
            buttonSize: _.has(properties, 'buttonSize.value') ? properties.buttonSize.value : '',
            buttonAlign: _.has(properties, 'buttonAlign.value') ? properties.buttonAlign.value : '',
            pollId: _.has(properties, 'pollId.value') ? properties.pollId.value : '',
        }
    };

    setInitialValues = () => {
        const ctx = this.context;
        const component = ctx.currentComponent;

        let properties = component.properties;

        if(!properties) return;
        // console.log('Properties', properties);
        let newState = _.merge(this.state, this.deserializeProperties(properties));
        // console.log('newState', newState);
        this.setState(newState);
    };
    


    render() {
        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />

                <Form
                    onSubmit={this.handleSubmit}
                    initialValues={this.state}
                    
                    
                    render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                        <form onSubmit={handleSubmit} noValidate>
                            <Paper style={{ padding: 16 }}>
                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                    Propiedades
                                </Typography>
                                <Grid container alignItems="flex-start" spacing={2}>

                                    <Grid item xs={12}>
                                        <Field
                                            fullWidth
                                            name="text"
                                            component={TextField}
                                                                                       
                                            label="Texto del botón"
                                            variant="outlined"    
                                            validate={this.validate}                                                                                  
                                        />
                                    </Grid>

                                    <Grid item sm={6} xs={12}>
                                        <label style={{marginRight:'10px'}}>{'Color del botón'}</label>
                                        <input type='color'  required value={this.state.style && this.state.style.background!== '' ? this.state.style.background:'#E0E0E0'} name="background"
                                               id="background" onChange={this.handleEventStyle}/>
                                    </Grid>

                                    <Grid item sm={6} xs={12}>
                                        <label style={{marginRight:'10px'}}>{'Color del texto'}</label>
                                        <input type='color' required value={this.state.style?this.state.style.color:'black'}  name="color"
                                               id="color" onChange={this.handleEventStyle}/>
                                    </Grid>

                                    <Grid container item spacing={2}>
                                        <Grid item xs={12} lg={6}>
                                            <Field
                                                fullWidth
                                                name="buttonSize"
                                                component={Select}
                                                label="Tamaño del botón"
                                                formControlProps={{fullWidth: true}}
                                                // variant="outlined"
                                            >
                                                <MenuItem value={'small'}>Chico</MenuItem>
                                                <MenuItem value={'medium'}>Mediano</MenuItem>
                                                <MenuItem value={'large'}>Grande</MenuItem>
                                                <MenuItem value={'block'}>Block</MenuItem>
                                            </Field>
                                        </Grid>
                                        <Grid item xs={12} lg={6}>
                                            <Field
                                                fullWidth
                                                name="buttonAlign"
                                                component={Select}
                                                label="Posición del botón"
                                                formControlProps={{fullWidth: true}}
                                                // variant="outlined"
                                            >
                                                <MenuItem value={'center'}>Centro</MenuItem>
                                                <MenuItem value={'left'}>Izquierda</MenuItem>
                                                <MenuItem value={'right'}>Derecha</MenuItem>
                                            </Field>
                                        </Grid>
                                    </Grid>

                                    <Grid item xs={12} style={{marginTop: '20px'}}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Acciones del botón
                                        </Typography>
                                    </Grid>

                                    <Grid item>
                                        <FormControl component="fieldset">
                                            <FormLabel component="legend">Tipo de acción del botón</FormLabel>
                                            <RadioGroup row>
                                                <FormControlLabel
                                                    label="Redirección a una página externa"
                                                    control={
                                                        <Field
                                                            name="buttonType"
                                                            component={Radio}
                                                            type="radio"
                                                            value="redirect"
                                                        />
                                                    }
                                                />
                                                <FormControlLabel
                                                    label="Redirección a una vista"
                                                    control={
                                                        <Field
                                                            name="buttonType"
                                                            component={Radio}
                                                            type="radio"
                                                            value="router"
                                                        />
                                                    }
                                                />
                                                <FormControlLabel
                                                    label="Mostrar encuesta"
                                                    control={
                                                        <Field
                                                            name="buttonType"
                                                            component={Radio}
                                                            type="radio"
                                                            value="poll"
                                                        />
                                                    }
                                                />
                                                <FormControlLabel
                                                    label="Descarga de un archivo de internet"
                                                    control={
                                                        <Field
                                                            name="buttonType"
                                                            component={Radio}
                                                            type="radio"
                                                            value="download"
                                                        />
                                                    }
                                                />
                                                <FormControlLabel
                                                    label="Hacer llamada"
                                                    control={
                                                        <Field
                                                            name="buttonType"
                                                            component={Radio}
                                                            type="radio"
                                                            value="phone"
                                                        />
                                                    }
                                                />
                                            </RadioGroup>
                                        </FormControl>
                                    </Grid>


                                    {
                                        values.buttonType === 'redirect' ? (
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="urlRedirect"
                                                    component={TextField}
                                                    multiline
                                                    label="URL externa"
                                                    variant="outlined"
                                                />
                                            </Grid>
                                        ) : ''
                                    }

                                    {
                                        values.buttonType === 'download' ? (
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="urlResource"
                                                    component={TextField}
                                                    multiline
                                                    label="URL externa"
                                                    variant="outlined"
                                                />
                                            </Grid>
                                        ) : ''
                                    }

                                    {
                                        values.buttonType === 'router' ? (
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="viewId"
                                                    component={Select}
                                                    label="Seleccionar una vista"
                                                    formControlProps={{fullWidth: true}}
                                                    variant="outlined"
                                                >
                                                    {
                                                        this.state.viewsList.map(
                                                            (item, index) => (
                                                                <MenuItem key={index} value={item._id}>{item.name}</MenuItem>
                                                            )
                                                        )
                                                    }
                                                </Field>
                                            </Grid>
                                        ) : ''
                                    }



                                    {
                                        values.buttonType === 'poll' ? (
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="pollId"
                                                    component={Select}
                                                    label="Seleccionar un cuestionario/formulario"
                                                    formControlProps={{fullWidth: true}}
                                                    variant="outlined"
                                                >
                                                    {
                                                        this.state.pollList.map(
                                                            (item, index) => (
                                                                <MenuItem key={index} value={item._id}>{item.name}</MenuItem>
                                                            )
                                                        )
                                                    }
                                                </Field>
                                            </Grid>
                                        ) : ''
                                    }


                                    {
                                        values.buttonType === 'phone' ? (
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="phoneNumber"
                                                    component={TextField}
                                                    label="Teléfono a marcar"
                                                    variant="outlined"
                                                />
                                            </Grid>
                                        ) : ''
                                    }


                                    {submitError && <div className="error">{submitError}</div>}
                                    <Grid item style={{ marginTop: 16 }} xs={12}>

                                        <SaveButtonSpinner
                                            loading={this.props.loading}
                                        />
                                    </Grid>
                                </Grid>
                            </Paper>
                        </form>
                    )}
                />
            </div>
        );
    }
}


export default ConfigurationFormButton;