import React, {Component, Fragment} from 'react';
import AdminLayout from "../../layouts/AdminLayout";
import {Divider, Grid, Paper} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import Button from "@material-ui/core/Button";
import cloneDeep from 'lodash/cloneDeep';
import {ApiServiceEvents} from "../../api-services/events";
import {Redirect,Link} from 'react-router-dom'
import FileBase64 from 'react-file-base64';
import Avatar from "@material-ui/core/Avatar";
import CircularProgress from "@material-ui/core/CircularProgress";
import {Trans, t} from "@lingui/macro";
import {withI18n} from "@lingui/react";
import Image from "material-ui-image/lib/components/Image/Image";
import DateFnsUtils from "@date-io/date-fns";
import AlarmIcon from "@material-ui/icons/AddAlarm";
import GlobalContext from "../../GlobalContext";
import {MuiPickersUtilsProvider,KeyboardDateTimePicker} from "@material-ui/pickers";
import moment from "moment";

const apiEvents = new ApiServiceEvents();


class CreateEvent extends Component{

    static contextType = GlobalContext;

    state = {
        logo: {
            value: 'data:image/png;base64,',
            name: 'example.png',
            type: 'imageBase64'
        },
        header: {
            value: 'data:image/png;base64,',
            name: 'example.png',
            type: 'imageBase64'
        },
        color:'',
        name:'',
        description:'',
        capacity:0,
        location:'',
        slug:'',
        user_firstName:'',
        user_lastName:'',
        startDate:new Date(),
        endDate:new Date(),
        user_email:'',
        user_company:'',
        user_password:'',
        user_passwordConfirm:'',
        redirectToList:false,
        redirectToLogOut:false,
        urlLogIn: '/security/signin/',
        loading:false,
        file:null,
        user:{},
        messagefile:'',
        messagefileHeader:''
    };


    handleEventInput=(e)=> {
        const name = e.target.name;
        const namesFieldsInteger = ['capacity']
        const patternChars = /[^A-Za-z0-9 ñÑáéíóúÁÉÍÓÚüÜ\,.:_!#?]/;
        const patternCharsName = /[^A-Za-z ñÑáéíóúÁÉÍÓÚüÜ]/;
        //const regNum = /^[0-9]+$/; 

        if(namesFieldsInteger.includes(e.target.name))
        {
            if(e.target.value<0){
                e.target.value = 0;
            }           
            e.target.value = parseInt(e.target.value);
        }        
        if(e.target.name == 'name' || e.target.name == 'location' || e.target.name == 'user_firstName' 
            || e.target.name == 'user_lastName' || e.target.name == 'user_company'){
            if(e.target.value.length > 50){
                e.target.value = e.target.value.slice(0,50);
            }            
        }

        if(e.target.name == 'description'){
            if(e.target.value.length > 250){
                e.target.value = e.target.value.slice(0,250);
            }
        }

        
        let ctx = this.context;

        if(e.target.name == 'name'){
            if(patternChars.test(e.target.value)){
                e.target.value = e.target.value.slice(0,-1);
                ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
            }
        }

        if(e.target.name == 'user_lastName' ||  e.target.name == 'user_firstName'){
            if(patternCharsName.test(e.target.value)){
                e.target.value = e.target.value.slice(0,-1);
                ctx.openNotification('No se permiten caracteres especiales ni números', 'error');                
            }
        }
        

        const value = e.target.value;
        this.setState({[name]: value});
    };


    /**
     * Enviamos los datos del evento para que se cree en la BD
     * @param e
     */
    handleSubmit=(e)=>{
        e.preventDefault()       
        let ctx = this.context;

        let {user_firstName,user_lastName,user_email ,user_company,user_passwordConfirm } = this.state

        let user =
            {
            firstName: user_firstName,
                lastName: user_lastName,
                email: user_email,
                company: user_company,
                password: user_passwordConfirm,
                groups:[]
        };



        let event = {
            logo:this.state.logo,
            color:this.state.color,
            name:this.state.name,
            description:this.state.description,
            capacity:this.state.capacity,
            location:this.state.location,
            slug:this.state.slug,
            icon:this.state.header,
            endDate:moment(this.state.endDate).format(),
            startDate:moment(this.state.startDate).format(),
            undefinedEnd:false
        };
        
        if(Date.parse(event.startDate) > Date.parse(event.endDate )){
            ctx.openNotification(<Trans>La fecha de inicio no puede ser mayor a la fecha de fin del evento</Trans>,'error');
            return;
        }


        this.setState({loading:true})

        if(this.validateFormEvent()){
            let req_state = {
                user,
                event
            }
            /// hacer la peticion para el create event
            apiEvents.createEvent(req_state).then(res=>{
                this.setState({redirectToList:true})
            }).catch(err=>{
                this.setState({loading:false})
                if(err.response.data.status == 'Token')
                {
                    //Redirect to
                    alert('Tu sesión ha expirado. Por favor, inicia sesión nuevamente.');
                    localStorage.removeItem('jwt');
                    this.setState({redirectToLogOut: true})
                }
                else{
                    alert(err.response.data.message)
                }
                
                
            })
        }else{
            this.setState({loading:false})
        }
    }

    // Callback~
    getFiles=(files)=>{
        let size = files.file.size/1024;
        let messagefile = '';
        if(size<=4000){
            if(files.base64)
                if(files.file.type.includes('image')){
                    let image = {
                        value: files.base64,
                        type: 'imageBase64',
                        name: files.name
                    };
                    this.setState({
                        logo: image
                    })
                }else{
                    messagefile=<Trans>Archivo no aceptado</Trans>
                }

        }else{
            messagefile=<Trans>Esta imagen es demasiado pesada, porfavor seleccione máximo 4mb</Trans>
        }

        this.setState({
            messagefile
        })
    }


    // Callback~
    getFilesHeader=(files)=>{
        let size = files.file.size/1024;
        let messagefileHeader = '';
        if(size<=4000){
            if(files.base64)
                if(files.file.type.includes('image')){
                    let image = {
                        value: files.base64,
                        type: 'imageBase64',
                        name: files.name
                    };
                    this.setState({
                        header: image
                    })
                }else{
                    messagefileHeader=<Trans>Archivo no aceptado</Trans>
                }

        }else{
            messagefileHeader=<Trans>Esta imagen es demasiado pesada, porfavor seleccione máximo 4mb</Trans>
        }

        this.setState({
            messagefileHeader
        })
    }


    handleStartDateChange = (isTime, date) =>{
        console.log(date,isTime)
        this.setState({
            startDate:date
        })
    }


    handleEndDateChange = (isTime, date) =>{
        console.log(date,isTime)
        this.setState({
            endDate:date
        })
    }




    /**
     * Nos ayuda a saber si lo que el usuario está capturando al crear un evento es válido
     * @returns {boolean} es válido el formulario
     */
    validateFormEvent=()=>{
        if(this.state.user_password!==this.state.user_passwordConfirm){
          // alert('Las contraseñas no coinciden')
          alert(this.props.i18n._(t`Las contraseñas no coinciden`))
          return false
        }

        //había la validación de que el correo tenga que se sectur pero se quita
        // if(!this.state.user_email.includes('@sectur')){
        //     alert('se requiere de un email @sectur.gob.mx')
        //     return false
        // }

        return true;
    };

    
    render() {

        if(this.state.redirectToList) return <Redirect push to={'/event/list'}/>
        if (this.state.redirectToLogOut) return <Redirect push to={this.state.urlLogIn} />;
        let today = new Date();
        let actualYear = today.getFullYear();
        return (
            <AdminLayout title={<Trans>Nuevo evento</Trans>}>
                <MuiPickersUtilsProvider utils={DateFnsUtils}>
                <Grid container>

                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans> Nuevo Evento</Trans>
                        </Typography>
                    </Grid>

                    <Grid item xs={12}>
                        <React.Fragment>
                            <Paper style={{padding:'20px'}}>
                                <Typography variant="h6" gutterBottom>
                                    <Trans> Datos generales</Trans>
                                </Typography>
                                <br/>

                                <form onSubmit={this.handleSubmit}>

                                    <Grid container spacing={3}>

                                        <Grid item xs={12} md={6}>

                                            <Image
                                                alt={'logo modulo'}
                                                src={this.state.logo.value}
                                                imageStyle={{
                                                    margin: 10,
                                                    width: 200
                                                }}
                                                style={{
                                                    margin: 10,
                                                    width: 150,
                                                    height: 150,
                                                    paddingTop: 30
                                                }}
                                            />
                                            <br/>



                                            <FileBase64
                                                accept="image/*"
                                                onDone={ this.getFiles.bind(this) } />
                                            <br/>
                                            <Typography variant="caption" color={'primary'} display="block" gutterBottom>
                                                * <Trans>Logo representativo del evento</Trans>
                                            </Typography>
                                            <span style={{color:'red'}}>{this.state.messagefile}</span>

                                        </Grid>

                                        <Grid item xs={12} md={6}>

                                            <Image
                                                alt={'logo header'}
                                                src={this.state.header.value}
                                                imageStyle={{
                                                    margin: 10,
                                                    width: 200
                                                }}
                                                style={{
                                                    margin: 10,
                                                    width: 150,
                                                    height: 150,
                                                    paddingTop: 30
                                                }}
                                            /><br/>


                                            <FileBase64
                                                accept="image/*"
                                                onDone={this.getFilesHeader.bind(this)}/>
                                            <br/>
                                            <Typography variant="caption" color={'primary'}  display="block" gutterBottom>
                                                * <Trans>Imagen para el menú lateral de la aplicación, así como del login del usuario</Trans>
                                            </Typography>

                                            <span style={{color:'red'}}>{this.state.messagefileHeader}</span>

                                        </Grid>



                                            <Grid item xs={12} md={12}>
                                                <TextField variant="outlined" required name="name" id="name" label={<Trans>Nombre</Trans>} onChange={this.handleEventInput} fullWidth />
                                            </Grid>


                                            <Grid item xs={12} md={6}>
                                                <TextField multiline
                                                           rows="4" variant="outlined" id="description"  name="description" onChange={this.handleEventInput} label={<Trans>Descripción</Trans>} fullWidth />
                                            </Grid>

                                        <Grid item xs={12} md={6}>

                                            <label style={{marginRight:'10px'}}><Trans>Color del evento</Trans></label>
                                            <input type='color' variant="outlined" required placeholder={'#FFFF00'} name="color" id="color"  onChange={this.handleEventInput}  />
                                            <br/>

                                            <p style={{color:'gray', fontSize:'12px'}}>* <Trans>Este color será utilizado como principal en la aplicación móvil</Trans></p>
                                        </Grid>


                                            <Grid item xs={12} md={6}>
                                                <TextField variant="outlined" type="number" min="0" pattern="^[0-9]+" name="capacity" required id="numAssistans" onChange={this.handleEventInput} label={<Trans>Num. Asistentes</Trans>} fullWidth />
                                            </Grid>
                                            <Grid item xs={12} md={6}>
                                                <TextField variant="outlined" required name="location" id="ubication" onChange={this.handleEventInput} label={<Trans>Ubicación</Trans>} fullWidth />
                                            </Grid>
                                            <Grid item xs={12} md={6}>
                                                <TextField
                                                    variant="outlined"
                                                    label="Url personalizada del evento"
                                                    required
                                                    fullWidth
                                                    name="slug"
                                                    onChange={this.handleEventInput}
                                                    InputProps={{
                                                        startAdornment: <InputAdornment position="start">app.tianguisturistico.com/</InputAdornment>,
                                                    }}
                                                    />
                                            </Grid>

                                    </Grid>

                                    <br/>

                                    <Typography variant="h6" gutterBottom>
                                        <Trans>Disponibilidad del evento</Trans>
                                    </Typography>
                                    <br/>

                                    <Grid container spacing={3}>
                                        <Grid item xs={6} >

                                            <KeyboardDateTimePicker
                                                value={this.state.startDate}
                                                style={{marginRight:10}}
                                                onChange={this.handleStartDateChange.bind(this,false)}
                                                label={<Trans>Fecha y hora de inicio del evento</Trans>}
                                                minDate={new Date(`${actualYear-3}-01-01`)}
                                                maxDate={new Date(`${actualYear+5}-01-01`)}
                                                onError={console.log}
                                                format="dd/MM/yyyy hh:mm a"
                                            />


                                            <KeyboardDateTimePicker
                                                value={this.state.endDate}
                                                onChange={this.handleEndDateChange.bind(this,false)}
                                                label={<Trans>Fecha y hora de fin del evento</Trans>}
                                                minDate={new Date(`${actualYear-3}-01-01`)}
                                                maxDate={new Date(`${actualYear+5}-01-01`)}
                                                onError={console.log}
                                                format="dd/MM/yyyy hh:mm a"
                                            />

                                        </Grid>




                                        <Grid item xs={6} style={{marginTop: 10}}>

                                            {/*<KeyboardDatePicker*/}
                                            {/*    disableToolbar*/}
                                            {/*    variant="inline"*/}
                                            {/*    format="dd/MM/yyyy"*/}
                                            {/*    margin="normal"*/}
                                            {/*    style={{marginRight:10}}*/}
                                            {/*    id="date-picker-inline-start"*/}
                                            {/*    label={<Trans>Fecha de fin del evento</Trans>}*/}
                                            {/*    value={this.state.endDate}*/}
                                            {/*    onChange={this.handleEndDateChange.bind(this,false)}*/}
                                            {/*    KeyboardButtonProps={{*/}
                                            {/*        'aria-label': 'change date',*/}
                                            {/*    }}*/}
                                            {/*/>*/}

                                            {/*<KeyboardTimePicker*/}
                                            {/*    margin="normal"*/}
                                            {/*    id="time-picker-start"*/}
                                            {/*    label={<Trans>Hora de finalización del evento</Trans>}*/}
                                            {/*    value={this.state.endDate}*/}
                                            {/*    onChange={this.handleEndDateChange.bind(this,true)}*/}
                                            {/*    KeyboardButtonProps={{*/}
                                            {/*        'aria-label': 'change time',*/}
                                            {/*    }}*/}
                                            {/*/>*/}



                                        </Grid>


                                        <Grid item xs={12}>
                                            <Typography variant="p" gutterBottom>
                                                <Trans>*Esta configuración de fechas será la que determine la temporalidad de los códigos QR del evento</Trans>
                                            </Typography>
                                        </Grid>



                                    </Grid>
                                    <br/>
                                    <Typography variant="h6" gutterBottom>
                                        <Trans>Datos del administrador</Trans>
                                    </Typography>
                                    <br/>

                                    <Grid container spacing={3}>

                                        <Grid item xs={12} md={6}>
                                            <TextField variant="outlined" required name="user_firstName" onChange={this.handleEventInput} id="firstName" label={<Trans>Nombre</Trans>} fullWidth />
                                        </Grid>

                                        <Grid item xs={12} md={6}>
                                            <TextField variant="outlined" required onChange={this.handleEventInput} name="user_lastName" id="lastName" label={<Trans>Apellido</Trans>} fullWidth />
                                        </Grid>

                                        <Grid item xs={12} md={6}>
                                            <TextField variant="outlined"  type="text" onChange={this.handleEventInput} name="user_company" required id="celphone" label={<Trans>Empresa</Trans>} fullWidth />
                                        </Grid>
                                        <Grid item xs={12} md={6}>
                                            <TextField variant="outlined"  type="email" onChange={this.handleEventInput} name="user_email" required id="emailAdmin" label={<Trans>Email</Trans>} fullWidth />
                                        </Grid>
                                        <Grid item xs={12} md={6}>
                                            <TextField variant="outlined" onChange={this.handleEventInput} type="password" name="user_password" required id="passwordAdmin" label={<Trans>Contraseña</Trans>} fullWidth />
                                        </Grid>

                                        <Grid item xs={12} md={6}>
                                            <TextField variant="outlined" onChange={this.handleEventInput} type="password" name="user_passwordConfirm" required id="passwordAdminConfirm" label={<Trans>Confirmar contraseña</Trans>} fullWidth />
                                        </Grid>


                                        <Grid item xs={12}>
                                            <Button component={Link} to='/event/list' style={{marginRight:'10px'}}   variant="contained">
                                                <Trans>Cancelar</Trans>
                                            </Button>

                                            {
                                                this.state.loading ? <CircularProgress size={20}/> :
                                                    <Button type="submit" variant="contained" color="primary">
                                                        <Trans>Guardar</Trans>
                                                    </Button>
                                            }
                                        </Grid>
                                    </Grid>
                                </form>

                            </Paper>
                        </React.Fragment>
                    </Grid>

                </Grid>
                </MuiPickersUtilsProvider>
            </AdminLayout>
            )
    }
}

export default withI18n()(CreateEvent)