import React, {Component} from 'react';
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import AddIcon from '@material-ui/icons/Add';
import GlobalContext from "../../../../GlobalContext";
import {ApiServiceEvents} from "../../../../api-services/events";
import PollListTable from "../../../../components/events/admin/polls/PollListTable";
import {Trans, t} from "@lingui/macro";
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';
import {withI18n} from "@lingui/react";
const apiEvents = new ApiServiceEvents();

/**
 *
 * This view allows you to view the list of polls
 * @class PollList
 * @extends {Component}
 */
class PollList extends Component {

    static contextType = GlobalContext;

    state={
        breadcrumbs:[
            {
                name:<Trans>Encuestas del evento</Trans>,
                active:true,
                link:''
            }
        ],
        _idEvent:null,
        polls:[],
        loading:true
    };

    componentDidMount() {
        const ctx = this.context;
        let slug = ctx.slug;

        apiEvents.getEventInfo('', slug).then(
            (response) => {
                this.setState({
                    groups:response.event.groups,
                    _idEvent: response.event._id,
                    loading:true
                });

                this.getPollList(slug);

            }
        ).catch(
            (err) => {
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );

    }

    /**
     * Function for delete poll selected
     * @param  {Object} resourceData
     */
    handlerDelete = (resourceData) =>
        new Promise(resolve => {
            let ctx = this.context;
            let {_idEvent} = this.state;
            let _idPoll = resourceData._id;

            apiEvents.removePoll(_idPoll).then(
                (response) => {
                    this.getPollList(_idEvent);
                    resolve();

                }
            ).catch(
                (err) => {
                    if(err.response.status === 403){
                        this.context.openPermissionNotification();
                    }
                    resolve();
                    if(err.response.status === 403){
                        ctx.openPermissionNotification();
                    }
                }
            );

        });

    /**
     * function for get poll list
     * @param  {number} _idEvent
     */
    getPollList(_idEvent){
        // console.log('Empresandodjdoi', _idEvent)
        let slug = this.context.slug;
        
        apiEvents.getPollEvents(slug).then((res)=>{
            // console.log('Obteniendo datos', res);
           this.setState({
               polls:res
           })
           
        }).catch((err)=>{
            console.log('err', err);
            if(err.response.status === 403){
                this.context.openPermissionNotification();
            }
        }).finally(()=> this.setState({loading:false}));
    }


    /**
     * Function for duplicate poll selected
     * @param  {number} idPoll
     */
    onCloneRow=(idPoll)=>{
        let ctx = this.context;
        let msg = '¿Deseas clonar esta encuesta?'
        let q = window.confirm(msg)
        let {_idEvent} = this.state;

        /**
            {
                "_id": "5d94eb5115dd974e043a9a03",
                "idiomCode": "it",
                "_idEvent": "5d8167052a8dbe0f51e8f891"
            }
         */
        let request = {
            _id:idPoll,
            idiomCode:this.context.language,
            _idEvent:_idEvent
        };
        if(q){
            apiEvents.clonePoll(request).then((res)=>{
                console.log('empezando');
                this.getPollList(_idEvent)
            }).catch(
                (err)=> {
                    console.log(err);

                    if(err.response.status === 404){
                        // alert('Hubo un error interno, por favor intente más tarde.')
                        alert(this.props.i18n._(t`Hubo un error interno, por favor intente más tarde`))
                    }

                    if(err.response.status === 403){
                        ctx.openPermissionNotification();
                    }
                }
            )
        }
    }


    render() {
        let {breadcrumbs,polls,loading} = this.state;
        polls.map((p)=>{
            p.name = p.name.length>60 ? p.name.substring(0, 55)+'...' : p.name;
        });
        const ctx = this.context;

        return (
            <AdminEventLayout breadcrumbs={breadcrumbs} title={<Trans>Encuestas del evento</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Listado de Encuestas/formularios</Trans>
                            <IconButton component={Link} push='true' to={`/${ctx.slug}/admin/poll/create/`} color="primary" aria-label="add to shopping cart">
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>

                    <Grid item xs={12}>
                        {
                            loading ? <Paper xs={12} style={{'textAlign': "center","paddingTop":50,"paddingBottom":50}}><CircularProgress /> </Paper>
                        :
                            <PollListTable
                            handlerDelete={this.handlerDelete}
                            onCloneRow={this.onCloneRow}
                            data={polls}/>
                        }
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(PollList);