import React, {useContext} from "react";

import {Link} from "react-router-dom";
import { Trans } from '@lingui/macro';

import GlobalContext from "../../../../GlobalContext";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import {ApiServiceEvents} from "../../../../api-services/events";
import Box from "@material-ui/core/Box";
import Avatar from "@material-ui/core/Avatar";
import FolderIcon from '@material-ui/icons/Folder';
import Badge from "@material-ui/core/Badge";
import Typography from "@material-ui/core/Typography";
import Button from "@material-ui/core/Button";
import Fab from "@material-ui/core/Fab";
import AddIcon from '@material-ui/icons/Add';
import EditIcon from '@material-ui/icons/Edit';
import DeleteIcon from '@material-ui/icons/Delete';
import FileCopy from '@material-ui/icons/FileCopy';
import NavigationIcon from '@material-ui/icons/Navigation';
import {makeStyles} from "@material-ui/core";
import ModalConfirmDelete from "../../../../components/events/admin/modules/ModalConfirmDelete";
import LinearProgress from "@material-ui/core/LinearProgress";
import CircularProgress from "@material-ui/core/CircularProgress";
import FacebookProgress from "../../../../components/FacebookProgress";
import ModalConfirmGeneric from "../../../../components/utils/ModalConfirmGeneric";
import Card from "@material-ui/core/Card";
import Tooltip from '@material-ui/core/Tooltip';
import AddToHomeScreenIcon from '@material-ui/icons/AddToHomeScreen';
import IconButton from "@material-ui/core/IconButton";
import Chip from "@material-ui/core/Chip";
import InputLabel from '@material-ui/core/InputLabel';
import SearchField from "../../../../components/fields/SearchField";
import SearchFieldAdd from "../../../../components/fields/SearchFieldAdd";
import VisibilityIcon from '@material-ui/icons/Visibility';
import VisibilityOffIcon from '@material-ui/icons/VisibilityOff';
import TurnedInIcon from '@material-ui/icons/TurnedIn';
import MenuItem from '@material-ui/core/MenuItem';
import FormHelperText from '@material-ui/core/FormHelperText';
import FormControl from '@material-ui/core/FormControl';
import Select from '@material-ui/core/Select';
import _ from "lodash";

const apiEvent = new ApiServiceEvents();

const useStyles = makeStyles(theme => ({
    fab: {
        margin: theme.spacing(1),
    },
    button: {
        margin: theme.spacing(1),
    },
    title: {
        fontSize: 14,
    },
    card: {
        minWidth: 275,
    },
    extendedIcon: {
        marginRight: theme.spacing(1),
    },
}));

const ButtonAdd = function FloatingActionButtons() {
    const classes = useStyles();
    const ctx = useContext(GlobalContext);

    return (
        <>
            <Tooltip title={<Trans>Crear módulo</Trans>} aria-label="add">
                <Fab component={Link} push='true' to={`/${ctx.slug}/admin/module/new/`} color="primary" aria-label="add" className={classes.fab}>
                    <AddIcon />
                </Fab>
            </Tooltip>
        </>
    );
};

const ButtonEdit = function (props) {
    const classes = useStyles();
    const ctx = useContext(GlobalContext);
    const moduleIdSelected = props.moduleSelected;
    return (
        <>
            <Tooltip title={<Trans>Editar</Trans>} aria-label="edit">
            <IconButton
                className={classes.button}
                aria-label="delete"
                component={Link}
                push='true'
                to={`/${ctx.slug}/admin/module/${moduleIdSelected}`}
                color="primary">
                <EditIcon />
            </IconButton>
            </Tooltip>
        </>
    );
};

const ButtonDuplicate = function (props) {
    const classes = useStyles();
    const ctx = useContext(GlobalContext);
    const moduleIdSelected = props.moduleSelected;

    function openModal() {
        props.handleOpenModal(moduleIdSelected)
    }

    return (
        <>
            <Tooltip title={<Trans>Duplicar</Trans>} aria-label="duplicate">
            <IconButton color="primary"
                 aria-label="duplicate"
                 onClick={openModal}
                className={classes.button}>
                <FileCopy />
            </IconButton>
            </Tooltip>
        </>
    );
};


const ButtonViews = function (props) {
        const classes = useStyles();
        const ctx = useContext(GlobalContext);
        const moduleIdSelected = props.moduleSelected;
        return (
            <>
                <Tooltip title={<Trans>Vistas</Trans>} aria-label="views">
                <IconButton color="primary"
                            aria-label="views"
                            component={Link}
                            push='true'
                            to={`/${ctx.slug}/admin/${moduleIdSelected}/view/list`}
                            className={classes.button}>
                    <AddToHomeScreenIcon />
                </IconButton>
                </Tooltip>
            </>
        )
}

const ButtonDelete = function (props) {
    const classes = useStyles();
    const ctx = useContext(GlobalContext);
    const moduleId = props.moduleSelectedForDelete;

    function openModal() {
        props.handleOpenModal(moduleId)
    }

    return (
        <>
            <Tooltip title={<Trans>Eliminar</Trans>} aria-label="add">
            <IconButton color="primary"
                        aria-label="delete"
                        onClick={openModal}
                        className={classes.button}>
                <DeleteIcon />
            </IconButton>
            </Tooltip>
        </>
    );
};

/**
 *
 * This view allows you to display the module list
 * @class ListModule
 * @extends {React.Component}
 */
class ListModule extends React.Component {
    static contextType = GlobalContext;

    state = {
        moduleList: [],
        moduleListBK: [],
        canGetModules: true,
        openModalDelete: false,
        openModalDuplicate: false,
        moduleSelectedForDelete: '',
        langModule:'',
        langs:[],
        moduleSelected: '',
        loadingDelete: false,
        loadingDuplicate: false,
        loadingModuleList: false,
        searchValue: '',
        breadcrumbs:[{
            name: <Trans>Lista de módulos</Trans>,
            active:true,
            link:''
        }]
    };

    /**
     * function confirm to module delete
     */
    handleConfirmDelete = () => {
        let ctx = this.context;
        let moduleIdSelected = this.state.moduleSelectedForDelete;
        // console.log('Deleted', moduleIdSelected);
        this.setState({loadingDelete:true});
        apiEvent.deleteModule(moduleIdSelected).then(
            (res) => {
                this.getModuleList();
                this.handleCloseModal();
                this.setState({loadingDelete:false});
            }
        ).catch(err => {
            console.log('Errr', err);
            if(err.response)
            if(err.response.status === 403){
                ctx.openPermissionNotification();
            }
            this.setState({loadingDelete:false});
            this.handleCloseModal();
        });
    };

    /**
     * Function to duplicate module selected
     */
    duplicateModule = () => {
        let ctx = this.context;
        this.setState({loadingDuplicate:true});
        let {langModule} = this.state;
        let moduleSelected = this.state.moduleSelected;
        let request = {_id: moduleSelected,idiomCode:langModule};
        apiEvent.duplicateModule(request).then(
            (response) => {
                // console.log(response);
                this.getModuleList();
                this.handleCloseModal();
                this.setState({loadingDuplicate:false});
                if(response.message)
                ctx.openSuccessNotification(response.message);
            }
        ).catch(
            (err) => {
                console.log('Err', err);
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                this.setState({loadingDuplicate:false});
                this.handleCloseModal();
            }
        );
    };

    /**
     * Function to close modal
     */
    handleCloseModal = () => {
        this.setState({openModalDelete:false, openModalDuplicate: false});
    };

    /**
     * Function to set id of the module to be deleted and open  modal
     * @param  {number} moduleId
     */ 
    handleOpenModal = (moduleId) => {
        this.setState({openModalDelete:true, moduleSelectedForDelete:moduleId});
    };

    /**
     * Function to set id of the module to be duplicated and open modal
     * @param  {number} moduleId
     */
    handleOpenModalDuplicate = (moduleId) => {
        this.setState({openModalDuplicate:true, moduleSelected:moduleId});
    };

    /**
     * Function to get modules list
     */
    getModuleList = () => {
        let ctx = this.context;
        this.setState({loadingModuleList: true, moduleList: []});
        apiEvent.getModuleList(ctx.slug).then(
            (response) => {
                // console.log('----->Lista de modulo', response);
                this.setState({
                    moduleList: response,
                    moduleListBK: response,
                    canGetModules: false,
                    loadingModuleList: false
                });
            }
        ).catch(
            (err) => {
                console.log(err);
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                this.setState({loadingModuleList: false});
            }
        );
    };

    /**
     * Function to set state when an input changes
     * @param  {Object} event
     */
    changeInput = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({
            [name]: value
        });
        this.filterModules(value);
    };

    /**
     * Function to make a module filter from a string
     * @param  {string} val
     */
    filterModules = (val) => {
        if(!val){
            this.setState({moduleList: this.state.moduleListBK});
            return
        }
        let listModules = this.state.moduleListBK;
        let filteredListModules = listModules.filter(
            (item) => {
                let nameLoweCase = item.name.toLowerCase();
                let valSearchLowerCase = val.toLowerCase();
                return nameLoweCase.includes(valSearchLowerCase);
            }
        );
        this.setState({moduleList: filteredListModules});
    };

    /**
     * Function to change module language
     * @param  {Object} event
     */
    handleChange =(event)=>{
        this.setState({
            langModule:event.target.value
        })
    }

    componentDidMount() {
        let ctx = this.context;
        let slug = this.props.match.params.slug;

        apiEvent.getIdiomList('',slug).then((res)=>{
            console.log('langs',res);
            this.setState({
                langs:res
            })
        }).catch((err)=>{
        });

        this.setState({
            langModule:ctx.language,
            langs:ctx.langs
        });
        this.getModuleList()
    }

    render() {
        let ctx = this.context;
        let {moduleList,langModule,langs} = this.state;
        let {loadingModuleList} = this.state;
        return (
            <AdminEventLayout breadcrumbs={this.state.breadcrumbs} title={<Trans>Administrador de eventos</Trans>}>
                <Grid container spacing={3}>
                    { loadingModuleList ? (<FacebookProgress />) : ''}
                    <Grid item xs={12} align='center'>
                        <SearchFieldAdd
                            searchValue={this.state.searchValue}
                            handleValue={this.changeInput}
                            linkTo={`/${ctx.slug}/admin/module/new/`}
                        />
                    </Grid>

                    {
                        moduleList.length===0?<p className={'text-center'}><Trans>Sin información</Trans></p>:''
                    }

                    {moduleList.map(
                        (module, index) => (
                            <Grid key={index} item={true} xs={12} md={4} lg={4}>
                                <Card style={{padding:'10px'}} key={index} align='center' xs={12} md={2} lg={2}>
                                        <Box color="text.primary" alignItems="center">
                                            {/*<Badge color="secondary" badgeContent={'Borrador'} anchorOrigin={{*/}
                                            {/*    horizontal:'right',*/}
                                            {/*    vertical:'bottom'*/}
                                            {/*}}>*/}
                                            <Avatar alt={module.name} src={module.icon?module.icon.value:''} />
                                            {/*</Badge>*/}
                                        </Box>
                                        <Box>
                                            {/*<Badge color={module.status === 1 ? 'primary': 'secondary'} badgeContent={1} variant="dot">*/}

                                                <Typography>{module.name}</Typography>
                                            {/*<Chip color="primary" size="small" avatar={<Avatar><FaceIcon /></Avatar>} />*/}
                                            <Chip color={module.status === 1 ? 'primary': 'default'} size="medium" label={module.status === 1 ? <Trans>Activo</Trans>: <Trans>Borrador</Trans>} />
                                            <br/>
                                            {
                                                module.isPublic ? (
                                                    <Chip
                                                        icon={<TurnedInIcon/>}
                                                        style={{marginTop: 3}}
                                                        color={module.isPublic ? 'primary': 'default'}
                                                        size="small"
                                                        label={<Trans>Página principal</Trans>}
                                                        variant="outlined"
                                                    />
                                                ) : ''
                                            }

                                            {/*</Badge>*/}

                                        </Box>

                                        <Box>
                                            <ButtonEdit moduleSelected={module._id} />
                                            {
                                                !module.isPublic?<ButtonDelete moduleSelectedForDelete={module._id} handleOpenModal={this.handleOpenModal} />:''
                                            }
                                            <ButtonDuplicate moduleSelected={module._id} handleOpenModal={this.handleOpenModalDuplicate} />
                                            <ButtonViews moduleSelected={module._id} />
                                        </Box>

                                </Card>
                            </Grid>

                        )
                    )}


                    <ModalConfirmDelete
                        handleConfirmDelete={this.handleConfirmDelete}
                        handleCloseModal={this.handleCloseModal}
                        open={this.state.openModalDelete}
                        loading={this.state.loadingDelete}
                    />
                    <ModalConfirmGeneric
                        title={<Trans>¿Deseas duplicar el módulo?</Trans>}
                        content={''}
                        labelOkButton={<Trans>Aceptar</Trans>}
                        open={this.state.openModalDuplicate}
                        loading={this.state.loadingDuplicate}
                        handleCloseModal={this.handleCloseModal}
                        handleConfirmButton={this.duplicateModule}
                    >
                        <FormControl>
                            <InputLabel id="demo-simple-select-helper-label"><Trans>Seleccione el Lenguaje</Trans></InputLabel>
                            <Select
                                labelId="demo-simple-select-helper-label"
                                id="demo-simple-select-helper"
                                value={langModule}
                                onChange={this.handleChange}
                            >
                                {
                                    langs.map((item,i)=>{
                                       return (item.availableLang?<MenuItem key={i} value={item.availableLang.code}>{item.availableLang.code}</MenuItem>:'')
                                    })
                                }
                            </Select>
                            <FormHelperText><Trans>El módulo se generará en el lenguaje seleccionado</Trans></FormHelperText>
                        </FormControl>


                    </ModalConfirmGeneric>


                </Grid>
            </AdminEventLayout>
        )
    }
}

export default ListModule