import React, {Component, StyleSheet} from 'react';
import {Trans} from "@lingui/macro";
import Typography from "@material-ui/core/Typography";
import DoneOutlinedIcon from '@material-ui/icons/DoneOutlined';
import ReportProblemOutlinedIcon from '@material-ui/icons/ReportProblemOutlined';
import ErrorOutlineOutlinedIcon from '@material-ui/icons/ErrorOutlineOutlined';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import PropTypes from 'prop-types'
import CloseOutlinedIcon from '@material-ui/icons/CloseOutlined';
import {propTypes} from "react-csv/src/metaProps";

let styles ={
    generic:{
        padding:20,
        paddingLeft:0,
        width:'100%'
    },
    warning:{
        backgroundColor :'#fff3cd',
        color:'#856404',
        padding:20,
        paddingLeft:10
    },
    error:{
        backgroundColor :'#f8d7da',
        color:'#721c24',
        padding:20,
        paddingLeft:10,
    },
    success:{
        backgroundColor :'#d4edda',
        color:'#155724',
        padding:20,
        paddingLeft:10
    },
    default:{
        backgroundColor :'#e2e3e5',
        color:'#383d41',
        padding:20,
        paddingLeft:10
    },
    info:{
        backgroundColor :'#d1ecf1',
        color:'#0c5460',
        padding:20,
        paddingLeft:10
    },
    icon:{
        position:'relative',
        top:6,
        right:2
    },
    iconClose:{
        float:'right',
        marginTop:-18,
        marginRight:-15,
        cursor:'pointer'
    }
};

let IconAlert = (props) => {
    switch (props.type) {
        case 'success':
            return (
                <DoneOutlinedIcon style={styles.icon}/>
                )
            break;
        case 'warning':
            return (
                <ReportProblemOutlinedIcon style={styles.icon}/>
            )
            break;
        case 'error':
            return (
                <ErrorOutlineOutlinedIcon style={styles.icon}/>
            )
            break;
        case 'info':
            return (
                <InfoOutlinedIcon style={styles.icon}/>
            )
            break;
        default:
            return (
                <></>
            )
    }
};




class AlertText extends Component {

    state = {
        hideAlert:false
    }

    onCloseAlert = (props)=>{
        this.props.onCloseAlert();
    }


    render() {
        let {showAlert} = this.props;

        if(showAlert){
            return (
                <div style={styles[this.props.type]}>
                    <Typography variant={'p'} style={styles.generic}>
                        <IconAlert type={this.props.type}/>
                        {' '}
                        {this.props.children}
                    </Typography>
                    {
                        this.props.closable && <CloseOutlinedIcon onClick={this.onCloseAlert} style={styles.iconClose}/>
                    }

                </div>

            )
        }else{
            return (
                <></>
            )
        }


    }
}

AlertText.defaultProps = {
    type:'default',
    closable:false,
    showAlert:true
};

AlertText.propTypes = {
    type:PropTypes.oneOf(['info', 'success','warning','error','default']),
};

export default AlertText;