import React from 'react';
import Button from '@material-ui/core/Button';
import { makeStyles } from '@material-ui/core/styles';
import DeleteIcon from '@material-ui/icons/Delete';
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import KeyboardVoiceIcon from '@material-ui/icons/KeyboardVoice';
import Icon from '@material-ui/core/Icon';
import SaveIcon from '@material-ui/icons/Save';
import CircularProgress from "@material-ui/core/CircularProgress";
import {Link as LinkRouter} from "react-router-dom";
import { Trans } from '@lingui/macro';

const useStyles = makeStyles(theme => ({
    button: {
        margin: theme.spacing(1),
    },
}));

export function SaveButton(props) {
    const classes = useStyles();
    const btnName = props.name || 'Guardar'
    
    return (
        <>
            <Button disabled={props.disabled} variant="contained" color="primary" type="submit" className={classes.button} startIcon={<SaveIcon />} {...props} >
                {btnName.toString()}
            </Button>
        </>
    );
}

export function SaveButtonSpinner(props) {
    const classes = useStyles();
    
    return (
        <>
            {
                props.loading ? <CircularProgress size={20}/> :
                    <SaveButton {...props} />
            }
        </>
    );
}

export function CancelButton(props) {
    const classes = useStyles();
    const btnName = props.name || 'Cancelar'

    return (
        <>
            <Button component={LinkRouter} push="true" variant="contained" color="default" type='submit' className={classes.button} {...props} /* startIcon={<SaveIcon />} */ >
                {btnName.toString()}
            </Button>
        </>
    );
}
