import React, { Component } from 'react';
import { Grid, Paper } from "@material-ui/core";
import _ from "lodash";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import Typography from "@material-ui/core/Typography";
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from "@material-ui/core/ListSubheader";
import SubjectIcon from '@material-ui/icons/Subject';
import ShortTextIcon from '@material-ui/icons/ShortText';
import RadioButtonCheckedIcon from '@material-ui/icons/RadioButtonChecked';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import TextFieldsIcon from '@material-ui/icons/TextFields';
import Dialog from '@material-ui/core/Dialog';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import PollConfigTextHeader from "../../../../components/events/admin/polls/PollConfigTextHeader";
import { ApiServiceEvents } from "../../../../api-services/events";
import PollConfigSimpleText from "../../../../components/events/admin/polls/PollConfigSimpleText";
import TextField from "@material-ui/core/TextField";
import PollConfigMultipleText from "../../../../components/events/admin/polls/PollConfigMultipleText";
import PollConfigMultiple from "../../../../components/events/admin/polls/PollConfigMultiple";
import PollConfigCheckBox from "../../../../components/events/admin/polls/PollConfigCheckBox";
import IconButton from '@material-ui/core/IconButton';
import DeleteIcon from '@material-ui/icons/Delete';
import ArrowDropUpIcon from '@material-ui/icons/ArrowDropUp';
import ArrowDropDownIcon from '@material-ui/icons/ArrowDropDown';
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import { t, Trans } from "@lingui/macro";
import LoadingOverlay from "react-loading-overlay";
import AttachFileIcon from '@material-ui/icons/AttachFile';
import EditIcon from '@material-ui/icons/Edit';
import GlobalContext from '../../../../GlobalContext';
import move from "lodash-move";
import { withI18n } from "@lingui/react";
const apiEvents = new ApiServiceEvents();

const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});


/**
 *
 * This view is for create the questions of polls
 * @class BuilderPoll
 * @extends {Component}
 */
class BuilderPoll extends Component {

    static contextType = GlobalContext;

    state = {
        _idPoll: '',
        questions: [],
        options: [],
        openDialog: false,
        loading: false,
        breadCrumbs: [],
        textLoading: <Trans>Cargando...</Trans>,
        titleDialog: <Trans>Agregar nuevo elemento</Trans>,
        currentQuestion: {
            type: null
        },
        /// elementos de una pregunta
        title: '',
        type: null,
        isRequired: false,
        option: [],
        position: null,
        opt: '',
        currentIdQuestion: null,
        poll: { name: '' }
    }


    /**
     * This function change state for open modal dialog
     */
    handleClickOpenDialog = () => {
        this.setState({
            openDialog: true
        })
    };

    /**
     * this function change state for close modal dialog 
     */
    handleCloseDialog = () => {
        this.setState({
            openDialog: false,
            currentIdQuestion: null,
            title: '',
            option: [],
            isRequired: false,
            type: null,
            opt: ''
        })
    };

    /**
     * This function is for ordering the poll questions
     * @param  {string} type
     * @param  {Object} question
     */
    orderQuestion = (type, question) => {
        let { questions, _idPoll } = this.state;

        //obtenemos la posicion de la pregunta en el arreglo (listo)
        let currentPostion = _.findIndex(questions, function (o) { return question._id === o._id; });

        if (type === 'up' && currentPostion === 0) {
            return;
        }


        if (type === 'down' && currentPostion === questions.length - 1) {
            return;
        }

        if (type === 'up') {
            //move change the position of section in the current array
            questions = move(questions, currentPostion, currentPostion - 1);
        } else {
            questions = move(questions, currentPostion, currentPostion + 1);
        }

        this.setState({ isLoading: true })


        let arrayPositionsIds = _.map(questions, '_id'); // los ids con las nuevas positions

        apiEvents.changeOrderQuestion(arrayPositionsIds, _idPoll).then((res) => {
            console.log(res)
            this.getQuestions(_idPoll);
        }).catch((err) => {
            console.log(err)
        }).finally(() => {
            this.setState({
                isLoading: false
            })
        })

        console.log(type, question)
    }

    componentDidMount() {
        let _idPoll = this.props.match.params.poll;
        let slug = this.props.match.params.slug;

        let breadCrumbs = [
            {
                name: <Trans>Lista encuestas</Trans>,
                active: false,
                link: `/${slug}/admin/poll/list/`
            },
            {
                name: this.props.i18n._(t`Diseñador de encuestas `) + ' ' + this.state.poll.name,
                active: true,
                link: ''
            }
        ];


        this.setState({
            _idPoll,
            breadCrumbs
        });
        this.getPollDetail(_idPoll);
        this.getQuestions(_idPoll);
    };

    /**
     * this function is for get poll questions
     * @param  {number} _idPoll
     */
    getQuestions = (_idPoll) => {
        this.setState({
            loading: true
        })
        apiEvents.getQuestionsPoll(_idPoll).then((res) => {
            if (res.questions) {
                console.log('questions', res.questions)
                this.setState({
                    questions: res.questions
                })
            }

        }).catch((err) => console.log(err.response)).finally(() => {
            this.setState({
                loading: false
            })
        })
    };


    getPollDetail = (_idPoll) => {

        let slug = this.props.match.params.slug;




        apiEvents.getDetailPoll(_idPoll).then((res) => {
            console.log('res', res)


            let breadCrumbs = [
                {
                    name: <Trans>Lista encuestas</Trans>,
                    active: false,
                    link: `/${slug}/admin/poll/list/`
                },
                {
                    name: this.props.i18n._(t`Diseñador de encuesta -  `) + ' ' + '"' + res.name + '"',
                    active: true,
                    link: ''
                }
            ];

            this.setState({
                poll: res,
                breadCrumbs
            })
        });
    };

    /**
     * this funciton is for send submit form question
     */
    handleSubmit = () => {

        let { title, type, isRequired, option, currentIdQuestion, position } = this.state;
        let { _idPoll, questions } = this.state;

        let ctx = this.context;

        let request = {
            _id: currentIdQuestion,
            _idPoll,
            title,
            type,
            isRequired: isRequired,
            option,
            position
        }

        console.log('pre api', request)
        if (request.type === 4 || request.type == 5) {
            if (request.option.length <= 0) {
                return ctx.openNotification(`Las preguntas ${request.type === 4 ? 'de opción múltiple' : 'con casillas de verificación'} requeridas deben tener al menos una opción`, 'error');
            }
        }
        this.handleCloseDialog();
        if (currentIdQuestion) {
            apiEvents.editQuestion(request).then((res) => {
                this.getQuestions(_idPoll)
            }).catch((err) => {
                console.log(err.response)
            }).finally(() => {

            })
        } else {
            request.position = questions.length + 1
            apiEvents.addQuestion(request).then((res) => {

                this.setState({
                    questions: res
                })

                this.getQuestions(_idPoll)

            }).catch((err) => {
                console.log(err.response)
            }).finally(() => {

            })
        }



    };


    // addOption=(opt)=>{
    //     let {options} = this.state;
    //     options.push(opt)
    // }


    /**
     * Function for create question component 
     * @param  {Object} question
     */
    makeComponents = (question) => {

        let jxhtml = <p></p>

        switch (question.type) {
            case 1:
                jxhtml = question.title && question.option[0] ?
                    <>
                        <Typography style={{ marginTop: '10px' }} variant="h5" gutterBottom>
                            {question.title.length > 40 ? question.title.substring(0, 35) + '...' : question.title}
                        </Typography>
                        <Typography variant="body2" gutterBottom>
                            {question.option[0].length > 60 ? question.option[0].substring(0, 55) + '...' : question.option[0]}
                        </Typography>
                    </> :
                    <>
                        <Typography style={{ marginTop: '10px' }} variant="h5" gutterBottom>
                            {question.title}
                        </Typography>
                        <Typography variant="body2" gutterBottom>
                            {question.option[0]}
                        </Typography>
                    </>
                break;
            case 2:
                jxhtml = question.title ?
                    <>
                        <TextField
                            autoFocus
                            margin="dense"
                            name="title"
                            id="title"
                            style={{ marginTop: 10 }}
                            required={question.isRequired}
                            label={question.title.length > 60 ? question.title.substring(0, 55) + '...' : question.title}
                            fullWidth
                        />
                    </> :
                    <>
                        <TextField
                            autoFocus
                            margin="dense"
                            name="title"
                            id="title"
                            style={{ marginTop: 10 }}
                            required={question.isRequired}
                            label={question.title}
                            fullWidth
                        />
                    </>
                break;
            case 3:
                jxhtml = question.title ?
                    <>

                        <TextField
                            id="outlined-multiline-static"
                            multiline
                            rows="4"
                            required={question.isRequired}
                            label={question.title.length > 60 ? question.title.substring(0, 55) + '...' : question.title}
                            fullWidth
                            margin="normal"
                        />
                    </> :
                    <>

                        <TextField
                            id="outlined-multiline-static"
                            multiline
                            rows="4"
                            required={question.isRequired}
                            label={question.title}
                            fullWidth
                            margin="normal"
                        />
                    </>
                break;
            case 4:
                jxhtml = question.title ?
                    <>
                        <h4 style={{ marginTop: 10 }} >{question.title.length > 60 ? question.title.substring(0, 55) + '...' : question.title} {
                            question.isRequired ? '*' : ''
                        }  </h4>
                        {
                            question.option.map((item, index) => {
                                return (
                                    <div key={index}>
                                        <label>
                                            <input disabled type="radio" name={index} />
                                            {item.length > 50 ? item.substring(0, 45) + '...' : item}
                                        </label>
                                        <br />
                                    </div>

                                )
                            })
                        }
                    </>
                    :
                    <>
                        <h4 style={{ marginTop: 10 }} >{question.title} {
                            question.isRequired ? '*' : ''
                        }  </h4>
                        {
                            question.option.map((item, index) => {
                                return (
                                    <div key={index}>
                                        <label>
                                            <input disabled type="radio" name={index} />
                                            {item}
                                        </label>
                                        <br />
                                    </div>

                                )
                            })
                        }
                    </>
                break;

            case 5:
                jxhtml = question.title ?
                    <>
                        <h4 style={{ marginTop: 10 }} >{question.title.length > 60 ? question.title.substring(0, 55) + '...' : question.title} {
                            question.isRequired ? '*' : ''
                        }</h4>
                        {
                            question.option.map((item, index) => {
                                return (
                                    <div key={index}>
                                        <label>
                                            <input disabled type="checkbox" name={index} />
                                            {item.length > 50 ? item.substring(0, 45) + '...' : item}
                                        </label>
                                        <br />
                                    </div>


                                )
                            })
                        }
                    </> :
                    <>

                        <h4 style={{ marginTop: 10 }} >{question.title} {
                            question.isRequired ? '*' : ''
                        }</h4>
                        {
                            question.option.map((item, index) => {
                                return (
                                    <div key={index}>
                                        <label>
                                            <input disabled type="checkbox" name={index} />
                                            {item}
                                        </label>
                                        <br />
                                    </div>


                                )
                            })
                        }
                    </>
                break;

            case 6:
                jxhtml =
                    <>

                        <h4 style={{ marginTop: 10 }} >{question.title.length > 60 ? question.title.substring(0, 55) + '...' : question.title} { question.isRequired ? '*' : '' }</h4>
                        <label>
                            <input disabled type="file" />
                        </label>
                    </>
                break;
        }

        return (
            <Grid container style={{ marginBottom: 20 }}>
                <Grid item xs={4}>
                    <IconButton onClick={this.deleteQuestion.bind(this, question)} edge="end" aria-label="delete">
                        <DeleteIcon />
                    </IconButton>
                    <IconButton onClick={this.editQuestion.bind(this, question)} edge="end" aria-label="edit">
                        <EditIcon />
                    </IconButton>
                    <IconButton edge="end" onClick={this.orderQuestion.bind(this, 'up', question)} aria-label="up">
                        <ArrowDropUpIcon />
                    </IconButton>
                    <IconButton edge="end" onClick={this.orderQuestion.bind(this, 'down', question)} aria-label="down">
                        <ArrowDropDownIcon />
                    </IconButton>
                </Grid>
                <Grid item xs={8}>
                    {jxhtml}
                </Grid>
            </Grid>
        )
    }

    deleteQuestion = (question) => {

        let confirmDelete = window.confirm('¿Estas seguro de eliminar la pregunta?');

        let { _idPoll } = this.state;
        if (confirmDelete) {

            apiEvents.removeQuestionPoll(_idPoll, question._id).then((res) => {
                this.getQuestions(_idPoll)
            }).catch((err) => {
                console.log(err.response)
            }).finally(() => {

            });
        }
    }



    addQuestion = (type, e) => {

        let { _idPoll, questions } = this.state;

        let question = {
            type: type,
            _idPoll,
            options: [],
            title: '',
            isRequired: false,
            position: this.state.questions.length + 1
        };


        this.handleClickOpenDialog();

        this.setState({
            type: type,
            titleDialog: <Trans>Agregar nuevo elemento</Trans>,
            currentQuestion: question
        })

    };


    editQuestion = (question) => {
        console.log(question)
        this.handleClickOpenDialog();
        let { title, option, isRequired, type, position } = question;
        this.setState({
            currentQuestion: question,
            titleDialog: <Trans>Editar elemento</Trans>,
            title,
            position,
            currentIdQuestion: question._id,
            option,
            isRequired,
            type
        })
    }


    handleInputChange = (event) => {
        console.log('value: ', event.target.value.length, event.target.value, event.target)
        const ctx = this.context
        const patternAlphanum = /^[,a-zA-ZÀ-ÿ\d\s-_¿?¡!]+$/ig
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        if (value && !patternAlphanum.test(value)) {
            event.target.value = event.target.value.slice(0, -1);
            ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
        } else if(value && value.length > 60) {
            event.target.value = event.target.value.slice(0, 60);
            ctx.openNotification('Límite de caracteres excedido', 'error');
        } else {
            if (name === 'description' ) {
                let newoptions = [];
                newoptions.push(value);
                this.setState({
                    option: newoptions
                })
            } else {
                this.setState({
                    [name]: value
                });
            }
        }

    };


    /**
     * delete options from questions type checkbox / radiobuttons
     */
    handleDeleteOpt = (index) => {
        let { option } = this.state;
        if (this.state.type == 4 || this.state.type == 5) {
            if (this.state.isRequired && this.state.option.length <= 1) {
                return this.context.openNotification('No se puede tener menos de una opción en preguntas requeridas');
            }
        }
        option.splice(index, 1);
        this.setState({
            option
        })
    }


    /**
     * for checkbox or radio buttons questions
     * **/
    addOption = () => {
        let { opt, option } = this.state;
        option.push(opt);
        this.setState({
            option,
            opt: ''
        });
    }



    render() {
        let { breadCrumbs, questions, openDialog, currentQuestion, titleDialog, options, opt, loading, textLoading, position, title, option, isRequired, type } = this.state;
        return (

            <LoadingOverlay
                active={loading}
                spinner
                text={textLoading}
            >
                <AdminEventLayout title={<Trans>Builder de encuesta/formulario</Trans>} breadcrumbs={breadCrumbs}>
                    <Grid container spacing={2}>
                        <Grid item xs={12}>
                            <Typography variant="h5" style={{ padding: '10px' }} component="h3">

                                <Trans>Configuración de Encuesta</Trans>
                            </Typography>
                        </Grid>

                        <Grid item xs={12} md={3}>
                            <Paper style={{ padding: '20px' }}>
                                <List
                                    component="nav"
                                    aria-labelledby="nested-list-subheader"
                                    subheader={
                                        <ListSubheader component="div" id="nested-list-subheader">
                                            <Trans>Tipos de pregunta</Trans>
                                        </ListSubheader>
                                    }
                                >
                                </List>
                                <List component="nav" aria-label="main mailbox folders">
                                    <ListItem button onClick={this.addQuestion.bind(this, 1)}>
                                        <ListItemIcon>
                                            <TextFieldsIcon />
                                        </ListItemIcon>
                                        <ListItemText primary={<Trans>Título y descripción</Trans>} />
                                    </ListItem>
                                    <ListItem button onClick={this.addQuestion.bind(this, 2)}>
                                        <ListItemIcon>
                                            <ShortTextIcon />
                                        </ListItemIcon>
                                        {/*<ListItemText primary={<Trans>Título y descripción</Trans>} />*/}
                                        <ListItemText primary={<Trans>Respuesta breve</Trans>} />
                                    </ListItem>
                                    <ListItem button onClick={this.addQuestion.bind(this, 3)}>
                                        <ListItemIcon>
                                            <SubjectIcon />
                                        </ListItemIcon>
                                        <ListItemText primary={<Trans>Párrafo</Trans>} />
                                    </ListItem>
                                    <ListItem button onClick={this.addQuestion.bind(this, 4)}>
                                        <ListItemIcon>
                                            <RadioButtonCheckedIcon />
                                        </ListItemIcon>
                                        <ListItemText primary={<Trans>Opción múltiple</Trans>} />
                                    </ListItem>
                                    <ListItem button onClick={this.addQuestion.bind(this, 5)}>
                                        <ListItemIcon>
                                            <CheckBoxIcon />
                                        </ListItemIcon>
                                        <ListItemText primary={<Trans>Casillas de verificación</Trans>} />
                                    </ListItem>
                                    <ListItem button onClick={this.addQuestion.bind(this, 6)}>
                                        <ListItemIcon>
                                            <AttachFileIcon />
                                        </ListItemIcon>
                                        <ListItemText primary={<Trans>Archivo adjunto</Trans>} />
                                    </ListItem>
                                </List>
                            </Paper>
                        </Grid>

                        <Grid item xs={12} md={9}>
                            <Paper style={{ padding: '20px' }}>
                                <List>

                                    {
                                        questions ? questions.map((item, index) => {
                                            return (
                                                <div key={index}>
                                                    {this.makeComponents(item)}
                                                </div>
                                            )
                                        }) : ''
                                    }
                                </List>
                            </Paper>
                        </Grid>

                    </Grid>


                    <Dialog
                        open={openDialog}
                        TransitionComponent={Transition}
                        keepMounted
                        onClose={this.handleCloseDialog}
                        aria-labelledby="alert-dialog-slide-title"
                        aria-describedby="alert-dialog-slide-description"
                    >
                        <DialogTitle id="alert-dialog-slide-title">{titleDialog}</DialogTitle>
                        <DialogContent>
                            <DialogContentText id="alert-dialog-slide-description">

                            </DialogContentText>
                            {
                                currentQuestion.type === 1 ?
                                    <PollConfigTextHeader
                                        title={title}
                                        type={type}
                                        isRequired={isRequired}
                                        option={option}
                                        position={position}
                                        handleSubmit={this.handleSubmit}
                                        handleInputChange={this.handleInputChange}
                                        onClose={this.handleCloseDialog}
                                    /> : ''
                            }

                            {
                                currentQuestion.type === 2 ?
                                    <PollConfigSimpleText
                                        title={title}
                                        type={type}
                                        isRequired={isRequired}
                                        option={option}
                                        handleInputChange={this.handleInputChange}
                                        handleSubmit={this.handleSubmit}
                                        onClose={this.handleCloseDialog}
                                    /> : ''
                            }

                            {
                                currentQuestion.type === 3 ?
                                    <PollConfigMultipleText
                                        title={title}
                                        type={type}
                                        isRequired={isRequired}
                                        option={option}
                                        handleInputChange={this.handleInputChange}
                                        handleSubmit={this.handleSubmit}
                                        onClose={this.handleCloseDialog}
                                    /> : ''
                            }


                            {
                                currentQuestion.type === 4 ?
                                    <PollConfigMultiple
                                        title={title}
                                        type={type}
                                        opt={opt}
                                        isRequired={isRequired}
                                        option={option}
                                        handleDeleteOpt={this.handleDeleteOpt}
                                        handleInputChange={this.handleInputChange}
                                        addOption={this.addOption}
                                        handleSubmit={this.handleSubmit}
                                        onClose={this.handleCloseDialog}
                                    /> : ''
                            }



                            {
                                currentQuestion.type === 5 ?
                                    <PollConfigCheckBox
                                        title={title}
                                        type={type}
                                        opt={opt}
                                        isRequired={isRequired}
                                        option={option}
                                        handleDeleteOpt={this.handleDeleteOpt}
                                        handleInputChange={this.handleInputChange}
                                        currentQuestion={currentQuestion}
                                        addOption={this.addOption}
                                        handleSubmit={this.handleSubmit}
                                        onClose={this.handleCloseDialog}
                                    /> : ''
                            }


                            {
                                currentQuestion.type === 6 ?
                                    <PollConfigSimpleText
                                        title={title}
                                        type={type}
                                        isRequired={isRequired}
                                        option={option}
                                        handleInputChange={this.handleInputChange}
                                        handleSubmit={this.handleSubmit}
                                        onClose={this.handleCloseDialog}
                                    /> : ''
                            }




                        </DialogContent>
                    </Dialog>


                </AdminEventLayout>
            </LoadingOverlay>
        );
    }
}

export default withI18n()(BuilderPoll);