import React, { Component } from 'react';
import GlobalContext from "../../../../GlobalContext";
import CircularProgress from "@material-ui/core/CircularProgress";
import MaterialTable from "material-table";
import { Redirect } from "react-router-dom";
import { loadCSS } from "fg-loadcss";
import Icon from "@material-ui/core/Icon";
import { Trans, t } from "@lingui/macro";
import { withI18n } from "@lingui/react";

class TableLangList extends Component {
    static contextType = GlobalContext;

    state = {
        columns: [
            { title: 'Lenguaje', field: 'availableLang.name' },
            { title: 'Código de lenguaje', field: 'availableLang.code' },
            { title: 'Activo', field: 'availableLang.isActive', type: 'boolean' },
        ]
    };

    componentDidMount() {
        loadCSS(
            'https://use.fontawesome.com/releases/v5.1.0/css/all.css',
            document.querySelector('#font-awesome-css'),
        );
    }

    render() {
        let props = this.props
        let { module, slug, isLoading } = this.props
        let ctx = this.context;

        if (this.state.redirectToEdit) return <Redirect push to={`/${slug}/admin/lang/${this.state.selectedRow}/`} />;


        return (
            <>
                {
                    isLoading ? <CircularProgress /> : <MaterialTable
                        title="Lenguajes"
                        columns={this.state.columns}
                        data={props.data}
                        options={{
                            actionsColumnIndex: -1,
                            pageSize: 10,
                            pageSizeOptions: [10, 20, 50, 100, 500],
                            paging: true,
                        }}
                        localization={ctx.tableConfig.localization}
                        actions={[
                            {
                                icon: () => (<Icon fontSize={'small'} className="fa fa-server" />),
                                tooltip: this.props.i18n._(`Descargar archivo PO: API`),
                                onClick: (event, rowData) => this.props.getPoLanguage('api', rowData)
                            },
                            {
                                icon: () => (<Icon fontSize={'small'} className="fa fa-laptop" />),
                                tooltip: this.props.i18n._(`Descargar archivo PO: Interfaz gráfica`),
                                onClick: (event, rowData) => this.props.getPoLanguage('front', rowData)
                            },
                            {
                                icon: () => (<Icon fontSize={'small'} className="fa fa-mobile-alt" />),
                                tooltip: this.props.i18n._(`Descargar archivo PO: APP`),
                                onClick: (event, rowData) => this.props.getPoLanguage('app', rowData)
                            },
                            {
                                icon: () => (<Icon fontSize={'small'} className="fa fa-file-upload" />),
                                tooltip: this.props.i18n._(`Importar archivo PO`),
                                onClick: (event, rowData) => this.props.handlerDialogImportPo(true, rowData)
                            },
                            {
                                icon: 'edit',
                                tooltip: 'Editar',
                                onClick: (event, rowData) => this.setState({ redirectToEdit: true, selectedRow: rowData.availableLang._id })
                            },
                        ]}
                        editable={{
                            onRowDelete: props.handlerDelete,
                        }}
                    />
                }
            </>
        );
    }
}

export default withI18n()(TableLangList);