import React, {Component} from 'react';
import { Header,Container } from 'semantic-ui-react'



class MainLayout extends Component {
    state = {
        headerStyle : {
            fontSize: '15px',
            textAlign: 'center',
        }
    };


    componentDidMount() {
        this.setState({
            headerStyle: {
               // background:'#ffc500'
            }
        })
    }


    render() {
        return (
            <div>
                <Header style={this.state.headerStyle} content={this.props.content} className={'no-radius header-movil'} as='h3' block/>
                <Container text>
                    { this.props.children}
                </Container>
            </div>
        );
    }
}

export default MainLayout;