import EventList from "../pages/events/EventList";
import SignIn from "../pages/security/SignIn";
import CreateEvent from "../pages/events/CreateEvent";
import EditEvent from "../pages/events/EditEvent";
import SignUp from "../pages/security/SignUp";
import AdminUserList from "../pages/security/AdminUserList";
import UpdateUser from "../pages/security/UpdateUser";
import RecoverPassword from "../pages/security/RecoverPassword";


const SUPER_ADMIN = 1;
const EVENT_ADMIN = 2;
const EVENT_VIEW = 3;

// SUPER ADMIN ROUTES
const superAdminRoutes = [
    {
        path: "/",
        exact: true,
        component: EventList,
        isProtected: true,
        routeType: SUPER_ADMIN,
    },
    {
        path: "/event/new/",
        exact: true,
        component: CreateEvent,
        isProtected: true,
        routeType: SUPER_ADMIN
    },
    {
        path: "/event/list/",
        exact: true,
        component: EventList,
        isProtected: true,
        routeType: SUPER_ADMIN
    },
    {
        path: "/event/:id/",
        exact: true,
        component: EditEvent,
        isProtected: true,
        routeType: SUPER_ADMIN
    },
    {
        path: "/user/new/",
        exact: true,
        component: SignUp,
        isProtected: true,
        routeType: SUPER_ADMIN
    },
    {
        path: "/user/list/",
        exact: true,
        component: AdminUserList,
        isProtected: true,
        routeType: SUPER_ADMIN
    },
    {
        path: "/user/:id/",
        exact: true,
        component: UpdateUser,
        isProtected: true,
        routeType: SUPER_ADMIN
    }
];

const securityRoutes = [
    {
        path: "/security/signin/",
        exact: false,
        component: SignIn,
        isProtected: false,
        routeType: SUPER_ADMIN
    },
    {
        path: "/security/password/reset/",
        exact: true,
        component: RecoverPassword,
        isProtected: false,
        routeType: SUPER_ADMIN
    },
];

const SuperAdminRoutes = superAdminRoutes.concat(securityRoutes);

export default SuperAdminRoutes