import React from 'react';
import MaterialTable from 'material-table';
import { Redirect } from "react-router-dom";
import Avatar from '@material-ui/core/Avatar';
import Icon from '@material-ui/core/Icon';
import { t, Trans } from "@lingui/macro";
import { loadCSS } from 'fg-loadcss';
import GlobalContext from "../../../../GlobalContext";
import moment from "moment";
import { withI18n } from "@lingui/react";

class inviteTable extends React.Component {
    static contextType = GlobalContext;
    state = {
        redirectToEdit: '',
        selectedRow: '',
        redirectToQr: false,
        columns: [
            // { title: 'Imagen', field: 'image', render: rowData => <Avatar alt="Remy Sharp" src={rowData.image} />},
            { title: <Trans>Nombre</Trans>, field: 'firstName' },
            { title: <Trans>Apellido</Trans>, field: 'lastName' },
            { title: <Trans>Email</Trans>, field: 'email' },
            { title: <Trans>Confirmó asistencia</Trans>, field: 'confirmAssistance', type: 'boolean' },
            { title: <Trans>Asistió al evento</Trans>, field: 'scheduleAssistance', type: 'boolean' },
        ],
    };

    componentDidMount() {
        loadCSS(
            'https://use.fontawesome.com/releases/v5.1.0/css/all.css',
            document.querySelector('#font-awesome-css'),
        );
    }

    render() {

        let ctx = this.context;

        if (this.state.redirectToEdit) return <Redirect push to={`/${ctx.slug}/admin/agenda/${this.state.selectedRow}/`} />;

        let state = this.state;
        let setState = this.setState;
        let data = this.props.rows;

        return (
            <MaterialTable
                title={<Trans>Lista de invitados</Trans>}
                columns={state.columns}
                data={data}
                options={{
                    actionsColumnIndex: -1,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100, 500],
                    paging: true,
                }}
                localization={this.context.tableConfig.localization}
            />
        );
    }
}

export default withI18n()(inviteTable)