import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import _ from "lodash";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import PollsForm from "../../../../components/events/admin/polls/PollsForm";
import {ApiServiceEvents} from "../../../../api-services/events";
import GlobalContext from "../../../../GlobalContext";
import {Redirect} from "react-router-dom";
import { Trans, t } from '@lingui/macro';
import {withI18n} from "@lingui/react";

const apiEvents = new ApiServiceEvents();

/**
 *
 * this view is for create new poll
 * @class EditPoll
 * @extends {Component}
 */
class EditPoll extends Component {

    static contextType = GlobalContext;

    state={
        name:'',
        description:'',
        _idEvent:null,
        _idPoll:null,
        groups:[],
        groupsSelected:[], // aqui se setean los grupos seleccionados
        loading:false,
        redirect: false,
        breadCrumbs:[],
        urlCurrentFile:'',
        isActive:false,
    };

    componentDidMount() {
        let slug = this.context.slug;
        let _idPoll = this.props.match.params.poll;

        let breadCrumbs = [
            {
                name:<Trans>Encuestas del evento</Trans>,
                active:false,
                link:`/${slug}/admin/poll/list/`
            },
            {
                name:<Trans>Editar encuesta</Trans>,
                active:true,
                link:''
            }
        ];


        this.setState({
            breadCrumbs,
            _idPoll
        });


        apiEvents.getEventInfo('', slug).then(
            (response) => {
                this.setState({
                    groups:response.event.groups,
                    _idEvent: response.event._id
                });

                this.getDetailPoll(_idPoll)
            }
        ).catch(
            (err) => {
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );

    }

    /**
     * Function for get poll details
     * @param  {number} _idPoll
     */
    getDetailPoll=(_idPoll)=>{
        apiEvents.getDetailPoll(_idPoll).then((res)=>{
            this.setState({
                groupsSelected: res.groups,
                name:res.name,
                isActive:res.isActive
            })
            console.log(res);
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
            }
        }).finally(()=>{})
    }



    
    /**
     * Functions for set state on groupsSelected when selecting groups
     * @param  {Object} arrayGroups
     */
    handleCheckGroups=(arrayGroups)=>{
        this.setState({
            groupsSelected:arrayGroups
        })
    };


    
    /** Function to set state when an input changes
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const ctx = this.context
        let value = null
        const name = event.target.name;
        const patternAlphanum = /^[a-zA-ZÀ-ÿ\d\s-_]+$/ig

        if(event.target.name === 'name'){
            if(event.target.value.length > 50){
                event.target.value = event.target.value.slice(0,50);
                ctx.openNotification('Límite de caracteres excedido', 'error');
            } else {
                if(!patternAlphanum.test(event.target.value)){
                    event.target.value = event.target.value.slice(0,-1);
                    ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
                }
            }
        }
        value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;

        this.setState({
            [name]: value
        });
    };


    /**
     * Function fot submit poll form
     * @param  {Object} e
     */
    handleSubmit=async (e)=>{
        e.preventDefault();
        let {name, _idEvent, groupsSelected, _idPoll, isActive } = this.state;

        if(groupsSelected.length<=0){
            alert(this.props.i18n._(t`Al menos un rol de seguridad es requerido`));
            return;
        }

        this.setState({
            loading:true
        });

        /**
         *
            {
                "_id": "5d977d2371bd1a0f4dff529f",
                "name": "Encuesta de prueba Koko",
                "description": "Test de Koko",
                "groups": ["5d8132505f90d20aae6d2394"],
                "isActive": true
            }
         **/

        let request = {
            _id:_idPoll,
            name,
            groups:groupsSelected,
            isActive
        };


        apiEvents.updatePoll(request).then((res)=>{
            console.log(res)
            this.setState({
                redirect:true
            })
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
            }
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })

    };


    render() {
        let {breadCrumbs,groups,groupsSelected,name,isActive} = this.state;
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/poll/list/`} />;

        return (
            <AdminEventLayout breadcrumbs={breadCrumbs} title={<Trans>Encuesta/formulario</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <PollsForm
                            titleForm={<Trans>Editar encuesta/formulario</Trans>}
                            handleSubmit={this.handleSubmit}
                            groups={groups}
                            groupsId={_.map(groups, '_id') }
                            groupsSelected={groupsSelected}
                            name={name}
                            isActive={isActive}
                            handleInputChange={this.handleInputChange}
                            handleCheckGroups={this.handleCheckGroups}
                            handleInputChangeFile={this.handleInputChangeFile}
                        />
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(EditPoll);