import React, {Component} from 'react';
import ConfigurationFormButton from "./ConfigurationComponents/ConfigurationFormButton";
import ConfigCard from "./ConfigurationComponents/ConfigCard";
import {GlobalContextBuilder} from "../../GlobalContext";

import ConfigImage from "./ConfigurationComponents/ConfigImage";
import ConfigText from "./ConfigurationComponents/ConfigText";
import ConfigYoutube from "./ConfigurationComponents/ConfigYoutube";
import ConfigSocial from "./ConfigurationComponents/ConfigSocial";
import ConfigGallery from "./ConfigurationComponents/ConfigGallery";
import ConfigRating from "./ConfigurationComponents/ConfigRating";
import ConfigMap from "./ConfigurationComponents/ConfigMap";



function ConfigFactory(props){
    let {component, section} = props;
    // console.log('component',component);
    if(component.type === 'button'){
        return <ConfigurationFormButton component={component} onSaveConfig={props.handlerSave} />;
    }
    if(component.type === 'card'){
        return <ConfigCard component={component}
                           onSaveConfig={props.handlerSave}
                />
    }
    if(component.type === 'image'){
        return <ConfigImage component={component}
                           onSaveConfig={props.handlerSave}
                />
    }
    if(component.type === 'text'){
        return <ConfigText component={component}
                           onSaveConfig={props.handlerSave}
                />
    }
    if(component.type === 'video'){
        return <ConfigYoutube component={component}
                           onSaveConfig={props.handlerSave}
                />
    }
    if(component.type === 'social'){
        return <ConfigSocial component={component}
                           onSaveConfig={props.handlerSave}
                />
    }
    if(component.type === 'gallery'){
        return <ConfigGallery component={component}
                           onSaveConfig={props.handlerSave}
                />
    }
    if(component.type === 'rating'){
        return <ConfigRating component={component}
                           onSaveConfig={props.handlerSave}
                />
    }
    if(component.type === 'map'){
        return <ConfigMap component={component}
                           onSaveConfig={props.handlerSave}
                />
    }
    return <ConfigurationFormButton component={component} onSaveConfig={props.handlerSave} />;
}

class ConfigBuilderComponent extends Component {

    static contextType = GlobalContextBuilder;    

    handlerSave= async (_config)=>{
        let {component,section} = this.props;
        let ctx = this.context;
        if(_config.buttonType)
        {
            switch (_config.buttonType.value) {
                case 'poll':
                    if(_config.pollId.value == '' || _config.pollId.value == undefined)
                    {
                        return alert('Debes seleccionar una encuesta');
                    }
                    break;
            
                default:
                    break;
            }
        }
        
        
        //console.log('Configuración a enviarASJHD',_config.buttonType);
        
        console.log('COMPONENT', component, _config);

        if(component.type=='social')
        {
            const r = await ctx.saveConfigComponent(component,_config,section);
            console.log('RRRRRR', r.message);
            if(!r.message.includes('404'))
            {
                this.onCloseConfig();            
            }            
        }
        else{
            ctx.saveConfigComponent(component,_config,section);
            this.onCloseConfig();
        }
        
        
    };

    onCloseConfig=()=>{
        this.props.onCloseConfig();
    };


    render() {
        return (
            <div>
                <ConfigFactory
                    component={this.props.component}
                    section={this.props.section}
                    handlerSave={this.handlerSave}
                />
            </div>
        );
    }
}

export default ConfigBuilderComponent;