import React, {Component} from 'react';
import Card from "@material-ui/core/Card";
import CardActionArea from "@material-ui/core/CardActionArea";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import Typography from "@material-ui/core/Typography";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import Rating from '@material-ui/lab/Rating';
import {GlobalContextBuilder} from "../../GlobalContext";
import ButtonBase from '@material-ui/core/ButtonBase';
import MenuComponent from "./MenuComponent";
import Link from "@material-ui/core/Link";
import parse from 'html-react-parser';
import YouTube from '@u-wave/react-youtube';
import FacebookIcon from '@material-ui/icons/Facebook';
import InstagramIcon from '@material-ui/icons/Instagram';
import TwitterIcon from '@material-ui/icons/Twitter';
import YouTubeIcon from '@material-ui/icons/YouTube';
import PinterestIcon from '@material-ui/icons/Pinterest';
import Carousel, { Dots } from '@brainhubeu/react-carousel';
// import '@brainhubeu/react-carousel/lib/style.css';
import { Map, TileLayer, Marker, Popup } from "react-leaflet";
import './ComponentStyles.css';
import '@brainhubeu/react-carousel/lib/style.css';
import _ from "lodash"
import Box from "@material-ui/core/Box";


const CardComponent =(props) => {

    const {properties} = props.component;
    const descriptionDefault = 'Lizards are a widespread group of squamate reptiles, with over 6,000 species, ranging\n' +
        '                    across all continents except Antarctica';

    const buttonLabel = _.has(properties, 'buttonName.value') && properties.buttonName.value ? properties.buttonName.value : 'Botón';

    const image = props.ctx.getImage(properties);

    let alignButton = 'row'; //left

    if(_.has(properties, 'buttonAlign.value') && properties.buttonAlign.value){
        let align = properties.buttonAlign.value;
        if(align === 'center'){
            alignButton = 'column';
        }
        if(align === 'right'){
            alignButton = 'row-reverse';
        }
    }

   return (<Card style={{maxWidth: '100%', minWidth: '50%'}}>
        {/*<CardActionArea>*/}
            <CardMedia
                style={{height: 200}}
                image={image}
                title={_.has(properties, 'title.value') && properties.title.value ? properties.title.value : 'Título'}
            />
            <CardContent>
                <Typography gutterBottom variant="h5" component="h2">
                    {_.has(properties, 'title.value') && properties.title.value ? properties.title.value : 'Título'}
                </Typography>
                <Typography variant="body2" color="textSecondary" component="p">
                    {_.has(properties, 'description.value') && properties.description.value ? properties.description.value : descriptionDefault}
                </Typography>
            </CardContent>
        {/*</CardActionArea>*/}
        <CardActions style={{display: 'flex', flexDirection: alignButton}}>
            <Button
                component={Link}
                size="small"
                color="primary">
                {buttonLabel}
            </Button>
            {/*<Button size="small" color="primary" disabled>*/}
            {/*    Acción 2*/}
            {/*</Button>*/}
        </CardActions>

    </Card>)
};



class ComponentBuilder extends Component {
    static contextType = GlobalContextBuilder;

    state = {
        component:{},
        properties:{},
        openMenuComponent: false,
    };


    onSetProperty = ()=>{
        // console.log('prpertie')
    };

    isSelected = () => {
        const ctx = this.context;
        return (ctx.currentComponent._id === this.props.component._id);
    };

    componentMoveUp = () => {
        this.componentMove('up');
    };

    componentMoveDown = () => {
        this.componentMove('down');
    };

    getSelectedComponent = () => {
        const ctx = this.context;
        const thisSection = this.props.section;
        const {component} = this.props;
        return component;
    };

    componentMove = (type) => {
        const ctx = this.context;
        const thisSection = this.props.section;
        const {component} = this.props;
        // console.log('Move', type);
        ctx.sortComponent(type, thisSection, component);
    };

    deleteComponent = () => {
        this.context.deleteComponent();
    };

    handleCloseMenuComponent = () => {
        // console.log('entrando 1 ');
        // event.persist();
        if(this.state.openMenuComponent)
            this.setState({openMenuComponent: false});
    };

    handleOpenMenuComponent = event => {
        // console.log('entrando 1 ');
        // event.persist();
        // console.log('entrando sisisi ');
        // if(event.type === 'click'){
            // console.log('entrando 2 ');
            this.setState({openMenuComponent: true});
        // }

    };


    render() {
        const localStyle = {
            selectedComponent: {
                border: '2px solid black',
            }
        };
        const ctx = this.context;
        const {component} = this.props;
        let props = this.props;

        const properties = component.properties ? component.properties : '';

        const isSelected = this.isSelected();

        const imageDefault = 'https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png';

        const Menu = () =>  (
            <MenuComponent
                hidden={!isSelected}
                moveUp={this.componentMoveUp}
                moveDown={this.componentMoveDown}
                open={this.state.openMenuComponent}
                handleCloseMenuComponent={this.handleCloseMenuComponent}
                handleOpenMenuComponent={this.handleOpenMenuComponent}
                deleteComponent={this.deleteComponent}
                openSettings={this.props.openSettings}
                closeSettings={this.props.closeSettings}
                component={component}
            />
        );

        if(this.props.component.type==='card'){
            return (
                <div style={ isSelected ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    <ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>

                        <CardComponent
                            onSetPropertes={this.onSetProperty}
                            ctx={this.context}
                            component={component}
                        />

                    </ButtonBase>
                </div>
            );
        }else if(this.props.component.type==='button'){
            let buttonStyle = _.has(component, 'properties.style.value') ? component.properties.style.value : {color: 'black', backgroundColor: '#E0E0E0'};            
            let buttonAlign = _.has(component, 'properties.buttonAlign.value') && properties.buttonAlign.value ? 
                properties.buttonAlign.value : 'center';

            const styleDiv = {
                textAlign: buttonAlign,
                cursor: 'pointer'
            };
            let buttonSize = _.has(component, 'properties.buttonSize.value') && properties.buttonSize.value ?
                properties.buttonSize.value : 'small';
            let fullWidth = false;
            if(buttonSize === 'block'){
                buttonSize = 'medium';
                fullWidth = true;
            }

           return(
               <div style={ctx.currentComponent._id === component._id ? localStyle.selectedComponent : {}}>
                   <Menu/>
                   <div onClick={props.handleCLick}
                        style={styleDiv}>
                       <Button
                           component={Link}
                           disabled
                           className={'boton-builder'}
                           style={buttonStyle}
                           variant="contained"
                           fullWidth={fullWidth}
                           size={buttonSize}
                       >
                           {_.has(properties, 'text.value') && properties.text.value
                               ? ((component.properties.text.value).length < 10 ? component.properties.text.value : (component.properties.text.value).substring(0,10)+'...') : 'Botón'}
                       </Button>
                   </div>
               </div>
           )
        }else if(this.props.component.type==='image'){
            return(
                <div style={ctx.currentComponent._id ===  component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    <ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>
                        <img src={this.context.getImage(properties)}
                             style={{width: '100%', maxHeight: '100%'}}
                             alt={_.has(properties, 'title.value') ? properties.title.value : 'image'}
                        />
                    </ButtonBase>
                </div>
            )
        }
        else if(this.props.component.type==='text'){
            return(
                <div style={ctx.currentComponent._id ===  component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    {/*<ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>*/}
                    {/*<ButtonBase onClick={props.handleCLick}>*/}
                    <div onClick={props.handleCLick} style={{cursor: 'pointer', overflow: 'scroll'}}>
                        {
                            _.has(properties, 'textHtml.value') ? parse(properties.textHtml.value) : (
                                <Typography variant="h4" component="h4">
                                    Texto
                                </Typography>
                            )
                        }
                    </div>
                    {/*</ButtonBase>*/}
                </div>
            )
        }
        else if(this.props.component.type==='video'){
            return(
                <div style={ctx.currentComponent._id === component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    <ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>
                        {/*<ButtonBase onClick={props.handleCLick}>*/}
                        <div onClick={props.handleCLick} style={{cursor: 'pointer'}}>
                            <center>
                                <YouTube
                                    video={_.has(properties, 'videoId.value') ? properties.videoId.value : ''}
                                    autoplay={_.has(properties, 'autoplay.value') ? properties.autoplay.value : false}
                                    muted={_.has(properties, 'muted.value') ? properties.muted.value : false}
                                    width={500}
                                    height={200}
                                    allowFullscreen={false}
                                />
                            </center>
                        </div>
                    </ButtonBase>
                </div>
            )
        }
        else if(this.props.component.type==='divisor'){
            return(
                <div style={ctx.currentComponent._id === component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    {/*<ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>*/}
                        {/*<ButtonBase onClick={props.handleCLick}>*/}
                        <div onClick={props.handleCLick} style={{cursor: 'pointer'}}>
                            <center>
                                <hr/>
                            </center>
                        </div>
                    {/*</ButtonBase>*/}
                </div>
            )
        }
        else if(this.props.component.type==='space'){
            return(
                <div style={ctx.currentComponent._id === component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    {/*<ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>*/}
                        {/*<ButtonBase onClick={props.handleCLick}>*/}
                        <div onClick={props.handleCLick} style={{cursor: 'pointer'}}>
                            <center>
                                <br/>
                            </center>
                        </div>
                    {/*</ButtonBase>*/}
                </div>
            )
        }
        else if(this.props.component.type==='social'){
            const facebookStyle = {
                color: '#3B5998',
            };
            const instagramStyle = {
                color: '#3f729b'
            };
            const twitterStyle = {
                color: '#00acee'
            };
            const youtubeStyle = {
                color: '#c4302b'
            };
            const pinterestStyle = {
                color: '#c8232c'
            };
            const wrapStyle = {
                verticalAlign: 'middle',
                display: 'inline-flex'
            };
            let align = _.has(properties, 'position.value') && properties.position.value ? properties.position.value : 'horizontal';
            let facebookLabel = _.has(properties, 'facebookLabel.value') && properties.facebookLabel.value ? properties.facebookLabel.value : '';
            let facebookActive = (_.has(properties, 'facebookActive.value') && properties.facebookActive.value);

            let instagramLabel = _.has(properties, 'instagramLabel.value') && properties.instagramLabel.value ? properties.instagramLabel.value : '';
            let instagramActive = (_.has(properties, 'instagramActive.value') && properties.instagramActive.value);

            let twitterLabel = _.has(properties, 'twitterLabel.value') && properties.twitterLabel.value ? properties.twitterLabel.value : '';
            let twitterActive = (_.has(properties, 'twitterActive.value') && properties.twitterActive.value);

            let youtubeLabel = _.has(properties, 'youtubeLabel.value') && properties.youtubeLabel.value ? properties.youtubeLabel.value : '';
            let youtubeActive = (_.has(properties, 'youtubeActive.value') && properties.youtubeActive.value);

            let pinterestLabel = _.has(properties, 'pinterestLabel.value') && properties.pinterestLabel.value ? properties.pinterestLabel.value : '';
            let pinterestActive = (_.has(properties, 'pinterestActive.value') && properties.pinterestActive.value);

            let somethingIsActive = (facebookActive || instagramActive || twitterActive || youtubeActive || pinterestActive);

            return(
                <div style={ctx.currentComponent._id === component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    {/*<ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>*/}
                        {/*<ButtonBase onClick={props.handleCLick}>*/}
                        <div onClick={props.handleCLick} style={{cursor: 'pointer'}}>
                            <center>
                                {
                                    align === 'horizontal' ? (
                                        <>
                                            {
                                                !somethingIsActive ? (<FacebookIcon style={facebookStyle} fontSize={'large'}/>) : ''
                                            }
                                            {
                                                facebookActive ? (
                                                    <Box style={wrapStyle} m={1}>
                                                        <FacebookIcon style={facebookStyle} fontSize={'large'}/>
                                                        {
                                                            facebookLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {facebookLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                ) : ''
                                            }
                                            {
                                                instagramActive ? (
                                                    <Box style={wrapStyle} m={1}>
                                                        <InstagramIcon style={instagramStyle} fontSize={'large'}/>
                                                        {
                                                            instagramLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {instagramLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                ) : ''
                                            }
                                            {
                                                twitterActive ? (
                                                    <Box style={wrapStyle} m={1}>
                                                        <TwitterIcon style={twitterStyle} fontSize={'large'}/>
                                                        {
                                                            twitterLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {twitterLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                ) : ''
                                            }
                                            {
                                                youtubeActive ? (
                                                    <Box style={wrapStyle} m={1}>
                                                        <YouTubeIcon style={youtubeStyle} fontSize={'large'}/>
                                                        {
                                                            youtubeLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {youtubeLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                ) : ''
                                            }
                                            {
                                                pinterestActive ? (
                                                    <Box style={wrapStyle} m={1}>
                                                        <PinterestIcon style={pinterestStyle} fontSize={'large'}/>
                                                        {
                                                            pinterestLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {pinterestLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                ) : ''
                                            }
                                        </>
                                    ) : (
                                        <>
                                            {
                                                !somethingIsActive ? (<FacebookIcon style={facebookStyle} fontSize={'large'}/>) : ''
                                            }
                                            {
                                                facebookActive ? (
                                                    <>
                                                    <Box style={wrapStyle} m={1}>
                                                        <FacebookIcon style={facebookStyle} fontSize={'large'}/>
                                                        {
                                                            facebookLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {facebookLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                        <br/>
                                                        </>
                                                ) : ''
                                            }
                                            {
                                                instagramActive ? (
                                                    <>
                                                    <Box style={wrapStyle} m={1}>
                                                        <InstagramIcon style={instagramStyle} fontSize={'large'}/>
                                                        {
                                                            instagramLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {instagramLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                        <br/>
                                                        </>
                                                ) : ''
                                            }
                                            {
                                                twitterActive ? (
                                                    <>
                                                    <Box style={wrapStyle} m={1}>
                                                        <TwitterIcon style={twitterStyle} fontSize={'large'}/>
                                                        {
                                                            twitterLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {twitterLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                        <br/>
                                                        </>
                                                ) : ''
                                            }
                                            {
                                                youtubeActive ? (
                                                    <>
                                                    <Box style={wrapStyle} m={1}>
                                                        <YouTubeIcon style={youtubeStyle} fontSize={'large'}/>
                                                        {
                                                            youtubeLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {youtubeLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                        <br/>
                                                        </>
                                                ) : ''
                                            }
                                            {
                                                pinterestActive ? (
                                                    <>
                                                    <Box style={wrapStyle} m={1}>
                                                        <PinterestIcon style={pinterestStyle} fontSize={'large'}/>
                                                        {
                                                            pinterestLabel ? (
                                                                <Typography
                                                                    style={{marginTop: 5}}
                                                                >
                                                                    {pinterestLabel}
                                                                </Typography>
                                                            ) : ''
                                                        }

                                                    </Box>
                                                        <br/>
                                                        </>
                                                ) : ''
                                            }
                                        </>
                                    )
                                }

                            </center>
                        </div>
                    {/*</ButtonBase>*/}
                </div>
            )
        }
        else if(this.props.component.type==='gallery'){
            const defaultImage = 'https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png';
            return(
                <div style={ctx.currentComponent._id === component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    {/*<ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>*/}
                        {/*<ButtonBase onClick={props.handleCLick}>*/}
                        <div onClick={props.handleCLick} style={{cursor: 'pointer'}}>
                            <center>
                                <Carousel
                                    arrows
                                >
                                    {
                                        _.has(properties, 'images.value') ? properties.images.value.map(
                                            (prop, index) => (
                                                <img key={index} src={_.has(prop, 'image.value') && prop.image.value ? prop.image.value : defaultImage} />
                                            )
                                        ) : <img src={defaultImage} />
                                    }

                                </Carousel>
                            </center>
                        </div>
                    {/*</ButtonBase>*/}
                </div>
            )
        }
        else if(this.props.component.type==='rating'){
            let defaultStars = 0;
            let stars = _.has(properties, 'stars.value') && properties.stars.value ? parseInt(properties.stars.value) : defaultStars;
            let size = _.has(properties, 'size.value') && properties.size.value ? properties.size.value : 'medium';
            return(
                <div style={ctx.currentComponent._id === component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    {/*<ButtonBase onClick={props.handleCLick} style={{width: '100%'}}>*/}
                        {/*<ButtonBase onClick={props.handleCLick}>*/}
                        <div onClick={props.handleCLick} style={{cursor: 'pointer'}}>
                            <center>
                                <Rating
                                    value={stars}
                                    size={size}
                                    readOnly={true}
                                />
                            </center>
                        </div>
                    {/*</ButtonBase>*/}
                </div>
            )
        }
        else if(this.props.component.type==='map'){
            const lat = _.has(properties, 'centralPointLat') && properties.centralPointLat ? properties.centralPointLat : 51.5;
            const lng = _.has(properties, 'centralPointLon') && properties.centralPointLon ? properties.centralPointLon : -0.1;
            const centroId = lat && lng ? [lat, lng] : [51.505, -0.09];
            const name = _.has(properties, 'name') && properties.name ? properties.name : 'Map';
            const points = _.has(properties, 'points') && properties.points ? properties.points : [];

            // console.log('centroId', centroId, 'name', name, 'points', points);

            return(
                <div style={ctx.currentComponent._id === component._id ? localStyle.selectedComponent : {}}>
                    <Menu/>
                    <div onClick={props.handleCLick} style={{cursor: 'pointer'}}>
                        <Map center={centroId} zoom={13} style={{height: 500, width: '100%'}}>
                            <TileLayer
                                attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                                url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                            />
                            {

                                points.map(
                                    (prop, index) => (
                                        <Marker
                                            key={index}
                                            position={
                                                _.has(prop, 'pointLat') && prop.pointLat && _.has(prop, 'pointLon') && prop.pointLon ? [prop.pointLat, prop.pointLon] : [20.987909, -89.621682]
                                            }
                                        >
                                            <Popup>
                                                {/*{prop.name} <br /> Easily customizable.*/}
                                                {prop.name}
                                                {
                                                    _.has(prop, 'address') && prop.address ? (
                                                        <>
                                                            <br/>
                                                            {prop.address}
                                                        </>
                                                    ) : ''
                                                }
                                            </Popup>
                                        </Marker>
                                    )
                                )
                            }

                        </Map>
                    </div>
                    {/*</ButtonBase>*/}
                </div>
            )
        }
        else{
            return (
                <div>
                    {
                        <p>{this.props.component.type}</p>
                    }
                </div>
            );
        }


    }
}



export default ComponentBuilder;