import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import FormLangs from "../../../../components/events/admin/lang/FormLangs";
import {Redirect} from "react-router-dom";
import {ApiServiceEvents} from "../../../../api-services/events";
import GlobalContext from "../../../../GlobalContext";
import _ from "lodash";
import {Trans} from "@lingui/macro";

const apiEvent = new ApiServiceEvents();

/**
 *
 * This view allows you to edit the language details
 * @class EditLang
 * @extends {Component}
 */
class EditLang extends Component {
    static contextType = GlobalContext;
    state= {
        code:'',
        name:'',
        slug:null,
        showActive:true,
        isActive:false,
        loading:false,
        redirect:false,
        idEvent: null,
        idIdiom:null,
        breadCrumbs:[]
    }


    componentDidMount() {

        let slug = this.props.match.params.slug;
        let _idIdiom = this.props.match.params.lang;
        let ctx = this.context;

        this.getInfoIdiom(_idIdiom);



        let breadCrumbs = [
            {
                name:<Trans>Lista de lenguajes</Trans>,
                active:false,
                link:`/${slug}/admin/lang/list`
            },
            {
                name:<Trans>Editar lenguaje</Trans>,
                active:true,
                link:``
            }
        ]

        this.setState({
            _idIdiom,
            slug,
            breadCrumbs
        })


        apiEvent.getEventInfo('',slug).then(
            (response) => {
                this.setState({
                    idEvent:response.event._id
                })
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );
    }

    /**
     * function to get details language
     * @param  {number} _idIdiom
     */
    getInfoIdiom(_idIdiom){
        let ctx = this.context;
        apiEvent.getIdiomInfo(_idIdiom).then((res)=>{
            console.log('info idiom',res)
            let lang = res.availableLang;


            this.setState({
                code: lang.code,
                name:lang.name,
                isActive:lang.isActive
            })

        }).catch((err)=>{
            if (_.has(err, 'response.status') && err.response.status === 403) {
                ctx.openPermissionNotification();
            }
            console.log(err)
        }).finally(()=>{})
    }

    /**
     * 
     * function to set values ​​to state when an input changes value
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;
        this.setState({
            [name]: value
        });
    };


    /**
     * Function that sends the language form to update
     * @param  {Object} e
     */
    handleSubmit = (e)  => {
        e.preventDefault();
        let ctx = this.context;
        let idLang = this.props.match.params.lang;
        let {idEvent,code,name,isActive} = this.state;

        this.setState({
            loading:true
        })

        let request = {
            _id:idLang,
            _idEvent:idEvent,
            code,
            name,
            isActive
        }

        // apiEvent.updateIdiom(request).then((res)=>{
        apiEvent.updateIdiomActive(request).then((res)=>{
            console.log(res)
            this.setState({
                redirect:true
            })
        }).catch((err)=>{
            console.log(err);
            if (_.has(err, 'response.status') && err.response.status === 403) {
                ctx.openPermissionNotification();
            }
            if(err.response.status === 404){
                ctx.openNotification(err.response.data.status, 'error');
            }
        }).finally(()=>{
            this.setState({loading:false})
        })


        // apiEvent.addIdiom(idEvent,code,name).then((res)=>{
        //     console.log('addidiom',res)
        //
        //     this.setState({
        //         redirect:true,
        //     })
        // }).catch((err)=>{
        //     console.log(err)
        // }).finally(()=>{
        //     this.setState({
        //         loading:false
        //     })
        // })


    };


    render() {
        let {code,name,loading,isActive,slug,breadCrumbs} = this.state;


        if(this.state.redirect) return <Redirect push to={`/${slug}/admin/lang/list`} />
        return (
            <AdminEventLayout breadcrumbs={breadCrumbs} title={'Editar lenguaje'}>
                <Grid container>
                    <Grid item xs={12}>
                        <FormLangs
                            handleInputChange={this.handleInputChange}
                            code={code}
                            name={name}
                            showActive={true}
                            isActive={isActive}
                            handleSubmit={this.handleSubmit}
                            loading={loading}
                            title={<Trans>Editar lenguaje</Trans>}
                        />
                    </Grid>

                </Grid>
            </AdminEventLayout>
        );
    }
}

export default EditLang;