import React from 'react';
import MaterialTable from 'material-table';
import { Redirect } from "react-router-dom";
import Avatar from '@material-ui/core/Avatar';
import Icon from '@material-ui/core/Icon';
import { loadCSS } from 'fg-loadcss';
import GlobalContext from "../../../../GlobalContext";
import moment from "moment";
import { t, Trans } from "@lingui/macro";
import { withI18n } from "@lingui/react";

class SubEventTable extends React.Component {
    static contextType = GlobalContext;
    state = {
        redirectToEdit: '',
        selectedRow: '',
        redirectToQr: false,
        columns: [
            // { title: 'Imagen', field: 'image', render: rowData => <Avatar alt="Remy Sharp" src={rowData.image} />},
            { title: <Trans>Nombre</Trans>, field: 'name' },
            { title: <Trans>Lugar</Trans>, field: 'place' },
            // { title: 'Dirección', field: 'address' },
            { title: <Trans>Comienza</Trans>, field: 'startTime', type: 'datetime', render: rowData => moment(rowData.startTime).format('DD/MM/YYYY h:mm:ss a') },
            { title: <Trans>Termina</Trans>, field: 'endTime', type: 'datetime', render: rowData => moment(rowData.endTime).format('DD/MM/YYYY h:mm:ss a') },
            { title: <Trans>¿Requiere acceso QR?</Trans>, field: 'requireQrCodeAccess', type: 'boolean' },

        ],
    };

    componentDidMount() {
        loadCSS(
            'https://use.fontawesome.com/releases/v5.1.0/css/all.css',
            document.querySelector('#font-awesome-css'),
        );
    }

    render() {

        let ctx = this.context;

        if (this.state.redirectToEdit) return <Redirect push to={`/${ctx.slug}/admin/sub-event/${this.state.selectedRow}/`} />;

        let state = this.state;
        let setState = this.setState;
        let data = this.props.rows;

        return (
            <MaterialTable
                title={<Trans>Sub-eventos</Trans>}
                columns={state.columns}
                data={data}
                editable={{
                    onRowDelete: this.props.handlerDelete,
                    // onRowAdd: newData => console.log('Hola mundo')
                }}
                actions={[
                    {
                        icon: 'edits',
                        tooltip: this.props.i18n._(t`Editar`),
                        onClick: (event, rowData) => this.setState({ redirectToEdit: true, selectedRow: rowData._id })
                    },
                    // rowData => ({
                    //     icon: 'delete',
                    //     tooltip: 'Delete User',
                    //     onClick: (event, rowData) => alert("You want to delete " + rowData.name),
                    //     disabled: rowData.birthYear < 2000
                    // })
                ]}
                options={{
                    actionsColumnIndex: -1,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100, 500],
                    paging: true,
                }}
                localization={this.context.tableConfig.localization}
            />
        );
    }
}

export default withI18n()(SubEventTable)