import React, { Component } from 'react';
import GlobalContext from "../../../../GlobalContext";
import moment from "moment";
import MaterialTable from "material-table";
import { t, Trans } from '@lingui/macro';
import _ from "lodash";
import { Redirect } from "react-router-dom";
import { withI18n } from "@lingui/react";

/**
 *
 * Component to display the chatbots table
 * @class ChatbotConfigTable
 * @extends {Component}
 */
class ChatbotConfigTable extends Component {

    static contextType = GlobalContext;

    state = {
        redirectToEdit: false,
        selectedRow: '',
        columns: [
            { title: <Trans>Nombre</Trans>, field: 'name' },
        ],
    };

    render() {
        const data = this.props.rows;
        const state = this.state;
        const ctx = this.context;
        let procesedData = _.map(data, function (x) {
            let isLargeName = x.name.length > 40
            let nameDisplay = isLargeName ? x.name.substr(0, 40) + '...' : x.name;
            return {
                _id: _.has(x, '_id') ? x._id : '0',
                name: _.has(x, 'name') ? nameDisplay : 'N/A',
            }

        });
        if (this.state.redirectToEdit && this.state.selectedRow) return <Redirect push to={`/${ctx.slug}/admin/chatbot/${this.state.selectedRow}/`} />;
        return (
            <MaterialTable
                title={<Trans>Configuración de pasos del chatbot</Trans>}
                columns={state.columns}
                data={procesedData}
                editable={{
                    onRowDelete: this.props.handlerDelete,
                }}
                actions={[
                    {
                        icon: 'edits',
                        tooltip: this.props.i18n._(t`Editar`),
                        onClick: (event, rowData) => this.setState({ redirectToEdit: true, selectedRow: rowData._id })
                    },
                    {
                        icon: 'filterNone',
                        tooltip: this.props.i18n._(t`Duplicar`),
                        onClick: (event, rowData) => this.props.onCloneRow(rowData._id)
                    },
                ]}
                options={{
                    actionsColumnIndex: -1,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100, 500],
                    paging: true,
                }}
                localization={this.context.tableConfig.localization}
            />
        );
    }
}

export default withI18n()(ChatbotConfigTable);