import React, { Component } from 'react'

import {Redirect, withRouter} from "react-router-dom"
import Toolbar from "@material-ui/core/Toolbar"
import Typography from "@material-ui/core/Typography"
import {t, Trans} from "@lingui/macro";
import LoginForm from "../../components/security/LoginForm"
import {ApiServiceSecurity} from '../../api-services/security'
import AppBar from "../../components/AppBar"
import GlobalContext from "../../GlobalContext";

const api = new ApiServiceSecurity();

/**
 *
 * This view is for admin user login 
 * @class SignIn
 * @extends {Component}
 */
class SignIn extends Component {

    static contextType = GlobalContext;

    state = {
        token:'',
        redirect: false,
        url: '',
        user: '',
        showError:false,
        password: '',
        loading:false,
        showMessageError: false,
        urlNextDefault: '/event/list/',
    };

    /**
     * function for validate JWT valid
     */
    validateJWT() {
        let ctx = this.context;
        if(ctx.tokenInvalidOrExpired){
            localStorage.removeItem('jwt');
            ctx.resetTokenInvalid();
        }else {
            const jwt = localStorage.getItem('jwt');
            if (jwt) {
                let new_state = {redirect: true, url: this.state.urlNextDefault};
                this.setState(new_state);
            }
        }
    };

    componentDidMount() {
        this.validateJWT(this.props);
    }

    /**
     * Function to get params from url
     * @returns {Object} params from url for example variable next
     */
    getParams = () => {
        return new URLSearchParams(this.props.location.search)
    };

    /**
     * Function to user login
     * @param  {Object} formData
     */
    login = (formData) => {
        let ctx = this.context;
        this.setState({
            loading:true
        })
        api.login(formData).then(res=>{
            if(res.status === 200){
                let userData = res.data.user;
                let groupsPerms = res.data.groupsPerms;
                delete userData.password;
                localStorage.setItem('jwt', res.data.userToken);
                localStorage.setItem('user', JSON.stringify(userData));
                localStorage.setItem('groupsPerms', JSON.stringify(groupsPerms));
                ctx.setToken(res.data.userToken);
                let params = this.getParams();
                let url = params.get('next') ? params.get('next') : this.state.urlNextDefault;
                let new_state = {redirect: true, url};
                ctx.setLanguage(res.data.user.lang);                
                this.setState(new_state);
            }else{
                this.setState({showMessageError: true, showError:true});
            }
        }).catch(err => {
            console.error('login error',err);
            this.setState({showMessageError: true, showError:true});
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })
    };


    /**
     * Function to submit form user login
     * @param  {Object} e
     */
    handleSubmit = (e) => {
        e.preventDefault();
        let formData = new FormData();
        let state = this.state;
        let user = state.user;
        let password = state.password;
        formData.append("email", user);
        formData.append("password", password);
        this.login(formData);
    };

    /**
     * Function to set state on user for login
     * @param  {Object} event
     */
    handleChangeUser = (event) => {
        this.setState({user: event.target.value, showMessageError:false});
    };


    /**
     * This function allows close the alert message in the form login
     */
    onCloseError =(e)=>{
        this.setState({
            showError:false
        })
    };

    /**
     * Function to set state on password for login
     * @param  {object} event
     */
    handleChangePassword = (event) => {
        this.setState({password: event.target.value, showMessageError:false});
    };


    render() {
        let ctx = this.context;
        if (this.state.redirect) {
            return <Redirect push to={this.state.url} />;
        }
        let state = this.state;
        return(
                <>
                    <br/>
                    <LoginForm
                        handleSubmit={this.handleSubmit}
                        handleUser={this.handleChangeUser}
                        version={ctx.version}
                        fromMainLogin={true}
                        loading={state.loading}
                        closeAlert={this.onCloseError}
                        showError={state.showError}
                        handlePassword={this.handleChangePassword}
                        showMessageError={state.showMessageError}
                    />
                </>
            )
    }
}

export default withRouter(SignIn)