import React, {useContext, useEffect} from "react";
import _ from "lodash";

import {
    Route,
    Redirect,
} from "react-router-dom";

import GlobalContext from "../../GlobalContext";
import {func} from "prop-types";


const SUPER_ADMIN = 1;
const EVENT_ADMIN = 2;
const EVENT_VIEW = 3;


let ProtectedRoute = ({ component: Component, ...rest }) => {
    const ctx = useContext(GlobalContext);
    const jwt = localStorage.getItem('jwt');

    let isAuthenticated = !!jwt;

    // let routeType = rest.routeType ? rest.routeType : SUPER_ADMIN;
    // let slug = '';
    //
    // if(routeType === EVENT_ADMIN || routeType === EVENT_VIEW) {
    //     slug = routeType === EVENT_ADMIN || routeType === EVENT_VIEW ? rest.computedMatch.params.slug : '';
    //
    //     if(!slug){
    //         return <Redirect
    //             to={{
    //                 pathname: '/not-found/',
    //             }}
    //         />
    //     }
    // }

    function getLoginPath(props) {
        let routeType = rest.routeType ? rest.routeType : SUPER_ADMIN;
        if(routeType === SUPER_ADMIN){
            return props.match.path ? `/security/signin/?next=${props.match.path}` : '/security/signin/'
        }else {
            // Event Admin
            let slug = props.match.params.slug;
            let next = props.match.path.replace(':slug', slug);


            return props.match.path ? `/${slug}/security/signin/?next=${next}` : '/security/signin/'
        }
    }

    function getInfoGroups() {
        let groupPermsText = localStorage.getItem('groupsPerms');
        return groupPermsText && groupPermsText !== 'undefined' ? JSON.parse(groupPermsText) : '';
    }

    function getInfoUserLogged(){
        let userData = localStorage.getItem('user');
        return userData && userData !== 'undefined' ? JSON.parse(userData) : '';
    }

    /**
     * This function validate the slug that the user is logued in
     */
    function isSLugCorrect(){
        let success = true;
        let slug = localStorage.getItem('slug');
        let pathname = window.location.pathname;
        let slug_storage = pathname.split('/')[1];

        if(slug!==slug_storage){
            success = false;
        }
        console.log('-- slug', slug, pathname.split('/'))
        return success;
    }

    function userHasPermission(pagePerms) {
        const currentSlug = rest.computedMatch.params.slug;
        let userData = getInfoUserLogged();

        // Se valida si es super admin o que no tenga esa atributo porq los superadmin no lo tienen
        if (userData.isSuperAdmin || !_.has(userData, 'isSuperAdmin')) return true;
        if(userData.eventSlug !== currentSlug) return false;

        if (userData.isAdmin) return true;

        if(pagePerms.length === 0) return true;
        let groupsPerms = getInfoGroups();

        let found = _.find(groupsPerms, function (x) {
            let perms = x.perms;
            console.log(perms, pagePerms, 'Numero', _.includes(perms, ...pagePerms));
            return _.includes(perms, ...pagePerms);
        });
        return !!(found);
    }

    function  checkPerms(props) {
        // console.log('propiedades', rest);
        let pagePerms = rest.perms ? rest.perms : [];
        let userHasPerm = userHasPermission(pagePerms);


        // console.log('HAS PERMS?', userHasPerm);
        return (userHasPerm) ? <Component {...props} /> : (
            <Redirect
                to={{
                    pathname: '/403/',
                    state: { from: props.location }
                }}
            />
        );
    }

    // let routeType = rest.routeType ? rest.routeType : SUPER_ADMIN;
    // let slug = ctx.slug;
    // const validSlug = isSLugCorrect();
    // console.log('validslug',slug)
    //
    // if(routeType===EVENT_ADMIN){
    //     if(!validSlug){
    //         return (
    //             <Route
    //                 {...rest}
    //                 render={props =>(
    //                     <div>
    //                         <Redirect
    //                             to={`/`}
    //                         />
    //                     </div>
    //                 )
    //                 }
    //
    //         />
    //     )
    //     }
    // }

    return (

        <Route
            {...rest}
            render={props =>
                isAuthenticated ? (
                    checkPerms(props)
                ) : (<div>
                    <Redirect
                        to={{
                            pathname: getLoginPath(props),
                            state: { from: props.location }
                        }}
                    />
                    </div>
                )
            }
        />
    )
};

export default ProtectedRoute
