import React, {Component} from 'react';
import AdminLayout from "../../layouts/AdminLayout";
import {Grid, Paper} from '@material-ui/core';
import Typography from "@material-ui/core/Typography";
import Card from "@material-ui/core/Card";
import IconButton from "@material-ui/core/IconButton";
import {Divider} from "semantic-ui-react";
import {ApiServiceEvents} from "../../api-services/events";
import AddIcon from '@material-ui/icons/Add';
import SimpleListEvents from "../../components/events/SimpleListEvents";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import TextField from "@material-ui/core/TextField";
import DialogActions from "@material-ui/core/DialogActions";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import Slide from '@material-ui/core/Slide';
import CircularProgress from "@material-ui/core/CircularProgress";
import Snackbar from "@material-ui/core/Snackbar";
import {Redirect} from "react-router-dom";
import GlobalContext from "../../GlobalContext";
import { Trans } from '@lingui/macro';
import SkeletonExample, {Media} from "../../components/skeletons/Skeleton";
import Tooltip from '@material-ui/core/Tooltip';


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});
// const apiEvents = new ApiServiceEvents();


class EventList extends Component {
    static contextType = GlobalContext;
    apiEvents = null;
    state={
        events:[],
        loadingModal:false,
        currentEvent:null,
        openModal:false,
        closeModal:true,
        openToast:false,
        msgToast:'',
        loading:true,
        redirectToCreate:false,
        apiEvents: null,
    };

    componentWillMount() {
        let ctx = this.context;
        let apiEvents = new ApiServiceEvents(ctx.token);
        this.apiEvents = apiEvents;
    }

    componentDidMount() {
        // let ctx = this.context;
        // let apiEvents = new ApiServiceEvents(ctx.token);
        // this.setState({apiEvents: apiEvents});
        this.getEventList();
        const token = window.localStorage.getItem('jwt');
        // console.log('token',token)
    }


    getEventList(){
        this.apiEvents.getEventList().then(res=>{
            this.setState({events:res})
            console.log('res',res);
        }).catch(err=>{
            this.setState({events:[]})
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })
    }

    _handleChangeStatusEvent=(event)=>{
        let message = `¿Desea cambiar el estatus del evento ${event.name}?`;
        if(window.confirm(message)){
            this.apiEvents.deactivateEvent(!event.isActive,event._id)
                .then(res=>{
                  console.log(res)
                  this.getEventList();
                }
            ).catch(err=>console.log(err));
            event.isActive?console.log('Evento desactivado'):console.log('Evento activado');
        }
    }

    _handleDeleteEvent=(event)=>{
        this.handleClickOpen(event)
    }


    handleClickOpen=(event)=> {
        this.setState({openModal:true,closeModal:false,currentEvent:event})
    }

    handleCloseModal=()=> {
        this.setState({closeModal:true, valueInputModal:'',openModal:false})
    }


    handleCloseToast=()=>{
        this.setState({openToast:true})
    }

    /**
     * Se ejecuta cuando se confirma la accion de eliminar
     */
    handleConfirmDelete=()=>{
        if(this.state.valueInputModal==='ELIMINAR'){
            this.setState({loadingModal:true});
            this.apiEvents.deleteEvent(this.state.currentEvent._id).then(res=>{
                this.handleCloseModal()
                this.getEventList()
                this.setState({loadingModal:false,openToast:true,msgToast:<Trans>Eliminado correctamente</Trans>});
                setInterval(()=>{
                    this.setState({openToast:false})
                },3000)
            }).catch(err=>console.log(err))
        }
    };

    /**
     * Se ejecuta cuando se escribe en el input del modal para eliminar
     * @param e
     */
    handleChangeDeleteInputModal=(e)=>{
        this.setState({valueInputModal: e.target.value});
    };


    onHandleCloneEvent=(event)=>{
        let slug = event.slug;
        if(window.confirm('¿Deseas clonar el evento?')){
                this.apiEvents.cloneEvent(slug).then((res)=>{
                    console.log('clonado', res)
                    this.getEventList()
                }).catch((err)=>{
                    console.log('err',err)
                })
        }
    }

    handleCreateLink=()=>{
        this.setState({
            redirectToCreate:true
        })
    }

    render() {
        if(this.state.redirectToCreate) return <Redirect push to={'/event/new/'} />
        const {events,openModal,msgToast,openToast,loadingModal,valueInputModal} = this.state
        return (
            <AdminLayout title='Eventos'>
                        <Typography variant="h5" style={{padding:'10px'}} component="h3">
                            Eventos
                            <Tooltip title={<Trans>Agregar Evento</Trans>}>
                                <IconButton onClick={this.handleCreateLink} color="primary" aria-label="add to shopping cart">
                                    <AddIcon/>
                                </IconButton>
                            </Tooltip>
                        </Typography>
                        <Divider/>
                        <Grid container style={{marginTop:'10px'}} spacing={3}>
                            {/*<SkeletonExample/>*/}
                            {
                                this.state.loading ? <SkeletonExample/> : <></>
                            }


                            {
                                events.length>0?
                                    <SimpleListEvents onHandleDeleteEvent={this._handleDeleteEvent}
                                                      onHandleCloneEvent={this.onHandleCloneEvent}
                                                      onHandleChangeStatus={this._handleChangeStatusEvent}
                                                      events={events}/>:
                                    (this.state.loading?'':(<Grid item xs={12}>
                                    <Typography variant="body2" align="center" color="textSecondary" component="p">
                                        <span><Trans>No existen eventos para mostrar</Trans></span>:''
                                    </Typography>
                                </Grid>))
                            }


                            {/*{*/}
                            {/*    <Grid item align="center" xs={12}>*/}
                            {/*        {this.state.loading?<CircularProgress />:''}*/}
                            {/*    </Grid>*/}
                            {/*}*/}

                        </Grid>


                <Dialog open={openModal}
                        onClose={this.handleCloseModal}
                        TransitionComponent={Transition}
                        keepMounted
                        aria-labelledby="form-dialog-title">
                    <DialogTitle id="form-dialog-title"><Trans>Eliminar evento</Trans></DialogTitle>
                    <DialogContent>
                        <DialogContentText>
                            <Trans>Por favor para eliminar el evento escribe en el campo de texto la palabra 'ELIMINAR' respetando las mayúsculas.</Trans>
                        </DialogContentText>
                        <TextField
                            autoFocus
                            margin="dense"
                            type="text"
                            value={this.state.valueInputModal}
                            onChange={this.handleChangeDeleteInputModal}
                            fullWidth
                        />
                    </DialogContent>
                    <DialogActions>
                        <Button onClick={this.handleCloseModal} color="primary">
                            Cancelar
                        </Button>
                        {
                            loadingModal ? <CircularProgress size={20}/> :
                            <Button disabled={valueInputModal!=='ELIMINAR'} onClick={this.handleConfirmDelete} color="primary">
                                <Trans>Confirmar</Trans>
                            </Button>
                        }
                    </DialogActions>
                </Dialog>

                <Snackbar
                    open={openToast}
                    ContentProps={{
                        'aria-describedby': 'message-id',
                    }}
                    message={<span id="message-id">{msgToast}</span>}
                />
            </AdminLayout>
        );
    }
}

export default EventList;