export const mainListItems = (
    <div>
        <ListItem button component={Link} to='/event/list/'>
            <ListItemIcon>
                <EventIcon />
            </ListItemIcon>
            <ListItemText primary="Eventos" />
        </ListItem>
        <ListItem button component={Link} to='/user/list/'>
            <ListItemIcon>
                <PeopleIcon />
            </ListItemIcon>
            <ListItemText primary="Usuarios" />
        </ListItem>
        {/*<ListItem button>*/}
        {/*    <ListItemIcon>*/}
        {/*        <BuildIcon />*/}
        {/*    </ListItemIcon>*/}
        {/*    <ListItemText primary="Configuración" />*/}
        {/*</ListItem>*/}
    </div>
);

