let PORT = '3000';
let URL = 'http://localhost';

try {
        let jenkinsVersion = require('./jenkins_version');
        if(jenkinsVersion){
                // URL = 'http://52.52.146.59';
                URL = 'https://api.tianguisturistico.com';
        }
        PORT = ''
} catch (e) {
        console.log(e.response || e.message);
}


try {
        let serverDev = require('./dev.txt');
        if(serverDev){
                URL = 'https://dev-api-sectur.ddns.net';
        }
        PORT = '';
} catch (e) {
        console.log(e.response || e.message);
}


const API_URL = PORT ? `${URL}:${PORT}` : `${URL}`;


export let config = {
        API_URL
};
