import React, {Component} from 'react';
import CssBaseline from "@material-ui/core/CssBaseline";
import {Field, Form} from "react-final-form";
import {Grid, Paper, Typography} from "@material-ui/core";
import {Select, TextField} from "final-form-material-ui";
import {SaveButtonSpinner} from "../../buttons/StandartButtons";
import {GlobalContextBuilder} from "../../../GlobalContext";
import MenuItem from "@material-ui/core/MenuItem";

class ConfigRating extends Component {
    static contextType = GlobalContextBuilder;

    state = {
        loading: true,
        request: {
            stars: {
                value: 0,
                type: 'int'
            },
            size: {
                value: '',
                type: 'string'
            }
        }
    };

    handleSubmit = (values) => {
        this.props.onSaveConfig(values);
    };

    validate = (values) => {

    };

    setInitialValues = () => {
        const ctx = this.context;
        const component = ctx.currentComponent;


        if(!component.properties) {
            this.setState({loading:false});
            return;
        }

        let newState = this.state;

        newState.request = component.properties;
        newState.loading = false;

        this.setState(newState);

    };

    componentDidMount() {
        this.setInitialValues();
    }

    render() {
        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                {
                    this.state.loading ? '' : (
                        <Form
                            onSubmit={this.handleSubmit}
                            initialValues={this.state.request}
                            validate={this.validate}
                            render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                                <form onSubmit={handleSubmit} noValidate>
                                    <Paper style={{ padding: 16 }}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Propiedades
                                        </Typography>
                                        <Grid container alignItems="flex-start" spacing={2}>

                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="stars.value"
                                                    component={TextField}
                                                    label="Número de estrellas"
                                                    variant="outlined"
                                                    margin="normal"
                                                />

                                            </Grid>
                                            {/*<Grid item xs={12}>*/}
                                            {/*    <Field*/}
                                            {/*        fullWidth*/}
                                            {/*        name="size.value"*/}
                                            {/*        component={TextField}*/}
                                            {/*        label="Tamaño"*/}
                                            {/*        variant="outlined"*/}
                                            {/*        margin="normal"*/}
                                            {/*    />*/}

                                            {/*</Grid>*/}
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    name="size.value"
                                                    component={Select}
                                                    label="Seleccionar un tamaño"
                                                    formControlProps={{fullWidth: true}}
                                                    variant="outlined"
                                                >
                                                    <MenuItem value={'small'}>Chico</MenuItem>
                                                    <MenuItem value={''}>Normal</MenuItem>
                                                    <MenuItem value={'large'}>Grande</MenuItem>
                                                </Field>
                                            </Grid>


                                            {submitError && <div className="error">{submitError}</div>}
                                            <Grid item style={{ marginTop: 16 }} xs={12}>

                                                <SaveButtonSpinner
                                                    loading={this.props.loading}
                                                />
                                            </Grid>
                                        </Grid>
                                    </Paper>
                                </form>
                            )}
                        />
                    )
                }
            </div>
        );
    }
}

export default ConfigRating;