import React, {Component} from 'react';
import SearchField from "./SearchField";
import AddIcon from '@material-ui/icons/Add';
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";

class SearchFieldAdd extends Component {
    render() {
        return (
            <SearchField
                buttons={
                <IconButton component={Link} to={this.props.linkTo}  color="primary" style={{padding: 10}} aria-label="directions">
                    <AddIcon />
                </IconButton>
            }

                {...this.props}

            />
        );
    }
}

export default SearchFieldAdd;