import React from "react";
import {
    FormGroup,
    FormControl,
    FormControlLabel, Grid, Paper} from "@material-ui/core";
import _ from "lodash";
import { Trans } from '@lingui/macro';

import AdminEventLayout from "../../../layouts/AdminEventLayout";
import Typography from "@material-ui/core/Typography";
import GlobalContext from "../../../GlobalContext";
import {ApiServiceEvents} from "../../../api-services/events";
import Wysiwyg from "../../../components/fields/Wysiwyg";
import {convertToRaw, EditorState} from "draft-js";
import draftToHtml from "draftjs-to-html";
import LoadingOverlay from "react-loading-overlay";
import Checkbox from "@material-ui/core/Checkbox";
import TextField from "@material-ui/core/TextField";
import Button from '@material-ui/core/Button';
import {CancelButton, SaveButtonSpinner} from "../../../components/buttons/StandartButtons";
import Radio from '@material-ui/core/Radio';
import RadioGroup from '@material-ui/core/RadioGroup';
import {Redirect} from "react-router-dom";
import AlertText from "../../../components/alerts/AlertText";
const apiEvent = new ApiServiceEvents();


/**
 *
 * This view allows send mass email
 * @export
 * @class SendGeneralMail
 * @extends {React.Component}
 */
export default class SendGeneralMail extends React.Component{
    static contextType = GlobalContext;

    state = {
        forPublic: false,
        open:false,
        userList: [],
        textHtml: '',
        subject:'',
        selectedValue:'email',
        rolesSelected:[],
        message:'',
        filesEmail:[],
        loading:false,
        send: false,
        resources:[],
        method:'email',
        redirect:false,
        resourcesSelected:[],
        checkQR:false,
        roles:[],
        forAdmin: false,
        forStaff: false,
        msgLoading:'cargando...',
        editorState: EditorState.createEmpty(),
        breadcrumbs:[{
            name: <Trans>Envío de mail masivo</Trans>,
            active:true,
            link:''
        }],
        errors:{
            subject: false
        }

    };

    /**
     * Function for editor email config
     * @param  {Object} editorState
     */
    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
            textHtml: draftToHtml(convertToRaw(editorState.getCurrentContent()))
            // text: editorState.getCurrentContent()
        });
    };


    componentDidMount() {
        this.loadRoles();
        let ctx = this.context;
        let slug = this.props.match.params.slug;

        let breadcrumbs=[
            {
                name:<Trans>Lista de usuarios</Trans>,
                active:false,
                link:`/${slug}/admin/user/list/`
            },
            {
            name: <Trans>Envío de mail masivo</Trans>,
            active:true,
            link:''
        }];

        this.setState({
            breadcrumbs
        })


        apiEvent.getEventInfoBySlug(ctx.slug).then(res=>{
           if(res._id){
               this.getResources(res._id);
           }
        }).catch((err)=>{
           console.log(err)
        });
    
    }
    /**
     * This funtion is for get resources event
     * @param  {number} idEvent
     */
    getResources(idEvent){
        let ctx = this.context;
        console.log('eventinfo',ctx.eventInfo)
        apiEvent.getResources(ctx.eventInfo._id).then((res)=>{
            console.log(res)
            this.setState({
                resources:res
            })
        }).catch((err)=>{
            console.log(err)
        }).finally(()=>{})
    }

    /**
     * function for get roles
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvent.getRoles(ctx.slug).then(
            (res) => {
                // console.log('Perfiles', res);
                this.setState({roles: res});
            }
        ).catch(
            (err) => {
                console.log('Error en la carga de perfiles', err)
            }
        );
    };

    
    /**
     * function to validate inputQR is checked
     * @param  {Object} event
     */
    handleCheckQR =(event)=>{
        let isChecked = event.target.checked;
        this.setState({
            checkQR:isChecked
        });
    };
    /**
     * function for manage roles selected and set state
     * @param  {number} id
     * @param  {Object} =>(event
     */
    handleCheckChange = (id) => (event) =>{
        let isChecked = event.target.checked;
        let {rolesSelected} = this.state;
        let existInArray = rolesSelected.includes(id); // validamos que exista en el arreglo de grupos

        if(!existInArray && isChecked){
            rolesSelected.push(id)
        }else if(existInArray && !isChecked){
            rolesSelected = _.remove(rolesSelected, function (o) {
                return o !== id
            });
        }
        this.setState({
            rolesSelected
        })
    }

    handleCheckChangeForAdmin = (id) => (event) => {
        let isChecked = event.target.checked;
        this.setState({
            forAdmin: isChecked
        })
    };

    handleCheckChangeForPublic = (id) => (event) => {
        let isChecked = event.target.checked;
        this.setState({
            forPublic: isChecked
        })
    };

    handleCheckChangeForStaff = (id) => (event) => {
        let isChecked = event.target.checked;
        this.setState({
            forStaff: isChecked
        })
    };

    /**
     * Function to change type message
     * @param  {Object} event
     */
    handleChangeType = (event) => {
        this.setState({
            selectedValue:event.target.value
        })
    };

    /**
     * Function for manage resources selected
     * @param  {number} id
     * @param  {Object} event
     */
    handleCheckChangeResource = (id) => (event) =>{
        let isChecked = event.target.checked;
        let {resourcesSelected} = this.state;
        let existInArray = resourcesSelected.includes(id); // validamos que exista en el arreglo de grupos

        if(!existInArray && isChecked){
            resourcesSelected.push(id)
        }else if(existInArray && !isChecked){
            resourcesSelected = _.remove(resourcesSelected, function (o) {
                return o !== id
            });
        }
        this.setState({
            resourcesSelected
        })
    }

    /**
     * function to set state when input element change
     * @param  {event} e
     */
    handleEventInput=(e)=> {
        const name = e.target.name;
        const value = e.target.value;
        this.setState({[name]: value});
    };

    /** function to get file from input file
     * @param  {} e
     */
    handleFile = (e) => {
        console.log('target',e.target.files)
        let files = [];
        for(const file in e.target.files){
            if(typeof e.target.files[file]==='object'){
                files.push(e.target.files[file]);
            }
        }
        this.setState({ filesEmail: files });
        console.log('state',files)
    };

    /**
     * This function shows the specific form of the message type
     */
    handleSendMessage= ()=>{
        let {selectedValue} = this.state;        
        if(selectedValue==='email'){
            this.handleSendEmail();
        }else{
            this.handleSendPush();
        }
    }

    /**
     * function send message type push 
     */
    handleSendPush= async ()=>{
        let ctx = this.context;
        // console.log('eventinfo',ctx.eventInfo._id)
        this.setState({
            loading:true,
            msgLoading:'enviando...'
        });


        let {rolesSelected,filesEmail,checkQR,
            resourcesSelected,textHtml,subject,message,
            forAdmin, forStaff, forPublic} = this.state;


        // let forAdmin = rolesSelected.includes('forAdmin');
        // let forStaff = rolesSelected.includes('forStaff');

        // _.remove(rolesSelected, function (n) {
        //     return n === 'forAdmin' || n === 'forStaff';
        // });

        let request = {
            method:['push'],
            _idEvent:ctx.eventInfo._id,
            groups:rolesSelected,
            title:subject,
            message:message,
            forAdmin: forAdmin,
            forStaff: forStaff,
            forPublic
        };


        try{
            let response = await apiEvent.sendPushMassive(request);
            console.log(response);
            ctx.openNotification("Enviado correctamente","success");
            this.setState({
                loading:false,
                redirect:true,
                msgLoading:'enviando...'
            })
        }catch (e) {
            console.log(e.response);
            ctx.openNotification("Hubo un error, por favor intente mas tarde","error");
        } finally {
            this.setState({
                loading:false,
                msgLoading:'enviando...'
            })
        }
    }

    /**
     * Function for validate data empty
     * @param  {Object} values
     * @returns {Object} errors
     */
    validate = (values) => {
        const errors = {};
        if (!values.subject) {
            errors.subject = 'Required';
        }

        return errors;
    };

    
    /**
     * Function to send email
     */
    handleSendEmail = () => {
        let ctx = this.context;
        let {rolesSelected,filesEmail,checkQR,resourcesSelected,textHtml,subject} = this.state;        
        if(subject === ""){
            this.setState({errors: {subject: true}})            
        }else{
            this.confirmSendEmail();
        }

    }
    
    /**
     * Function for confirm send email
     */
    confirmSendEmail=()=>{

        let ctx = this.context;
        console.log('eventinfo',ctx.eventInfo._id)
        this.setState({
            loading:true,
            msgLoading:'enviando...'
        })

        let {rolesSelected,filesEmail,checkQR,resourcesSelected,textHtml,subject, forAdmin, forStaff} = this.state;
        let formData = new FormData();
        let index = 0;
        for (const file of filesEmail) {
            formData.append(`files[${index}]`, file, file.name);
            index++;
        }

        

        // let forAdmin = rolesSelected.includes('forAdmin');
        // let forStaff = rolesSelected.includes('forStaff');
        //
        // _.remove(rolesSelected, function (n) {
        //     return n === 'forAdmin' || n === 'forStaff';
        // });


        formData.append("_idEvent",ctx.eventInfo._id);
        formData.append("subject",subject);
        formData.append("message",textHtml);
        if(rolesSelected.length > 0) {
            // console.log('tiene algo', rolesSelected)
            formData.append("groups", `[${rolesSelected}]`);
        }
        else {
            // console.log('Esta vacio', rolesSelected)
            formData.append("groups", ``);
            // console.log(formData.get('groups'))
        }
        formData.append("sendQR",checkQR);
        formData.append("resourcesIds", `${resourcesSelected}`);
        formData.append("forAdmin", forAdmin);
        formData.append("forStaff", forStaff);

        if(!forStaff && !forAdmin && rolesSelected.length<=0){
            ctx.openNotification(<Trans>Se requiere que selecciones al menos un perfil.</Trans>,"error");
            this.setState({
                loading:false
            });
            return false;
        }


        apiEvent.sentEmailMassive(formData).then((res)=>{
            console.log(res)
            ctx.openNotification(<Trans>Enviado correctamente</Trans>,"success");
        }).catch((err)=>{
            console.log(err)
            ctx.openNotification(<Trans>Hubo un error, por favor intente mas tarde</Trans>,"error");
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })




    }

    render() {
        const ctx = this.context;
        let {loading,msgLoading,selectedValue,message,errors} = this.state;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/user/list/`} />;

        return (
            <LoadingOverlay
                active={loading}
                spinner
                text={msgLoading}
            >
                <AdminEventLayout title={<Trans>Email masivo</Trans>} breadcrumbs={this.state.breadcrumbs}>
                    <Grid container title=''>

                        <Grid item lg={6} xs={12}>
                            <Typography variant="h5" style={{padding: '10px'}} component="h3">
                                <Trans>Enviar mensaje masivo</Trans>
                            </Typography>
                        </Grid>

                        <Grid item xs={12}>
                            <RadioGroup row aria-label="typeMessage" name="typeMessage" value={selectedValue} onChange={this.handleChangeType}>
                            <FormControlLabel
                                value="email"
                                control={<Radio color="primary" />}
                                label={<Trans>Email</Trans>}
                                labelPlacement="end"
                            />

                            <FormControlLabel
                                value="push"
                                control={<Radio color="primary" />}
                                label={<Trans>Push notification</Trans>}
                                labelPlacement="end"
                            />
                            </RadioGroup>

                            <Paper style={{padding:'20px', marginBottom:'20px'}}>
                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                    <Trans>Para</Trans>:
                                </Typography>
                                <FormControl component="fieldset">
                                    {/*<FormLabel component="legend">Sauces</FormLabel>*/}
                                    <FormGroup row>
                                        {
                                            selectedValue !== 'email' ? (
                                                <FormControlLabel
                                                    control={<Checkbox
                                                        checked={this.state.forPublic}
                                                        onChange={this.handleCheckChangeForPublic('forPublic')}
                                                    />}
                                                    label={<Trans>¿Es público?</Trans>}
                                                />
                                            ) : ''
                                        }

                                        <FormControlLabel
                                            control={<Checkbox
                                                checked={this.state.forAdmin}
                                                onChange={this.handleCheckChangeForAdmin('forAdmin')}
                                                 />}
                                            label={'ADMIN'}
                                        />
                                        <FormControlLabel
                                            control={<Checkbox
                                                checked={this.state.forStaff}
                                                onChange={this.handleCheckChangeForStaff('forStaff')}
                                                 />}
                                            label={'STAFF'}
                                        />
                                        {
                                            this.state.roles.map(
                                                (rol, index) => (
                                                    <FormControlLabel
                                                        key={rol._id}
                                                        control={<Checkbox
                                                            onChange={this.handleCheckChange(rol._id)}
                                                            value={rol._id} />}
                                                        label={rol.name}
                                                    />
                                                )
                                            )
                                        }
                                    </FormGroup>
                                </FormControl>

                                <div>
                                <label>
                                    <Trans>Límite de caracteres</Trans>: {this.state.subject.length}/63
                                </label>
                                <TextField
                                    id="standard-search"                                    
                                    label={selectedValue==='email'?<Trans>Asunto</Trans>:<Trans>Título</Trans>}
                                    type="text"
                                    required
                                    name={"subject"}
                                    style={{
                                        width:'100%',
                                        marginBottom:'20px'
                                    }}
                                    inputProps={{maxLength: 63}}
                                    onChange={this.handleEventInput}
                                    margin="normal"
                                    error= {errors.subject}
                                    helperText={errors.subject ? <Trans>Campo Requerido</Trans> : ''}
                                    variant="outlined"
                                />
                                </div>

                                {
                                    selectedValue==='email'?
                                        <div>
                                            <label style={{marginTop:'20px', marginBottom:'20px'}}>
                                                <Trans>Adjuntos</Trans>:
                                                <input
                                                    onChange={this.handleFile}
                                                    style={{marginBottom:'20px'}}
                                                    name="numberOfGuests"
                                                    multiple
                                                    type="file"/>
                                            </label>

                                            <Wysiwyg
                                                editorState={this.state.editorState}
                                                style={{
                                                    height:100
                                                }}
                                                onEditorStateChange={this.onEditorStateChange}
                                            />


                                            <FormControl component="fieldset">
                                                {/*<FormLabel component="legend">Sauces</FormLabel>*/}
                                                <FormGroup row>
                                                    <FormControlLabel
                                                        control={<Checkbox
                                                            onChange={this.handleCheckQR}
                                                        />}
                                                        label={<Trans>Incluir QR del evento</Trans>}
                                                    />
                                                </FormGroup>
                                            </FormControl>


                                            <Typography variant="h6" align="left" component="h6" gutterBottom>
                                                <Trans>Incluir los siguientes archivos</Trans>:
                                            </Typography>
                                            <FormControl component="fieldset">
                                                {/*<FormLabel component="legend">Sauces</FormLabel>*/}
                                                <FormGroup row>
                                                    {
                                                        this.state.resources.map(
                                                            (res, index) => (
                                                                <FormControlLabel
                                                                    key={res._id}
                                                                    control={<Checkbox
                                                                        onChange={this.handleCheckChangeResource(res._id)}
                                                                        value={res._id} />}
                                                                    label={res.name}
                                                                />
                                                            )
                                                        )
                                                    }
                                                </FormGroup>



                                            </FormControl>
                                        </div>:
                                       <div>

                                       <label>
                                           <Trans>Límite de caracteres</Trans>: {this.state.message.length}/300
                                        </label>
                                        <TextField
                                            id="outlined-multiline-static"
                                            label={<Trans>Mensaje para notificación push</Trans>}
                                            multiline
                                            onChange={this.handleEventInput}
                                            rows="4"
                                            name='message'
                                            style={{width:'100%'}}
                                            defaultValue={message}
                                            margin="normal"
                                            variant="outlined"
                                            inputProps={{maxLength: 300}}
                                        />
                                       </div>
                                }

                            </Paper>


                        </Grid>


                        <Grid item xs={12} style={{marginTop:20}}>
                            <Paper style={{padding:'20px',marginBottom:'20px'}}>
                                <label>
                                    <Typography variant="h6" style={{padding: '10px'}} component="h6">
                                        <Trans>Variables disponibles para utilizar en el mensaje</Trans>:
                                    </Typography>

                                    <AlertText type={'info'}>
                                        <Trans>Puedes incluir cada uno de las siguientes etiquetas en el cuerpo de tu mensaje, dichas etiquetas serán sustituidas por el valor que a continuación se muestra</Trans>:
                                    </AlertText>


                                    {
                                        selectedValue==='email'?
                                            <ul style={{listStyleType: 'none'}}>
                                                <li>
                                                    <Typography variant="h6" style={{padding: '10px'}} component="h6">
                                                        <Trans>Del usuario</Trans> (<Trans>Sólo disponible para mensajes de correo</Trans>)
                                                    </Typography>
                                                </li>
                                                <li>{'{{firstName}}'} = <Trans>Nombre</Trans>  </li>
                                                <li>{'{{lastName}}'} = <Trans>Apellido</Trans> </li>
                                                <li>{'{{email}}'} = <Trans>Correo electrónico</Trans> </li>
                                                <li>{'{{company}}'} = <Trans>Empresa</Trans> </li>
                                                <li>{'{{urlAssistance}}'} = <Trans>Agregar url de confirmación de asistencia</Trans> </li>
                                            </ul>:''
                                    }



                                    <ul style={{listStyleType: 'none'}}>
                                        <li>
                                            <Typography variant="h6" style={{padding: '10px'}} component="h6">
                                                <Trans>Del evento</Trans>
                                            </Typography>

                                        </li>
                                        <li>{'{{eventName}}'} = <Trans>Nombre del evento</Trans>  </li>
                                        <li>{'{{eventDate}}'} = <Trans>Fecha del evento</Trans> </li>
                                        <li>{'{{eventLocation}}'} = <Trans>Lugar del evento</Trans> </li>
                                    </ul>
                                </label>
                            </Paper>
                        </Grid>



                        <Grid item style={{ marginTop: 16 }} xs={12}>

                            <CancelButton
                                to={`/${ctx.slug}/admin/user/list/`}
                            />


                            <Button variant="contained" onClick={this.handleSendMessage} color="primary">
                                <Trans>Enviar mensaje</Trans>
                            </Button>



                        </Grid>


                    </Grid>
                </AdminEventLayout>
            </LoadingOverlay>

        )
    }
}