import React, {Component} from 'react';
import GlobalContext from "../../../../GlobalContext";
import {CssBaseline, FormControl, FormControlLabel, FormGroup, Grid, InputLabel, Paper, Typography} from "@material-ui/core";
import {Field, Form} from "react-final-form";
import {Checkbox, Radio, Select, TextField} from "final-form-material-ui";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import {DatePickerWrapper, TimePickerWrapper} from "../../../fields/DatePickers";
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import moment from "moment";
import { Trans } from '@lingui/macro';
import {CancelButton, SaveButtonSpinner} from "../../../buttons/StandartButtons";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import arrayMutators from "final-form-arrays";
import Box from "@material-ui/core/Box";
import Image from "material-ui-image/lib/components/Image/Image";
import _ from "lodash";
import FileBase64 from "react-file-base64";
import MenuItem from "@material-ui/core/MenuItem";
import {FieldArray} from "react-final-form-arrays";
import Button from "@material-ui/core/Button";
let randomColor = require('randomcolor');

/**
 *
 * chatbot form view
 * @class StepForm
 * @extends {Component}
 */
class StepForm extends Component {
    static contextType = GlobalContext;
    /**
     * function to obtain the residue 
     * @param  {number} num
     * @returns {number} Residue of the division of the number X in 2, 1 or 0
     */
    isNumOdd = (num) => {
        return num % 2;
    };
    
    /**
     * @param  {string} color
     * @param  {number} index
     * @returns {string} exadecimal color string
     */
    getColor = (color, index) => {
        if(this.isNumOdd(index)) {
            // return color[0];
            return '#f9f9fa';
        }
        else {
            // return color[1];
            return '#f0f8ff';
        }
    };

    render() {
        let ctx = this.context;

        let templateItem = {
            id: '',
            text: '',
            nextStep: '',
            isFinal: false,
            typeStep: 'text',
            options: []
        };

        let templateItemOption = {
            id: '',
            text: '',
            nextStep: '',
        };

        let color = randomColor({
            luminosity: 'light',
            hue: 'monochrome',
            count: 2
        });

        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                <Typography variant="h4" align="center" component="h1" gutterBottom>
                    {
                        this.props.title ? this.props.title : <Trans>Crear usuario</Trans>
                    }
                </Typography>

                <Form
                    onSubmit={this.props.onSubmit}
                    initialValues={this.props.initialValues}
                    validate={this.props.validate}
                    mutators={{
                        ...arrayMutators
                    }}
                    render={({
                                 submitError,
                                 handleSubmit,
                                 form: {
                                     mutators: { push, pop }
                                 }, // injected from final-form-arrays above
                                 reset,
                                 submitting,
                                 pristine,
                                 values
                    }) => (
                        <form onSubmit={handleSubmit} noValidate>
                            <Paper style={{ padding: 16 }}>
                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                    <Trans>Configuración general</Trans>
                                </Typography>
                                <Grid
                                    container
                                    style={{marginTop: 10}}
                                    alignItems="center"
                                    spacing={2}>

                                    <Grid item xs={12} style={{marginTop: 20}}>

                                        <Grid item xs={12}>
                                            <Field
                                                fullWidth
                                                required
                                                name="name"
                                                component={TextField}
                                                type="text"
                                                label={<Trans>Nombre</Trans>}
                                                variant="outlined"
                                            />
                                        </Grid>

                                        <Grid item xs={12} style={{marginTop: 20}}>
                                            <FormControl component="fieldset">
                                                <FormLabel component="legend">
                                                    <Trans>Perfiles de seguridad</Trans>
                                                </FormLabel>
                                                <FormGroup row>
                                                    {
                                                        this.props.roles.map(
                                                            (rol, index) => (
                                                                <FormControlLabel
                                                                    key={index}
                                                                    label={rol.name}
                                                                    control={
                                                                        <Field
                                                                            name="groups"
                                                                            component={Checkbox}
                                                                            type="checkbox"
                                                                            value={rol._id}
                                                                        />
                                                                    }
                                                                />
                                                            )
                                                        )
                                                    }
                                                </FormGroup>
                                            </FormControl>
                                        </Grid>

                                    </Grid>

                                    <Typography variant="h6" align="left" style={{marginTop: 20}} component="h6" gutterBottom>
                                        <Trans>Flujo de mensajes (Pasos)</Trans>
                                    </Typography>

                                    <FieldArray name="steps">
                                        {({fields}) =>
                                            fields.map((name, index) => (
                                                // Rows image item
                                                <Grid container
                                                      item xs={12}
                                                      key={index}
                                                      style={{
                                                          backgroundColor: this.getColor(color, index),
                                                          // filter: 'blur(8px)',
                                                          marginTop: 5,                                                          
                                                      }}
                                                >
                                                    <Grid container item xs={12} spacing={1}>

                                                        <Grid container item xs={12} style={{marginTop: 15}}>
                                                            <FormControl component="fieldset">
                                                                <FormLabel component="legend">
                                                                    <Trans>Tipo de paso #{values.steps[index].id}</Trans>
                                                                </FormLabel>
                                                                <RadioGroup row>
                                                                    <FormControlLabel
                                                                        label={<Trans>Texto</Trans>}
                                                                        control={
                                                                            <Field
                                                                                name={`${name}.typeStep`}
                                                                                component={Radio}
                                                                                type="radio"
                                                                                value="text"
                                                                            />
                                                                        }
                                                                    />
                                                                    <FormControlLabel
                                                                        label={<Trans>Opción</Trans>}
                                                                        control={
                                                                            <Field
                                                                                name={`${name}.typeStep`}
                                                                                component={Radio}
                                                                                type="radio"
                                                                                value="option"
                                                                            />
                                                                        }
                                                                    />
                                                                </RadioGroup>
                                                            </FormControl>
                                                        </Grid>

                                                        {/*Col properties step*/}

                                                        {
                                                            values.steps[index].typeStep === 'text' ? (
                                                                <Grid container
                                                                      direction="row"
                                                                      justify="space-between"
                                                                      alignItems="center"
                                                                      spacing={1}                                                                      
                                                                      item>
                                                                    <Grid item xs={1}>
                                                                        <InputLabel><Trans>Num. paso</Trans></InputLabel>
                                                                        <Field
                                                                            fullWidth
                                                                            name={`${name}.id`}
                                                                            component={TextField}                                                                            
                                                                            variant="outlined"
                                                                            margin="normal"
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={7} >                                                                        
                                                                        <InputLabel><Trans>Texto</Trans></InputLabel>
                                                                        <Field
                                                                            fullWidth
                                                                            name={`${name}.text`}
                                                                            component={TextField}
                                                                            
                                                                            variant="outlined"
                                                                            margin="normal"
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={1}>
                                                                    <InputLabel><Trans>Sig. paso</Trans></InputLabel>
                                                                        <Field
                                                                            fullWidth
                                                                            name={`${name}.nextStep`}
                                                                            component={TextField}                                                                            
                                                                            variant="outlined"
                                                                            margin="normal"
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={2}>
                                                                        <FormControlLabel

                                                                            label={<Trans>¿Es paso final?</Trans>}
                                                                            name={`${name}.nextStep`}
                                                                            labelPlacement="bottom"
                                                                            control={
                                                                                <Field
                                                                                    name={`${name}.isFinal`}
                                                                                    component={Checkbox}
                                                                                    type="checkbox"
                                                                                />
                                                                            }
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={1}>
                                                                        <span
                                                                            onClick={() => {
                                                                                fields.remove(index)
                                                                            }
                                                                            }
                                                                            style={{cursor: 'pointer'}}
                                                                        >
                                                                          ❌
                                                                        </span>
                                                                    </Grid>
                                                                </Grid>
                                                            ) : (
                                                                <Grid container
                                                                      direction="row"
                                                                      justify="space-between"
                                                                      alignItems="center"
                                                                      spacing={1}
                                                                      item>
                                                                    <Grid item xs={11}>
                                                                        <Field
                                                                            fullWidth
                                                                            name={`${name}.id`}
                                                                            component={TextField}
                                                                            label={<Trans>Número de paso</Trans>}
                                                                            variant="outlined"
                                                                            margin="normal"
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={1}>
                                                                        <span
                                                                            onClick={() => {
                                                                                fields.remove(index)
                                                                            }
                                                                            }
                                                                            style={{cursor: 'pointer'}}
                                                                        >
                                                                          ❌
                                                                        </span>
                                                                    </Grid>
                                                                    <Grid item xs={12}>
                                                                        <Button
                                                                            variant="contained"
                                                                            color="default"
                                                                            size="small"
                                                                            onClick={() => push(`${name}.options`, templateItemOption)}
                                                                            style={{marginRight: 20}}
                                                                        >
                                                                            <Trans>Agregar opción</Trans>
                                                                        </Button>
                                                                    </Grid>

                                                                    <FieldArray name={`${name}.options`}>
                                                                        {({fields}) =>
                                                                            fields.map((name2, index2) => (
                                                                                    <Grid key={index2} container item xs={12} spacing={1}>
                                                                                        {/*<Grid container item xs={12}>*/}
                                                                                            <Grid item xs={8}>
                                                                                                <Field
                                                                                                    fullWidth
                                                                                                    name={`${name2}.text`}
                                                                                                    component={TextField}
                                                                                                    label={<Trans>Texto</Trans>}
                                                                                                    variant="outlined"
                                                                                                    margin="normal"
                                                                                                />
                                                                                            </Grid>
                                                                                            <Grid item xs={3}>
                                                                                                <Field
                                                                                                    fullWidth
                                                                                                    name={`${name2}.nextStep`}
                                                                                                    component={TextField}
                                                                                                    label={<Trans>Siguiente paso</Trans>}
                                                                                                    variant="outlined"
                                                                                                    margin="normal"
                                                                                                />
                                                                                            </Grid>
                                                                                            <Grid item xs={1}>

                                                                                                <Button
                                                                                                    variant="outlined"
                                                                                                    color="secondary"
                                                                                                    onClick={() => {
                                                                                                        fields.remove(index2)
                                                                                                    }}
                                                                                                    style={{marginTop: 20}}
                                                                                                >
                                                                                                    <Trans>Eliminar</Trans>
                                                                                                </Button>
                                                                                            </Grid>
                                                                                        {/*</Grid>*/}
                                                                                    </Grid>



                                                                            ))
                                                                        }
                                                                    </FieldArray>
                                                                </Grid>
                                                            )
                                                        }


                                                    </Grid>

                                                    {/*<Grid container item xs={12} spacing={1}>*/}
                                                    {/*    <Grid item xs={12}>*/}
                                                    {/*        <hr/>*/}
                                                    {/*    </Grid>*/}
                                                    {/*</Grid>*/}


                                                </Grid>

                                            ))
                                        }
                                    </FieldArray>

                                    <Grid container
                                          direction="row"
                                          justify="center"
                                          alignItems="center"
                                          item xs={12}
                                          spacing={2}
                                          style={{marginTop:5}}>
                                        <div className="buttons">
                                            <Button
                                                variant="contained"
                                                color="primary"
                                                size="small"
                                                onClick={() => push('steps', templateItem)}
                                                style={{marginRight: 20}}
                                            >
                                                <Trans>Agregar paso</Trans>
                                            </Button>
                                            <Button
                                                size="small"
                                                variant="contained"
                                                color="secondary"
                                                onClick={() => pop('steps')}
                                            >
                                                <Trans>Eliminar último paso</Trans>
                                            </Button>
                                        </div>
                                    </Grid>

                                    {submitError && <div className="error">{submitError}</div>}
                                    <Grid item style={{ marginTop: 26 }} xs={12}>

                                        <CancelButton to={`/${ctx.slug}/admin/chatbot/`} />

                                        <SaveButtonSpinner loading={this.props.loading} disabled={this.props.disableButton} />
                                    </Grid>
                                    {/*<pre>{JSON.stringify(values, 0, 2)}</pre>*/}

                                </Grid>
                            </Paper>
                        </form>
                    )}
                />
            </div>
        );
    }
}

export default StepForm;