import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import {Link} from "react-router-dom";
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';
import AddIcon from '@material-ui/icons/Add';
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import GlobalContext from "../../../../GlobalContext";
import SubEventTable from "../../../../components/events/admin/sub-events/SubEventTable";
import {ApiServiceEvents} from "../../../../api-services/events";
import _ from "lodash";
import {Trans} from "@lingui/macro";

const apiEvent = new ApiServiceEvents();

/**
 * 
 * This view allow display subevent list
 * @class SubEventList
 * @extends {Component}
 */
class SubEventList extends Component {

    static contextType = GlobalContext;

    state = {
        subEvents: [],
        loading: false,
        breadCrumbs:[
            {
                name: <Trans>Lista de eventos alternos</Trans>,
                active:true,
                link:``
            },
        ]
    };

    /**
     * This function is for get sub-event information.
     */
    loadSubEvents = () => {
        this.setState({loading:true});
        apiEvent.getSubEventList(this.context.slug).then(
            (res) => {
                // console.log('Respuesta', res);
                this.setState({
                    subEvents: res
                });
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    this.context.openPermissionNotification();
                }
            }
        ).finally(()=> this.setState({loading:false}));
    };


    /**
     * funtcion to delete sub event selected
     * @param  {Object} oldData
     */
    handlerDelete = (oldData) => new Promise(resolve => {
        let ctx = this.context;
        apiEvent.deleteSubEvent(oldData._id).then(
            (response) => {
                let oldState = this.state;
                oldState.subEvents = _.remove(this.state.subEvents, function (x) {
                    // {_id: oldData._id}
                    return x._id !== oldData._id;
                });
                this.setState(oldState);
                ctx.openSuccessNotification(<Trans>Sub-evento eliminado</Trans>);
                resolve();
                // console.log('se borro usuario', response._id, oldData._id);
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    this.context.openPermissionNotification();
                }
                resolve();
                console.log('Error', err.response);
            }
        );

    });

    componentDidMount() {
        this.loadSubEvents();
    }

    render() {
        const slug = this.context.slug;
        return (
            <AdminEventLayout breadcrumbs={this.state.breadCrumbs} title={<Trans>Administrador de eventos</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Lista de eventos alternos</Trans>
                            <IconButton component={Link} push='true' to={`/${slug}/admin/sub-event/create/`} color="primary" aria-label="">
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>
                    <Grid item xs={12}>
                        { this.state.loading ?   
                            <Paper xs={12} style={{'textAlign': "center","paddingTop":50,"paddingBottom":50}}><CircularProgress /> </Paper>
                            :
                            <SubEventTable
                                rows={this.state.subEvents}
                                handlerDelete={this.handlerDelete}
                            />
                        }
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default SubEventList;