import React, {Component} from 'react';
import RecoverPasswordForm from "../../components/security/RecoverPasswordForm";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import queryString from "query-string"
import {ApiServiceSecurity} from '../../api-services/security'
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import SetNewPasswordForm from "../../components/security/SetNewPasswordForm";
import {withI18n} from "@lingui/react";
import { Trans, t } from '@lingui/macro';
import GlobalContext from "../../GlobalContext";

const api = new ApiServiceSecurity();

/**
 *
 * This view is for  password recovery
 * @class RecoverPassword
 * @extends {Component}
 */
class RecoverPassword extends Component {

    static contextType = GlobalContext;

    state={
        email:'',
        recoverSent:false,
        msgSentEmail:'',
        token:'',
        loading:false,
        password:'',
        password2:'',
        message:'',
        showError:false,
        slug:null,
        showMsgSuccess: false
    }


    
    /**
     * Function to set the new password, comes from the submit form to confirm the new password
     * @param  {Object} e
     */
    handleSubmitReset=(e)=>{
        e.preventDefault()

        let {password, password2} = this.state
        // console.log('entra submit',password,password2)
        if(password!==password2){
            this.setState({
                showError:true,
                message:'Las contraseñas no coinciden'
            })


        }else{
            this.setState({
                showError:false,
                message:'',
                loading:true
            })

            api.resetPasswordConfirm(this.state.token, this.state.password).then((res)=>{
                // alert('La contraseña ha sido actualizada satisfactoriamente')
                alert(this.props.i18n._(t`La contraseña ha sido actualizada satisfactoriamente`));
                // si el cambio de contraseña es desde el admin del evento entonces lo redireccionamos al login del admin de eventos
                let url = '/';
                this.setState({showMsgSuccess: true});
                // if(this.state.slug){
                //     url = `/${this.state.slug}/security/signin`
                // }
                // window.location.href=url

            }).catch((err)=>{

                this.setState({
                    showError:true,
                    message:err.response.data.message
                })
            }).finally(()=>{
                this.setState({
                    loading:false
                })
            })

        }
    }


    
    /**
     * Function to send request the password change (email field only form)
     * @param  {Object} e
     */
    handleSubmit =(e)=>{
        const {email,slug} = this.state;
        let msjText = this.props.i18n._(t`Para finalizar el proceso, se ha enviado un email a `)  +  ` ${email} ` + this.props.i18n._(t` con las instrucciones para el reestablecimiento de la contraseña.`) ;
        e.preventDefault();
        let msgSentEmail = msjText;
        this.setState({loading:true})

        api.recoverPassword(email,slug).then((res)=>{
            this.setState({
                recoverSent:true,
                msgSentEmail
            })
        }).catch((err)=>{
            let message = this.props.i18n._(t`Hubo un error en la conexión, porfavor intente mas tarde.`)
            let ctx = this.context;
            if(err.response){
               message= err.response.data.message
               switch (err.response.data.status) {
                   case 'User':
                       message = 'El usuario no existe';
                       break;
                    case 'Event':
                        message = 'No existe el evento o se encuentra inactivo';               
                   default:
                       break;
               }
            }
            ctx.openNotification(message,'error')
            //alert(message)

        }).finally(()=>{
            this.setState({loading:false})
        });


    }


    componentDidMount() {
        const params = queryString.parse(this.props.location.search);
        const slug = params.slug
        this.setState({
            slug
        })
        this.validateToken();
    }


    
    /**
     * We validate that the password reset token is valid or has not been used
     */
    validateToken=()=>{
        const params = queryString.parse(this.props.location.search);
        if(params.token){
            api.validateTokenResetPassword(params.token).then((res)=>{
                this.setState({
                    token:params.token
                })
            }).catch((err)=>{
                // alert('El token es inválido o ya ha sido utilizado.')
                alert(this.props.i18n._(t`El token es inválido o ya ha sido utilizado`))
            });
        }
    }

    /**
     * Function to set state when a input element change
     * @param  {Object} e
     */
    handleEventInput=(e)=> {
        const name = e.target.name;
        const value = e.target.value;
        this.setState({[name]: value,showError:false,message:'' });
    }


    render() {
        const {recoverSent,msgSentEmail,loading,token,message,showError} = this.state
        function ContentMsg(){
            return (
                <Grid
                    container
                    spacing={0}
                    style={{marginTop:'20%'}}
                    justify="center"
                >
                    <Paper style={{padding:'10px'}}>
                        <p style={{textAlign:'center'}}>
                            <MailOutlineIcon style={{ fontSize: 130,color:'gray' }}/>
                        </p>

                        <Typography variant="h6" component="h6">
                            {msgSentEmail}
                        </Typography>
                    </Paper>
                </Grid>
            )
        }


        return (
            <div>
                {
                    this.state.showMsgSuccess ? (
                        <Grid
                            container
                            spacing={0}
                            style={{marginTop:'20%'}}
                            justify="center"
                        >
                            <Paper style={{padding:'10px'}}>
                                <p style={{textAlign:'center'}}>
                                    <MailOutlineIcon style={{ fontSize: 130,color:'gray' }}/>
                                </p>

                                <Typography variant="h6" component="h6">
                                    <Trans>Tu contraseña ha sido re-establecida</Trans>
                                </Typography>
                            </Paper>
                        </Grid>
                    ) :
                        token?<SetNewPasswordForm title={<Trans>Por favor confirma tu nueva contraseña</Trans>}
                                                  handleSubmitReset={this.handleSubmitReset}
                                                  handleEventInput={this.handleEventInput}
                                                  showError={showError}
                                                  loading={loading}
                                                  message={message}
                        />:(recoverSent?<ContentMsg/>:
                            <RecoverPasswordForm
                                loading={loading}
                                handleSubmit={this.handleSubmit}
                                handleEventInput={this.handleEventInput}
                                title={<Trans>Ingresa tu email de usuario para recuperar contraseña</Trans>}/>)


                }
            </div>
        );
    }
}

export default withI18n()(RecoverPassword);