import React from "react";
import {FormControl, FormControlLabel, FormGroup, Grid, Paper} from "@material-ui/core";
import _ from "lodash";
import {t, Trans} from '@lingui/macro';

import AdminEventLayout from "../../../layouts/AdminEventLayout";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import HistoryIcon from '@material-ui/icons/History';
import AddIcon from '@material-ui/icons/Add';
import {Redirect} from "react-router-dom";
import GlobalContext from "../../../GlobalContext";
import TableUserList from "../../../components/events/security/TableUserList";
import {ApiServiceEvents} from "../../../api-services/events";
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import { CSVLink, CSVDownload } from "react-csv";
import moment from 'moment'
import CloseIcon from '@material-ui/icons/Close';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import Wysiwyg from "../../../components/fields/Wysiwyg";
import {convertToRaw, EditorState} from "draft-js";
import draftToHtml from "draftjs-to-html";
import LoadingOverlay from "react-loading-overlay";
import LinearProgress from '@material-ui/core/LinearProgress';
import CircularProgress from '@material-ui/core/CircularProgress';
import FullScreenDialogGeneric from "../../../components/modals/FullScreenDialogGeneric";
import ImportUserForm from "../../../components/security/ImportUserForm";
import MailIcon from '@material-ui/icons/Mail';
import TextsmsIcon from '@material-ui/icons/Textsms';
import GetAppIcon from '@material-ui/icons/GetApp';
import Slide from '@material-ui/core/Slide';
import Checkbox from "@material-ui/core/Checkbox";
import AlertText from "../../../components/alerts/AlertText";
const apiEvent = new ApiServiceEvents();


const Transition = React.forwardRef(function Transition(props, ref) {
    return <Slide direction="up" ref={ref} {...props} />;
});

/**
 *
 * This view allow view user list
 * @export
 * @class EventUserList
 * @extends {React.Component}
 */
export default class EventUserList extends React.Component{
    tableRef = React.createRef();

    static contextType = GlobalContext;

    state = {
        open:false,
        userList: [],
        textHtml: '',
        subject:'',
        filesEmail:[],
        redirectToGeneralMail:false,
        redirectToEmailList:false,
        redirectTohistory: false,
        loading:false,
        resourcesSelected:[],
        msgLoading:'cargando...',
        editorState: EditorState.createEmpty(),
        userSelected:{
            resources:[]
        },
        redirectToCreate: false,
        breadcrumbs:[{
            name: <Trans>Lista de usuarios</Trans>,
            active:true,
            link:''
        }],
        openModalImportCsv: false,
        groups: [],
        loadingImport: false,
        fileCsv: '',
        headercsv: [],
        datacsv: [],        
    };

    
    /**
     * function to config editor for mail
     * @param  {Object} editorState
     */
    onEditorStateChange = (editorState) => {
        this.setState({
            editorState,
            textHtml: draftToHtml(convertToRaw(editorState.getCurrentContent()))
            // text: editorState.getCurrentContent()
        });
    };

    /**
     * Function to set state for redirect to create
     */
    redirectCreate = () => this.setState({redirectToCreate: true});

    /**
     * Function to get user list
     * @param  {Object} query
     */
    getUserList = async (query) => {
        // console.log('Query', query);
        console.log('Cambios');
        let ctx = this.context;
        let {datacsv} = this.state;

        let headers = [
            { label: "Nombre", key: "firstName" },
            { label: "Apellido", key: "lastName" },
            { label: "Empresa", key: "company" },
            { label: "Confirmar asistencia", key: "confirmAssistance" },
            { label: "email", key: "email" }
          ];

        this.setState({ headercsv: headers })        
        apiEvent.getEventUserListComplete('', ctx.slug, query.search).then((res)=>{
            let data = []
            res.eventUsers.map((item,index) => {
                data.push({firstName:item.firstName, lastName:item.lastName, company:item.company, confirmAssistance:item.confirmAssistance===1?`Pendiente por confirmar`:(item.confirmAssistance===2?`Confirmado`:`Declinado`), email:item.email});
            });
            this.setState({datacsv:data});            
        });

        return new Promise((resolve, reject) => {
            // prepare your data and then call resolve like this:
            // let eventId = '5d851ed3b740c6a6ed62f2f9';
            let slug = ctx.slug;
            return apiEvent.getEventUserList('', slug, query.page +1, query.search).then(
                (res) => {
                    // console.log('res', res.eventUsers);
                    // console.log('Respuesta de usuarios evento', res.length, res);
                    this.setState({userList: res});
                    let data = []
                    res.eventUsers.map((item,index) => {
                        if(item.firstName){
                            item.firstName = item.firstName.length>=15 ? item.firstName.substring(0,12)+'...': item.firstName;
                        }
                        if(item.lastName){
                            item.lastName = item.lastName.length>=15 ? item.lastName.substring(0,12)+'...': item.lastName;
                        }
                        if(item.company){
                            item.company = item.company.length>=15 ? item.company.substring(0,12)+'...': item.company;
                        }
                        if(item.email){
                            item.email = item.email.length>=30 ? item.email.substring(0,25)+'...': item.email;
                        }
                        data.push({firstName:item.firstName, lastName:item.lastName, company:item.company, confirmAssistance:item.confirmAssistance===1?`Pendiente por confirmar`:(item.confirmAssistance===2?`Confirmado`:`Declinado`), email:item.email});
                    });
                    //this.setState({datacsv:data})
                    resolve({
                        data: res.eventUsers, // your data array
                        page: res.pagination.page > 0 ? res.pagination.page - 1 : 0, // current page number
                        // totalCount: res.pagination.totalCount ? res.pagination.totalCount : res.length, 
                        totalCount: res.pagination.totalCount // total page number
                    });
                }
            ).catch(err => {
                console.log('Error, no se pudo cargar los usuarios', err);
                if(_.has(err, 'response.status') && err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                resolve({
                    data: [], // your data array
                    page: 0, // current page number
                    totalCount: 0, // total page number
                });
            });

        })
    };

    /**
     * function to get the file when an input element changes
     * @param  {Object} e
     */
    handleFile = (e) => {
        console.log('target',e.target.files)
        let files = [];
        for(const file in e.target.files){
            if(typeof e.target.files[file]==='object'){
                files.push(e.target.files[file]);
            }
        }
        this.setState({ filesEmail: files });
        console.log('state',files)
    };

    /**
     * Function to delete user selected
     * @param  {Object} oldData
     */
    handlerDelete = (oldData) =>
        new Promise(resolve => {
            let ctx = this.context;
            let isActive = !oldData.isActive;
            apiEvent.deleteUserEvent(oldData._id, ctx.slug).then(
                (response) => {
                    resolve();
                    console.log('se borro usuario', response);
                    // this.getUserList();
                    // let newUserList = this.state.userList;
                    // let index = _.findIndex(this.state.userList, {_id: oldData._id});
                    // newUserList.splice(index, 1, {id: 100, name: 'New object.'})
                }
            ).catch(
                (err) => {
                    resolve();
                    console.log(err, err);
                    try{
                        if(err.response.status === 403){
                            ctx.openPermissionNotification();
                        }
                        console.log('Error', err.response);
                    }catch (e) {

                    }

                }
            );

        });

    componentDidMount() {
        this.loadRoles();
    }

    /**
     * Function to set state for open dialog
     * @param  {Object} useractive
     */
    handleClickOpen = (useractive) => {
        console.log(useractive)
        this.setState({
            open:true,
            userSelected:useractive
        })
    };

    /**
     * Function to set state when a input elemente change
     * @param  {Object} e
     */
    handleEventInput=(e)=> {
        const name = e.target.name;
        const value = e.target.value;
        this.setState({[name]: value});
    };
    
    /**
     * Function to confirm send email to user
     */
    handleSendEmailUser=()=>{
        // this.handleClose();
        
        const ctx = this.context;
        let {textHtml,filesEmail,userSelected,subject,resourcesSelected} = this.state;
        if(subject == '' || subject == undefined)
        {
            return ctx.openNotification(<Trans>Asunto requerido</Trans>,"error");
        }
        if(textHtml == '' || textHtml == undefined)
        {
            return ctx.openNotification(<Trans>Mensaje requerido</Trans>,"error");
        }
       
        this.setState({
            loading:true,
            msgLoading:'enviando...'
        })
        let formData = new FormData();
        let index = 0;
        for (const file of filesEmail) {
            formData.append(`files[${index}]`, file, file.name);
            index++;
        }

        // formData.append("files",filesEmail);
        formData.append("message",textHtml);
        formData.append("eventUser",userSelected._id);
        formData.append("_idEvent",userSelected._idEvent);
        formData.append("subject",subject);
        formData.append("resourcesIds", `${resourcesSelected}`);
        apiEvent.sentEmailToUser(formData).then((res)=>{
            ctx.openNotification(<Trans>Enviado correctamente</Trans>,"success");
            this.handleClose();
        }).catch((error)=>{
            ctx.openNotification(<Trans>Hubo un error, por favor intente más tarde</Trans>,"error");
        }).finally(()=>{
            
            this.setState({
                loading:false,
                msgLoading:'cargando...',
                filesEmail: []
            })
        })

    };

    /**
     * function to close dialog
     */
    handleClose = () => {
        this.setState({
            open:false,
            resourcesSelected:[]

        })
    };

    
    /**
     * function to open view for user import
     * @param  {Boolean} val
     */
    setOpenImportCsv = (val) => {
        this.setState({
            openModalImportCsv: val
        });
    };

    /**
     * function to get file on import file
     * @param  {Object} selectorFiles
     */
    handleChange = (selectorFiles) => {
        console.log(selectorFiles);
        this.setState({fileCsv: selectorFiles});
    };
    /**
     * function to send import file
     * @param  {Object} values
     */
    handleSubmitImport = (values) => {
        // debugger;
        const ctx = this.context;
        let formData = new FormData();
        let fileCsv = this.state.fileCsv;
        // console.log('Archivo csv', this.state.fileCsv);
        formData.append('slug', this.context.slug);
        console.log('Form data', formData);
        if(_.has(values, 'groups') && values.groups.length > 0) {
            // console.log('si entro al groups')
            formData.append('group', values.groups);
        }else {
            ctx.openNotification(<Trans>Debes seleccionar al menos un perfil</Trans>, 'error');
            return
        }
        // console.log('fileCsv[0]', fileCsv[0], 'fileCsv', fileCsv);
        if (fileCsv && fileCsv.length > 0) {
            // console.log('si entro al csv', fileCsv[0]);
            formData.append("csv_file", fileCsv[0]);
        }
        else {
            ctx.openNotification(<Trans>Debes cargar un archivo csv</Trans>, 'error');
            return
        }
        this.setState({loadingImport: true});
        apiEvent.loadUsersCsv(formData).then(
            (res) => {
                console.log('res', res);
                let msg = `Importado con éxito`;
                ctx.openSuccessNotification(msg);
                this.setOpenImportCsv(false);
                // this.getUserList('');
                this.tableRef.current.onQueryChange();
            }
        ).catch(
            (err) => console.log('error', err)
        ).finally(() => this.setState({loadingImport: false}));
    };

    /**
     * Function to validate form for file import
     * @param  {Object} values
     */
    validateImportForm = (values) => {
        const ctx = this.context;
        // console.log('Validate', values);
        const errors = {};
        if(!_.has(values, 'groups')) {
            console.log('sin perfil 1');
            errors.groups = this.props.i18n._(t`Debes seleccionar por lo menos un perfil`);
            // ctx.openNotification(errors.groups, 'error')
        }
        else {
            if(_.has(values, 'groups') && values.groups.length <= 0) {
                console.log('sin perfil 2');
                errors.groups = this.props.i18n._(t`Debes seleccionar por lo menos un perfil`);
                ctx.openNotification(errors.groups, 'error')
            }
        }

        // if (!values.groups && !values.groups.length > 0) {
        //     errors.groups = 'Debes seleccionar por lo menos un perfil'
        // }

        return errors;
    };


    /**
     * Function for manage resources selected
     * @param  {number} id
     * @param  {Object} event
     */
    handleCheckChangeResource = (id) => (event) =>{
        let isChecked = event.target.checked;
        let {resourcesSelected} = this.state;
        let existInArray = resourcesSelected.includes(id); // validamos que exista en el arreglo de grupos

        if(!existInArray && isChecked){
            resourcesSelected.push(id)
        }else if(existInArray && !isChecked){
            resourcesSelected = _.remove(resourcesSelected, function (o) {
                return o !== id
            });
        }
        this.setState({
            resourcesSelected
        })
    }

    /**
     * function to load roles
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvent.getRoles(ctx.slug).then(
            (res) => {
                this.setState({groups: res});
            }
        ).catch(
            (err) => {
                console.log('Error en la carga de perfiles', err)
            }
        );
    };

    /**
     * 
     * @returns filename of .csv
     */
    getFileName() {
        let d = new Date();        
        let dformat = `${d.getMonth()+1}/${d.getFullYear()}_${d.getHours()}:${d.getMinutes()}`;
    
        
        return "CatalogoUsuarios_" + dformat + ".csv";
    }


    // END IMPORT USER SECTION

    render() {
        const ctx = this.context;
        let {loading,msgLoading} = this.state;
        if(this.state.redirectToCreate) return <Redirect push to={`/${ctx.slug}/admin/user/new/`}/>;
        if(this.state.redirectToEmailList) return <Redirect push to={`/${ctx.slug}/admin/email-list/`}/>;
        if(this.state.redirectToGeneralMail) return <Redirect push to={`/${ctx.slug}/admin/user/general-email/`}/>;
        if(this.state.redirectTohistory) return <Redirect push to={`/${ctx.slug}/admin/user/notification-history/`}/>;

        return (
            <LoadingOverlay
                active={loading}
                spinner
                text={msgLoading}
            >
            <AdminEventLayout title={<Trans>Administrador de eventos</Trans>} breadcrumbs={this.state.breadcrumbs}>
                <Grid container title=''>

                    <Grid item lg={3} xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Lista de usuarios</Trans>
                            <IconButton onClick={this.redirectCreate} color="primary" aria-label="add user">
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>
                    <Grid
                        container
                        item
                        lg={9}
                        xs={12}
                        style={{marginBottom:10}}
                        direction="row"
                        justify="flex-end"
                        alignItems="center"
                    >
                        <Button
                            variant="contained"
                            color="primary"
                            style={{marginRight:10}}
                            startIcon={<HistoryIcon />}
                            onClick={() => this.setState({redirectTohistory:true})}
                        >
                            <Trans>Historial de notificaciones push</Trans>
                        </Button>

                        <Button
                            variant="contained"
                            color="secondary"
                            style={{marginRight:10}}
                            startIcon={<TextsmsIcon />}
                            onClick={() => this.setState({redirectToGeneralMail:true})}
                        >
                            <Trans>Mensajes masivos</Trans>
                        </Button>


                        <Button
                            variant="contained"
                            color="default"
                            style={{marginRight:10}}
                            startIcon={<CloudUploadIcon />}
                            onClick={() => {this.setOpenImportCsv(true)}}
                        >
                            <Trans>Importar</Trans>
                        </Button>

                        <Button
                            variant="contained"
                            color="default"
                            startIcon={< CloudDownloadIcon />}
                        >   
                            
                            <CSVLink style={{color:'black',textDecoration:'none'}} filename={this.getFileName()}
                            data={this.state.datacsv} headers={this.state.headercsv}><Trans>Exportar</Trans></CSVLink>
                        </Button>

                    </Grid>


                    <Grid item xs={12}>
                        <TableUserList
                            handleClickOpenMail={this.handleClickOpen}
                            rows={this.getUserList}
                            handlerDelete={this.handlerDelete}
                            tableRef={this.tableRef}                                                     
                        />
                    </Grid>
                </Grid>


                <FullScreenDialogGeneric
                    setOpen={this.setOpenImportCsv}
                    open={this.state.openModalImportCsv}
                    title={<Trans>Importación de usuarios </Trans>}
                >
                    <ImportUserForm
                        onSubmit={this.handleSubmitImport}
                        // validate={this.validateImportForm}
                        roles={this.state.groups}
                        downloadCSV={true}
                        linkfile={'/files/template_user.csv'}
                        loading={this.state.loadingImport}
                        handleChange={this.handleChange}
                        buttonConfirmBar={false}
                    />
                </FullScreenDialogGeneric>
                <Dialog fullScreen open={this.state.open} onClose={this.handleClose} TransitionComponent={Transition}>
                    <AppBar>
                        <Toolbar>
                            <IconButton edge="start" color="inherit" onClick={this.handleClose} aria-label="close">
                                <CloseIcon />
                            </IconButton>
                            <Typography variant="h6">
                                {<Trans>Enviar email a</Trans>}  {this.state.userSelected.firstName} {this.state.userSelected.lastName}
                            </Typography>
                            {/*<Button autoFocus edge={'end'} color="inherit" onClick={this.handleSendEmailUser}>*/}
                            {/*    <Trans>Enviar</Trans>*/}
                            {/*</Button>*/}
                        </Toolbar>
                    </AppBar>
                    <DialogTitle id="form-dialog-title">--</DialogTitle>
                    <DialogContent style={{marginTop:20}}>
                        <Grid container style={{display: 'flex',
                            flexWrap: 'wrap',}}>
                            <Grid item xs={12}>
                                <TextField
                                    id="standard-search"
                                    label={<Trans>Asunto</Trans>}
                                    required
                                    type="text"
                                    name={"subject"}
                                    style={{
                                        width:'100%'
                                    }}
                                    onChange={this.handleEventInput}
                                    margin="normal"
                                />
                                <label>
                                    <Trans>Adjuntos</Trans>:
                                    <input
                                        onChange={this.handleFile}
                                        name="numberOfGuests"
                                        multiple
                                        type="file"/>
                                </label>
                                <Wysiwyg
                                    editorState={this.state.editorState}
                                    style={{
                                        height:100
                                    }}
                                    onEditorStateChange={this.onEditorStateChange}
                                />


                            </Grid>


                            {
                            this.state.userSelected.resources.length>0?
                                <Grid item  xs={12}>
                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                <Trans>Incluir los siguientes archivos</Trans>:
                                </Typography>
                                <FormControl component="fieldset">
                                <FormGroup row>
                                {
                                    this.state.userSelected.resources.map(
                                        (res, index) => (
                                            <FormControlLabel
                                                key={res._id}
                                                control={<Checkbox
                                                    onChange={this.handleCheckChangeResource(res._id)}
                                                    value={res._id} />}
                                                label={res.name}
                                            />
                                        )
                                    )
                                }
                                </FormGroup>



                                </FormControl>
                                </Grid>:''
                            }



                            <Grid item xs={12} style={{marginTop:20}}>
                                <Paper style={{padding:'20px',marginBottom:'20px'}}>
                                    <label>
                                        <Typography variant="h6" style={{padding: '10px'}} component="h6">
                                            <Trans>Variables disponibles para utilizar en el mensaje</Trans>:
                                        </Typography>

                                        <AlertText type={'info'}>
                                            <Trans>Puedes incluir cada uno de las siguientes etiquetas en el cuerpo de tu mensaje, dichas etiquetas serán sustituidas por el valor que a continuación se muestra</Trans>:
                                        </AlertText>



                                        <ul style={{listStyleType: 'none'}}>
                                            <li>
                                                <Typography variant="h6" style={{padding: '10px'}} component="h6">
                                                    <Trans>Del usuario</Trans> (<Trans>Sólo disponible para mensajes de correo</Trans>)
                                                </Typography>
                                            </li>
                                            <li>{'{{firstName}}'} = <Trans>Nombre</Trans>  </li>
                                            <li>{'{{lastName}}'} = <Trans>Apellido</Trans> </li>
                                            <li>{'{{email}}'} = <Trans>Correo electrónico</Trans> </li>
                                            <li>{'{{company}}'} = <Trans>Empresa</Trans> </li>
                                            <li>{'{{urlAssistance}}'} = <Trans>Agregar url de confirmación de asistencia</Trans> </li>
                                        </ul>




                                        <ul style={{listStyleType: 'none'}}>
                                            <li>
                                                <Typography variant="h6" style={{padding: '10px'}} component="h6">
                                                    <Trans>Del evento</Trans>
                                                </Typography>

                                            </li>
                                            <li>{'{{eventName}}'} = <Trans>Nombre del evento</Trans>  </li>
                                            <li>{'{{eventDate}}'} = <Trans>Fecha del evento</Trans> </li>
                                            <li>{'{{eventLocation}}'} = <Trans>Lugar del evento</Trans> </li>
                                        </ul>
                                    </label>
                                </Paper>
                            </Grid>

                            <Grid item xs={12} style={{marginTop:10}}>
                                <Button disabled={loading} variant="contained" style={{marginRight:10}} onClick={this.handleClose} color="default">
                                    <Trans>Cancelar</Trans>
                                </Button>


                                <Button disabled={loading} variant="contained" onClick={this.handleSendEmailUser} color="primary">
                                    {loading? <Trans>Enviando...</Trans>:<Trans>Enviar</Trans>}
                                </Button>
                            </Grid>
                        </Grid>

                    </DialogContent>
                </Dialog>

            </AdminEventLayout>
            </LoadingOverlay>

        )
    }
}