import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from '@material-ui/core/FormControlLabel';
import Checkbox from '@material-ui/core/Checkbox';
import Button from "@material-ui/core/Button";
import DialogActions from "@material-ui/core/DialogActions";
import InputAdornment from "@material-ui/core/InputAdornment";
import IconButton from "@material-ui/core/IconButton";
import AddIcon from '@material-ui/icons/Add';
import Chip from "@material-ui/core/Chip";
import Avatar from "@material-ui/core/Avatar";

class PollConfigCheckBox extends Component {




    handleSubmit=(e)=> {
        e.preventDefault();
        this.props.handleSubmit(5)
    }

    handleDelete=(index)=>{
        this.props.handleDeleteOpt(index);
    };


    addOption=()=>{

        let {opt,option} = this.state;
        option.push(opt);
        this.setState({
            option,
            opt:''
        });
        //this.props.addOption(opt);


        // this.setState({
        //     options: options
        // })

    };

    render() {

        let {option,opt} = this.props;
        return (
            <div>
                <form onSubmit={this.handleSubmit}>

                    <Grid container>

                        <Grid item xs={12}>

                            <TextField
                                autoFocus
                                margin="dense"
                                name="title"
                                id="title"
                                required
                                label="Título de la pregunta"
                                fullWidth
                                value={this.props.title}
                                onChange={this.props.handleInputChange}
                            />
                        </Grid>

                        <Grid item xs={12}>

                            <TextField
                                id="outlined-adornment-password"
                                label="Agregar elementos"
                                name="opt"
                                value={this.props.opt}
                                onChange={this.props.handleInputChange}
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <IconButton
                                                edge="end"
                                                aria-label="toggle password visibility"
                                                onClick={this.props.addOption}
                                            >
                                                <AddIcon/>
                                            </IconButton>
                                        </InputAdornment>
                                    ),
                                }}
                            />



                        </Grid>

                        <Grid item xs={12} style={{marginTop:20}}>
                            {
                                this.props.option.map((item,i)=>{
                                    return (
                                        <Chip
                                            key={i}
                                            label={item}
                                            onDelete={this.handleDelete.bind(this,i)}
                                        />
                                    )
                                })
                            }

                        </Grid>


                        <Grid item xs={12}>
                            <FormControlLabel
                                control={
                                    <Checkbox
                                        checked={this.props.isRequired}
                                        onChange={this.props.handleInputChange}
                                        value="checkedB"
                                        name={"isRequired"}
                                        color="primary"
                                    />
                                }
                                label="Es requerido"
                            />
                        </Grid>

                        <Grid item xs={12}>
                            <Button onClick={this.props.onClose} color="default">
                                Cancelar
                            </Button>
                            <Button type={'submit'} color="primary">
                                Aceptar
                            </Button>
                        </Grid>

                    </Grid>

                </form>
            </div>
        );
    }
}

export default PollConfigCheckBox;