import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import ResourcesForm from "../../../components/events/admin/resources/ResourcesForm";
import GlobalContext from "../../../GlobalContext";
import _ from "lodash";
import {Redirect} from "react-router-dom";
import {ApiServiceEvents} from "../../../api-services/events";

const apiEvents = new ApiServiceEvents();

/**
 *
 * This view allow resorce edit per user
 * @class EditMyResources
 * @extends {Component}
 */
class EditMyResources extends Component {

    static contextType = GlobalContext;

    state = {
        resourceFile:null,
        name:'',
        _idEvent:null,
        _idResource:null,
        eventInfo:null,
        availableGroups:[],
        availableGroupsIds:[],
        loading:false,
        redirect: false,
        breadCrumbs:[],
        urlCurrentFile:'',
        userId:''
    };


    componentDidMount() {

        let slug = this.context.slug;
        let _idResource = this.props.match.params.resource;
        let userId = this.props.match.params.user;


        console.log(userId,_idResource)

        let breadCrumbs = [
            {
                name:'Listado de usuarios',
                active:false,
                link:`/${slug}/admin/resources/list/`
            },
            {
                name:'Recursos del usuario',
                active:false,
                link:`/${slug}/admin/user/user-resources/${userId}`
            },
            {
                name:'Editar recurso',
                active:true,
                link:''
            }
        ];

        this.setState({
            userId,
            breadCrumbs,
            _idResource
        })

        apiEvents.getEventInfo('', slug).then(
            (response) => {
                this.setState({
                    groups:response.event.groups,
                    _idEvent: response.event._id
                });

                this.getResourceByIdUser(userId, _idResource)
            }
        ).catch(
            (err) => {
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );
    }

    /**
     * this function gets the resource to edit
     * @param  {number} _idEvent
     * @param  {number} _id
     */
    getResourceByIdUser(_idEvent, _id){
        console.log('entra getresourcesbyiser')
        apiEvents.getResourceByIdUser(_idEvent,_id).then((res)=>{

            this.setState({
                name:res.name,
                urlCurrentFile:`${res.file}`
            })
        }).catch((err)=>console.log(err)).finally(()=>{
            this.setState({
                loading:false
            })
        })
    }

    /**
     * function to set estate when a input elemente change
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const ctx = this.context
        let value = null
        const name = event.target.name;
        const patternAlphanum = /^[,.;a-zA-ZÀ-ÿ\d\s-_]+$/ig

        if(event.target.name === 'name'){
            if(event.target.value.length > 50){
                event.target.value = event.target.value.slice(0,50);
                ctx.openNotification('Límite de caracteres excedido', 'error');
            } else {
                if(!patternAlphanum.test(event.target.value)){
                    event.target.value = event.target.value.slice(0,-1);
                    ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
                }
            }
        }
        value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;

        this.setState({
            [name]: value
        });
    };

    /**
     * Function to get file when input element change
     * @param  {Object} file
     */
    handleInputChangeFile=(file)=>{
        console.log(file)
        this.setState({
            resourceFile:file
        })
    };


    /**
     * Function to submit resource form
     * @param  {Object} e
     */
    handleSubmit=async (e)=>{
        e.preventDefault();
        let {resourceFile,name, _idEvent, groupsSelected, _idResource, userId } = this.state;

        this.setState({
            loading:true
        });

        let formData = new FormData();
        if(resourceFile){
            formData.append("resourceFile", resourceFile);
        }

        formData.append("name", name);
        formData.append("_idEvent", _idEvent);
        formData.append("_id", _idResource);
        formData.append("eventUser", userId);

        try{
            let response = await apiEvents.updateResourceUser(formData);
            this.setState({
                redirect:true
            })
        }catch (e) {
            alert(e.response.data.message)
        }finally {
            this.setState({
                loading:false
            })
        }

    };



    render() {

        let {groups, loading,groupsSelected,breadCrumbs,name,urlCurrentFile,userId } = this.state;
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/user/user-resources/${userId}`} />;

        return (
            <AdminEventLayout breadcrumbs={breadCrumbs} title="Recursos">
                <Grid container>
                    <Grid item xs={12}>
                        <ResourcesForm
                            titleForm={'Editar recurso del usuario'}
                            handleSubmit={this.handleSubmit}
                            groups={[]}
                            groupsId={[]}
                            fromUserResources={true}
                            redirectBack={`/${ctx.slug}/admin/user/user-resources/${userId}/`}
                            groupsSelected={groupsSelected}
                            loading={loading}
                            name={name}
                            urlCurrentFile={urlCurrentFile}
                            handleInputChange={this.handleInputChange}
                            handleCheckGroups={this.handleCheckGroups}
                            handleInputChangeFile={this.handleInputChangeFile}
                        />
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default EditMyResources;