                                    {/*<MuiPickersUtilsProvider utils={DateFnsUtils}>*/}
                                    {/*    <Grid item xs={6}>*/}
                                    {/*        <Field*/}
                                    {/*            name="rendez-vous"*/}
                                    {/*            component={DatePickerWrapper}*/}
                                    {/*            fullWidth*/}
                                    {/*            margin="normal"*/}
                                    {/*            label="Rendez-vous"*/}
                                    {/*        />*/}
                                    {/*    </Grid>*/}
                                    {/*    <Grid item xs={6}>*/}
                                    {/*        <Field*/}
                                    {/*            name="alarm"*/}
                                    {/*            component={TimePickerWrapper}*/}
                                    {/*            fullWidth*/}
                                    {/*            margin="normal"*/}
                                    {/*            label="Alarm"*/}
                                    {/*        />*/}
                                    {/*    </Grid>*/}
                                    {/*</MuiPickersUtilsProvider>*/}




