import React from 'react';
import MaterialTable from 'material-table';
import { Redirect } from "react-router-dom";
import GlobalContext from "../../GlobalContext";

class MaterialTableDemo extends React.Component {
    static contextType = GlobalContext;
    // const [state, setState] = React.useState({
    state = {
        redirectToEdit: '',
        selectedRow: '',
        columns: [
            { title: 'Nombre', field: 'firstName' },
            { title: 'Apellido(s)', field: 'lastName' },
            { title: 'Email', field: 'email' },
            // { title: '¿Activo?', field: 'isActiveDisplay' },
            // { title: 'ID', field: '_id' },
            // { title: 'Birth Year', field: 'birthYear', type: 'numeric' },
            // {
            //     title: 'Birth Place',
            //     field: 'birthCity',
            //     lookup: { 34: 'İstanbul', 63: 'Şanlıurfa' },
            // },
        ],
        // data: [
        //     { name: 'Mehmet', surname: 'Baran', birthYear: 1987, birthCity: 63 },
        //     {
        //         name: 'Zerya Betül',
        //         surname: 'Baran',
        //         birthYear: 2017,
        //         birthCity: 34,
        //     },
        // ],
    };
    render() {
        let ctx = this.context;

        if (this.state.redirectToEdit) return <Redirect push to={`/user/${this.state.selectedRow}/`} />;

        let state = this.state;
        let setState = this.setState;
        let data = this.props.rows;

        return (
            <MaterialTable
                title="Usuarios"
                columns={state.columns}
                data={data}
                editable={{
                    // onRowAdd: newData =>
                    //     new Promise(resolve => {
                    //         setTimeout(() => {
                    //             resolve();
                    //             const data = [...state.data];
                    //             data.push(newData);
                    //             setState({...state, data});
                    //         }, 600);
                    //     }),
                    // onRowUpdate: (newData, oldData) =>
                    //     new Promise(resolve => {
                    //         setTimeout(() => {
                    //             resolve();
                    //             const data = [...state.data];
                    //             data[data.indexOf(oldData)] = newData;
                    //             setState({...state, data});
                    //         }, 600);
                    //     }),
                    onRowDelete: this.props.handlerDelete,
                }}
                actions={[
                    {
                        icon: 'edit',
                        tooltip: 'Editar',
                        onClick: (event, rowData) => this.setState({ redirectToEdit: true, selectedRow: rowData._id })
                    },
                    // rowData => ({
                    //     icon: 'delete',
                    //     tooltip: 'Delete User',
                    //     onClick: (event, rowData) => alert("You want to delete " + rowData.name),
                    //     disabled: rowData.birthYear < 2000
                    // })
                ]}
                options={{
                    actionsColumnIndex: -1,
                    pageSize: 10,
                    pageSizeOptions: [10, 20, 50, 100, 500],
                    paging: true,
                }}
                localization={ctx.tableConfig.localization}
            />
        );
    }
}

export default MaterialTableDemo