import axios from 'axios'
import { config } from '../config'
const BASE_URL = `${config.API_URL}/security/api`;

export class ApiServiceSecurity {

    constructor(token){
        // axios.defaults.headers.common['token'] = token ? token : window.localStorage.getItem('jwt');
    }

    login(req) {
        return axios.post(`${BASE_URL}/user/login`,req).then(response=> response);
    }


    loginEvent(email,password,slug){
        return axios.post(`${BASE_URL}/user/event-login`, {email,slug,password}).then(response=> response.data);
    }

    users() {
        return axios.get(`${BASE_URL}/user/list`, {
            headers: { 'Content-Type' :'application/x-www-form-urlencoded' }
        }).then(response=> response);
    }

    addSuperUser(request){
        let url = `${BASE_URL}/user/add/`;
        return axios.post(url, request).then(response => response.data);
    }

    updSuperUser(request){
        let url = `${BASE_URL}/user/update/`;
        return axios.put(url, request).then(response => response.data);
    }

    getSuperUserList(){
        let url = `${BASE_URL}/user/list/`;
        return axios.get(url).then(response => response.data);
    }

    getSuperUserDetail(userId){
        let url = `${BASE_URL}/user/?_id=${userId}`;
        // let url = `${BASE_URL}/user/${userId}/`;
        return axios.get(url).then(response => response.data);
    }

    updActiveSuperUser(userId, active){
        let url = `${BASE_URL}/user/active/`;
        let request = {_id: userId, isActive:active};
        return axios.put(url, request).then(response => response.data);
    }

    recoverPassword(email,slug){
        let url = `${BASE_URL}/user/pwd-reset-request/add`;
        return axios.post(url,{email,slug}).then(response => response.data);
    }


    resetPasswordConfirm(tokenRequest,newPassword){
        let url = `${BASE_URL}/user/pwd-reset-request/confirm`;
        return axios.put(url,{tokenRequest,newPassword}).then(response => response.data);
    }

    validateTokenResetPassword(tokenRequest){
        let url = `${BASE_URL}/user/pwd-reset-request/validate-token`;
        return axios.post(url,{tokenRequest}).then(response => response.data)
    }

}



