import React, {Component} from 'react';
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import Grid from "@material-ui/core/Grid";
import Container from "@material-ui/core/Container";
import Fab from "@material-ui/core/Fab";
import AddIcon from '@material-ui/icons/Add';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import SectionBuilder from "../../../components/builder/SectionBuilder";
import GlobalContext, { CtxProviderBuilder} from "../../../GlobalContext";
import {ApiServiceEvents} from "../../../api-services/events";
import move from 'lodash-move'
import _ from 'lodash'
import {t,Trans} from "@lingui/macro";
import LoadingOverlay from 'react-loading-overlay';
import {withI18n} from "@lingui/react";
import {Typography} from "@material-ui/core";
import Tooltip from "@material-ui/core/Tooltip";


const apiEvent = new ApiServiceEvents();
const useStyles = theme => ({
    fab: {
        margin: theme.spacing(1),
    }
});

class BuilderEvent extends Component {

    static contextType = GlobalContext;

    /**
     * Agregamos un componente dentro de una sección
     * @param component
     */
    setComponent = async (component,section)=>{

        let {sections} = this.state;
        let indexSection = _.findIndex(sections, function(o) { return section._id === o._id; })
        let order = sections.length+1;
        // console.log('setcomponent',component,section,indexSection)
        let currentSection = sections[indexSection];
        // console.log('current', currentSection)
        currentSection.components.push(component);
        sections[indexSection] = currentSection;

        let {_idModule,_idView} = this.state;

        try{
            // console.log(component, section)
            let res = await apiEvent.addComponent(component.type, _idModule, _idView, section._id,order);
            // console.log('res add',res);
            this.getSections(_idModule, _idView);
        }catch (e) {
            if(e.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(_idModule,_idView);
            }
            // console.log(e.response);
        }

    }

    componentDidMount() {
        let _idModule = this.props.match.params.module;
        let _idView = this.props.match.params.view;
        let slug = this.props.match.params.slug;

        let breadCrumbs = [
            {
                name:<Trans>Lista de módulos</Trans>,
                active:false,
                link:`/${slug}/admin/module/list`
            },
            {
                name:<Trans>Lista de vistas del módulo</Trans>,
                active:false,
                link:`/${slug}/admin/${_idModule}/view/list`
            },
            {
                name:'Builder',
                active:true,
                link:''
            }
        ];

        this.setState({
            _idView,
            _idModule,
            breadCrumbs,
            slug
        });
        this.getSections(_idModule,_idView);
        this.getViewDetail(_idView);
        this.getAllPerms();
    }


    getAllPerms = () => {
        let slug = this.props.match.params.slug;
        apiEvent.getEventInfo('', slug).then(
            (res) => {
                // console.log('Perms', res);
                if(_.has(res, 'event.groups'))
                    this.setState({allPerms: res.event.groups});
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    this.context.openPermissionNotification();
                }
            }
        )
    };


    addSection = ()=>{

        this.setState({loading:true});
        let {sections,_idModule,_idView} = this.state;

        let section = {
            _idModule,
            _idView,
            properties:{background:'transparent'},
            groups:[],
            order:sections.length+1,
            name:"Sección " + (sections.length+1),
            components:[]
        };

        apiEvent.addSection(section).then((res)=>{
            this.getSections(_idModule,_idView);
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(_idModule,_idView);
            }
        }).finally(()=>this.setState({loading:false}));




        this.setState({
            sections: sections
        })
    };


    getViewDetail = (idView) => {
        apiEvent.getViewInfo(idView).then(
            (res) => {


                let slug = this.props.match.params.slug;

                let breadCrumbs = [
                    {
                        name:<Trans>Lista de módulos</Trans>,
                        active:false,
                        link:`/${slug}/admin/module/list`
                    },
                    {
                        name:this.props.i18n._(t`Lista de vistas del módulo - `) +' '+ res.name,
                        active:false,
                        link:`/${slug}/admin/${res._id}/view/list`
                    },
                    {
                        name:'Builder',
                        active:true,
                        link:''
                    }
                ];


                this.setState({viewInfo: res, breadCrumbs});
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    this.context.openPermissionNotification();
                }
            }
        );
    };


    getSections=(_idModule,_idView)=>{
        this.setState({loading:true});
        apiEvent.getSectionBuilder(_idModule,_idView).then((res)=>{

            let sectionCurrentFound = _.find(res, {_id: this.state.currentSection._id});
            let componentCurrentFound = null;
            if(sectionCurrentFound){
                componentCurrentFound = _.find(sectionCurrentFound.components, {_id: this.state.currentComponent._id});
            }


            // console.log('sectionCurrentFound', sectionCurrentFound);
            // console.log('componentCurrentFound', componentCurrentFound);

            this.setState({
                sections: res,
                currentSection: sectionCurrentFound ? sectionCurrentFound : this.state.currentSection,
                currentComponent: componentCurrentFound ? componentCurrentFound : this.state.currentComponent,
            });
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
            }
        }).finally(()=>{
            this.setState({loading:false})
        })
    }


    deleteSection=(section)=>{
        this.setState({loading:true});
        apiEvent.removeSection(this.state._idModule,this.state._idView, section._id).then((res)=>{
            this.getSections(this.state._idModule,this.state._idView);
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(this.state._idModule,this.state._idView);
            }
        }).finally(()=>this.setState({loading:false}));
    }

    cloneSection=(section)=>{
        let {sections,_idModule,_idView} = this.state;
        section.order = sections.length+1;
        section.name =  `sección ${section.order}`;
        section._id = null;
        section._idModule = _idModule;
        section._idView = _idView;

        //Eliminamos los ids de los componentes para que el backend genere nuevos
        section.components.map((item,i)=>{
            delete item._id;
            return item;
        });


        this.setState({loading:true})

        apiEvent.addSection(section).then((res)=>{
            this.getSections(_idModule,_idView);
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(_idModule,_idView);
            }
        }).finally(()=>{this.setState({loading:false})})

    }

    sortSection=(type,section)=>{
        let {sections,_idModule, _idView} = this.state;
        // obtenemos la posicion de la seccion en el arreglo
        let currentPostion = _.findIndex(sections, function(o) { return section._id === o._id; });
        if(type==='up'){
            //move change the position of section in the current array
          sections = move(sections,currentPostion, currentPostion-1);
        }else{
          sections =  move(sections,currentPostion, currentPostion+1);
        }
        let arrayPositionsIds = _.map(sections,'_id'); // los ids con las nuevas positions
        this.setState({loading:true})
        apiEvent.reorderSection(_idModule,_idView,arrayPositionsIds).then((res)=>{
                this.getSections(_idModule,_idView);
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(_idModule,_idView);
            }
                console.log(err.response);
        }).finally(()=>{
            this.setState({loading:false})
        });

    };

    sortComponent=(type, section, selectedComponent)=>{
        let {_idModule, _idView} = this.state;

        let componentList = section.components ? section.components : [];
        if(section.needOrder){
            componentList = _.orderBy(componentList, ['order'], ['asc']);
        }
        let idSelectedComponent = selectedComponent._id ? selectedComponent._id : '';


        // obtenemos la posicion de la seccion en el arreglo
        let currentPosition = _.findIndex(componentList, function(o) { return idSelectedComponent === o._id; });
        if(type === 'up'){
            //move change the position of section in the current array
            componentList = move(componentList, currentPosition, currentPosition-1);
        }else{
            componentList =  move(componentList, currentPosition, currentPosition+1);
        }
        let arrayPositionsIds = _.map(componentList,'_id'); // los ids con las nuevas positions
        this.setState({loading:true});
        apiEvent.reorderComponent(_idModule,_idView,arrayPositionsIds, section._id).then((res)=>{
                this.getSections(_idModule,_idView);
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(_idModule,_idView);
            }
        }).finally(()=>{
            this.setState({loading:false})
        });

    };

    /**
     * Nos sirve  para setear las properties de un componente
     * @param component
     * @param configProperties
     * @param section
     */
    saveConfigComponent=(component, configProperties, section)=>{
        this.setState({loading:true});
        let ctx = this.context;        
        let {_idModule,_idView} = this.state;

        return apiEvent.setPropertyComponent(component._id, configProperties, _idModule, _idView, section._id).then((res)=>{
            console.log('exito',res);
            ctx.openSuccessNotification(<Trans>Configuración guardada.</Trans>);
            console.log("component/config",configProperties);
            this.getSections(_idModule,_idView);  
            return res;             
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(_idModule,_idView);
                ctx.openNotification(<Trans>No fue posible guardar configuración, intente más tarde.</Trans>, 'error');
            }
            else if(err.response.status === 404)
            {
                console.log('ERROR ', err.response);
                switch (err.response.data.message) {
                    case 'facebook':                        
                        ctx.openNotification(<Trans>No existe el perfil de Facebook.</Trans>, 'error');        
                        break;
                    case 'instagram':
                        ctx.openNotification(<Trans>No existe el perfil de Instagram.</Trans>, 'error');        
                        break;
                    case 'pinterest':
                        ctx.openNotification(<Trans>No existe el perfil de Pinterest.</Trans>, 'error');        
                        break;
                    case 'youtube':
                        ctx.openNotification(<Trans>No existe el perfil de Youtube.</Trans>, 'error');        
                        break;
                    case 'twitter':
                        ctx.openNotification(<Trans>No existe el perfil de Twitter.</Trans>, 'error');        
                        break;
                    default:
                        break;
                }
                
                return err;
            }
            
        }).finally(()=> this.setState({loading:false}))


    };


    changeNameSection=(section)=>{
        let {_idModule, _idView} = this.state;
        let request = {
            _idModule,
            _idView,
            _id:section._id,
            name:section.name,
            order:section.order,
            groups:section.groups
        }

        this.setState({loading:true})

        apiEvent.changeNameSection(request).then((res)=>{
            this.getSections(_idModule, _idView)
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(_idModule, _idView)
            }
            console.log(err)
        }).finally(()=>{
            this.setState({loading:false})
        })

    };

    _changeSectionPerms = (section, values, returnPromise) => {
        let {_idModule, _idView} = this.state;
        let request = {
            _idModule,
            _idView,
            _id:section._id,
            name:section.name,
            order:section.order,
            groups:values.viewPerms
        };

        let promise = apiEvent.changeNameSection(request);

        if(returnPromise) return promise;

        let ctx = this.context;

        this.setState({loading:true});

        promise.then((res)=>{
            this.getSections(_idModule, _idView);
            ctx.openSuccessNotification(<Trans>Permisos guardados</Trans>);
        }).catch((err)=>{
            if(err.response.status === 403){
                this.context.openPermissionNotification();
                this.getSections(_idModule, _idView);
            }else {
                console.log(err);
                ctx.openNotification(<Trans>No fue posible guardar los permisos</Trans>);
            }
        }).finally(()=>{
            this.setState({loading:false})
        })
    };

    changeSectionPerms = (section, perms) => {
        this._changeSectionPerms(section, perms, false);
    };

    setCurrentComponent = (component) => {
        // console.log('Componente actual', component);
        this.setState({currentComponent: component});
    };

    setCurrentSection = (section) => {
        // console.log('Sección actual', section);
        this.setState({currentSection: section});
    };

    setCurrentSectionAndComponent = (section, component) => {
        this.setState({currentSection: section, currentComponent: component});
    };

    getImage = (properties, withDomain=true) => {
        const mediaDomain = this.state.mediaDomain;
        const isBase64 = _.has(properties, 'image.value') ? properties.image.value.includes('data:image') : false;

        let image = 'https://x.kinja-static.com/assets/images/logos/placeholders/default.png';
        // let image = 'https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png';

        if(_.has(properties, 'image.value')){
            image = isBase64 ? properties.image.value : `${properties.image.value}`;
        }
        // console.log('Returnimage', image);
        return image;
    };

    deleteComponent = (msg='Componente eliminado') => {
        let idModule = this.state._idModule;
        let idView = this.state._idView;
        // let idSection = this.state._idSection;
        let currentComponentId = this.state.currentComponent._id;
        let currentSectionId = this.state.currentSection._id;
        let ctx = this.context;
        this.setState({loading: true});
        apiEvent.removeComponent(idModule, idView, currentSectionId, currentComponentId).then(
            (res) => {
                ctx.openSuccessNotification(msg);
                this.getSections(idModule,idView);
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    this.context.openPermissionNotification();
                    this.getSections(idModule,idView);
                }else
                    ctx.openNotification(<Trans>Error al eliminar un componente</Trans>, 'error')
            }
        ).finally(() => {
            this.setState({loading: false});
        })
    };


    state={
        sections:[],
        addSection: this.addSection,
        setComponent:this.setComponent,
        deleteSection:this.deleteSection,
        deleteComponent:this.deleteComponent,
        cloneSection:this.cloneSection,
        sortSection:this.sortSection,
        sortComponent:this.sortComponent,
        changeNameSection: this.changeNameSection,
        saveConfigComponent:this.saveConfigComponent,
        setCurrentComponent:this.setCurrentComponent,
        setCurrentSection:this.setCurrentSection,
        setCurrentSectionAndComponent:this.setCurrentSectionAndComponent,
        changeSectionPerms:this.changeSectionPerms,
        getImage:this.getImage,
        _idView:null,
        _idModule:null,
        slug:'',
        msgLoading:<Trans>cargando...</Trans>,
        breadCrumbs:[],
        currentComponent: {_id: ''},
        currentSection: {_id: ''},
        mediaDomain: 'https://appeventssectur.s3.amazonaws.com/',
        viewInfo: {_id:''},
        allPerms: ''
    };



    render() {

        const {classes} = this.props;
        let {sections,loading,msgLoading,viewInfo} = this.state;
        let permissions = _.has(this.state.viewInfo, 'views.groups') ? this.state.viewInfo.views.groups : [];
        let viewName = _.has(viewInfo, 'views.name')? viewInfo.views.name : 'Vista';
        return (
            <CtxProviderBuilder value={this.state}>
                <LoadingOverlay
                    active={loading}
                    spinner
                    text={msgLoading}
                >
                    <AdminEventLayout breadcrumbs={this.state.breadCrumbs} title={'Builder'}>

                    <Container maxWidth="lg">
                        {/* There is already an h1 in the page, let's not duplicate it. */}
                        <Typography style={{textAlign:'center'}} variant="h6" component="h6">
                            {viewName.toUpperCase()}
                        </Typography>
                        <hr/>
                        {
                            sections.map((section, index) => {
                                return (
                                    <SectionBuilder
                                        onDeleteSection={this.handleDeleteSection}
                                        key={index}
                                        sizeSections={sections.length}
                                        position={index}
                                        section={section}
                                        viewPermissions={permissions}
                                        allPerms={this.state.allPerms}
                                    />
                                )
                            })
                        }

                        <div align={'center'}>
                            <Tooltip title={<Trans>Agregar sección</Trans>}>
                            <Fab color="primary" onClick={this.addSection} aria-label="add"
                                 className={classes.fab}>
                                <AddIcon/>
                            </Fab>
                            </Tooltip>
                        </div>
                    </Container>
                </AdminEventLayout>
                </LoadingOverlay>
            </CtxProviderBuilder>
        );
    }
}





export default withI18n()(withStyles(useStyles)(BuilderEvent));