import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import Card from "@material-ui/core/Card";
import Typography from "@material-ui/core/Typography";
import IconButton from "@material-ui/core/IconButton";
import CardContent from "@material-ui/core/CardContent";
import CardMedia from "@material-ui/core/CardMedia";
import DeleteIcon from '@material-ui/icons/Delete';
import EditIcon from '@material-ui/icons/Edit';
import CardActions from "@material-ui/core/CardActions";
import ToggleOffTwoToneIcon from '@material-ui/icons/ToggleOffTwoTone';
import ToggleOnTwoToneIcon from '@material-ui/icons/ToggleOnTwoTone';
import FilterNoneIcon from '@material-ui/icons/FilterNone';
import {Link} from 'react-router-dom';
import Tooltip from '@material-ui/core/Tooltip';
import moment from 'moment';
import _ from "lodash";
import {Trans} from "@lingui/macro";
import StopIcon from '@material-ui/icons/Stop';
import RadioButtonCheckedIcon from '@material-ui/icons/RadioButtonChecked';

class SimpleListEvents extends Component {


    state ={
        current:null,
        valueInputModal:''
    };

    /**
     * Pasamos al listado de eventos el objeto de evento que se quiere cambiar de estatus
     * @param event
     */
    handleChangeStatus(event){
        this.props.onHandleChangeStatus(event)
    }

    handleDeleteEvent(event){
        this.props.onHandleDeleteEvent(event);
    }

    handleCloneEvent(event){
        this.props.onHandleCloneEvent(event);
    }


    render() {
        const {events} = this.props;
        return (
            events.map((event,index)=>{
                const imageDefault = 'https://user-images.githubusercontent.com/194400/49531010-48dad180-f8b1-11e8-8d89-1e61320e1d82.png';
                let image = _.has(event, 'logo.value') ? event.logo.value : event.logo;
                // console.log('IMAGEN', event.name,  event);
                return(
                    <Grid key={event._id}  item xs={12} md={4} lg={4}>
                        <Card style={{wordWrap: "break-word"}} >
                            <CardMedia
                                style={{
                                    height: 0,
                                    paddingTop: '56.25%', // 16:9
                                }}
                                image={image ? image : imageDefault}
                                title={event.name}
                            />
                            <CardContent>
                                <Typography variant="body2" component="h4">
                                    <StopIcon  style={{padding:0,margin:0, color:event.color, fontSize:15}}  />  {event.name}
                                </Typography>
                                <Typography variant="body2" color="textSecondary" component="p">
                                    {event.description.length>100?`${event.description.substring(0, 100)}...`: event.description}
                                </Typography>
                                {
                                    _.has(event.availability,'startDate')?<Typography style={{marginTop:'10px'}} color={'primary'} variant="caption" display="block" gutterBottom>
                                        { `${moment(event.availability.startDate).format('DD/MM/YYYY, h:mm a')} - ${moment(event.availability.endDate).format('DD/MM/YYYY, h:mm a')} ` }
                                    </Typography>:<Typography style={{marginTop:'10px'}} color={'secondary'} variant="caption" display="block" gutterBottom><Trans>Fecha no configurada</Trans></Typography>
                                }

                                {/*<a href={`/${event.slug}/admin`}>{`www.sectur.com/${event.slug}/admin`}</a>*/}
                                <Link push='true' to={`/${event.slug}/admin`}>{`app.tianguisturistico.com/${event.slug.length>17 ? event.slug.substring(0,12)+'...': event.slug}/admin`}</Link>
                                <br/>

                            </CardContent>
                            <CardActions disableSpacing>

                                <Tooltip title={event.isActive?<Trans>Deshabilitar</Trans>:<Trans>Habilitar</Trans>}>
                                    <IconButton onClick={this.handleChangeStatus.bind(this,event)} aria-label="">
                                        {
                                            event.isActive?<ToggleOnTwoToneIcon style={{color:'green'}}/>:<ToggleOffTwoToneIcon/>
                                        }
                                    </IconButton>
                                </Tooltip>
                                <Tooltip title={<Trans>Editar</Trans>}>
                                <IconButton component={Link} to={'/event/'+event._id} aria-label="">
                                    <EditIcon  />
                                </IconButton>
                                </Tooltip>

                                <Tooltip title={<Trans>Eliminar</Trans>}>
                                <IconButton onClick={this.handleDeleteEvent.bind(this,event)} aria-label="">
                                    <DeleteIcon/>
                                </IconButton>
                                </Tooltip>

                                <Tooltip title={<Trans>Duplicar evento</Trans>}>
                                <IconButton onClick={this.handleCloneEvent.bind(this,event)}>
                                    <FilterNoneIcon/>
                                </IconButton>
                                </Tooltip>

                            </CardActions>
                        </Card>


                    </Grid>
                )
            })
        );
    }
}

export default SimpleListEvents;