import React from 'react';
import Button from '@material-ui/core/Button';
import TextField from '@material-ui/core/TextField';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CircularProgress from "@material-ui/core/CircularProgress";

export default function ModalFormGeneric(props) {
    // const [open, setOpen] = React.useState(false);
    //
    // const handleClickOpen = () => {
    //     setOpen(true);
    // };
    //
    // const handleClose = () => {
    //     setOpen(false);
    // };

    const open = props.open;
    const handleClose = props.handleClose;
    const text = props.text ? props.text : '';
    const title = props.title ? props.title : '';
    const loading = props.loading ? props.loading : false;
    const maxWidth = props.maxWidth ? props.maxWidth : 'sm';
    const fullScreen = props.fullScreen ? props.fullScreen : false;
    const onlyCancel = props.onlyCancel ? props.onlyCancel : false;

    return (
        <div>

            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="form-dialog-title"
                fullScreen={fullScreen}
                // fullWidth
                maxWidth={maxWidth}
            >
                <DialogTitle id="form-dialog-title">{title}</DialogTitle>
                <DialogContent>
                    <DialogContentText>
                        {text}
                    </DialogContentText>
                    {props.children}
                </DialogContent>
                <DialogActions>
                    <Button onClick={handleClose} color="primary">
                        Cancelar
                    </Button>
                    {
                        !onlyCancel ? (
                            <>
                            {
                                loading ? <CircularProgress size={20}/> :
                                    <Button onClick={props.confirm} color="primary">
                                        Aceptar
                                    </Button>
                            }
                            </>
                        ) : ''
                    }


                </DialogActions>
            </Dialog>
        </div>
    );
}
