import React, {Component} from "react";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import Grid from "@material-ui/core/Grid";
import Paper from "@material-ui/core/Paper";
import PersonIcon from "@material-ui/core/SvgIcon/SvgIcon";
import {Link, Redirect, withRouter} from "react-router-dom";
import _ from "lodash"

import CreateModuleForm from "../../../../components/events/admin/modules/CreateModuleForm";

import GlobalContext from "../../../../GlobalContext";
import {ApiServiceEvents} from "../../../../api-services/events";
import CreateViewForm from "../../../../components/events/admin/views/FormCreateView";
import {t, Trans} from "@lingui/macro";
import {withI18n} from "@lingui/react";
import Resizer from 'react-image-file-resizer';


const apiEvent = new ApiServiceEvents();


function not(a, b) {
    return a.filter(value => b.indexOf(value) === -1);
}

/**
 *
 * This view allow view update
 * @class EditView
 * @extends {Component}
 */
class EditView extends Component {


    static contextType = GlobalContext;

    state = {
        logo: {
            value: 'data:image/png;base64,',
            name: 'example.png',
            type: 'imageBase64'
        },
        eventInfo: '',
        _idModule:null,
        _idView:'',
        isVisible: false,
        availableGroups: [],
        availableGroupsIds: [],
        selectedGroupsIds: [],
        checked: [],
        name: '',
        slug:'',
        isPublicModule:false,
        errorMessage: '',
        showNotiError: false,
        redirect: false,
        status: 2,
        loading: false,
        breadCrumbs:[],
        changeLogo: false,
        imageUri:null,
        editarViewLegend:this.props.i18n._(t`Editar vista`)
    };

    /**
     * Function to change state on isVisible when checkbox checked
     */
    handleChangeCheck = () => {
        this.setState({
            isVisible: !(this.state.isVisible)
        })
    };
    
    /**
     * Function to get file when input file element change
     * @param  {Object} files
     */
    getFiles=(files)=>{
        if(files.base64) {
            Resizer.imageFileResizer(
                files.file,
                800,
                1600,
                files.type,
                100,
                0,
                uri => {
                    console.log(""+uri)
                    this.setState({imageUri:uri},()=>{
                        let image = {
                            value: uri,
                            type: 'imageBase64',
                            name: files.name
                        };
                        this.setState({
                            logo: image,
                            changeLogo: true
                        })
                    })
                },
                'base64'
            );
        }
    };

    /**
     * Function to set the status when an input element changes
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        this.setState({
            [name]: value
        });
    };

    /**
     * Function to set availables groups from item selected
     * @param  {Object} items
     */
    handleSetGroup = (items) => {
        let oldState = this.state;
        oldState.availableGroupsIds = items;
        this.setState(oldState);
    };

    /**
     * Function to set state on groups selected
     * @param  {Object} items
     */
    handleSetGroupSelected = (items) => {
        let oldState = this.state;
        oldState.selectedGroupsIds = items;
        this.setState(oldState);
    };

    /**
     * Function to set state on checked items when change input check
     * @param  {Object} checkedItems
     */
    handleSetChecked = (checkedItems) => {
        let oldState = this.state;
        oldState.checked = checkedItems;
        this.setState(oldState);
    };

    
    /**
     * Function to get event id
     */
    getEventId = () => {
        let eventInfo = this.state.eventInfo;
        return eventInfo ? eventInfo.event._id : '';
    };

    /**
     * Function to clean request with form json
     * @returns {Object} Json
     */
    getCleanedRequest = () => {
        const {logo, name, selectedGroupsIds, status,_idModule,_idView, isVisible} = this.state;
        let request = {
            name,
            _idModule,
            _id:_idView,
            groups: selectedGroupsIds,
            isActive: isVisible
        };
        if(this.state.changeLogo)
            request.image = logo;
        return request;
    };

    /**
     * function to send the view update form
     * @param  {Object} e
     */
    handleSubmit = (e)  => {
        e.preventDefault();
        let ctx = this.context;
        this.setState({errorMessage: '', showNotiError: false, loading:true});
        let request = this.getCleanedRequest();
        console.log('Request', request);
        apiEvent.updateView(request).then(
            (response) => {
                this.setState({redirect:true, loading:false});
            }
        ).catch(
            err => {
                console.log(err);
                if(err.response.status === 403){
                    ctx.openPermissionNotification();
                }
                this.setState({loading:false})
            }
        );
    };

    componentDidMount() {

        let moduleId = this.props.match.params.module;
        let _idView = this.props.match.params.view;
        let slug = this.props.match.params.slug;

        this.setState({
            slug,
            _idModule:moduleId,
            _idView
        })


        let breadCrumbs = [
            {
                name:<Trans>Lista de módulos</Trans>,
                active:false,
                link:`/${slug}/admin/module/list`
            },
            {
                name:<Trans>Lista de vistas del módulo</Trans>,
                active:false,
                link:`/${slug}/admin/${moduleId}/view/list`
            },
            {
                name:this.state.editarViewLegend,
                active:true,
                link:``
            }
        ]

        this.setState({breadCrumbs})


        apiEvent.getModuleDetail(moduleId).then(
            (res) => {
                // console.log('Grupos del modulo', res.groups);
                // this.handleSetGroupSelected(res.groups);


                let breadCrumbs = [
                    {
                        name:<Trans>Lista de módulos</Trans>,
                        active:false,
                        link:`/${slug}/admin/module/list`
                    },
                    {
                        name:this.props.i18n._(t`Lista de vistas`)+ ` - ${res.name}`,
                        active:false,
                        link:`/${slug}/admin/${moduleId}/view/list`
                    },
                    {
                        name:this.state.editarViewLegend,
                        active:true,
                        link:``
                    }
                ]

                this.setState({breadCrumbs})


                let data = {
                    isPublicModule: res.isPublic
                };
                this.setState(data);
            }
        ).catch(
            err => {
                console.log('Error al obtener detail', err);
                this.setState({loadingDetail: false})
            }
        )


        apiEvent.getEventInfo('',slug).then(
            (response) => {
                console.log('Response', response);
                this.setState({
                    eventInfo: response,
                    availableGroups: response.event.groups,
                    availableGroupsIds: response.event.groups.map(o => o._id),
                });
                this.getViewInfo(_idView);
            }
        ).catch(
            (err) => {
                console.log('Error al obtener info del evento para los grupos', err);
            }
        );
    }

    /**
     * Function to get views details
     * @param  {} _idView
     */
    getViewInfo=async (_idView)=>{
        try{
            let res = await apiEvent.getViewInfo(_idView);
            this.setState({
                selectedGroupsIds: res.views.groups,
                availableGroupsIds: this.getIntersectionGroups(res.views.groups),
                logo: res.views.image,
                name:res.views.name,
                editarViewLegend:this.props.i18n._(t`Editar vista`)+ ` - ${res.views.name}`,
                isVisible: _.has(res, 'views.isActive') ? res.views.isActive : true
            })
        }catch (e) {
            console.log('errorr',e)
        }
    }

    /**
     * function to obtain relationship available groups and selected groups
     * @param  {Object} groupB
     */
    getIntersectionGroups = (groupB) => {
        let groupA = this.state.availableGroupsIds;
        // let groupB = this.state.selectedGroupsIds;
        let newGroupA = not(groupA, groupB);
        console.log(groupA, groupB);
        console.log('newGroupA', newGroupA);
        return newGroupA;
    };


    render() {


        let {_idModule,slug,_idView} = this.state

        if(this.state.redirect) return <Redirect push to={`/${slug}/admin/${_idModule}/view/list`} />

        return (
            <AdminEventLayout breadcrumbs={this.state.breadCrumbs}>
                <Grid container>
                    <CreateViewForm
                        handleLogo={this.getFiles}
                        title={this.state.editarViewLegend}
                        handleInputChange={this.handleInputChange}
                        availableGroupsIds={this.state.availableGroupsIds}
                        availableGroups={this.state.availableGroups}
                        selectedGroupsIds={this.state.selectedGroupsIds}
                        checked={this.state.checked}
                        handleSetGroup={this.handleSetGroup}
                        handleSetGroupSelected={this.handleSetGroupSelected}
                        handleSetChecked={this.handleSetChecked}
                        logo={this.state.logo}
                        name={this.state.name}
                        module={_idModule}
                        isPublic={this.state.isPublicModule}
                        isVisible={this.state.isVisible}
                        handleSubmit={this.handleSubmit}
                        handleChangeCheck={this.handleChangeCheck}
                        // status={this.state.status}
                        loading={this.state.loading}
                    />
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(EditView);