import React, { Component, Fragment } from 'react'
import { Switch, Route, Redirect } from 'react-router-dom'
import {I18nProvider, withI18n} from '@lingui/react'
import catalogEn from './locales/en/messages'

import routes from "./routes"
import ProtectedRoute from "./components/security/ProtectedRoute"
import { Trans, t } from '@lingui/macro';
import Code404 from "./pages/Code404"
import Code403 from "./pages/Code403"
import GlobalContext, {CtxProvider} from "./GlobalContext";
import {ApiServiceEvents} from "./api-services/events";
import {Notification, SuccessNotification} from "./components/notifications/CustomNotifications";
import _ from "lodash";

const apiEvent = new ApiServiceEvents();


class App extends Component {

  static contextType = GlobalContext;

  _getInfoEvent = (slug) => {
    new Promise((resolve, reject) => {
      let wait = setTimeout(() => {
        resolve('Promise A win!');
        let oldState = this.state;
        oldState.globalCtx.eventInfo = 'Aqui tengo info de event';
        this.setState(oldState);
      }, 400)
    })
  };

  getIdiomList = (slug) =>{
    // obtenemos el idioma
    apiEvent.getIdiomListPublic('',slug).then((res)=>{
      let oldState = this.state;
      oldState.globalCtx.langs = res;
      this.setState({oldState})
    }).catch((err)=>{
      console.log('Ocurrió un error al obtener info del idioma', err.response);
    })
  };

  getInfoEvent = (slug) => {
    
    apiEvent.getEventInfoBySlug(slug).then(
        (res) => {

          let oldState = this.state;
          console.log('Info del evento -->', oldState.globalCtx.language, res);
          let hasLang = false;
          res.availableLang.map((lang)=>{
            if(lang.code == oldState.globalCtx.language)
            {
              hasLang = true;
            }
          })

          
          oldState.globalCtx.eventInfo = res;
          this.setState(oldState);
          if(!hasLang)
          {
            this.setLanguage(oldState.globalCtx.defaultLanguage);
          }

          // this.setUserLangInLoad();
        }
    ).catch(
        (err) => {
          console.log('Ocurrió un error al obtener info del evento', err);
        }
    )
  };

  setLanguage = (language, withCompile=true) => {
    let oldState = this.state;
    apiEvent.changeUserIdiom(this.state.globalCtx.eventInfo._id, language).then((res) => {
      console.log('Se cambio lan desde el back ', language)
    }).catch((err) => {
      console.log(err)
    }).finally(
        () => {
          oldState.globalCtx.language = language;
          oldState.globalCtx.loadingLanguage = true;
          this.setState(oldState, () => {
            if(withCompile && language !== this.state.globalCtx.defaultLanguage)
              this.getCompileLang(language);
            else {
              let global = this.state.globalCtx;
              global.loadingLanguage = false;
              this.setState({globalCtx:global});
            }
          });
        }
    );



  };

  // loadCatalog = async (language, content) => {
  //   // load compiled messages.js
  //   const catalog = await import(
  //    /* webpackMode: "lazy", webpackChunkName: "i18n-[index]" */
  //    `locale/${language}/messages.js`)
  //   // return React.lazy(() => import(`public/locales/${language}/messages.js`));
  // };

  getCompileLang = (language) => {
    apiEvent.getCompileLanguage('front', language).then(
        (res) => {
          // console.log('Respuesta compilado', res);
          // this.loadCatalog(language, res);
          let globalCtx = this.state.globalCtx;
          globalCtx.catalogs[language] = eval(res);
          this.setState({globalCtx: globalCtx});
          // console.log('set lang', this.state.globalCtx.catalogs);
        }
    ).catch(
        (err) => {
          console.log('err compilado', err);
          // this.openNotification( this.props.i18n._(t`Error al cargar el idioma`)+' '+language , 'error');
          this.openNotification( <Trans>Error al cargar el idioma</Trans> , 'error');
          this.setLanguage(this.state.globalCtx.defaultLanguage, false);
        }
    ).finally(
        () => {
          let globalState = this.state.globalCtx;
          globalState.loadingLanguage = false;
          this.setState({globalCtx: globalState});
        }

    );
  };

  setEventSlug = slug => {
    // console.log('Slug', slug);
    let oldState = this.state;
    oldState.globalCtx.slug = slug;
    this.setState(oldState);
    this.getInfoEvent(slug); // Descomentar para obtener siempre el data del event
    this.getIdiomList(slug); // obtenemos el listado de idiomas
  };

  setToken = token => {
    let oldState = this.state;
    oldState.globalCtx.token = token;
    this.setState(oldState);
  };

  resetTokenInvalid = () => {
    // console.log('Es token invalido', this.state.globalCtx.tokenInvalidOrExpired);
    let oldState = this.state;
    oldState.globalCtx.tokenInvalidOrExpired = false;
    this.setState(oldState);
  };

  // openSuccessNotification = (text=this.props.i18n._(t`Elemento guardado exitosamente.`)) => {
  openSuccessNotification = (text=<Trans>Elemento guardado exitosamente</Trans>) => {
    let olsState = this.state;
    olsState.globalCtx.openSuccessNotificationFlag = true;
    olsState.globalCtx.textSuccessNotification = text;
    this.setState(olsState);
  };

  closeSuccessNotification = () => {
    let olsState = this.state;
    olsState.globalCtx.openSuccessNotificationFlag = false;
    olsState.globalCtx.textSuccessNotification = '';
    this.setState(olsState);
  };

  openNotification = (text=<Trans>Elemento guardado exitosamente</Trans>, type='info') => {
    let olsState = this.state;
    olsState.globalCtx.openNotificationFlag = true;
    olsState.globalCtx.textNotification = text;
    olsState.globalCtx.typeNotification = type;
    this.setState(olsState);
  };

  closeNotification = () => {
    let olsState = this.state;
    olsState.globalCtx.openNotificationFlag = false;
    olsState.globalCtx.textNotification = '';
    this.setState(olsState);
  };

  openPermissionNotification = (text='') => {
    let olsState = this.state;
    olsState.globalCtx.openNotificationFlag = true;
    // olsState.globalCtx.textNotification = text ? text : this.props.i18n._(t`No tienes permisos suficientes`);
    olsState.globalCtx.textNotification = text ? text : <Trans>No tienes permisos suficientes</Trans>;
    olsState.globalCtx.typeNotification = 'error';
    this.setState(olsState);
  };

  setCurrentVersion = () => {
    let olsState = this.state;
    try {
      let jenkinsVersion = require('./jenkins_version');
      let version = `${jenkinsVersion.major}.${jenkinsVersion.minor}.${jenkinsVersion.revision}`;
      olsState.globalCtx.version = version;
      this.setState({
        olsState
      })
    } catch (e) {
      // console.log(e.response)
    }
  };

  clearLanguageCatalog = (language) => {
    let newState = this.state;
    // console.log('entrando a limpiar',  `globalCtx.catalogs.${language}`, newState.globalCtx.catalogs);
    if(_.has(newState, `globalCtx.catalogs.${language}`)){
      // newState.globalCtx.catalogs[language] = '';
      delete newState.globalCtx.catalogs[language];
      this.setState(newState);
      // console.log('Se limpia el catalogo')
      if(this.state.globalCtx.language === language){
        // console.log('Se recompila el lenguaje', language);
        this.getCompileLang(language);
      }
    }
  };

  state = {
    globalCtx: {
      language: "es",
      defaultLanguage: "es",
      loadingLanguage: false,
      version:'v',
      langs:[],
      catalogs: {},
      clearLanguageCatalog: this.clearLanguageCatalog,
      setLanguage: this.setLanguage,
      setEventSlug: this.setEventSlug,
      setToken: this.setToken,
      openSuccessNotification: this.openSuccessNotification,
      closeSuccessNotification: this.closeSuccessNotification,
      openPermissionNotification: this.openPermissionNotification,
      openNotification: this.openNotification,
      closeNotification: this.closeNotification,
      resetTokenInvalid: this.resetTokenInvalid,
      slug: '',
      token: '',
      openSuccessNotificationFlag: false,
      textSuccessNotification: '',
      openNotificationFlag: false,
      textNotification: '',
      typeNotification: 'info',
      tokenInvalidOrExpired: false,
      mediaDomain: 'https://appeventssectur.s3.amazonaws.com/',
      eventInfo: {
        _id: '',
      },
      tableConfig: {
        localization: {
          body: {
            emptyDataSourceMessage: <Trans>Sin información</Trans>,
            editRow: {
              deleteText: <Trans>¿Estás seguro de eliminar este elemento?</Trans>,
              saveTooltip: <Trans>Confirmar</Trans>,
              cancelTooltip: <Trans>Cancelar</Trans>
            },
            deleteTooltip: <Trans>Eliminar</Trans>
          },
          toolbar: {
            searchTooltip: <Trans>Buscar</Trans>,
            placeholder: 'Buscar',
            searchPlaceholder: 'Buscar'
          },
          header: {
            actions: <Trans>Acciones</Trans>
          },
          pagination: {
            labelRowsSelect: 'filas',
            labelDisplayedRows: `{from} - {to} de {count}`,
            firstTooltip: 'Primera página',
            previousTooltip: 'Página anterior',
            nextTooltip: 'Página Siguiente',
            lastTooltip: 'Última página'
          }
        }
      },
      groupsPermissions: {
        userEvent: {
          view: 'UserEvent_view',
          add: 'UserEvent_add',
          update: 'UserEvent_edit',
          delete: 'UserEvent_delete',
        },
        module: {
          view: 'Module_view',
          add: 'Module_add',
          update: 'Module_edit',
          delete: 'Module_delete',
        },
        view: {
          view: 'View_view',
          add: 'View_add',
          update: 'View_edit',
          delete: 'View_delete',
        },

      },
    }
  };


  setUserLangInLoad = () => {
    let userTxt = localStorage.getItem('user');
    let user = userTxt ? JSON.parse(userTxt) : '';
    // console.log('User', user);
    const userLang = _.has(user, 'lang') && user.lang ? user.lang : 'es';
    console.log('User Lang', userLang)
    // let state = this.state;
    // state.globalCtx.language = userLang;
    // this.setState(state);
    // this.getCompileLang(userLang);
  };


  componentDidMount() {
    // will set the version software from jenkins_version file
    this.setCurrentVersion();
  }

  render() {

    // const catalogs = { en: catalogEn };
    const catalogs = this.state.globalCtx.catalogs;
    const activateLang = this.state.globalCtx.language;
    const defaultLang = this.state.globalCtx.defaultLanguage;
    const loadingLang = this.state.globalCtx.loadingLanguage;

    const renderScreen = (activateLang === defaultLang || _.has(catalogs, activateLang)) && !loadingLang;

    // console.log('RENDer', this.state.globalCtx.language, catalogs);

    return <CtxProvider value={this.state.globalCtx}>
      {
        renderScreen ? (
            <I18nProvider language={this.state.globalCtx.language} catalogs={catalogs}>
              <SuccessNotification
                  handleClose={this.closeSuccessNotification}
                  text={this.state.globalCtx.textSuccessNotification}
                  open={this.state.globalCtx.openSuccessNotificationFlag}
              />
              <Notification
                  handleClose={this.closeNotification}
                  text={this.state.globalCtx.textNotification}
                  open={this.state.globalCtx.openNotificationFlag}
                  msgType={this.state.globalCtx.typeNotification}
              />
              <Fragment>
                <Switch>
                  <Route
                      path="/403/"
                      exact={true}
                      component={Code403}/>
                  {routes.map(
                      (route, index) => (
                          route.isProtected ?
                              <ProtectedRoute
                                  key={index}
                                  path={route.path}
                                  exact={route.exact}
                                  component={route.component}
                                  routeType={route.routeType}
                                  perms={route.perms}
                              /> :
                              <Route
                                  key={index}
                                  path={route.path}
                                  exact={route.exact}
                                  component={route.component}
                                  routeType={route.routeType}
                                  perms={route.perms}
                              />
                      )
                  )}
                  <Route component={Code404}/>

                </Switch>
              </Fragment>
            </I18nProvider>
        ) : 'Cargando...'
      }
    </CtxProvider>
  }
}

export default withI18n()(App);
