import React, {Component} from 'react';
import Table from "@material-ui/core/Table";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import TableCell from "@material-ui/core/TableCell";
import TableBody from "@material-ui/core/TableBody";
import moment from "moment";
import {Grid} from "@material-ui/core";
import _ from "lodash";
import {ApiServiceEvents} from "../../../../api-services/events";
import Paper from "@material-ui/core/Paper";
import CircularProgress from "@material-ui/core/CircularProgress";
import InviteTable from "./InviteTable";
import {Trans} from "@lingui/macro";
import CloudDownloadIcon from "@material-ui/icons/CloudDownload";
import {CSVLink} from "react-csv";
import Button from "@material-ui/core/Button";

const apiEvent = new ApiServiceEvents();

class ListInvites extends Component {
    state = {
        loading: true,
        industryConfirmList: [],
        idEventAgenda: 0,
        datacsv: [],
        headercsv: []
    };


    /**
     * Function to get user list
     * @param  {Object} query
     */
    getUserList = (query) => {
        let ctx = this.context;
        let headers = [
            {label: "Nombre", key: "firstName"},
            {label: "Apellido", key: "lastName"},
            {label: "Asistió al evento", key: "scheduleAssistance"},
            {label: "Confirmar asistencia", key: "confirmAssistance"},
            {label: "email", key: "email"}
        ];

        this.setState({headercsv: headers})

        return new Promise((resolve, reject) => {
            // prepare your data and then call resolve like this:
            // let eventId = '5d851ed3b740c6a6ed62f2f9';
            let slug = ctx.slug;
            return apiEvent.getListInvite(this.props.idEventAgenda, this.props.slug).then(
                (res) => {
                    // console.log('res', res.eventUsers);
                    // console.log('Respuesta de usuarios evento', res);
                    this.setState({industryConfirmList: res, loading: false});
                    let data = [];
                    this.state.industryConfirmList.map((item, index) => {
                        data.push({
                            firstName: item.firstName,
                            lastName: item.lastName,
                            confirmAssistance: item.confirmAssistance === false ? `-` : `confirmó asistencia`,
                            scheduleAssistance: item.scheduleAssistance === false ? `-` : 'Asistió',
                            email: item.email
                        });
                    });
                    this.setState({datacsv: data})
                    /*resolve({
                        data: res.eventUsers, // your data array
                        page: res.pagination.page - 1, // current page number
                        totalCount: res.pagination.totalCount ? res.pagination.totalCount : res.length, // total page number
                    });*/
                }
            ).catch(err => {
                console.log('Error, no se pudo cargar los usuarios', err);
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                /*resolve({
                    data: [], // your data array
                    page: 0, // current page number
                    totalCount: 0, // total page number
                });*/
            });

        })
    };


    componentDidMount() {
        this.getUserList();
        /*const idEvent = this.props.idEventAgenda;
        this.setState({
            idEventAgenda:idEvent
        });
        /!**
         * When a event is for industry, we can get the assistanslist
         *!/
        apiEvent.getListInvite(this.props.idEventAgenda,this.props.slug).then((res) => {
            console.log("response",res);
            this.setState({
                industryConfirmList: res,
                loading:false
            })
        }).catch((err) => {
            console.log(err)
        });*/
    }

    render() {
        return (
            <div>
                <Grid
                    item
                    style={{marginTop: 30}}
                    direction="row"
                    justify="flex-end"
                    xs={12}>
                    {this.state.loading ?
                        <Paper xs={12} style={{
                            'textAlign': "center",
                            "paddingTop": 50,
                            "paddingBottom": 50
                        }}><CircularProgress/> </Paper>
                        :
                        <div>
                            <Button
                                style={{marginBottom: 10}}
                                variant="contained"
                                color="default"
                                startIcon={< CloudDownloadIcon/>}
                            >
                                <CSVLink style={{color: 'black', textDecoration: 'none'}}
                                         filename={this.props.nameEvent + `_${moment().format('DDMMYYYY_h:mm:ss')}.csv`}
                                         data={this.state.datacsv}
                                         headers={this.state.headercsv}><Trans>Exportar</Trans></CSVLink>
                            </Button>
                            <InviteTable
                                rows={this.state.industryConfirmList}
                            />
                        </div>

                    }
                </Grid>
            </div>
        );
    }
}

export default ListInvites;