import React, {Component} from 'react';
import GlobalContext from "../../../../GlobalContext";
import {t, Trans} from "@lingui/macro";
import Grid from "@material-ui/core/Grid";
import StepForm from "../../../../components/events/admin/chatbot/StepForm";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import {Redirect, withRouter} from "react-router-dom";
import {ApiServiceEvents} from "../../../../api-services/events";
import _ from "lodash"
import {withI18n} from "@lingui/react";
import {withStyles} from "@material-ui/core";


const apiEvent = new ApiServiceEvents();

/**
 *
 *This view is for updating the chatbot steps
 * @class StepUpdate
 * @extends {Component}
 */
class StepUpdate extends Component {

    static contextType = GlobalContext;

    state = {
        breadCrumbs: [
            {
                name:<Trans>Chatbot</Trans>,
                active:false,
                link:'',
            },
            {
                name:<Trans>Editar paso de chatbot</Trans>,
                active:true,
                link:''
            },
        ],
        groups: [],
        initial: {
            name: '',
            groups: [],
            steps: []
        },
        loading: false
    };
    /**
     * This function loads the profile list
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvent.getRoles(ctx.slug).then(
            (res) => {
                this.setState({groups: res});
            }
        ).catch(
            (err) => {
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                console.log('Error en la carga de perfiles', err)
            }
        );
    };
    /**
     * function to load chatbot flow details
     */
    loadChatbotItem = () => {
        const chatId = this.props.match.params.id;
        const ctx = this.context;
        apiEvent.getDetailWorkFlowChatbot(chatId).then(
            (res) => {
                let nameDisplay;
                let isLargeName = res.name.length>40;
                nameDisplay = isLargeName ? res.name.substr(0,40) + '...' : res.name;
                let breadCrumbs= [
                    {
                        name:<Trans>Chatbot</Trans>,
                        active:false,
                        link:`/${ctx.slug}/admin/chatbot/`,
                    },
                    {
                        name:this.props.i18n._(t`Editar paso de chatbot - `) +' '+ nameDisplay,
                        active:true,
                        link:''
                    }
                ];
                let initial = {
                    name: res.name,
                    groups: res.groups,
                    steps: res.property,
                };
                this.setState({initial: initial,breadCrumbs});
            }
        ).catch(
            (err) => {
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    this.context.openPermissionNotification();
                }
                console.log('Error al obtener el detalle de una conf de chatbot', err);
            }
        );
    };

    componentDidMount() {
        const ctx = this.context;
        let newState = this.state;
        newState.breadCrumbs = [
            {
                name:<Trans>Chatbot</Trans>,
                active:false,
                link:`/${ctx.slug}/admin/chatbot/`,
            },
            {
                name:<Trans>Editar paso de chatbot</Trans>,
                active:true,
                link:''
            },
        ];
        this.loadRoles();
        this.loadChatbotItem();
    }

    /**
     * @param  {Object} values
     * @returns {string JSON} json with slug, groups, name and property  
     */
    getRequest = (values) => {
        console.log(values);
        return {
            _id: this.props.match.params.id,
            slug: this.context.slug,
            groups: values.groups,
            name: values.name,
            property: values.steps
        }
    };

    /**
     * Function to send the chatbot form
     * @param  {Object} values
     */
    onSubmit = (values) => {
        let isValidRequest = !this.postValidate(values);
        if(isValidRequest) {
            const request = this.getRequest(values);
            this.setState({loading: true});
            let ctx = this.context;
            apiEvent.updateWorkFlowChatbot(request).then(
                (res) => {
                    console.log(res);
                    this.context.openSuccessNotification(<Trans>Configuración guardada</Trans>);
                }
            ).catch(
                (err) => {
                    if (_.has(err, 'response.status') && err.response.status === 403) {
                        ctx.openPermissionNotification();
                    }
                    console.log('Error al crear un flujo de chatbot', err);
                }
            ).finally(
                () => {
                    this.setState({loading: false});
                }
            );
        }
    };
    /**
     * Function to validate if there is a security profile and a final step in the chatbot flow
     * @param  {Object} values
     * @returns {Boolean} True o false if exist Errors
     */
    postValidate = (values) => {
        let existsErrors = false;
        if (values.groups.length < 1) {
            existsErrors = true;
            this.context.openNotification(<Trans>Los perfiles de seguridad son requeridos</Trans>, 'error');
        }
        let questionsText = _.map(values.steps, function (d) {
            if(d.typeStep === 'text'){
                return {
                    id: d.id,
                    isFinal: d.isFinal
                }
            }
        });
        console.log('questionsText', questionsText);
        let existFinalStep = _.find(questionsText, {isFinal: true});
        if(!existFinalStep){
            existsErrors = true;
            this.context.openNotification(<Trans>Debe existir un paso final</Trans>, 'error');
        }
        console.log('Request', values);
        return existsErrors;
    };
    /**
     * @param  {Object} values
     * @returns {Object} Object With error of steps
     */
    
    validate = (values) => {
        // console.log('values', values);

        let idsSteps = _.map(values.steps, 'id');

        // let nextSteps = _.map(values.steps, function (x) {
        //     if(!x.isFinal)
        //         return x.nextStep;
        // });

        let errors = {
            steps: []
        };
        if (!values.name) {
            errors.name = <Trans>Requerido</Trans>;
        }

        _.map(values.steps, function (x, i) {
            if(!x.id){
                errors.steps[i] = {
                    id: <Trans>Requerido</Trans>
                }
            }
            if(x.typeStep === 'text'){
                if(!x.text){
                    errors.steps[i] = {
                        text: <Trans>Requerido</Trans>
                    }
                }
                if(!x.nextStep){
                    errors.steps[i] = {
                        nextStep: <Trans>Requerido</Trans>
                    }
                }else {

                    if (!x.isFinal && !idsSteps.includes(x.nextStep)) {
                        errors.steps[i] = {
                            nextStep: <Trans>ID no encontrado</Trans>
                        }
                    }
                }

            }else {
                // option type
                _.map(x.options, function (y, b) {
                    let errorsOptions = {options: []};
                    if(!y.isFinal && y.nextStep && !idsSteps.includes(y.nextStep)){
                        errorsOptions.options[b] = {
                            nextStep: <Trans>ID no encontrado</Trans>
                        }
                    }
                    errors.steps[i] = errorsOptions;
                })

            }

        });


        return errors;
    };

    render() {
        return (
            <AdminEventLayout
                title={<Trans>Administrador de eventos</Trans>}
                breadcrumbs={this.state.breadCrumbs}
            >
                <Grid container>
                    <Grid item xs={12}>
                        {/*<div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>*/}
                        <StepForm
                            onSubmit={this.onSubmit}
                            roles={this.state.groups}
                            initialValues={this.state.initial}
                            validate={this.validate}
                            title={<Trans>Editar flujo de chatbot</Trans>}
                            loading={this.state.loading}
                        />
                        {/*</div>*/}
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(withRouter(StepUpdate));