import React, {Component} from "react";
import {Grid, makeStyles, Paper, withStyles, Checkbox, FormControlLabel} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import FileBase64 from "react-file-base64";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import Button from "@material-ui/core/Button";
import {Link} from "react-router-dom";
import CircularProgress from "@material-ui/core/CircularProgress";
import RolesWidget from "../../../security/RolesWidget";
import Box from "@material-ui/core/Box";
import GlobalContext from "../../../../GlobalContext";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputBase from '@material-ui/core/InputBase';
import {Trans} from "@lingui/macro";


const BootstrapInput = withStyles(theme => ({
    root: {
        'label + &': {
            marginTop: theme.spacing(3),
        },
    },
    input: {
        borderRadius: 4,
        position: 'relative',
        backgroundColor: theme.palette.background.paper,
        border: '1px solid #ced4da',
        fontSize: 16,
        padding: '10px 26px 10px 12px',
        transition: theme.transitions.create(['border-color', 'box-shadow']),
        // Use the system font instead of the default Roboto font.
        fontFamily: [
            '-apple-system',
            'BlinkMacSystemFont',
            '"Segoe UI"',
            'Roboto',
            '"Helvetica Neue"',
            'Arial',
            'sans-serif',
            '"Apple Color Emoji"',
            '"Segoe UI Emoji"',
            '"Segoe UI Symbol"',
        ].join(','),
        '&:focus': {
            borderRadius: 4,
            borderColor: '#80bdff',
            boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
        },
    },
}))(InputBase);

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    margin: {
        margin: theme.spacing(1),
    },
}));

class CreateViewForm extends React.Component{
    static contextType = GlobalContext;

    render() {
        let ctx = this.context;
        return (
            <React.Fragment>
                <Paper style={{padding:'20px', width:'100%'}}>
                    <Typography variant="h6" gutterBottom>
                        {this.props.title}
                    </Typography>
                    <br/>

                    <form onSubmit={this.props.handleSubmit}>

                        <Grid container spacing={3}>

                            <Grid item xs={12} md={6}>
                                <Box alignItems="center">
                                    <img alt={'logo modulo'} src={this.props.logo.value} style={{
                                        margin: 10,
                                        width: 150,
                                        height: 150,
                                    }}/><br/>

                                    <FileBase64
                                        onDone={this.props.handleLogo} />
                                </Box>

                            </Grid>


                            <Grid item xs={12} md={12}>
                                <TextField variant="outlined"
                                           helperText={<Trans>Este valor aparacerá como título de la vista en la aplicación móvil</Trans>}
                                           required name="name" id="name" label="Nombre" value={this.props.name} onChange={this.props.handleInputChange} fullWidth />

                            </Grid>
                            <Grid item xs={12} md={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={this.props.isVisible}
                                            onChange={this.props.handleChangeCheck}
                                            value="isVisible"
                                            inputProps={{
                                                'aria-label': 'primary checkbox',
                                            }}
                                        />
                                    }
                                    label={<Trans>Visible en el menú lateral de la aplicación </Trans>}
                                />
                            </Grid>


                            {/*<Grid item xs={12} md={6}>*/}
                            {/*    /!*<TextField variant="outlined" id="description"  name="description" onChange={this.handleEventInput} label="Descripción" fullWidth />*!/*/}
                            {/*    <Select*/}
                            {/*        value={this.props.status}*/}
                            {/*        onChange={this.props.handleInputChange}*/}
                            {/*        input={<BootstrapInput name="status" id="age-customized-select" />}*/}
                            {/*    >*/}
                            {/*        <MenuItem value={1}>Activo</MenuItem>*/}
                            {/*        <MenuItem value={2}>Borrador</MenuItem>*/}
                            {/*    </Select>*/}
                            {/*</Grid>*/}


                        </Grid>

                        <br/>
                        {
                            !this.props.isPublic?<Typography variant="h6" gutterBottom>
                                Roles de seguridad
                            </Typography>:''
                        }

                        <br/>

                        <Grid container spacing={3}>

                            {
                                !this.props.isPublic?<Grid item xs={12} md={12}>
                                <RolesWidget
                                availableGroupsIds={this.props.availableGroupsIds}
                                availableGroups={this.props.availableGroups}
                                selectedGroupsIds={this.props.selectedGroupsIds}
                                handleSetGroup={this.props.handleSetGroup}
                                handleSetGroupSelected={this.props.handleSetGroupSelected}
                                checked={this.props.checked}
                                handleSetChecked={this.props.handleSetChecked}
                                />
                                </Grid>:''
                            }


                            <Grid item xs={12}>
                                <Button component={Link} to={`/${ctx.slug}/admin/${this.props.module}/view/list/`} style={{marginRight:'10px'}}   variant="contained">
                                    Cancelar
                                </Button>

                                {
                                    this.props.loading ? <CircularProgress size={20}/> :
                                        <Button type="submit" variant="contained" color="primary">
                                            Guardar
                                        </Button>
                                }
                            </Grid>
                        </Grid>
                    </form>

                </Paper>
            </React.Fragment>
        )
    }
}

export default CreateViewForm