import React from 'react';
import MaterialTable from 'material-table';
import {Redirect} from "react-router-dom";
import {Paper, Grid, Button} from "@material-ui/core";
import CircularProgress from "@material-ui/core/CircularProgress";
import SearchIcon from '@material-ui/icons/Search';
import GlobalContext from "../../../../GlobalContext";
import moment from "moment";
import DateFnsUtils from '@date-io/date-fns';
import {Trans} from "@lingui/macro";
import {
    DatePicker,
    MuiPickersUtilsProvider
  } from '@material-ui/pickers';

class ChangeLogTableList extends React.Component {
    static contextType = GlobalContext;
    constructor(props) {
        super(props)
        this.state = {
            redirectToEdit:false,
            redirectToBuilder:false,
            startDate: new Date(),
            endDate: new Date(),
            columns: [
                { title: 'Fecha', field: 'date' },
                { title: 'Usuario', field: 'user' },
                { title: 'Módulo', field: 'module' },
                { title: 'Acción', field: 'action' },
                { title: 'Request', field: 'bodyRequest' },
    
            ]
        };
    }

    handlechangeStart = (value) => {
        let ctx = this.context;
        console.log(value);
        this.setState({startDate:value})
        if(this.state.endDate != undefined && this.state.endDate != null)
        {
            if(value.getTime() > this.state.endDate.getTime())
            {
                ctx.openNotification('El rango del evento no coincide','error')
            }
        }
        /* this.setState({startDate: moment(value,'DD/MM/YYYY',true).format('DD/MM/YYYY')}) */
        console.log(this.state)

        /* this.setState({startDate: moment(value).format()}) */
    }

    handlechangeEnd = (value) => {
        let ctx = this.context;
        console.log(value);
        this.setState({endDate:value})  
        if(this.state.startDate != undefined && this.state.startDate != null)
        {
            if(value.getTime() < this.state.startDate.getTime())
            {
                ctx.openNotification('El rango del evento no coincide','error')
            }
        }
        /* this.setState({endDate: moment(value,'DD-MM-YYYY',true).format('DD-MM-YYYY')}) */
        console.log(this.state)
        /* this.setState({endDate: moment(value).format()}) */
    }

    handleSubmit = (e) => {
        console.log(this.state);
        let query = {
            page: 0,
            startFilter: moment(this.state.startDate,'YYYY-MM-DD',true).format('YYYY-MM-DD'),
            endFilter: moment(this.state.endDate,'YYYY-MM-DD',true).format('YYYY-MM-DD')
        }
        //this.props.rows(query)
        this.props.tableRef.current.onQueryChange(query)
    }

    render() {
        let ctx = this.context;
        let state = this.state;
        let props = this.props;
        let data = props.rows;

        return (
            <>
                <Paper>
                    <Grid container direction="row" justify="flex-end" alignItems="center" spacing={2}>
                        <Grid item xs={3}>
                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <DatePicker
                                    fullWidth
                                    format="dd/MM/yyyy"
                                    onChange={this.handlechangeStart}
                                    margin="normal"
                                    label="Fecha inicial"
                                    inputVariant="outlined"
                                    value={this.state.startDate}
                                    
                                />
                            </MuiPickersUtilsProvider>
                        </Grid> 
                        <Grid item xs={3}>
                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                <DatePicker
                                    fullWidth
                                    format="dd/MM/yyyy"
                                    onChange={this.handlechangeEnd}
                                    margin="normal"
                                    label="Fecha Final"
                                    inputVariant="outlined"
                                    value={this.state.endDate}
                                    
                                />
                            </MuiPickersUtilsProvider>
                        </Grid>
                        <Grid item xs={2}>
                            <Button onClick={this.handleSubmit} variant="contained" color="primary" size="large" startIcon={<SearchIcon />} >
                            Buscar
                        </Button>
                        </Grid>
                    </Grid>
                </Paper>

                <MaterialTable
                    title={<Trans>Bitácora de cambios</Trans>}
                    tableRef={this.props.tableRef}
                    columns={state.columns}
                    data={data}
                    options={{
                        actionsColumnIndex: -1,
                        pageSize:10,
                        pageSizeOptions: [10, 20, 50, 100, 500],
                        paging: true,
                        sorting: false,
                        paginationType: 'normal',
                    }}   
                    // localization={{
                    //     body:{ emptyDataSourceMessage:
                    //     <p> No se encontraron resultados </p> } }}
                    localization={this.context.tableConfig.localization}
                    />
                  

            </>
        );
    }
}


export default ChangeLogTableList