import React from 'react';
import MaterialTable from 'material-table';
import EditView from "../../../../pages/events/admin/views/EditView";
import {Redirect} from "react-router-dom";
import CreateUserForm from "../../../security/CreateUserForm";
import CircularProgress from "@material-ui/core/CircularProgress";
import GlobalContext from "../../../../GlobalContext";
import {withI18n} from "@lingui/react";
import { Trans, t } from '@lingui/macro';

class ViewListTable extends React.Component {
    static contextType = GlobalContext;

    state = {
        columns: [
            {
                field: 'order',
                title: <Trans>Orden</Trans>,
                render: (rowData) => <b>{rowData.order+1}</b>
            },
            { title: <Trans>Nombre</Trans>, field: 'name' },
            { title: <Trans>Visible en el menú lateral de la aplicación</Trans>, field: 'isActive', type: 'boolean' }
        ]
    };
    render() {
        let props = this.props
        let {module,slug,isLoading} = this.props

        let ctx = this.context;

        if (this.state.redirectToEdit) return <Redirect push to={`/${slug}/admin/${module}/view/${this.state.selectedRow}/`}/>;
        if (this.state.redirectToBuilder) return <Redirect push to={`/${slug}/admin/builder/${module}/${this.state.selectedRow}/`}/>;



        return (
            <>
                {
                    <MaterialTable
                        title={<Trans>Vistas</Trans>}
                        columns={this.state.columns}
                        tableRef={this.props.tableRef}
                        data={props.data}
                        options={{
                            actionsColumnIndex: -1,
                            pageSize:10,
                            pageSizeOptions: [10, 20, 50, 100, 500],
                            paging: true,
                        }}
                        localization={ctx.tableConfig.localization}
                        actions={[
                            {
                                icon: 'keyboard_arrow_up',
                                tooltip: <Trans>Subir</Trans>,
                                onClick: (event, rowData) => props.sortViews('up',rowData)
                            },
                            {
                                icon: 'keyboard_arrow_down',
                                tooltip: <Trans>Bajar</Trans>,
                                onClick: (event, rowData) => props.sortViews('down',rowData)
                            },
                            {
                                icon: 'edit',
                                tooltip: <Trans>Editar</Trans>,
                                onClick: (event, rowData) => this.setState({redirectToEdit: true, selectedRow: rowData._id})
                            },
                            {
                                icon: 'filterNone',
                                tooltip: <Trans>Clonar</Trans>,
                                onClick: (event, rowData) => props.onCloneRow(rowData._id)
                            },
                            {
                                icon: 'brush',
                                tooltip: <Trans>Diseñador</Trans>,
                                onClick: (event, rowData) => this.setState({redirectToBuilder: true, selectedRow: rowData._id})
                            },
                            {
                                icon: 'file_copy',
                                tooltip: <Trans>Copiar a módulo</Trans>,
                                onClick: (event, rowData) => props.handleShowCloneViewToModule(rowData._id)
                            },
                            {
                                icon: 'swap_vert',
                                tooltip: <Trans>Cambiar posición</Trans>,
                                onClick: (event, rowData) => props.handleMovePositionData(rowData)
                            }
                            // rowData => ({
                            //     icon: 'delete',
                            //     tooltip: 'Delete User',
                            //     onClick: (event, rowData) => alert("You want to delete " + rowData.name),
                            //     disabled: rowData.birthYear < 2000
                            // })
                        ]}
                        editable={{
                            onRowDelete: props.handlerDelete,
                        }}
                    />
                }


            </>


        );
    }
}


export default withI18n()(ViewListTable)