import React from "react";
import {Redirect, withRouter} from "react-router-dom"
import {Grid} from "@material-ui/core";
import Noti from "../../components/notifications/Noti";
import CreateUserForm from "../../components/security/CreateUserForm";
import AdminLayout from "../../layouts/AdminLayout";
import {ApiServiceSecurity} from "../../api-services/security";
import {ApiServiceEvents} from "../../api-services/events";
import GlobalContext from "../../GlobalContext";
import { Trans } from '@lingui/macro';
const apiSecurity = new ApiServiceSecurity();
const apiEvents = new ApiServiceEvents();
/**
 *
 * View to update user information
 * @class UpdateUser
 * @extends {React.Component}
 */
class UpdateUser extends React.Component{
    static contextType = GlobalContext;
    state = {
        firstName: '',
        lastName: '',
        email: '',
        passwordConfirm:'',
        passwordChange:'',
        isLoadingPassword:false,
        isProfile:false,
        errorMessage: '',
        redirectLogin: false,
        urlRedirectLogin: '/user/list/',
        showNotiError: false,
        isActive: true,
        _id: '',
        titleCreateForm: 'Editar usuario'
    };

    /**
     * function to set state when a input element change
     * @param  {Object} event
     */
    handleInputChange = (event) => {
        const ctx = this.context
        let value = null
        const name = event.target.name;
        const patternAlphanum = /^[,.;a-zA-ZÀ-ÿ\d\s-_]+$/ig;
        const patternEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if(event.target.name === 'firstName' || event.target.name === 'lastName'){
            if(event.target.value.length > 50){
                event.target.value = event.target.value.slice(0,50);
                ctx.openNotification('Límite de caracteres excedido', 'error');
            } else {
                if(!patternAlphanum.test(event.target.value)){
                    event.target.value = event.target.value.slice(0,-1);
                    ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
                }
            }
        }
        if(event.target.name === 'email'){
            if(event.target.value.length > 100){
                event.target.value = event.target.value.slice(0,100);
                ctx.openNotification('Límite de caracteres excedido', 'error');
            } else {
                if(!patternEmail.test(event.target.value)){
                    event.target.value = event.target.value.slice(0,-1);
                    ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
                }
            }
        }
        value = event.target.type === 'checkbox' ? event.target.checked : event.target.value;
        
        this.setState({
            [name]: value
        });
    };

    /**
     * Function to submit user form
     * @param  {object} e
     */
    handleSubmit = (e) => {
        e.preventDefault();
        let userId = this.props.match.params.id;
        let state = this.state;
        let request = {
            firstName: state.firstName,
            lastName: state.lastName,
            email: state.email,
            _id: userId,
        };
        let promise = apiSecurity.updSuperUser(request);

        promise.then(
            (response) => {
                // console.log('Todo ok', response);
                this.setState({redirectLogin:true})
            }
        ).catch( error => {
            // console.log('Error', error.response);
            this.setState({
                errorMessage: error.response ? error.response.data.message.message : 'Sin conexión',
                showNotiError: true,
            });
        })
    };


    /**
     * Function for get and call the api change password
     * @param {Object} e data form
     */
    handleSubmitPassword = (e) =>{
        e.preventDefault();

        let ctx = this.context;
        let {passwordChange, passwordConfirm} = this.state;
        if(passwordChange===passwordConfirm){
            this.setState({
                isLoadingPassword:true
            })
            apiEvents.changePasswordUser(passwordChange).then((res)=>{
                console.log(res)
                if(res.message){
                    ctx.openNotification(res.message,"success")
                    this.setState({
                        passwordConfirm:'',
                        passwordChange:''
                    })
                }

            }).catch((err)=>{
                ctx.openNotification(<Trans>Hubo un error al realizar la operación, por favor intenta mas tarde.</Trans>,'error');
            }).finally(()=>{this.setState({isLoadingPassword:false})})
        }else{
            ctx.openNotification(<Trans>Las credenciales no coinciden</Trans>,'error');
            return
        }

    }

    /**
     * funciton to close notification
     */
    handleCloseNoti = () => {
        this.setState({
            errorMessage: '',
            showNotiError: false
        })
    };

    componentDidMount() {
        this.initComponent();
    }


    initComponent = async ()=>{
        let userId = this.props.match.params.id;
        // console.log('UserId', userId);
        let promise = apiSecurity.getSuperUserDetail(userId);
        const userStr = await localStorage.getItem('user');
        const user = JSON.parse(userStr);
        promise.then(
            (response) => {
                console.log(response);

                let userState = {
                    firstName: response.firstName,
                    lastName: response.lastName,
                    email: response.email,
                    isActive: response.isActive,
                    _id: response._id,
                };
                let isProfile = false;


                if(response._id===user._id){
                    isProfile=true;
                }

                userState.isProfile = isProfile;

                this.setState(userState);
            }
        ).catch(
            (err) => {
                console.log(err.response);
            }
        )
    }

    render() {

        if (this.state.redirectLogin) {
            return <Redirect push to={this.state.urlRedirectLogin} />;
        }

        // const userId = this.props.match.params.id;
        let {firstName,isProfile,passwordConfirm, passwordChange,isLoadingPassword} = this.state;
        let {lastName} = this.state;
        let {email} = this.state;
        let {password} = this.state;
        let {titleCreateForm} = this.state;
        return (
            <AdminLayout>
                <Grid container>
                    <Grid item xs={12} md={12} lg={12} >
                        {/*<Paper style={{padding: '10px'}}>*/}
                        {/*{this.state.errorMessage}*/}
                        <Noti msg={this.state.errorMessage} open={this.state.showNotiError} handleClose={this.handleCloseNoti}/>
                        <CreateUserForm
                            firstName={firstName}
                            lastName={lastName}
                            email={email}
                            password={password}
                            passwordConfirm={passwordConfirm}
                            passwordChange={passwordChange}
                            handleInputChange={this.handleInputChange}
                            handleSubmit={this.handleSubmit}
                            handleSubmitPassword={this.handleSubmitPassword}
                            showPassword={false}
                            isLoadingPassword={isLoadingPassword}
                            title={titleCreateForm}
                            isProfile={isProfile}
                            buttonTitle='Actualizar'
                        />
                        {/*</Paper>*/}
                    </Grid>
                </Grid>
            </AdminLayout>
        )
    }
}

export default withRouter(UpdateUser)