import React, {Component} from 'react';
import {Link} from "react-router-dom";
import Typography from "@material-ui/core/Typography";
import Breadcrumbs from "@material-ui/core/Breadcrumbs";
import HomeIcon from '@material-ui/icons/Home';

class CustomBreadCrumbs extends Component {

    render() {

        let {breadcrumbs, homelink } = this.props
        return (
            <>
                <Breadcrumbs aria-label="breadcrumb">
                    <Link color="inherit" push={'true'} to={homelink} onClick={this.handleClickLink}>
                        <HomeIcon style={{
                            marginRight: 2,
                            width: 20,
                            height: 20,
                            color:'rgb(1, 87, 155)'
                        }} />
                    </Link>
                    {
                        breadcrumbs.map((item, i) => {
                            return (
                                item.active?<Typography key={i} color="textPrimary">{item.name}</Typography>:<Link key={i} color="inherit" push={'true'} to={item.link}>
                                    {item.name}
                                </Link>
                            )
                        })
                    }
                </Breadcrumbs>
            </>
        );
    }
}

export default CustomBreadCrumbs;