import React, {Component, createRef} from 'react';
import { Map, TileLayer, Marker, Popup } from "react-leaflet";
import {Trans} from "@lingui/macro";

class LocateMap extends Component {

    state = {
        hasLocation: false,
        loading: true,
        latlng: {
            lat: 51.505,
            lng: -0.09,
        },
    };

    mapRef = createRef();

    setCurrentLocate = () => {
        const map = this.mapRef.current;
        if (map != null) {
            map.leafletElement.locate()
        }
    };

    handleClick = (e) => {
        console.log(e.latlng);
        this.setState({latlng: e.latlng, hasLocation:true});
        this.props.handleClickPoint(e.latlng);
        // const map = this.mapRef.current;
        // if (map != null) {
        //     map.leafletElement.locate()
        // }
    };

    componentDidMount() {
        this.setCurrentLocate();
    }

    handleLocationFound = (e) => {
        this.setState({
            hasLocation: true,
            latlng: e.latlng,
            loading: false,
        });
        this.props.handleClickPoint(e.latlng);
    };

    render() {

        const marker = this.state.hasLocation ? (
            <Marker position={this.state.latlng}>
                <Popup><Trans>Estas aquí</Trans></Popup>
            </Marker>
        ) : null;

        const display = this.state.loading ? 'none':'block';

        return (
            <>
                {
                    this.state.loading ? (
                        'Cargando ubicación...'
                    ) : null
                }
                <Map
                    center={this.state.latlng}
                    length={4}
                    onClick={this.handleClick}
                    onLocationfound={this.handleLocationFound}
                    ref={this.mapRef}
                    zoom={13}
                    style={{height: 500, width: '100%'}}
                >
                    <TileLayer
                        attribution='&amp;copy <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
                        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
                    />
                    {marker}
                </Map>
            </>
        );
    }
}

export default LocateMap;