import React, {Component} from 'react';
import {Grid} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import AdminLayout from "../../../../layouts/AdminLayout";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import Paper from '@material-ui/core/Paper';
import CircularProgress from '@material-ui/core/CircularProgress';
import {Link} from "react-router-dom";
import AddIcon from '@material-ui/icons/Add';
import IconButton from "@material-ui/core/IconButton";
import GlobalContext from "../../../../GlobalContext";
import ResourceListTable from "../../../../components/events/admin/resources/ResourceListTable";
import {ApiServiceEvents} from "../../../../api-services/events";
import { Trans } from '@lingui/macro';

const apiEvents = new ApiServiceEvents();

/**
 *
 * This view allows you to display the resource list
 * @class ResourcesList
 * @extends {Component}
 */
class ResourcesList extends Component {

    state = {
        data:[],
        loading:false,
        moduleId:'',
        slug:'',
        _idEvent:'',
        redirect: false,
        breadcrumbs:[
            {
            name: <Trans>Archivos del evento</Trans>,
            active:true,
            link:''
            }
        ]
    };


    static contextType = GlobalContext;

    componentDidMount() {
        let slug = this.context.slug;
        this.setState({loading:true});
        apiEvents.getEventInfo('', slug).then(
            (response) => {
                this.getResources(response.event._id);
                this.setState({
                    // eventInfo: response.event,
                    // availableGroups: response.event.groups,
                    // availableGroupsIds: response.eventInfo.event.groups.map(o => o._id),
                    _idEvent: response.event._id
                });
            }
        ).catch(
            (err) => {
                if(err.response.status === 403){
                    this.context.openPermissionNotification();
                }
                console.log('Error al obtener info del evento para los grupos', err);
            }
        )
    }

    /**
     * Function to delete resource selected
     * @param  {Object} resourceData
     */
    handlerDelete = (resourceData) =>
        new Promise(resolve => {
            let ctx = this.context;
            let {_idEvent} = this.state;
            let _idResource = resourceData._id;

            apiEvents.removeResource(_idEvent, _idResource).then(
                (response) => {
                    resolve();
                    this.getResources(_idEvent);
                }
            ).catch(
                (err) => {
                    if(err.response.status === 403){
                        this.context.openPermissionNotification();
                    }
                    resolve();
                    if(err.response.status === 403){
                        ctx.openPermissionNotification();
                    }
                }
            );

        });

    /**
     * Function to get resource details
     * @param  {number} _idEvent
     */
    getResources=async (_idEvent)=>{
        try{
            const res = await apiEvents.getResources(_idEvent)
            this.setState({
                data:res,
                loading:false
            })
        }catch (e) {
            if(e.response.status === 403){
                this.context.openPermissionNotification();
            }
        }finally {
            this.setState({
                loading:false
            })
        }

    }
    

    render() {
        const ctx = this.context;
        let {data,loading,breadcrumbs} = this.state;                
        return (
            <AdminEventLayout breadcrumbs={breadcrumbs} title={<Trans>Archivos</Trans>} >
                <Grid container>
                    <Grid item xs={12}>
                        <Typography variant="h5" style={{padding: '10px'}} component="h3">
                            <Trans>Listado de archivos del evento</Trans>
                            <IconButton component={Link} push='true' to={`/${ctx.slug}/admin/resources/create/`} color="primary">
                                <AddIcon/>
                            </IconButton>
                        </Typography>
                    </Grid>

                    <Grid item xs={12}>
                        { loading ?   
                            <Paper xs={12} style={{'textAlign': "center","paddingTop":50,"paddingBottom":50}}><CircularProgress /> </Paper>
                            :
                            <ResourceListTable
                           handlerDelete={this.handlerDelete}
                           data={data}/>
                            }
                    </Grid>
                </Grid>
            </AdminEventLayout>
        );
    }
}

export default ResourcesList;