import React from 'react';
import CssBaseline from '@material-ui/core/CssBaseline';
import Typography from '@material-ui/core/Typography';
import { makeStyles } from '@material-ui/core/styles';
import Container from '@material-ui/core/Container';
import Link from '@material-ui/core/Link';
import SentimentDissatisfiedIcon from '@material-ui/icons/SentimentDissatisfied';


const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        minHeight: '100vh',
    },
    main: {
        marginTop: theme.spacing(8),
        marginBottom: theme.spacing(2),
    },
    footer: {
        padding: theme.spacing(2),
        marginTop: 'auto',
        backgroundColor: 'white',
    },
}));

/**
 *
 * This view is for show 404 error
 * @export
 * @returns
 */
export default function Code404() {
    const classes = useStyles();

    return (
        <div className={classes.root}>
            <CssBaseline />
            <Container component="main" className={classes.main} maxWidth="sm">

                <Typography variant="h2" component="h1" gutterBottom>
                    <SentimentDissatisfiedIcon/> 404
                </Typography>
                <Typography variant="h5" component="h2" gutterBottom>

                    {'No podemos encontrar el sitio'}<br/>
                    {'El link al cual intentas acceder probablemente no esté disponible.'}
                </Typography>
                <Typography variant="body1">Sitio no encontrado.</Typography>
            </Container>
        </div>
    );
}