import React from "react";
import GlobalContext from "../../../../GlobalContext";
import {Field, Form} from "react-final-form";
import {FormControlLabel, Grid, Paper, Typography} from "@material-ui/core";
import BoxQr from "../qr/BoxQr";
import {MuiPickersUtilsProvider} from "@material-ui/pickers";
import DateFnsUtils from "@date-io/date-fns";
import {Checkbox, TextField} from "final-form-material-ui";
import {CancelButton, SaveButtonSpinner} from "../../../buttons/StandartButtons";
import {DatePickerWrapper, TimePickerWrapper} from "../../../../components/fields/DatePickers"
import { Trans } from '@lingui/macro';


class SingletonAvailabilityForm extends React.Component{

    static contextType = GlobalContext;



    render() {

        let ctx = this.context;

        return (
            <Form
                onSubmit={this.props.onSubmit}
                initialValues={this.props.initialValues}
                validate={this.props.validate}
                render={({ submitError, handleSubmit, reset, submitting, pristine, values }) => (
                    <form onSubmit={handleSubmit} noValidate>
                        <Paper style={{ padding: 16 }}>

                            <Grid container alignItems="flex-start" spacing={2}>

                                <Grid item xs={12}>
                                    <Grid container>
                                        <Grid item xs={12} style={{marginTop: 5, marginBottom: 0}}>
                                            <Typography>
                                                <Trans>Fecha</Trans>
                                            </Typography>
                                        </Grid>
                                        <Grid item xs={6}>
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                <Grid item xs={11}>
                                                    <Field
                                                        required
                                                        name="validFromDate"
                                                        component={DatePickerWrapper}
                                                        fullWidth
                                                        margin="normal"
                                                        label={<Trans>Desde</Trans>}
                                                        inputVariant="outlined"
                                                    />
                                                </Grid>
                                                {/*<Grid item xs={6}>*/}
                                                {/*    <Field*/}
                                                {/*        name="fromHour"*/}
                                                {/*        component={TimePickerWrapper}*/}
                                                {/*        fullWidth*/}
                                                {/*        margin="normal"*/}
                                                {/*        label="Hora"*/}
                                                {/*    />*/}
                                                {/*</Grid>*/}
                                            </MuiPickersUtilsProvider>
                                        </Grid>
                                        <Grid item xs={6}>
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                <Grid item xs={11}>
                                                    <Field
                                                        required
                                                        name="validToDate"
                                                        component={DatePickerWrapper}
                                                        fullWidth
                                                        margin="normal"
                                                        label={<Trans>Hasta</Trans>}
                                                        inputVariant="outlined"
                                                    />
                                                </Grid>
                                                {/*<Grid item xs={6}>*/}
                                                {/*    <Field*/}
                                                {/*        name="fromHour"*/}
                                                {/*        component={TimePickerWrapper}*/}
                                                {/*        fullWidth*/}
                                                {/*        margin="normal"*/}
                                                {/*        label="Hora"*/}
                                                {/*    />*/}
                                                {/*</Grid>*/}
                                            </MuiPickersUtilsProvider>
                                        </Grid>
                                        <Grid item xs={12} style={{marginTop: 35, marginBottom: 7}}>
                                            <Typography>
                                                <Trans>Horario</Trans>
                                            </Typography>
                                        </Grid>

                                        <Grid item xs={6}>
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>
                                                <Grid item xs={11}>
                                                    <Field
                                                        name="fromHour"
                                                        component={TimePickerWrapper}
                                                        fullWidth
                                                        margin="normal"
                                                        label={<Trans>Hora de inicio</Trans>}
                                                        inputVariant="outlined"
                                                    />
                                                </Grid>
                                            </MuiPickersUtilsProvider>
                                        </Grid>
                                        <Grid item xs={6}>
                                            <MuiPickersUtilsProvider utils={DateFnsUtils}>

                                                <Grid item xs={11}>
                                                    <Field
                                                        name="toHour"
                                                        component={TimePickerWrapper}
                                                        fullWidth
                                                        margin="normal"
                                                        label={<Trans>Hora fin</Trans>}
                                                        inputVariant="outlined"
                                                    />
                                                </Grid>
                                            </MuiPickersUtilsProvider>
                                        </Grid>

                                        {submitError && <div className="error">{submitError}</div>}
                                        <Grid item style={{ marginTop: 16 }} xs={12}>
                                            <CancelButton
                                                to={`/${ctx.slug}/admin/`}
                                            />

                                            <SaveButtonSpinner
                                                loading={this.props.loading}
                                            />
                                        </Grid>
                                    </Grid>
                                </Grid>



                            </Grid>
                        </Paper>
                    </form>
                )}
            />
        )
    }
}

export default SingletonAvailabilityForm