import React from "react";
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import {Grid} from "@material-ui/core";
import EventCreateUserFormFinalForm from "../../../components/events/security/EventCreateUserFormFinalForm";
import {ApiServiceEvents} from "../../../api-services/events";
import GlobalContext from "../../../GlobalContext";
import _ from "lodash";
import {FORM_ERROR} from "final-form";
import {Redirect, withRouter} from "react-router-dom"
import EventUserMetaDataForm from "../../../components/events/security/EventUserMetaDataForm";
import UserUpdNavs from "../../../components/security/UserUpdNavs";
import {t, Trans} from '@lingui/macro';
import {withI18n} from "@lingui/react";
import imgDefault from '../../../assets/userprofile.png';

const apiEvent = new ApiServiceEvents();

/**
 *
 * This view allow update user
 * @class EventUpdUser
 * @extends {React.Component}
 */
class EventUpdUser extends React.Component{
    static contextType = GlobalContext;

    state = {
        loading: false,
        redirect: false,
        imageAvatar: {
           type:'imageBase64',
           value:'data:image/png;base64,',
           name:'logo.png'
        },
        roles: [],
        userData: {
            _id: '',
            firstName: '',
            lastName: '',
            image: null,
            company:'',
            phone: '',
            email: '',
            isAdmin: false,
            groups: [],
            metadata: [],
            confirmAssistance: 1
        },
        breadcrumbs: [],
        currentTab: 0,
    };

    /**
     * Function to set state on metadata from user
     * @param  {Object} meta
     */
    setMetadata = (meta) => {
        let oldState = this.state;
        oldState.userData.metadata = meta;
        this.setState(oldState);
    };

    /**
     * Function for get file image and set to state in base64
     * @param  {Object} files
     */
    getFiles=(files)=>{
        if(files.base64) {
            let oldState = this.state;
            oldState.userData.image = {
                type:'imageBase64',
                value:files.base64,
                name:files.name
            };
            this.setState(oldState);
        }
    };

    /**
     * function to switch between navigation tabs
     * @param  {Object} event
     * @param  {Object} newValue
     */
    handleChangeNav = (event, newValue) => {
        this.setState({currentTab: newValue});
    };

    /**
     * Function to validate empty values
     * @param  {Object} values
     * @returns {Object} errors
     */
    validate = (values) => {
        const errors = {};       
        const numChar = 50;     
        const numCharEmail = 100;   
        
        
        const patternChars = /[^A-Za-z0-9 ñÑáéíóúÁÉÍÓÚüÜ\,.:_!#?]/;
        const patternCharsName = /[^A-Za-z ñÑáéíóúÁÉÍÓÚüÜ]/;
        const patternEmail = /^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

        if (!values.firstName) {
            errors.firstName = 'Required';
        }

        if(values.firstName || values.lastName){            
            if(patternCharsName.test(values.firstName)){
                errors.firstName = 'No se permiten caracteres especiales ni números';                
            }
            if(values.firstName.length>numChar){
                errors.firstName = `Solo se permiten ${numChar} caracteres`;
            }
        }        

        if (!values.lastName) {
            errors.lastName = 'Required';
        }

        if(values.lastName){            
            if(patternCharsName.test(values.lastName)){
                errors.lastName = 'No se permiten caracteres especiales ni números';                
            }
            if(values.lastName.length>numChar){
                errors.lastName = `Solo se permiten ${numChar} caracteres`;
            }
        }

        if(values.company){            
            if(patternChars.test(values.company)){
                errors.company = 'No se permiten caracteres especiales ni números';
            }
            if(values.company.length>numChar){
                errors.company = `Solo se permiten ${numChar} caracteres`;
            }
        }

        if (!values.email) {
            errors.email = 'Required';
        }

        if(values.email){
            if(values.email.length>numCharEmail) {
                errors.email = `El correo solo permite ${numCharEmail} caracteres`;
            }
            if(!patternEmail.test(values.email)){
                errors.email = 'Este no es un formato de email válido';
            }
        }
        return errors;
    };
    
    /**
     * Function to submit user form
     * @param  {Object} values
     */
    handleSubmit = async (values)  => {
        let ctx = this.context;
        let slug = ctx.slug;
        this.setState({loading: true});

        let request = _.clone(values);


        if(this.state.userData.image){
            request.image = this.state.userData.image
        }else{
            delete request.image;
        }

        request.slug = slug;
        request._idUser = this.props.match.params.id;
        request.groups = _.map(values.groups, function (x) {
            return {
                _idGroup: x
            }
        });
        console.log('Request update', request);
        let errors = {};
        try {
            let res = await apiEvent.updEventUser(request);
            // this.setState({redirect: true, loading:false});
            this.setState({redirect: false, loading:false});
            ctx.openSuccessNotification(this.props.i18n._(t`Datos actualizados`));
        }
        catch (e) {
            console.log('Error al crear usuario', e.response);
            if(e.response.status === 403){
                ctx.openPermissionNotification();
            }
            if(e.response.data && e.response.data.message && typeof e.response.data.message === 'string') {
                errors = {[FORM_ERROR]: e.response.data.message};
            }

            if(e.response.data && e.response.data.message.message && typeof e.response.data.message.message === 'string') {
                errors = {[FORM_ERROR]: e.response.data.message.message};
            }
        }
        this.setState({loading: false});
        return errors;
    };
    handleInputChange = (event)=>{
        const target = event.target;
        const value = target.type === 'checkbox' ? target.checked : target.value;
        const name = target.name;

        let ctx = this.context;
        const patternChars = /[^A-Za-z0-9 ñÑáéíóúÁÉÍÓÚüÜ\,.:!#?]/;
        const patternCharsName = /[^A-Za-z ñÑáéíóúÁÉÍÓÚüÜ]/;

        // if(target.name == 'firstName'){
        //     if(event.target.value.length>50){
        //         event.target.value = event.target.value.slice(0,50);
        //     }
        //     if(patternCharsName.test(event.target.value)){
        //         event.target.value = event.target.value.slice(0,-1);
        //         ctx.openNotification('No se permiten ciertos caracteres especiales', 'error');
        //     }
        // }

        console.log(target.name);
        
        this.setState({
            [name]: value
        });
    }

    /**
     * Function to load roles
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvent.getRoles(ctx.slug).then(
            (res) => {
                this.getUserDetail();
                // console.log('Perfiles', res);
                // let roles = _.map(res, function (o) {
                //     return o._id;
                // });
                this.setState({roles: res});
            }
        ).catch(
            (err) => {
                console.log('Error en la carga de perfiles', err)
            }
        );
    };

    /**
     * Function to get user details
     */
    getUserDetail = () => {
        let ctx = this.context;
        let slug = ctx.slug;
        let userId = this.props.match.params.id;
        apiEvent.getEventUserDetail(userId, slug).then(
            (res) => {
                console.log('Respuesta', res);
                let oldState =this.state;
                oldState.userData = res.eventUser;
                oldState.userData.image = _.has(oldState.userData, 'image.value')? oldState.userData.image : null;
                oldState.userData.groups = _.map(oldState.userData.groups, function (o) {
                    return o._idGroup;
                });
                this.setState(oldState);
                this.setState({
                    breadcrumbs: [
                        {
                            name: this.props.i18n._(t`Lista de usuarios`),
                            active: false,
                            link: `/${ctx.slug}/admin/user/list/`
                        },
                        {
                            name: `${res.eventUser.firstName} ${res.eventUser.lastName}`,
                            active: true,
                            link: '',
                        }

                    ]
                });
            }
        ).catch(
            (err) => {
                //console.log(err);
                if(err.response){
                    if(err.response.status === 403){
                        ctx.openPermissionNotification(this.props.i18n._(t`Si no logras ver la información es probable que no disponga de permisos pero si de edición. Contacte al administrador del sistema para asignarle permisos de lectura.`));
                    }
                }

                this.setState({loading:false})
            }
        );

    };

    componentDidMount() {
        if(_.has(this.props, 'match.params.isMeta')){
            this.setState({currentTab: 1})
        }
        let ctx = this.context;
        this.loadRoles();
        this.setState({
            breadcrumbs: [
                {
                    name: this.props.i18n._(t`Lista de usuarios`),
                    active: false,
                    link: `/${ctx.slug}/admin/user/list/`
                },
                {
                    name: this.props.i18n._(t`Editar usuario`),
                    active: true,
                    link: '',
                }

            ]
        });
    }

    render() {
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/user/list/`} />;
        return (
            <AdminEventLayout title={<Trans>Administrador de eventos</Trans>} breadcrumbs={this.state.breadcrumbs}>
                <Grid container>
                    <Grid
                        container
                        item xs={12}
                        direction="row"
                        justify="center"
                        alignItems="center"
                    >
                        <UserUpdNavs currentTab={this.state.currentTab} handleChangeNav={this.handleChangeNav}/>
                    </Grid>
                    <Grid item xs={12}>
                        {
                            this.state.userData._id ? (
                                <>
                                    {
                                        this.state.currentTab === 0 ? (
                                            <EventCreateUserFormFinalForm
                                                handleInputChange = {this.handleInputChange}
                                                logo={_.has(this.state.userData, 'image.value')?this.state.userData.image.value:imgDefault}
                                                handleLogo={this.getFiles}
                                                onSubmit={this.handleSubmit}
                                                validate={this.validate}
                                                loading={this.state.loading}
                                                roles={this.state.roles}
                                                initialValues={this.state.userData}
                                                isUpd={true}
                                                title={<Trans>Editar usuario</Trans>}
                                            />
                                        ) : <EventUserMetaDataForm setMetadata={this.setMetadata} userData={this.state.userData}/>
                                    }
                                </>
                            ) : ''
                        }

                    </Grid>
                </Grid>
            </AdminEventLayout>
        )
    }
}

export default withI18n() (withRouter(EventUpdUser))