import React, {Component} from 'react';
import Grid from "@material-ui/core/Grid";
import MailOutlineIcon from "@material-ui/core/SvgIcon/SvgIcon";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import { Trans } from '@lingui/macro';
import {ApiServiceEvents} from "../../api-services/events";
import GlobalContext from "../../GlobalContext";
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';
import Image from 'material-ui-image';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Helmet from 'react-helmet'

const apiEvents = new ApiServiceEvents();
class ConfirmAssistantEvent extends Component {
    static contextType = GlobalContext;

    state = {
        color:'',
        logo:'',
        name: <Trans>Evento</Trans>,
        slug:null,
        loading:true,
        eventUser:'',
        description:'',
        finalConfirm:false
    }



    componentDidMount() {

        let slug = this.props.match.params.slug;
        let eventUser = this.props.match.params.eventUser;

        this.setState({
            slug,
            eventUser
        })


        apiEvents.getEventInfoBySlug(slug).then(res=>{
            let {color,logo, name,description} = res
            console.log(res)
            this.setState({
                color,
                logo:res.logo.value,
                name,
                description
            })
        }).catch(err=>{
            /// cuando no exista
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })
    }



    confirmAssistant=(confirm)=>{
        let {slug,eventUser} = this.state;
        this.setState({
            loading:true
        })
        apiEvents.confirmAssistant(slug,eventUser,confirm).then((res)=>{
            console.log(res)
            this.setState({
                finalConfirm:true
            })
        }).catch((error)=>{
            this.setState({
                loading:false
            })
        }).finally(()=>{
            this.setState({
                loading:false
            })
        })
    }


    render(){
        return (
            <Grid
                container
                spacing={0}
                justify="center"
                style={{marginTop:'10%'}}
            >
            <Helmet bodyAttributes={{style: `background-color : ${this.state.color}`}}/>
            <Card style={{width:'70%'}}>
                    <CardMedia
                        src={this.state.logo}
                        style={{height:240}}
                        component={'img'}
                        title={<Trans>Contemplative Reptile</Trans>}
                    />
                    <CardContent>
                        <Typography gutterBottom variant="h5" component="h2">
                            <Trans>Confirmar asistencia a </Trans> {this.state.name}
                        </Typography>
                        <Typography variant="body2" color="textSecondary" component="p">
                            {this.state.description}
                        </Typography>
                    </CardContent>
                <CardActions>


                    {
                        this.state.loading?<CircularProgress color="secondary"/>:''
                    }

                    {
                        !this.state.finalConfirm?<div>
                            <Button style={{marginRight: '10px'}}
                                    onClick={this.confirmAssistant.bind(this, false)}><Trans>No asistiré</Trans></Button>
                            <Button  color="primary"
                                    onClick={this.confirmAssistant.bind(this, true)}>
                                <Trans>Sí asistiré</Trans>
                            </Button>
                        </div>:''
                    }
                    {
                        this.state.finalConfirm?
                            <Typography variant="h6" style={{display: 'flex', justifyContent: 'center', marginTop:10}} component="h6">
                                <Trans> Gracias por tu respuesta </Trans>
                            </Typography>:''
                    }

                </CardActions>
            </Card>
            </Grid>
        )
    }

    render2() {
        return (
            <div>
                <Grid
                    container
                    spacing={0}
                    justify="center"
                    style={{marginTop:'10%'}}
                >

                    <Paper style={{width:'500px'}}>
                        <Image
                            alt={'logo'}
                            src={this.state.logo}
                            imageStyle={{
                                width: '100%',
                                height:'auto'
                            }}
                        />
                    </Paper>
                    <Paper style={{width:'500px'}}>
                        <Typography variant="h6" style={{margin:20}} component="h6">
                            <Trans>Confirmar asistencia a </Trans> {this.state.name}
                        </Typography>

                        {
                            this.state.loading?<CircularProgress color="secondary"/>:''
                        }

                        {
                            !this.state.finalConfirm?<div style={{margin:20}}>
                                    <Button style={{marginRight: '10px'}} variant="contained"
                                            onClick={this.confirmAssistant.bind(this, false)}><Trans>No asistiré</Trans></Button>
                                    <Button variant="contained" color="primary"
                                            onClick={this.confirmAssistant.bind(this, true)}>
                                        <Trans>Si asistiré</Trans>
                                    </Button>
                                </div>:''
                        }
                        {
                            this.state.finalConfirm?
                                <Typography variant="h6" style={{display: 'flex', justifyContent: 'center', marginTop:10}} component="h6">
                                    <Trans> Gracias por tu respuesta </Trans>
                                </Typography>:''
                        }


                    </Paper>


                </Grid>
            </div>
        );
    }
}

export default ConfirmAssistantEvent;