import React from 'react';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import LocateMap from "./LocateMap";
import {Trans} from "@lingui/macro";

export default function DialogLocateMap(props) {
    const [open, setOpen] = [props.open, props.openSelectLocate];
    const theme = useTheme();
    const fullScreen = useMediaQuery(theme.breakpoints.down('lg'));

    const handleClickOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>

            <Dialog
                fullScreen={fullScreen}
                open={open}
                onClose={handleClose}
                aria-labelledby="responsive-dialog-title"
            >
                <DialogTitle id="responsive-dialog-title">{<Trans>Selecciona una ubicación</Trans>}</DialogTitle>
                <DialogContent>
                    <LocateMap
                        handleClickPoint={props.handleClickPoint}
                    />
                </DialogContent>
                <DialogActions>
                    <Button autoFocus onClick={handleClose} color="primary">
                        <Trans>Cancelar</Trans>
                    </Button>
                    <Button onClick={props.confirmPoint} color="primary" autoFocus>
                        <Trans>Confirmar ubicación</Trans>
                    </Button>
                </DialogActions>
            </Dialog>
        </div>
    );
}
