import React, {Component} from 'react';
import CssBaseline from "@material-ui/core/CssBaseline";
import {Field, Form} from "react-final-form";
import arrayMutators from 'final-form-arrays'
import { FieldArray } from 'react-final-form-arrays'
import {FormControl, FormControlLabel, Grid, Paper, Typography} from "@material-ui/core";
import {Select, Radio, TextField} from "final-form-material-ui";
import {SaveButtonSpinner} from "../../buttons/StandartButtons";
import Button from '@material-ui/core/Button';
import Box from "@material-ui/core/Box";
import Image from "material-ui-image/lib/components/Image/Image";
import FileBase64 from "react-file-base64";
import _ from "lodash";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import MenuItem from "@material-ui/core/MenuItem";
import {ApiServiceEvents} from "../../../api-services/events";
import {GlobalContextBuilder} from "../../../GlobalContext";


const apiEvent = new ApiServiceEvents();

class ConfigGallery extends Component {

    static contextType = GlobalContextBuilder;

    state = {
        loading: true,
        request: {
            images: []
        },
        images: {},
        viewsList: []
    };


    handleSubmit = (values) => {
        // this.props.onSaveConfig(values);
        let request = {
            images: {
                value: [],
                type: 'arrayImages'
            }
        };
        let state = this.state;
        request.images.value = _.map(values.images, function (x, index) {
            let newValue = _.cloneDeep(x);
            let image = {
                value: state.images[index.toString()].image,
                type: state.images[index.toString()].imageType,
                name: state.images[index.toString()].imageName,
            };
            newValue.image = image;
            return newValue;
        });
        this.props.onSaveConfig(request);
    };

    validate = (values) => {
        // console.log(values)
    };

    componentDidMount() {
        // this.setState({loading: false});
        this.getViews();
        this.setInitialValues();
    }

    removeImg = (index, values) => {
        if (index > -1) {
            let newImages = {};
            let images = this.state.images;
            delete images[index];
            let toI = values.images.length - 1;
            for(let i=0; i<toI; i++){
                let key = Object.keys(images)[i];
                newImages[i.toString()] = images[key];
            }
            this.setState({images: newImages});
        }
    };

    setInitialValues = () => {
        const ctx = this.context;
        const component = ctx.currentComponent;

        let properties = component.properties;

        if(!properties){
            this.setState({loading: false});
            return;
        }
        let newState = this.state;
        newState.request.images = properties.images.value;
        newState.loading = false;
        _.map(properties.images.value, function (x, index) {
            // console.log('X', x, index);
            newState.images[index.toString()] = {
                image: x.image.value,
                imageName: x.image.name,
                imageType: x.image.type
            };
        });
        this.setState(newState);
    };

    getFiles=(index, files)=>{
        // console.log('index', index);
        // console.log('values', values);
        // values.images[0].image.value = files.base64;
        if(files.base64) {
            let newState = this.state;
            newState.images[index.toString()] = {
                image: files.base64,
                imageName: files.name,
                imageType: 'imageBase64'
            };
            this.setState(newState);
        }
    };

    getViews = () => {
        let ctx = this.context;
        let moduleId = ctx._idModule;
        // console.log('module', moduleId);
        apiEvent.getViewsList(moduleId).then(
            (res) => {
                let array = [];
                res.map((view,i)=>{
                    array.push(view.views)
                });

                if(array.length>0){
                    this.setState({
                        viewsList:array
                    })
                }
            }
        ).catch(
            (err) => {
                console.log('Error en carga de vistas', err);
            }
        );
    };

    render() {
        let templateItem = {
            image: {
                value: '',
                type: 'imageBase64',
                name: '',
            },
            name: {
                value: '',
                type: 'string'
            },
            action: {
                value:'',
                type: 'string'
            }
        };
        let imageToView = 'https://x.kinja-static.com/assets/images/logos/placeholders/default.png';

        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                {
                    this.state.loading ? '' : (
                        <Form
                            onSubmit={this.handleSubmit}
                            mutators={{
                                ...arrayMutators
                            }}
                            validate={this.validate}
                            initialValues={this.state.request}
                            render={({
                                         handleSubmit,
                                         form: {
                                             mutators: { push, pop }
                                         }, // injected from final-form-arrays above
                                         pristine,
                                         form,
                                         submitting,
                                         values
                                     }) => {
                                return (
                                    <form onSubmit={handleSubmit} noValidate>
                                        <Paper style={{ padding: 16 }}>

                                            <Typography variant="h6" align="left" component="h6" gutterBottom>
                                                Propiedades
                                            </Typography>

                                            <Grid
                                                container
                                                alignItems="flex-start"
                                                spacing={2}
                                                style={{marginTop: 20}}
                                            >
                                                {/*Row Buttons*/}
                                                <Grid container item xs={12} spacing={2}>
                                                    <div className="buttons">
                                                        <Button
                                                            variant="contained"
                                                            color="primary"
                                                            onClick={() => push('images', templateItem)}
                                                            style={{marginRight: 20}}
                                                        >
                                                            Agregar imagen
                                                        </Button>
                                                        <Button
                                                            variant="contained"
                                                            color="secondary"
                                                            onClick={() => pop('images')}
                                                        >
                                                            Eliminar imagen
                                                        </Button>
                                                    </div>
                                                </Grid>

                                                <FieldArray name="images">
                                                    {({fields}) =>
                                                        fields.map((name, index) => (
                                                            // Rows image item
                                                            <div key={index}>
                                                                <Grid container item xs={12} spacing={1}>
                                                                    {/*Col Image input*/}
                                                                    <Grid item xs={12} lg={6} alig={'center'}>
                                                                        <Box align="center">
                                                                            <h4>Imagen #{index + 1}</h4>
                                                                                    <Image
                                                                                        alt={'image'}
                                                                                        src={
                                                                                            _.has(this.state.images[index.toString()], 'image') ?
                                                                                                this.state.images[index.toString()].image
                                                                                                : imageToView
                                                                                        }
                                                                                        imageStyle={{
                                                                                            margin: 10,
                                                                                            width: 150,
                                                                                            height: 150,
                                                                                        }}
                                                                                        style={{
                                                                                            margin: 10,
                                                                                            width: 150,
                                                                                            height: 150,
                                                                                            paddingTop: 30
                                                                                        }}

                                                                                    />

                                                                            <br/>


                                                                            <FileBase64
                                                                                onDone={this.getFiles.bind(this, index)} />
                                                                        </Box>
                                                                    </Grid>

                                                                    {/*Col properties image*/}
                                                                    <Grid container item xs={12} lg={5}>
                                                                        <Grid item xs={12}>
                                                                            <Field
                                                                                fullWidth
                                                                                name={`${name}.name.value`}
                                                                                component={TextField}
                                                                                label="Nombre"
                                                                                variant="outlined"
                                                                                margin="normal"
                                                                            />
                                                                        </Grid>
                                                                        <Grid item xs={12}>
                                                                            <FormControl component="fieldset">
                                                                                <FormLabel component="legend">Tipo de acción del botón</FormLabel>
                                                                                <RadioGroup row>
                                                                                    <FormControlLabel
                                                                                        label="Redirección a una página externa"
                                                                                        control={
                                                                                            <Field
                                                                                                name={`${name}.action.value`}
                                                                                                component={Radio}
                                                                                                type="radio"
                                                                                                value="redirect"
                                                                                            />
                                                                                        }
                                                                                    />
                                                                                    <FormControlLabel
                                                                                        label="Redirección a una vista"
                                                                                        control={
                                                                                            <Field
                                                                                                name={`${name}.action.value`}
                                                                                                component={Radio}
                                                                                                type="radio"
                                                                                                value="router"
                                                                                            />
                                                                                        }
                                                                                    />
                                                                                    <FormControlLabel
                                                                                        label="Descarga de un archivo de internet"
                                                                                        control={
                                                                                            <Field
                                                                                                name={`${name}.action.value`}
                                                                                                component={Radio}
                                                                                                type="radio"
                                                                                                value="download"
                                                                                            />
                                                                                        }
                                                                                    />
                                                                                    <FormControlLabel
                                                                                        label="Hacer llamada"
                                                                                        control={
                                                                                            <Field
                                                                                                name={`${name}.action.value`}
                                                                                                component={Radio}
                                                                                                type="radio"
                                                                                                value="phone"
                                                                                            />
                                                                                        }
                                                                                    />
                                                                                </RadioGroup>
                                                                            </FormControl>
                                                                        </Grid>

                                                                        {
                                                                            _.has(values.images[index], 'action.value') &&
                                                                            values.images[index].action.value === 'redirect' ? (
                                                                                <Grid item xs={12}>
                                                                                    <Field
                                                                                        fullWidth
                                                                                        name={`${name}.urlRedirect.value`}
                                                                                        component={TextField}
                                                                                        multiline
                                                                                        label="URL externa"
                                                                                        variant="outlined"
                                                                                    />
                                                                                </Grid>
                                                                            ) : ''
                                                                        }
                                                                        {
                                                                            values.images[index].action.value === 'download' ? (
                                                                                <Grid item xs={12}>
                                                                                    <Field
                                                                                        fullWidth
                                                                                        name={`${name}.urlResource.value`}
                                                                                        component={TextField}
                                                                                        multiline
                                                                                        label="URL externa"
                                                                                        variant="outlined"
                                                                                    />
                                                                                </Grid>
                                                                            ) : ''
                                                                        }
                                                                        {
                                                                            values.images[index].action.value === 'phone' ? (
                                                                                <Grid item xs={12}>
                                                                                    <Field
                                                                                        fullWidth
                                                                                        name={`${name}.phone.value`}
                                                                                        component={TextField}
                                                                                        label="Hacer llamada"
                                                                                        variant="outlined"
                                                                                    />
                                                                                </Grid>
                                                                            ) : ''
                                                                        }
                                                                        {
                                                                            values.images[index].action.value === 'router' ? (
                                                                                <Grid item xs={12}>
                                                                                    <Field
                                                                                        fullWidth
                                                                                        name={`${name}.viewId.value`}
                                                                                        component={Select}
                                                                                        label="Seleccionar vista"
                                                                                        // variant="outlined"
                                                                                        formControlProps={{fullWidth: true}}
                                                                                    >
                                                                                        {
                                                                                            this.state.viewsList.length > 0 ? (
                                                                                            this.state.viewsList.map(
                                                                                                (item, index) => (
                                                                                                    <MenuItem key={index} value={item._id}>{item.name}</MenuItem>
                                                                                                )
                                                                                            )
                                                                                            ) : (
                                                                                                <MenuItem key={index} value="">No existen vistas</MenuItem>
                                                                                            )
                                                                                        }
                                                                                    </Field>
                                                                                </Grid>
                                                                            ) : ''
                                                                        }


                                                                    </Grid>
                                                                    <Grid item xs={12} lg={1}>
                                                                        <span
                                                                            onClick={() => {
                                                                                    this.removeImg(index, values);
                                                                                    fields.remove(name)
                                                                                }
                                                                            }
                                                                            style={{cursor: 'pointer'}}
                                                                        >
                                                                          ❌
                                                                        </span>
                                                                    </Grid>

                                                            </Grid>

                                                                <Grid key={name} container item xs={12} spacing={1}>
                                                                    <Grid item xs={12}>
                                                                        <hr/>
                                                                    </Grid>
                                                                </Grid>


                                                            </div>

                                                        ))
                                                    }
                                                </FieldArray>

                                                {/*Row buttons submit*/}
                                                <Grid container item xs={12} spacing={3}>
                                                    <div className="buttons">
                                                        {/*<button type="submit" disabled={submitting || pristine}>*/}
                                                        {/*    Submit*/}
                                                        {/*</button>*/}
                                                        <SaveButtonSpinner/>
                                                        {/*<button*/}
                                                        {/*    type="button"*/}
                                                        {/*    onClick={form.reset}*/}
                                                        {/*    disabled={submitting || pristine}*/}
                                                        {/*>*/}
                                                        {/*    Reset*/}
                                                        {/*</button>*/}
                                                    </div>
                                                </Grid>

                                                {/*<pre>{JSON.stringify(values, 0, 2)}</pre>*/}
                                                {/*<pre>{JSON.stringify(this.state.images, 0, 2)}</pre>*/}
                                            </Grid>
                                        </Paper>
                                    </form>
                                )
                            }}
                        />
                    )
                }
            </div>
        );
    }
}

export default ConfigGallery;