import React from 'react';
import {Form, Field} from 'react-final-form';
import {TextField, Checkbox, Radio, Select} from 'final-form-material-ui';
import {FormHelperText} from '@material-ui/core';
import {Link as LinkRouter} from "react-router-dom";
import {
    Typography,
    Paper,
    Link,
    Grid,
    Button,
    CssBaseline,
    RadioGroup,
    FormLabel,
    MenuItem,
    FormGroup,
    FormControl,
    FormControlLabel,
} from '@material-ui/core';
import {
    MuiPickersUtilsProvider,
    TimePicker,
    DatePicker,
} from '@material-ui/pickers';
import FileBase64 from "react-file-base64";
import Box from "@material-ui/core/Box";
import Image from 'material-ui-image';
import DateFnsUtils from '@date-io/date-fns';
import GlobalContext from "../../../../GlobalContext";
import {CancelButton, SaveButtonSpinner} from "../../../buttons/StandartButtons";
import {DatePickerWrapper, EndDatePickerWrapper} from "../../../fields/DatePickers";
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableCell from '@material-ui/core/TableCell';
import TableHead from '@material-ui/core/TableHead';
import TableRow from '@material-ui/core/TableRow';
import moment from 'moment';
import PropTypes from 'prop-types';
import {Trans} from "@lingui/macro";

class AgendaForm extends React.Component {
    static contextType = GlobalContext;

    render() {
        let ctx = this.context;
        return (
            <div style={{padding: 16, margin: 'auto', maxWidth: 800}}>
                <CssBaseline/>
                <Typography variant="h4" align="center" component="h1" gutterBottom>
                    {
                        this.props.title ? this.props.title : 'Crear usuario'
                    }
                </Typography>

                <Form
                    onSubmit={this.props.onSubmit}
                    initialValues={this.props.initialValues}
                    validate={this.props.validate}
                    render={({submitError, handleSubmit, reset, submitting, pristine, values}) => (
                        <form onSubmit={handleSubmit} noValidate>
                            <Paper style={{padding: 16}}>
                                <Typography variant="h6" align="left" component="h6" gutterBottom>
                                    Datos generales
                                </Typography>
                                <Grid container style={{marginTop: 10}} alignItems="flex-start" spacing={2}>
                                    <Grid item xs={6}>
                                        <Field
                                            fullWidth
                                            required
                                            name="name"
                                            component={TextField}
                                            type="text"
                                            label="Nombre"
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <Grid item xs={6}>
                                        <Field
                                            fullWidth
                                            required
                                            name="place"
                                            component={TextField}
                                            type="text"
                                            label="Lugar"
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Field
                                            fullWidth
                                            required
                                            name="address"
                                            component={TextField}
                                            multiline
                                            label="Dirección"
                                            variant="outlined"
                                        />
                                    </Grid>
                                    <Grid item xs={12}>
                                        <Field
                                            fullWidth
                                            name="speaker"
                                            component={TextField}
                                            multiline
                                            label="Expositor"
                                            variant="outlined"
                                        />
                                    </Grid>

                                    <Grid item xs={12} style={{marginTop: 10}}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Periodo de tiempo
                                        </Typography>
                                    </Grid>

                                    <MuiPickersUtilsProvider utils={DateFnsUtils}>

                                        <Grid item xs={6}>
                                            <Field
                                                name="startTimeDate"
                                                component={DatePickerWrapper}
                                                fullWidth
                                                required
                                                // margin="normal"
                                                label="Fecha y hora de inicio"
                                                inputVariant="outlined"
                                            />
                                        </Grid>
                                        <Grid item xs={6}>
                                            <Field
                                                name="endTimeDate"
                                                component={EndDatePickerWrapper}
                                                fullWidth
                                                required
                                                // margin="normal"
                                                label="Fecha y hora de término"
                                                inputVariant="outlined"
                                            />
                                        </Grid>
                                    </MuiPickersUtilsProvider>

                                    <Grid item xs={12}>
                                        <FormControlLabel
                                            labelPlacement={'end'}
                                            label={<Typography variant="h6" align="left" component="h5">
                                                ¿Es evento privado?
                                            </Typography>}
                                            control={
                                                <Field
                                                    name="isIndustryEvent"
                                                    component={Checkbox}
                                                    type="checkbox"
                                                />
                                            }
                                        />
                                        <FormHelperText>
                                            Al ser un evento privado, se puede gestionar la confirmación así como los
                                            accesos a esta.
                                        </FormHelperText>
                                    </Grid>

                                    {
                                        values.isIndustryEvent ?
                                            <Grid item xs={12}>
                                                <Field
                                                    fullWidth
                                                    required={values.isIndustryEvent}
                                                    name="capacity"
                                                    component={TextField}
                                                    type="number"
                                                    // disabled={this.props.industryConfirmList.length>0}
                                                    style={{marginBottom: 10}}
                                                    label="Número de personas"
                                                    variant="outlined"
                                                />

                                                <Field
                                                    fullWidth
                                                    required={values.isIndustryEvent}
                                                    name="responsible"
                                                    component={TextField}
                                                    type="text"
                                                    style={{marginBottom: 10}}
                                                    label="Nombre del responsable"
                                                    variant="outlined"
                                                />

                                                <Field
                                                    fullWidth
                                                    required={values.isIndustryEvent}
                                                    name="contact"
                                                    component={TextField}
                                                    type="text"
                                                    style={{marginBottom: 10}}
                                                    label="Contacto"
                                                    variant="outlined"
                                                />
                                                <FormControlLabel
                                                    label={'¿Requiere acceso QR?'}
                                                    control={
                                                        <Field
                                                            name="requireQrCodeAccess"
                                                            component={Checkbox}
                                                            type="checkbox"
                                                        />
                                                    }
                                                />
                                                {/*<FormHelperText>*/}
                                                {/*    Al ser un evento privado, esta activdad adquiere la funcionalidad de notificar a los invitados de acuerdo al perfil seleccionado.*/}
                                                {/*    Adicional, se les envia una confirmación de asistencia asi como gestionar la asistencia a través de su código QR.*/}
                                                {/*</FormHelperText>*/}

                                                <p><b>Confirmados</b>: {this.props.industryConfirmList.length}</p>

                                                <Table size="small" aria-label="a dense table">
                                                    <TableHead>
                                                        <TableRow>
                                                            <TableCell>Fecha</TableCell>
                                                            <TableCell>Nombre</TableCell>
                                                            <TableCell>E-mail</TableCell>
                                                        </TableRow>
                                                    </TableHead>
                                                    <TableBody>
                                                        {this.props.industryConfirmList.map((row, index) => (
                                                            <TableRow key={index}>
                                                                <TableCell component="th" scope="row">
                                                                    {moment(row.createdAt).format('DD/MM/YYYY, h:mm:ss a')}
                                                                </TableCell>
                                                                <TableCell component="th" scope="row">
                                                                    {row.firstName} {row.lastName}
                                                                </TableCell>
                                                                <TableCell component="th" scope="row">
                                                                    {row.email}
                                                                </TableCell>
                                                            </TableRow>
                                                        ))}
                                                    </TableBody>
                                                </Table>
                                            </Grid> : ''
                                    }

                                    <Grid item xs={12} style={{marginTop: 20}}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            Perfiles
                                        </Typography>
                                        <FormControl component="fieldset">
                                            {/*<FormLabel component="legend">Sauces</FormLabel>*/}
                                            <FormGroup row>
                                                {
                                                    this.props.roles.map(
                                                        (rol, index) => (
                                                            <FormControlLabel
                                                                key={index}
                                                                label={rol.name}
                                                                control={
                                                                    <Field
                                                                        name="groups"
                                                                        component={Checkbox}
                                                                        type="checkbox"
                                                                        value={rol._id}
                                                                    />
                                                                }
                                                            />
                                                        )
                                                    )
                                                }
                                            </FormGroup>
                                        </FormControl>
                                    </Grid>


                                    {submitError && <div className="error">{submitError}</div>}
                                    <Grid item style={{marginTop: 16}} xs={12}>

                                        <CancelButton
                                            to={`/${ctx.slug}/admin/agenda/list/`}
                                        />

                                        <SaveButtonSpinner
                                            loading={this.props.loading}
                                        />
                                    </Grid>
                                </Grid>
                            </Paper>
                        </form>
                    )}
                />
            </div>
        );
    }
}


AgendaForm.propTypes = {
    industryConfirmList: PropTypes.array,
}


AgendaForm.defaultProps = {
    industryConfirmList: []
};


export default AgendaForm
