import React, {Component} from 'react';
import CssBaseline from "@material-ui/core/CssBaseline";
import {FormControl, FormControlLabel, FormGroup, Grid, Paper, Typography} from "@material-ui/core";
import Box from "@material-ui/core/Box";
import Image from "material-ui-image/lib/components/Image/Image";
import FileBase64 from "react-file-base64";
import {Field, Form} from "react-final-form";
import {Checkbox, Radio, Select, TextField} from "final-form-material-ui";
import FormLabel from "@material-ui/core/FormLabel";
import RadioGroup from "@material-ui/core/RadioGroup";
import MenuItem from "@material-ui/core/MenuItem";
import {CancelButton, SaveButtonSpinner} from "../../buttons/StandartButtons";
import arrayMutators from "final-form-arrays";
import {Trans} from "@lingui/macro";
import {FieldArray} from "react-final-form-arrays";
import Button from "@material-ui/core/Button";
import GlobalContext, {GlobalContextBuilder} from "../../../GlobalContext";
import _ from "lodash";
import DialogLocateMap from "./DialogLocateMap";
import RoomIcon from '@material-ui/icons/Room';
import AlertText from "../../alerts/AlertText";

class ConfigMap extends Component {

    static contextType = GlobalContextBuilder;

    state = {
        loading: true,
        initData: {
            visible: true,
            points: []
        },
        openDialogLocate: false,
        selectPoint: '',
        latLngTmp: '',
    };

    handleSubmit = (values) => {
        // console.log('values',values);
        this.props.onSaveConfig(values);
    };

    setInitialValues = () => {
        const ctx = this.context;
        const component = ctx.currentComponent;

        if(!component.properties) {
            this.setState({loading:false});
            return;
        }

        let newState = this.state;

        newState.initData = component.properties;
        newState.loading = false;

        this.setState(newState);

    };

    validate = (values) => {


        let errors = {
            points: []
        };
        if (!values.name) {
            errors.name = <Trans>Requerido</Trans>;
        }

        if (!values.centralPointLat) {
            errors.centralPointLat = <Trans>Requerido</Trans>;
        }else {
            if(isNaN(values.centralPointLat)){
                errors.centralPointLat = <Trans>Este valor debe ser numérico</Trans>;
            }
        }

        if (!values.centralPointLon) {
            errors.centralPointLon = <Trans>Requerido</Trans>;
        }else{
            if(isNaN(values.centralPointLon)){
                errors.centralPointLon = <Trans>Este valor debe ser numérico</Trans>;
            }
        }

        _.map(values.points, function (x, i) {
            if(!x.name){
                errors.points[i] = {
                    name: <Trans>Requerido</Trans>
                }
            }

            if(!x.pointLat){
                errors.points[i] = {
                    pointLat: <Trans>Requerido</Trans>
                }
            }else {
                if(isNaN(x.pointLat)){
                    errors.points[i] = {
                        pointLat: <Trans>Este valor debe ser numérico</Trans>
                    }
                }
            }

            if(!x.pointLon){
                errors.points[i] = {
                    pointLon: <Trans>Requerido</Trans>
                }
            }else {
                if(isNaN(x.pointLon)){
                    errors.points[i] = {
                        pointLon: <Trans>Este valor debe ser numérico</Trans>
                    }
                }
            }
        });


        return errors;
    };

    componentDidMount() {
        this.setInitialValues();
    }

    openSelectLocate = (val) => {
        this.setState({openDialogLocate: val})
    };

    setAndOpenLocate = (id) => {
        console.log('id', id);
        this.openSelectLocate(true);
        this.setState({selectPoint: id});
    };

    handleClickPoint = (latLng) => {
        const {selectPoint} = this.state;
        console.log('Hola', latLng, selectPoint);
        this.setState({latLngTmp: latLng});
    };

    confirmPoint = (state) => {
        let {lastFormState} = state;
        let {values} = lastFormState;
        console.log('Confirmacion de punto', this.state.selectPoint, this.state.latLngTmp, values.points);
        let {initData, selectPoint, latLngTmp} = this.state;
        if(_.has(values, 'points')){
            if(!values.points[selectPoint]){
                values.points[selectPoint] = {
                    name: '',
                    pointLat: latLngTmp.lat,
                    pointLon: latLngTmp.lng,
                    address: '',
                };
            }else {
                console.log('auiq 2');
                values.points[selectPoint].pointLat = latLngTmp.lat;
                values.points[selectPoint].pointLon = latLngTmp.lng;
            }

        }

        this.setState({openDialogLocate: false});
        console.log('initData', this.state.initData);
        return values;

    };

    render() {

        let ctx = this.context;

        let templateItem = {
            name: '',
            pointLat: '',
            pointLon: '',
            address: '',
        };

        return (
            <div style={{ padding: 16, margin: 'auto', maxWidth: 1200 }}>
                <CssBaseline />
                {
                    this.state.loading ? '' : (
                        <Form
                            onSubmit={this.handleSubmit}
                            initialValues={this.state.initData}
                            validate={this.validate}
                            mutators={{
                                setPoints: (args, state, utils) => {
                                    console.log('Aqui empieza');
                                    let initData = this.confirmPoint(state, args);
                                    if(_.has(initData, 'points')){
                                        utils.changeValue(state, 'points', () => initData.points)
                                    }
                                },
                                ...arrayMutators
                            }}
                            render={({
                                         submitError,
                                         handleSubmit,
                                         form: {
                                             mutators: { push, pop, setPoints }
                                         }, // injected from final-form-arrays above
                                         reset,
                                         submitting,
                                         pristine,
                                         values
                                     }) => (
                                <form onSubmit={handleSubmit} noValidate>
                                    <Paper style={{ padding: 16 }}>
                                        <Typography variant="h6" align="left" component="h6" gutterBottom>
                                            <Trans>Configuración del componente mapa</Trans>
                                        </Typography>
                                        <Grid
                                            container
                                            style={{marginTop: 10}}
                                            alignItems="center"
                                            spacing={2}>

                                            <Grid item xs={12} style={{marginTop: 20}}>

                                                <Grid item xs={12}>
                                                    <Field
                                                        fullWidth
                                                        required
                                                        name="name"
                                                        component={TextField}
                                                        type="text"
                                                        label={<Trans>Nombre</Trans>}
                                                        variant="outlined"
                                                    />
                                                </Grid>

                                            </Grid>

                                            {/*<Grid item xs={12}>*/}
                                                <Grid item lg={5} md={5} xs={12}>
                                                    <Field
                                                        fullWidth
                                                        required
                                                        name="centralPointLat"
                                                        component={TextField}
                                                        type="text"
                                                        label={<Trans>Punto central latitud</Trans>}
                                                        variant="outlined"
                                                    />
                                                </Grid>
                                                <Grid item lg={5} md={5} xs={12}>
                                                    <Field
                                                        fullWidth
                                                        required
                                                        name="centralPointLon"
                                                        component={TextField}
                                                        type="text"
                                                        label={<Trans>Punto central longitud</Trans>}
                                                        variant="outlined"
                                                    />
                                                </Grid>
                                                <Grid item lg={2} md={2} xs={12}>
                                                    <FormControlLabel
                                                        label={<Trans>Visible</Trans>}
                                                        control={
                                                            <Field
                                                                name="visible"
                                                                component={Checkbox}
                                                                type="checkbox"
                                                            />
                                                        }
                                                    />
                                                </Grid>

                                            <Grid item xs={12}>
                                            <AlertText type={'info'}>
                                                <Trans>Los datos anteriores hacen referencia al punto central del mapa. ejemplo: Lugar sede del evento</Trans>.
                                            </AlertText>
                                            </Grid>

                                            {/*</Grid>*/}

                                            <Typography variant="h6" align="left" style={{marginTop: 20}} component="h6" gutterBottom>
                                                <Trans>Puntos en el mapa</Trans>
                                            </Typography>

                                            <FieldArray name="points">
                                                {({fields}) =>
                                                    fields.map((name, index) => (
                                                        // Rows image item
                                                        <Grid container
                                                              item xs={12}
                                                              key={index}
                                                        >
                                                            <Grid container item xs={12} spacing={1}>

                                                                <Grid container
                                                                      direction="row"
                                                                      justify="space-between"
                                                                      alignItems="center"
                                                                      spacing={1}
                                                                      item>
                                                                    <Grid item xs={6} lg={2}>
                                                                        <Field
                                                                            fullWidth
                                                                            required
                                                                            name={`${name}.pointLat`}
                                                                            component={TextField}
                                                                            type="text"
                                                                            label={<Trans>Latitud</Trans>}
                                                                            variant="outlined"
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={6} lg={2}>
                                                                        <Field
                                                                            fullWidth
                                                                            required
                                                                            name={`${name}.pointLon`}
                                                                            component={TextField}
                                                                            type="text"
                                                                            label={<Trans>Longitud</Trans>}
                                                                            variant="outlined"
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={6} lg={2}>
                                                                        <Field
                                                                            fullWidth
                                                                            required
                                                                            name={`${name}.name`}
                                                                            component={TextField}
                                                                            type="text"
                                                                            label={<Trans>Nombre</Trans>}
                                                                            variant="outlined"
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={6} lg={4}>
                                                                        <Field
                                                                            fullWidth
                                                                            name={`${name}.address`}
                                                                            component={TextField}
                                                                            type="text"
                                                                            label={<Trans>Dirección</Trans>}
                                                                            variant="outlined"
                                                                        />
                                                                    </Grid>
                                                                    <Grid item xs={6} lg={1}>
                                                                        <Button variant="outlined" size={'small'} color="primary" onClick={this.setAndOpenLocate.bind(this, index)}>
                                                                            <RoomIcon/>
                                                                        </Button>
                                                                    </Grid>
                                                                    <Grid item xs={6} lg={1}>
                                                                        <span
                                                                            onClick={() => {
                                                                                fields.remove(index)
                                                                            }
                                                                            }
                                                                            style={{cursor: 'pointer'}}
                                                                        >
                                                                          ❌
                                                                        </span>
                                                                    </Grid>
                                                                </Grid>


                                                            </Grid>

                                                            {/*<Grid container item xs={12} spacing={1}>*/}
                                                            {/*    <Grid item xs={12}>*/}
                                                            {/*        <hr/>*/}
                                                            {/*    </Grid>*/}
                                                            {/*</Grid>*/}


                                                        </Grid>

                                                    ))
                                                }
                                            </FieldArray>

                                            <Grid container
                                                  direction="row"
                                                  justify="center"
                                                  alignItems="center"
                                                  item xs={12}
                                                  spacing={2}
                                                  style={{marginTop:5}}>
                                                <div className="buttons">
                                                    <Button
                                                        variant="contained"
                                                        color="primary"
                                                        size="small"
                                                        onClick={() => push('points', templateItem)}
                                                        style={{marginRight: 20}}
                                                    >
                                                        <Trans>Agregar punto</Trans>
                                                    </Button>
                                                    <Button
                                                        size="small"
                                                        variant="contained"
                                                        color="secondary"
                                                        onClick={() => pop('points')}
                                                    >
                                                        <Trans>Eliminar último punto</Trans>
                                                    </Button>
                                                </div>
                                            </Grid>

                                            {submitError && <div className="error">{submitError}</div>}
                                            <Grid item style={{ marginTop: 26 }} xs={12}>

                                                <SaveButtonSpinner
                                                    loading={this.props.loading}
                                                />
                                            </Grid>
                                            {/*<pre>{JSON.stringify(values, 0, 2)}</pre>*/}

                                        </Grid>
                                    </Paper>
                                    <DialogLocateMap
                                        open={this.state.openDialogLocate}
                                        openSelectLocate={this.openSelectLocate}
                                        handleClickPoint={this.handleClickPoint}
                                        confirmPoint={setPoints}
                                    />
                                </form>
                            )}
                        />
                    )
                }
            </div>
        );
    }
}

export default ConfigMap;