import React from "react";
import {Checkbox, FormControlLabel, Grid, makeStyles, Paper, withStyles} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import FileBase64 from "react-file-base64";
import TextField from "@material-ui/core/TextField";
import InputAdornment from "@material-ui/core/InputAdornment";
import Button from "@material-ui/core/Button";
import {Link} from "react-router-dom";
import CircularProgress from "@material-ui/core/CircularProgress";
import RolesWidget from "../../../security/RolesWidget";
import Box from "@material-ui/core/Box";
import GlobalContext from "../../../../GlobalContext";
import Select from "@material-ui/core/Select";
import MenuItem from "@material-ui/core/MenuItem";
import InputBase from '@material-ui/core/InputBase';
import {t,Trans} from "@lingui/macro";
import {CancelButton, SaveButtonSpinner} from "../../../buttons/StandartButtons";
import FormHelperText from "@material-ui/core/FormHelperText";
import {withI18n} from "@lingui/react";
import AlertText from "../../../alerts/AlertText";


const BootstrapInput = withStyles(theme => ({
    root: {
        'label + &': {
            marginTop: theme.spacing(3),
        },
    },
    input: {
        borderRadius: 4,
        position: 'relative',
        backgroundColor: theme.palette.background.paper,
        border: '1px solid #ced4da',
        fontSize: 16,
        padding: '10px 26px 10px 12px',
        transition: theme.transitions.create(['border-color', 'box-shadow']),
        // Use the system font instead of the default Roboto font.
        fontFamily: [
            '-apple-system',
            'BlinkMacSystemFont',
            '"Segoe UI"',
            'Roboto',
            '"Helvetica Neue"',
            'Arial',
            'sans-serif',
            '"Apple Color Emoji"',
            '"Segoe UI Emoji"',
            '"Segoe UI Symbol"',
        ].join(','),
        '&:focus': {
            borderRadius: 4,
            borderColor: '#80bdff',
            boxShadow: '0 0 0 0.2rem rgba(0,123,255,.25)',
        },
    },
}))(InputBase);

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexWrap: 'wrap',
    },
    margin: {
        margin: theme.spacing(1),
    },
}));

class CreateModuleForm extends React.Component{
    static contextType = GlobalContext;

    render() {
        let ctx = this.context;
        return (
            <>
                <Paper style={{padding:'20px'}}>
                    <Typography variant="h6" gutterBottom>
                        {this.props.title}
                    </Typography>
                    <br/>

                    <form onSubmit={this.props.handleSubmit}>

                        <Grid container spacing={3}>

                            <Grid item xs={12} md={6}>
                                <Box alignItems="center">
                                    <img alt={'logo modulo'} src={this.props.logo.value} style={{
                                        margin: 10,
                                        width: 150,
                                        height: 150,
                                    }}/><br/>

                                    <FileBase64
                                        onDone={this.props.handleLogo} />
                                </Box>

                            </Grid>


                            <Grid item xs={12} md={12}>
                                <TextField variant="outlined" required name="name" id="name" label={<Trans>Nombre</Trans>} value={this.props.name} onChange={this.props.handleInputChange} fullWidth />
                            </Grid>


                            <Grid item xs={12} md={6}>
                                {/*<TextField variant="outlined" id="description"  name="description" onChange={this.handleEventInput} label="Descripción" fullWidth />*/}
                                <label htmlFor=""><Trans>Estatus</Trans></label> <br/>
                                <Select
                                    value={this.props.status}
                                    onChange={this.props.handleInputChange}
                                    input={<BootstrapInput name="status" id="age-customized-select" />}
                                >
                                    <MenuItem value={1}>Activo</MenuItem>
                                    <MenuItem value={2}>Borrador</MenuItem>
                                </Select>
                            </Grid>

                            <Grid item xs={12} md={12}>
                                <FormControlLabel
                                    control={
                                        <Checkbox
                                            checked={this.props.isPublic}
                                            onChange={this.props.handleChangeCheck}
                                            value="isPublic"
                                            inputProps={{
                                                'aria-label': 'primary checkbox',
                                            }}
                                        />
                                    }
                                    label={<Trans>¿Es página principal?</Trans>}
                                />
                                <FormHelperText id="component-helper-text">Solo un módulo puede ser "página principal". El módulo que esté en este modo se utilizará como vista principal del evento en la aplicación móvil.</FormHelperText>
                                <br/>
                                {
                                    this.props.isPublic?<AlertText type={'warning'} id="component-helper-text">
                                        <Trans>Al ser público, el módulo y sus vistas no tendrán ninguna restricción de visualización para los usuarios.</Trans>
                                    </AlertText>:''
                                }



                            </Grid>


                        </Grid>

                        <br/>
                        {
                            !this.props.isPublic?<Typography variant="h6" gutterBottom>
                                <Trans>Roles de seguridad</Trans>
                            </Typography>:''
                        }

                        <br/>

                        <Grid container spacing={3}>

                            {
                                !this.props.isPublic?<Grid item xs={12} md={12}>
                                    <RolesWidget
                                        availableGroupsIds={this.props.availableGroupsIds}
                                        availableGroups={this.props.availableGroups}
                                        selectedGroupsIds={this.props.selectedGroupsIds}
                                        handleSetGroup={this.props.handleSetGroup}
                                        handleSetGroupSelected={this.props.handleSetGroupSelected}
                                        checked={this.props.checked}
                                        handleSetChecked={this.props.handleSetChecked}
                                    />
                                </Grid>:''
                            }





                            <Grid item xs={12}>
                                {/*<Button component={Link} to={`/${ctx.slug}/admin/module/list/`} style={{marginRight:'10px'}}   variant="contained">*/}
                                {/*    Cancelar*/}
                                {/*</Button>*/}

                                <CancelButton
                                    to={`/${ctx.slug}/admin/module/list/`}
                                />

                                <SaveButtonSpinner
                                    loading={this.props.loading}
                                />

                            </Grid>
                        </Grid>
                    </form>

                </Paper>
            </>
        )
    }
}

export default withI18n() (CreateModuleForm);