import React, {useContext} from 'react';
import clsx from 'clsx';
import { makeStyles } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
import Drawer from '@material-ui/core/Drawer';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import Badge from '@material-ui/core/Badge';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import MeetingRoomIcon from '@material-ui/icons/MeetingRoom';
import {Redirect, withRouter} from "react-router-dom";
import GlobalContext from "../GlobalContext";
import Paper from "@material-ui/core/Paper";
import MenuItem from '@material-ui/core/MenuItem';
import Menu from '@material-ui/core/Menu';
import MenuList from '@material-ui/core/MenuList';
import PropTypes from 'prop-types';
import CustomBreadCrumbs from "../components/CustomBreadCrumbs";
import {ApiServiceEvents} from "../api-services/events";
import TranslateIcon from '@material-ui/icons/Translate';
import LanguageIcon from '@material-ui/icons/Language';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import {Helmet} from "react-helmet";
import {t,Trans} from "@lingui/macro";
import _ from "lodash";
import {withI18n} from "@lingui/react";
import Tooltip from '@material-ui/core/Tooltip';

function Copyright(props) {
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <a href="https://www.gob.mx/sectur/">
                SECTUR
            </a>{' '}
            {new Date().getFullYear()}
            {'.'} v {props.version}
        </Typography>
    );
}

const drawerWidth = 240;

const useStyles = makeStyles(theme => ({
    root: {
        display: 'flex',
        flexDirection: 'column',
        minHeight: '100vh',
    },
    toolbar: {
        paddingRight: 24, // keep right padding when drawer closed
    },
    toolbarIcon: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'flex-end',
        padding: '0 8px',
        ...theme.mixins.toolbar,
    },
    footer: {
        padding: theme.spacing(2),
        marginTop: 'auto'
    },
    appBar: {
        zIndex: theme.zIndex.drawer + 1,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
    },
    appBarShift: {
        marginLeft: drawerWidth,
        width: `calc(100% - ${drawerWidth}px)`,
        transition: theme.transitions.create(['width', 'margin'], {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    menuButton: {
        marginRight: 36,
    },
    menuButtonHidden: {
        display: 'none',
    },
    title: {
        flexGrow: 1,
    },
    drawerPaper: {
        position: 'relative',
        whiteSpace: 'nowrap',
        width: drawerWidth,
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.enteringScreen,
        }),
    },
    drawerPaperClose: {
        overflowX: 'hidden',
        transition: theme.transitions.create('width', {
            easing: theme.transitions.easing.sharp,
            duration: theme.transitions.duration.leavingScreen,
        }),
        width: theme.spacing(7),
        [theme.breakpoints.up('sm')]: {
            width: theme.spacing(9),
        },
    },
    appBarSpacer: theme.mixins.toolbar,
    content: {
        flexGrow: 1,
        height: '100vh',
        overflow: 'auto',
    },
    main:{
        marginBottom: theme.spacing(2),
    },
    container: {
        paddingTop: theme.spacing(4),
        paddingBottom: theme.spacing(4),
    },
    paperContent:{
        padding: theme.spacing(1, 2),
    },
    paper: {
        padding: theme.spacing(2),
        display: 'flex',
        overflow: 'auto',
        flexDirection: 'column',
    },
    fixedHeight: {
        height: 240,
    },
}));

const AdminEventLayoutContent = (props) => {

    const classes = useStyles();

    const ctx = useContext(GlobalContext);

    const eventInfo = ctx.eventInfo;
    const idiomas = ctx.langs;

    const eventTitle = eventInfo ? eventInfo.name : '';

    const menuId = 'primary-search-account-menu';
    const userStr = localStorage.getItem('user');
    const user = JSON.parse(userStr);
    // const userName = `${user.firstName} ${user.lastName}`;
    const userName = `${user.firstName}`;
    // console.log('user', userName);
    const availableLangActives = _.filter(idiomas, {availableLang: {isActive: true}});

    return (
        <div className={classes.root}>
            <CssBaseline />
            <Helmet defaultTitle={eventInfo?eventInfo.name:this.props.i18n._(t`Panel configuración de evento`)}/>
            <AppBar position="absolute" style={eventInfo?{background:eventInfo.color}:{}} className={classes.appBar}>
                <Toolbar className={classes.toolbar}>
                    <Typography component="h1" variant="h6" color="inherit" noWrap className={classes.title}>
                        <Trans>Panel de configuración del evento</Trans> - {eventTitle}
                    </Typography>
                    <Button
                        edge="end"
                        aria-label="username"
                        aria-controls={menuId}
                        aria-haspopup="true"
                        onClick={props.handleMyProfile}
                        color="inherit"
                    >
                        <AccountCircleIcon />  {userName}
                    </Button>
                    <Button
                        edge="end"
                        aria-label="Language user"
                        aria-controls={menuId}
                        aria-haspopup="true"
                        onClick={props.onHandleClickIdiom}
                        color="inherit"
                    >
                        <LanguageIcon />  {ctx.language}
                    </Button>
                        <Tooltip title={<Trans>Cerrar Sesión</Trans>}>
                        <IconButton color="inherit" onClick={props.handleLogOut}>
                                <MeetingRoomIcon />
                        </IconButton>
                    </Tooltip>
                </Toolbar>
            </AppBar>
            <Container component="main" className={classes.main}>
                <div className={classes.appBarSpacer} />

                <Container maxWidth="lg" className={classes.container}>

                    <Grid container spacing={3}>
                        <Grid item xs={12}>
                            <Paper elevation={0} className={classes.paperContent}>
                                <CustomBreadCrumbs homelink={props.homelink} breadcrumbs={props.breadcrumbs}/>
                            </Paper>
                        </Grid>
                        {props.children}
                    </Grid>
                </Container>
            </Container>
            <footer className={classes.footer}>
                <Container maxWidth="sm">
                    <Copyright version={ctx.version} />
                </Container>
            </footer>


            <Menu
                id="simple-menu"
                anchorEl={props.anchorEl}
                keepMounted
                open={Boolean(props.anchorEl)}
                onClose={props.handleCloseMenu}
            >
                {
                    availableLangActives && availableLangActives.length > 0 ? idiomas.map((lang, index) => {
                        if(lang.availableLang.isActive)
                        return (<MenuItem key={index} onClick={props.handleChangeIdiom.bind(this,lang.availableLang.code)}>{lang.availableLang.name}</MenuItem>)
                    }) : (
                        <MenuItem onClick={()=>{}}>{ctx.language}</MenuItem>
                    )
                }
            </Menu>

        </div>
    );
};

let apiEvent = new ApiServiceEvents();

class AdminEventLayout extends React.Component{
    static contextType = GlobalContext;

    state = {
        redirectToLogOut: false,
        redirectToMyProfile: false,
        urlLogIn: '',
        anchorEl:null,
        setAnchorEl: null
    };

    handleMyProfile= () => {
        // console.log(localStorage.getItem('user'));
        let ctx = this.context;
        // console.log(ctx);
        let urlRedirect = `/${ctx.slug}/admin/myprofile/`;
        this.setState({redirectToMyProfile: true, urlProfile: urlRedirect})
        }

    handleLogOut = () => {
        // console.log("Event")
        let ctx = this.context;
        localStorage.removeItem('jwt');
        let urlRedirect = `/${ctx.slug}/security/signin/`;
        this.setState({redirectToLogOut: true, urlLogIn: urlRedirect})
    };

    handleClickIdiom = event => {
        this.setState({
            anchorEl:event.currentTarget
        })
    };

    handleCloseMenu = () => {
        this.setState({
            anchorEl:null
        })
    };


    testTokenValidOrExpired = () => {
        let ctx = this.context;        
        // if (!ctx.tokenInvalidOrExpired) {
        // apiEvent.getEventUserDetail(ctx.eventInfo._id, ctx.slug).then(
        //     (res) => {
        //         // console.log('si se pudo');
        //     }
        // ).catch(
        //     (err) => {
        //         if(err.response) {
        //             ctx.tokenInvalidOrExpired = !!(err.response.status && err.response.status === 498);
        //             if (ctx.tokenInvalidOrExpired)
        //                 ctx.openNotification('Sesión expirada');
        //         }else{
        //             if(err.message === 'Network Error'){
        //                 ctx.openNotification('Error de red');
        //             }
        //         }
        //     }
        // )
        // }
    };


    componentDidMount(){
        let ctx = this.context;
        let slug = this.props.match.params.slug ? this.props.match.params.slug : '';
        this.setState({urlLogIn:`/${slug}/security/signin/`});
        // console.log('langs', ctx.langs);
        ctx.setEventSlug(slug);
        this.testTokenValidOrExpired();        
    }


    handleChangeIdiom=(lang_code)=>{
        this.setState({
            anchorEl:null
        });
        let ctx = this.context;

        if(ctx.eventInfo){
            ctx.setLanguage(lang_code);
            // apiEvent.changeUserIdiom(ctx.eventInfo._id,lang_code).then((res)=>{
            //     console.log('Se cambio lan desde el back ')
            // }).catch((err)=>{
            //     console.log(err)
            // })
        }

    };

    render() {
        let ctx = this.context;
        if (this.state.redirectToLogOut || ctx.tokenInvalidOrExpired)return <Redirect push to={this.state.urlLogIn} />;
        if (this.state.redirectToMyProfile )return <Redirect push to={this.state.urlProfile} />;

        return (
            <AdminEventLayoutContent
                handleCloseMenu={this.handleCloseMenu}
                onHandleClickIdiom={this.handleClickIdiom}
                homelink={`/${ctx.slug}/admin/`}
                handleChangeIdiom={this.handleChangeIdiom}
                breadcrumbs={this.props.breadcrumbs}
                title={this.props.title}
                anchorEl={this.state.anchorEl}
                handleLogOut={this.handleLogOut}
                handleMyProfile={this.handleMyProfile}
                >
                
                {this.props.children}
            </AdminEventLayoutContent>
        )
    }
}

AdminEventLayout.propTypes = {
    breadcrumbs: PropTypes.array,
};


// Especifica los valores por defecto de props:
AdminEventLayout.defaultProps = {
    breadcrumbs: []
};

export default withI18n() (withRouter(AdminEventLayout))