import React, {Component} from 'react';
import Grid from "@material-ui/core/Grid";
import AdminEventLayout from "../../../layouts/AdminEventLayout";
import { Trans } from '@lingui/macro';
import GlobalContext from "../../../GlobalContext";
import {Paper} from "@material-ui/core";
import Typography from "@material-ui/core/Typography";
import _ from "lodash";
import {ApiServiceEvents} from "../../../api-services/events";
import moment from 'moment'
import Avatar from "@material-ui/core/Avatar";
import Image from 'material-ui-image'

const apiEvent = new ApiServiceEvents();

class InfoEvent extends Component {

    static contextType = GlobalContext;

    state = {
        loadingEventInfo:false,
        eventInfo:null,
        breadcrumbs: [
            {
                name: <Trans>Información del evento</Trans>,
                active: true,
                link: '',
            }
        ],
    };


    componentDidMount() {
        this.getEventInfo();
    }

    /**
     * This function retrieves event info.
     */
    getEventInfo = () => {
        let ctx = this.context;
        this.setState({
            loadingEventInfo: true,
        });
        apiEvent.getEventInfo('', ctx.slug).then(
            (info) => {
                console.log('Evento data', info);
                this.setState({
                    eventInfo:info.event
                })
            }
        ).catch(
            (err) => {
                if (_.has(err, 'response.status') && err.response.status === 403) {
                    ctx.openPermissionNotification();
                }
                console.log('Error al consultar el detalle del evento', err);
            }
        ).finally(()=>{
            this.setState({
                loadingEventInfo: false,
            });
        });
    };

    render() {

        const ctx = this.context;
        const {eventInfo} = this.state;


        return (
            <AdminEventLayout title={<Trans>Información del evento</Trans>} breadcrumbs={this.state.breadcrumbs}>
                <Grid container>
                    {
                        eventInfo?<Grid item xs={12}>
                            <Paper style={{padding:'20px'}}>
                                <Image src={eventInfo.logo.value}  style={{
                                    margin: 10,
                                    width: 250,
                                    height: 150,
                                    paddingTop: 30
                                }} />
                                <Typography variant="h4" gutterBottom>
                                    {ctx.eventInfo.name}
                                </Typography>
                                <Typography variant="subtitle1" style={{textAlign:'justify'}} gutterBottom>
                                    {eventInfo.description}
                                </Typography>
                                <hr/>
                                <Typography variant="subtitle1" gutterBottom>
                                    <b> <Trans>Temporalidad</Trans></b>:  {moment(eventInfo.availability.startDate).format('DD/MM/YYYY, h:mm a')} - {moment(eventInfo.availability.endDate).format('DD/MM/YYYY, h:mm a')}
                                </Typography>
                                <Typography variant="subtitle1" gutterBottom>
                                    <b><Trans>Fecha de creación</Trans></b>:  {moment(eventInfo.createdAt).format('DD/MM/YYYY, h:mm a')}
                                </Typography>
                                <Typography variant="subtitle1" gutterBottom>
                                    <b> <Trans>Capacidad</Trans></b>:  {eventInfo.capacity}
                                </Typography>

                                <Typography variant="subtitle1" gutterBottom>
                                    <b> <Trans>Lugar del evento</Trans></b>:  {eventInfo.location}
                                </Typography>

                            </Paper>
                        </Grid>:'cargando...'
                    }


                </Grid>
            </AdminEventLayout>
        );
    }
}

export default InfoEvent;