import React, {Component} from 'react';
import moment from "moment";
import _ from "lodash";
import {FORM_ERROR} from "final-form";
import {Grid, withStyles} from "@material-ui/core";
import SubEventForm from "../../../../components/events/admin/sub-events/SubEventForm";
import AdminEventLayout from "../../../../layouts/AdminEventLayout";
import GlobalContext from "../../../../GlobalContext";
import {Redirect, withRouter} from "react-router-dom";
import {ApiServiceEvents} from "../../../../api-services/events";
import {t,Trans} from "@lingui/macro";
import {withI18n} from "@lingui/react";

const apiEvent = new ApiServiceEvents();

/**
 *
 * this view allow updating sub events
 * @class UpdateSubevent
 * @extends {Component}
 */
class UpdateSubevent extends Component {

    static contextType = GlobalContext;

    state = {
        groups: [],
        loading: false,
        redirect: false,
        initialValues: {
            name: '',
            address: '',
            place: '',
            requireQrCodeAccess: false,
            groups: [],
            isActive: true,
            isDeleted: false,
            _id: '',
            _idEvent: '',
            startDate: '',
            endDate: '',
            startTime: '',
            endTime: '',
        },
        request: {
            name: '',
            address: '',
            place: '',
            requireQrCodeAccess: false,
            groups: [],
            startTime: '',
            endTime: '',
        },
        breadCrumbs:[
            {
                name:<Trans>Lista de eventos alternos</Trans>,
                active:false,
                link:``
            },
            {
                name:<Trans>Actualizar eventos alternos</Trans>,
                active:true,
                link:``
            },
        ]
    };

    /**
     * Function to merge date and tiime
     * @param  {Object} dateObj
     * @param  {Object} timeObj
     * @returns {string} Date with format datetime
     */
    mergeDateAndTime = (dateObj, timeObj) => {
        let dateFromString = moment(dateObj).format();
        let fromHourString = moment(timeObj).format();
        let indexDateFrom = dateFromString.indexOf('T');
        let indexTimeFrom = fromHourString.indexOf('T');
        let dateFrom = dateFromString.slice(0, indexDateFrom);
        let hourFrom = fromHourString.slice(indexTimeFrom + 1, -1);
        return `${dateFrom}T${hourFrom}0`;
    };

    /**
     * Function for submit form subevent
     * @param  {Object} values
     */
    handleSubmit = async (values) => {
        const ctx = this.context;
        let {name, place, address, requireQrCodeAccess,
            startDate, startTime, endDate, endTime} = values;

        let start = this.mergeDateAndTime(startDate, startTime);
        let end = this.mergeDateAndTime(endDate, endTime);

        // console.log('NEWWW', newDate);

        let groups = _.map(values.groups, function (x) {
            return {
                _idGroup: x
            }
        });

        let request = {
            _idAlternativeEvent: this.props.match.params.id,
            slug: ctx.slug,
            startTime: start,
            endTime: end,
            isActive: true,
            name, place, address,
            requireQrCodeAccess,
            groups,
        };
        let errors = {};
        request = _.merge(this.state.request, request);
        this.setState({loading: true});
        try {
            let res = await apiEvent.updateSubEvent(request);
            ctx.openSuccessNotification(<Trans>Evento alterno actualizado</Trans>);
            this.setState({redirect: true, loading:false});
        }
        catch (e) {
            if(e.response.message && typeof e.response.message === 'string') {
                errors = {[FORM_ERROR]: e.response.message};
            }

            if(e.response.data && e.response.data.message.message && typeof e.response.data.message.message === 'string') {
                errors = {[FORM_ERROR]: e.response.data.message.message};
            }

            if(e.response.status === 403){
                this.context.openPermissionNotification();
            }
        }
        this.setState({loading: false});
    };

    /**
     * Function to validate empty fields in the form
     * @param  {Object} values
     * @returns {Object} errors
     */
    validate = (values) => {
        const errors = {};
        if (!values.name) {
            errors.name = <Trans>Requerido</Trans>;
        }
        if (!values.startDate) {
            errors.startDate = <Trans>Requerido</Trans>;
        }
        if (!values.endDate) {
            errors.endDate = <Trans>Requerido</Trans>;
        }
        if (!values.startTime) {
            errors.startTime = <Trans>Requerido</Trans>;
        }
        if (!values.endTime) {
            errors.endTime = <Trans>Requerido</Trans>;
        }
        if (!values.place) {
            errors.place = <Trans>Requerido</Trans>;
        }
        if (!values.address) {
            errors.address = <Trans>Requerido</Trans>;
        }
        return errors;
    };

    /**
     * Function for load user profiles
     */
    loadRoles = () => {
        let ctx = this.context;
        apiEvent.getRoles(ctx.slug).then(
            (res) => {
                // console.log('Perfiles', res);
                this.setState({groups: res});
            }
        ).catch(
            (err) => {
                console.log('Error en la carga de perfiles', err)
            }
        );
    };

    /**
     * Function for load sub event details
     */
    getDetailInfo = () => {
        let ctx = this.context;
        let subEventId = this.props.match.params.id;
        apiEvent.getDetailSubEvent(subEventId).then(
            (res) => {

                let request = res;
                let breadCrumbs = [
                    {
                        name:<Trans>Lista de sub-eventos</Trans>,
                        active:false,
                        link:`/${ctx.slug}/admin/sub-event/list/`,
                    },
                    {
                        name:this.props.i18n._(t`Actualizar `) +' '+ res.name,
                        active:true,
                        link:``
                    },
                ];

                request.groups = _.map(request.groups, '_idGroup');
                request.startDate = request.startTime;
                request.endDate = request.endTime;
                this.setState({initialValues: request, breadCrumbs});
            }
        ).catch((err) => console.log('err', err))
    };

    componentDidMount() {
        this.loadRoles();
        this.getDetailInfo();
        let oldState = this.state;
        oldState.breadCrumbs = [
            {
                name:<Trans>Lista de sub-eventos</Trans>,
                active:false,
                link:`/${this.context.slug}/admin/sub-event/list/`,
            },
            {
                name:<Trans>Actualizar evento alterno</Trans>,
                active:true,
                link:``
            },
        ];
        this.setState(oldState);
    }

    render() {
        let ctx = this.context;
        if(this.state.redirect) return <Redirect push to={`/${ctx.slug}/admin/sub-event/list/`} />;
        return (
            <AdminEventLayout breadcrumbs={this.state.breadCrumbs} title={<Trans>Crear nuevo Evento alterno</Trans>}>
                <Grid container>
                    <Grid item xs={12}>
                        {/*<div style={{ padding: 16, margin: 'auto', maxWidth: 1000 }}>*/}
                        <SubEventForm
                            onSubmit={this.handleSubmit}
                            initialValues={this.state.initialValues}
                            validate={this.validate}
                            roles={this.state.groups}
                            loading={this.state.loading}
                            title={<Trans>Actualizar evento alterno</Trans>}
                            isUpd={true}
                        />
                        {/*</div>*/}
                    </Grid>

                </Grid>
            </AdminEventLayout>
        );
    }
}

export default withI18n()(withRouter(UpdateSubevent));